import { IpcService } from './../../common/ipc/services/ipc.service';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { FileOpResult, IFileSystem, ReadDirResult, ReadFileResult, WriteFileResult } from 'libs/flowchart-ts/flowchart-core/src/lib/models/fs-interface';

@Injectable({
  providedIn: 'root'
})
export class FileService implements IFileSystem {

  constructor(private http: HttpClient,
    private ipcService: IpcService) { }
  writeFile(path: string, content: string): Promise<WriteFileResult> {
    return new Promise<WriteFileResult>((resolve, reject) => {
      this.ipcService.writeFile(path, content).then(result => {
        if (result.code != 0) {
          reject(result.msg);
          return;
        }
        resolve({ success: true, msg: '' });
      });
    })
  }
  read(path: string): Promise<ReadFileResult> {
    return new Promise<ReadFileResult>((resolve, reject) => {
      this.ipcService.readFile(path).then(result => {
        resolve({ success: result.code === 0, msg: result.msg, data: result.data });
      })
    })
  }
  readDir(path: string): Promise<ReadDirResult> {
    return new Promise<ReadDirResult>((resolve, reject) => {
      this.ipcService.readDir(path).then(result => {
        if (result.code != 0) {
          reject(result.msg);
          return;
        }
        resolve({ success: result.code === 0, msg: result.msg, files: result.args });
      })
    })
  }

  exists(path: string): Promise<boolean> {
    return new Promise<boolean>((resolve, reject) => {
      this.ipcService.exists(path).then(exists => {
        resolve(exists);
      })
    })
  }

  createDir(path: string): Promise<FileOpResult> {
    return new Promise<FileOpResult>((resolve, reject) => {
      this.ipcService.createDir(path).then(result => {
        resolve({ success: result.code === 0, msg: result.msg })
      })
    })
  }

  // tslint:disable-next-line:ban-types
  readFile(callback: Function) {
    const input = document.createElement('input');
    input.style.position = 'fixed';
    input.style.left = '0';
    input.style.top = '0';
    input.style.opacity = '0';
    input.type = 'file';
    input.onchange = (e: any) => {
      const file = e.target.files[0];
      const fileReader = new FileReader();
      fileReader.readAsText(file);
      fileReader.onload = () => {
        callback(fileReader.result);
      };
    };
    document.body.appendChild(input);
    input.click();
    document.body.removeChild(input);
  }

  deleteFile(path: string): Promise<FileOpResult> {
    return new Promise<FileOpResult>((resolve, reject) => {
      this.ipcService.deleteFile(path).then(result => {
        resolve({ success: result.code === 0, msg: result.msg })
      })
    })
  }
  renameFile(path: string, newName: string): Promise<FileOpResult> {
    return new Promise<FileOpResult>((resolve, reject) => {
      this.ipcService.renameFile(path, newName).then(result => {
        resolve({ success: result.code === 0, msg: result.msg })
      });
    })
  }

  GET(url: string) {
    this.http.get(url).subscribe(data => {
      return url;
    });
  }

}
