import { ToastLevel } from './../toast.constants';
import { Component, Inject, Input, OnInit } from '@angular/core';
import { MAT_SNACK_BAR_DATA } from '@angular/material';

@Component({
  selector: 'app-common-toast',
  templateUrl: './common-toast.component.html',
  styleUrls: ['./common-toast.component.css']
})
export class CommonToastComponent implements OnInit {

  msg: string;
  level: ToastLevel;
  constructor(@Inject(MAT_SNACK_BAR_DATA) private data: any) { }

  ngOnInit() {
    this.msg = this.data.msg;
    this.level = this.data.level;
  }

}
