import { APP_HTTP_RESULT_CODE } from './../../constants/http/http.constants';
import { AutoComplete, CompletionItem } from 'libs/flowchart-ts/flowchart-generator/src/lib/auto-complete';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HttpResult } from 'src/app/common/http/http.result.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';

@Injectable({
  providedIn: 'root'
})
export class CodeHintService {

  private static autoCompletion: AutoComplete;
  private static currentChart: Chart;
  constructor(private http: HttpClient) { }

  async getHintList(chart: Chart, text: string, index: number) {
    // const url = 'api/codehint/hint';
    // const result =
    //   await this.http.post<HttpResult<CodeHintItem[]>>(url, { project: chart.project, chart: chart.uid, text, cursor: index }).toPromise();
    // return result;
    if (!CodeHintService.autoCompletion || !CodeHintService.currentChart || chart.uid !== CodeHintService.currentChart.uid) {
      CodeHintService.autoCompletion = new AutoComplete(chart);
      CodeHintService.currentChart = chart;
    }
    const list = CodeHintService.autoCompletion.prepareCompletionItems(text, index);
    return new HttpResult<CompletionItem[]>(APP_HTTP_RESULT_CODE.OK, '', list);

  }
}
