import { Group } from 'libs/flowchart-ts/flowchart-core/src/lib/models/group.model';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { IChartOpArgs } from './chart.op.arguments.interface';
export class CreateNodeArgs implements IChartOpArgs {
    project: string;
    chart: string;
    // node object json string
    node: ChartNode;
    parent?: ChartNode;
    group?: Group;

    constructor(project: string, chart: string, node: ChartNode) {
        this.project = project;
        this.chart = chart;
        this.node = node;
    }
}
