import { COMMAND_NAME } from 'src/app/constants/command/command.constants';
import { ExecuteResultCode } from './../../constants/command/command.constants';
import { Chart } from "libs/flowchart-ts/flowchart-core/src/lib/models/chart.model";
import { Command, CommandExecuteResult } from "./command.model";
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';

export class CreateChartCommand extends Command {
    readonly name: string = COMMAND_NAME.CREATE_CHART;
    project: Project;
    chart: Chart;
    private index: number;

    constructor(project: Project, chart: Chart) {
        super();
        this.project = project;
        this.chart = chart;
    }
    execute(): CommandExecuteResult {
        this.index = this.project.charts.length;
        this.project.addChart(this.chart);
        return new CommandExecuteResult(0, '');
    }

    revert(): CommandExecuteResult {

        this.project.removeChart(this.chart);
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}

export class UpdateChartCommand extends Command {
    readonly name: string = COMMAND_NAME.UDPATE_CHART;
    chart: Chart;
    updateKeyValues: Map<string, any>;
    prevKeyValues
    constructor(chart: Chart, kvPairs: Map<string, any>) {
        super();
        this.chart = chart;
        this.updateKeyValues = kvPairs;
        this.prevKeyValues = new Map<string, any>();
    }

    execute(): CommandExecuteResult {
        this.updateKeyValues.forEach((v, k) => {
            this.prevKeyValues.set(k, this.chart[k]);
            this.chart[k] = v;
            if (k === 'name') {
                this.prevKeyValues.set('path', this.chart.path);
                this.chart.path = this.chart.path.substr(0, this.chart.path.lastIndexOf('.') + 1) + v;
            }
        });
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }

    revert(): CommandExecuteResult {
        this.prevKeyValues.forEach((v, k) => {
            this.chart[k] = v;
        })
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}

export class RemoveChartCommand extends Command {
    readonly name: string = COMMAND_NAME.REMOVE_CHART;
    project: Project;
    chart: Chart;

    constructor(project: Project, chart: Chart) {
        super();
        this.project = project;
        this.chart = chart;
    }

    execute(): CommandExecuteResult {
        const project = this.chart.project;
        project.removeChart(this.chart);
        return new CommandExecuteResult(0, '');
    }

    revert(): CommandExecuteResult {

        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}