import { Directive, ElementRef, EventEmitter, HostListener, Output } from '@angular/core';

@Directive({
  selector: '[clickOutside]'
})
export class ClickOutsideDirective {
  constructor(private elementRef: ElementRef) { }

  @Output()
  public clickOutside = new EventEmitter();

  @HostListener('document:click', ['$event.target'])
  public onClick(targetElement) {
    const clickInside = this.elementRef.nativeElement.contains(targetElement);
    if (!clickInside) {
      this.clickOutside.emit(null);
    }
  }

  @HostListener('document:contextmenu', ['$event.target'])
  public onContextMenu(targetElement) {
    const contextmenuInside = this.elementRef.nativeElement.contains(targetElement);
    if (!contextmenuInside) {
      this.clickOutside.emit(null);
    }
  }
}
