import { createSelector } from '@ngrx/store';
import { getFlowChartState, FlowchartState } from '../../reducers';
import * as fromRecentProjectsList from '../../reducers/projects/recent-projects-list.reducer';

export const getRecentProjectsListState = createSelector(
    getFlowChartState,
    (state: FlowchartState) => state.recentProjecetsList
);

export const getAllRecentProjectsList = createSelector(getRecentProjectsListState, fromRecentProjectsList.getRecentProjectsList);

export const getProjectsLoaded = createSelector(
    getRecentProjectsListState,
    fromRecentProjectsList.getRecentProjectsListLoaded
);

export const getProjectsLoading = createSelector(
    getRecentProjectsListState,
    fromRecentProjectsList.getRecentProjectsListLoading
);
