export class StringUtils {
    static isNullOrWhiteSpace(...strs: string[]) {
        if (strs) {
            for (const str of strs) {
                if (!str || str.trim() === '') {
                    return true;
                }
                // const newStr = str.replace(/[ ]/g, '');
                // if (newStr === '') {
                //     return true;
                // }
            }
            return false;
        }
        return true;
    }
}
