import { MatMenuTrigger } from '@angular/material/menu';
import { ChartData } from 'src/app/core/models/chart-data.model';
import { Store } from '@ngrx/store';
import { Component, Input, OnInit, ViewChild, OnDestroy, SimpleChanges } from '@angular/core';
import { MenuTarget, CommonMenuService } from 'src/app/services/chart/common-menu.service';
import { Subscription } from 'rxjs';
import { Transform } from 'src/app/models/canvas/Canvas';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';
import { Connector, ConnectorType, ShapeType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { Group } from 'libs/flowchart-ts/flowchart-core/src/lib/models/group.model';
import { NodeViewType } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.enums';


@Component({
  selector: 'app-menu-bar',
  templateUrl: './menu-bar.component.html',
  styleUrls: ['./menu-bar.component.css']
})
export class MenuBarComponent implements OnInit, OnDestroy {

  @Input() chart: Chart;
  @Input() transform: Transform;
  @Input() offsetX = 0;
  @Input() offsetY = 0;
  @ViewChild('subNodeMenuBtn', { read: MatMenuTrigger, static: false }) subNodeMenuTrigger: MatMenuTrigger;
  @ViewChild('changeNodeTypeMenuBtn', { read: MatMenuTrigger, static: false }) changeNodeTypeMenuTrigger: MatMenuTrigger;
  initTransform: Transform = new Transform(0, 0, 1);
  menuRootTransform: Transform = new Transform(0, 0, 1);
  rect: Rect;
  selectedItemsSubscription: Subscription;
  selectedItems: ChartData[];
  selectedNodes: ChartNode[];
  selectedConnectors: Connector[];
  selectedGroups: ChartNode[];
  childComponent;
  isConnector = false;
  hasGroups = false;
  hasNodes = false;

  menuTarget: MenuTarget;
  constructor(private store: Store,
    private menuService: CommonMenuService) { }

  ngOnInit() {
    this.initData();
    this.initDataObservation();
  }

  ngOnDestroy() {
    this.selectedItemsSubscription.unsubscribe();
  }

  ngOnChanges(changes: SimpleChanges): void {
    // console.log(changes.transform, changes.chart);
    if (changes.chart) {
      console.log('"???????????????????');
      // this.initTransform = new Transform(0, 0, 1);
      this.menuRootTransform = new Transform(0, 0, this.transform.scale);
      // this.initTransform = changes.transform.currentValue;
      // this.menuRootTransform = new Transform(
      //   changes.transform.currentValue.translateX - this.initTransform.translateX,
      //   changes.transform.currentValue.translateY - this.initTransform.translateY,
      //   1);
    }
    if (changes.transform && changes.transform.currentValue) {
      if (changes.transform.firstChange) {
        // console.log('first');
        setTimeout(() => {
          this.initTransform = changes.transform.currentValue;
        }, 10);
      } else {
        // if (changes.transform.currentValue.scale !== changes.transform.previousValue.scale) {
        //   this.menuRootTransform = new Transform(changes.transform.currentValue.translateX * this.transform.scale - this.initTransform.translateX
        //     , 0, this.transform.scale);
        //   return;
        // }
        this.menuRootTransform = new Transform(
          (changes.transform.currentValue.translateX * this.transform.scale - this.initTransform.translateX),
          (changes.transform.currentValue.translateY * this.transform.scale - this.initTransform.translateY),
          this.transform.scale);
        // console.log(this.menuRootTransform.translateX, this.transform.scale);
        // this.initTransform = new Transform(
        //   this.initTransform.translateX + this.menuRootTransform.translateX * this.transform.scale,
        //   this.initTransform.translateY + this.menuRootTransform.translateY * this.transform.scale, this.transform.scale);
        // this.menuRootTransform = new Transform(0, 0, this.transform.scale);

      }
    }
  }

  initData() {
    this.rect = new Rect();
    this.rect.x = 0;
    this.rect.y = 0;
  }

  initDataObservation() {
    this.selectedItemsSubscription = this.menuService.selectedItems.subscribe(items => {
      this.selectedItems = items;
      this.hasGroups = this.menuService.hasGroups;
      this.hasNodes = this.menuService.hasNodes;
      this.menuTarget = this.menuService.menuTarget;
      this.update();
    });
  }

  update() {
    this.initTransform = new Transform(
      this.initTransform.translateX + this.menuRootTransform.translateX,
      this.initTransform.translateY + this.menuRootTransform.translateY, this.transform.scale);
    this.menuRootTransform = new Transform(0, 0, this.transform.scale);
    // this.initTransform = changes.transform.currentValue;

    if (this.selectedItems === null || this.selectedItems.length === 0) {
      return;
    }
    if (this.selectedItems.length === 1) {
      const item = this.selectedItems[0];
      if (item instanceof ChartNode) {
        const nodeEle = document.getElementById(item.uid);
        if (nodeEle) {
          const nodeRect = nodeEle.getBoundingClientRect();
          this.rect = new Rect();
          this.rect.x = nodeRect.left;
          this.rect.y = nodeRect.top - 60;
          this.rect.w = item.rect.w + 20;
          this.rect.h = item.rect.h + 20;
        }
      } else if (item instanceof Connector) {
        this.menuTarget = MenuTarget.ConnectorOnly;
        const conn = document.getElementById(`connector_${item.start.uid}_${item.end.uid}`);
        const conRect = conn.getBoundingClientRect();
        this.rect = new Rect();
        this.rect.x = conRect.left;
        this.rect.y = conRect.top - 60;
      } else if (item instanceof Group) {
      }
    } else {
      this.menuTarget = MenuTarget.Complex;
      let left = Number.POSITIVE_INFINITY;
      let top = Number.POSITIVE_INFINITY;
      let leftNode, topNode;
      let right = Number.NEGATIVE_INFINITY;
      let bottom = Number.NEGATIVE_INFINITY;
      this.selectedItems.forEach(item => {
        if (item instanceof ChartNode) {
          const nodeEle = document.getElementById(item.uid);
          if (nodeEle) {
            const rect = nodeEle.getBoundingClientRect();
            left = Math.min(left, rect.left);
            if (item.rect.x === left) {
              leftNode = item;
            }
            top = Math.min(top, rect.top - 60);
            if (item.rect.y === top) {
              topNode = item;
            }
            right = Math.max(right, rect.right);
            bottom = Math.max(bottom, rect.right);
          }
        } else if (item instanceof Group) {
          const groupEle = document.getElementById(`group_${item.nodes[0].uid}`);
          if (groupEle) {
            const rect = groupEle.getBoundingClientRect();
            left = Math.min(rect.left, left);
            item.remarks && item.remarks.length > 0 ? top = Math.min(rect.top - 40, top) : top = Math.min(rect.top - 60, top);
            top = Math.min(rect.top - 40, top);
            right = Math.max(right, rect.right);
            bottom = Math.max(bottom, rect.bottom);
          }
        }
      });
      this.rect.x = left;
      this.rect.y = top;

    }

  }


  onDeleteClick($event) {
    $event.stopPropagation();
    this.menuService.delete();
  }

  onCutClick($event) {
    $event.stopPropagation();
    this.menuService.copy();
    this.menuService.delete();
  }

  onCopyClick($event) {
    $event.stopPropagation();
    this.menuService.copy();
  }

  onAddChildNodeClick($event, viewType: NodeViewType) {
    $event.stopPropagation();
    this.subNodeMenuTrigger.closeMenu();
    this.menuService.addChild(viewType);
  }

  onChangeNodeTypeClick($event, viewType: NodeViewType) {
    $event.stopPropagation();
    this.changeNodeTypeMenuTrigger.closeMenu();
    // this.menuService.changeNodeViewType(viewType);
  }

  onChangeConnectorTypeClick($event, type: ConnectorType) {
    $event.stopPropagation();
    this.menuService.changeConnectorType(type);
  }

  onChangeConnectorShapeClick($event, shape: ShapeType) {
    $event.stopPropagation();
    this.menuService.changeConnectorShape(shape);
  }

  onAddGroupClick($event) {
    $event.stopPropagation();
    this.menuService.addGroup();
  }

  onDeleteGroupClick($event) {
    $event.stopPropagation();
    this.menuService.deleteGroup();
  }

}
