import { ConnectorPathService } from './../../../services/chart/connector-path.service';
import { Component, OnInit, Input, Output, EventEmitter, OnChanges, ViewChild, SimpleChanges, ElementRef, ChangeDetectionStrategy } from '@angular/core';
import { Point } from 'src/app/models/point.model';
import { MatMenuTrigger } from '@angular/material/menu';
import { GroupService } from 'src/app/services/chart/group.service';
import { Connector } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { Group, LayoutType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/group.model';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';

@Component({
  // tslint:disable-next-line:component-selector
  selector: '[app-connector]',
  templateUrl: './connector.component.html',
  styleUrls: ['./connector.component.css'],
  // changeDetection: ChangeDetectionStrategy.OnPush
})
export class ConnectorComponent implements OnInit, OnChanges {
  @Input() delta: Point = new Point(0, 0);

  @Input() connector: Connector;
  @Input() isSelected: boolean;
  @Input() selectedGroups: Group[];
  @Output() connectorChange: EventEmitter<any> = new EventEmitter();
  @ViewChild('connector', { static: false }) connectorMenu: MatMenuTrigger;
  @ViewChild('svg', { static: false }) svg: ElementRef;
  startNodeX;
  startNodeY;
  endNodeX;
  endNodeY;
  startPoint: Point;
  endPoint: Point;
  nodeGroupMap: Map<string, Group> = new Map<string, Group>();

  @Output() outputConnector: EventEmitter<Connector> = new EventEmitter();
  @Output() updateConnector = new EventEmitter<Connector>();
  @Output() connectorClick: EventEmitter<any> = new EventEmitter<any>();

  connectorTypeToColor = ['red', 'green', '#00a6ff'];

  private _path: string;
  get path(): string {
    if (!this.connector.path) {
      this.connector.path = this.getLine();
    }
    return this.connector.path;
  }

  set path(value: string) {
    this.connector.path = value;
  }
  // 正在被点击的元素，用来判断是否要显示连线的颜色菜单
  constructor(
    private groupService: GroupService,
    private connectorPathService: ConnectorPathService
  ) { }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.connector) {
      this.initLineAttribute();
    }
  }

  generatePoint() {
    // if (this.delta && (this.delta.x != 0 && this.delta.y != 0)) {
    this.startPoint = new Point(this.startNodeX, this.startNodeY);
    this.endPoint = new Point(this.endNodeX, this.endNodeY);
    // } else {
    // }
  }

  move(delta, isStartSelected: boolean, isEndSelected: boolean) {
    if (isStartSelected) {
      this.startNodeX = this.connector.start.rect.cx - delta.x;
      this.startNodeY = this.connector.start.rect.y + this.connector.start.rect.h - delta.y;
    }

    if (isEndSelected) {
      this.endNodeX = this.connector.end.rect.cx - delta.x;
      this.endNodeY = this.connector.end.rect.y - delta.y;
    }

    this.generatePoint();
    this.path = this.getLine();
  }

  update() {
    this.initLineAttribute();
  }


  ngOnInit() {
    this.initLineAttribute();
    this.initNodeGroupMap();
  }

  initNodeGroupMap() {
    this.nodeGroupMap = this.groupService.getNodeGroupMap(this.connector.chart);
  }

  // 初始化连线属性
  initLineAttribute() {
    this.startNodeX = this.connector.start.rect.cx;
    this.startNodeY = this.connector.start.rect.y + this.connector.start.rect.h;
    this.endNodeX = this.connector.end.rect.cx;
    this.endNodeY = this.connector.end.rect.y;
    this.generatePoint();
    this.path = this.getLine();
  }

  mousedownLine($event) {
    $event.preventDefault();
    $event.stopPropagation();
  }

  onConnectorClick($event) {
    $event.preventDefault();
    $event.stopPropagation();
    this.connectorClick.emit($event);
  }

  getLine() {
    this.generatePoint();
    return this.connectorPathService.getConnectorPath(this.connector, this.startPoint, this.endPoint);
  }

  changeConnector(typeIndex: number) {
    this.connector.type = typeIndex;
  }

  // 判断节点是否可以移动
  // 移动的节点不在自动布局的group中或者有group被选中(拖动)
  judgeNodeMoveable(node: ChartNode): boolean {
    const moveable = (!(this.nodeGroupMap.has(node.uid) && this.nodeGroupMap.get(node.uid).layout === LayoutType.AUTO))
      || (this.selectedGroups && this.selectedGroups.length > 0);
    return moveable;
  }
}
