import { ExecuteResultCode } from './../../../constants/command/command.constants';
import { Component, OnInit, Input, ViewChild, OnChanges, SimpleChanges, EventEmitter, Output, SimpleChange, ViewChildren, QueryList } from '@angular/core';
import { GroupService } from 'src/app/services/chart/group.service';
import { Store } from '@ngrx/store';
import * as fromStore from '../../../store';
import { Point } from 'src/app/models/point.model';
import { CanvasService } from 'src/app/services/chart/canvas.service';
import { KEY_CODE } from 'src/app/constants/common/key.code.constants';
import { ElementRef } from '@angular/core';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { Group, LayoutType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/group.model';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';

@Component({
  // tslint:disable-next-line:component-selector
  selector: '[app-group]',
  templateUrl: './group.component.html',
  styleUrls: ['./group.component.css']
})
export class GroupComponent implements OnInit, OnChanges {

  constructor(
    private canvasService: CanvasService,
    private groupService: GroupService,
    private store: Store<fromStore.FlowchartState>) { }

  @ViewChild('text', { static: false }) text;
  @ViewChildren('groupRemarks')groupRemarks: QueryList<GroupComponent>;

  @Input() group: Group;
  @Input() chart: Chart;
  @Input() delta: Point;
  groupNotesEditing = false;
  @Output() layoutChange: EventEmitter<LayoutType> = new EventEmitter<LayoutType>();
  rect: Rect;
  isEditingName: boolean;
  mouseoverGroup = false;
  // mouseoutGroup = true;
  @ViewChild('nameInput', { static: false }) nameInput: ElementRef;

  @Input() isSelected: boolean;

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.group) {
      this.initGroup();
    }
  }

  ngOnInit() {
    this.delta = new Point(0, 0);
    this.initGroup();
  }

  trackList(index: number) {
    return index;
  }

  // 思路，用新的nodes数组代替不可改变的this.group.groupNodes
  // 检测被拖拽的节点是否在group中，根据uid与delta，修改新的nodes坐标：以this.group.groupNodes中的节点位置为基准，与delta相加
  // 再利用输入型变量delta的改变，多次触发getFourSidesByGroup来重新定位group的位置信息
  initGroup() {
    if (!this.rect) {
      this.rect = this.groupService.getNodesBorderRect(this.group.nodes);
    }
  }

  autoLayout() {
    // this.layoutChange.emit(LayoutType.AUTO);
    if(this.group.layout === LayoutType.AUTO) {
      return;
    }
    this.group.layout = LayoutType.AUTO;
    const result = this.groupService.autoLayout(this.chart, this.group);
    if (result.code === ExecuteResultCode.OK) {
      this.rect = this.groupService.getNodesBorderRect(this.group.nodes);
    }
    this.layoutChange.emit(LayoutType.AUTO);
  }

  manualLayout() {
    // this.layoutChange.emit(LayoutType.MANUAL);
    if(this.group.layout === LayoutType.MANUAL) {
      return;
    }
    this.group.layout = LayoutType.MANUAL;
  }

  async groupMouseDown() {
    const existSelect = this.group.nodes.find(n => n.isSelected === true);
    if (!existSelect) {
      const currentChart = this.canvasService.getCurrentChart();
      this.canvasService.removeNodeSelection(await currentChart);
    }
  }

  onGroupNameDblClick($event) {
    this.isEditingName = true;
    setTimeout(() => {
      this.nameInput.nativeElement.value = this.group.name || 'untitled';
      this.nameInput.nativeElement.focus();
      this.nameInput.nativeElement.selectionStart = 0;
      this.nameInput.nativeElement.selectionEnd = this.nameInput.nativeElement.value.length;
    }, 0);
  }

  onNameInputKeyDown($event) {
    switch ($event.keyCode) {
      case KEY_CODE.ENTER:
        this.saveName($event.target.value);
        break;
    }
  }

  onNameInputBlur($event) {
    this.saveName($event.target.value);
  }

  saveName(name: string) {
    this.isEditingName = false;
    this.group.name = name;
  }

  move(offset) {
    this.rect.x += offset.x;
    this.rect.y += offset.y;
  }

  // updateRect改为整体处理selectedNodes，通过delta，更新newGroupNodes对应的node的坐标
  updateRect(nodes: ChartNode[], delta: Point) {
    const rects = [];
    const noChangesNodeRects = this.group.nodes.filter(n => !nodes.includes(n));
    let noChangeRects = [];
    if (noChangesNodeRects) {
      noChangeRects = noChangesNodeRects.map(n => n.rect);
    }
    nodes.forEach(n => {
      if (this.group.nodes.includes(n)) {
        const newRect = Rect.createWithLeftTopVertex(n.rect.x - delta.x, n.rect.y - delta.y, n.rect.w, n.rect.h);
        rects.push(newRect);
      }
    });

    this.rect = this.groupService.getRectsBorder(rects.concat(noChangeRects));
  }

  onGroupNotesValueChange(e, index: number) {
    this.group.remarks[index] = e;
    // const target = this.chart.groups.find(g => this.group.uid === g.uid);
    // target.remarks[index] = e;
  }

  onDeleteRemark(i) {
    this.group.remarks.splice(i, 1);
  }

  mousedownOnNotes($event) {
    $event.stopPropagation();
  }

  rightClickOnNotes($event) {
    console.log(this.group);
    console.log(this.chart.groups);
    $event.preventDefault();
    $event.stopPropagation();
  }

  mousemoveOnNotes($event) {
    $event.stopPropagation();
  }

}
