  initNodeText(viewType: NodeViewType) {
    switch (viewType) {
      case NodeViewType.COMMON:
        return '';
      case NodeViewType.STOP_NODE:
        return 'stop_node()';
      case NodeViewType.STOP_FLOW:
        return 'stop_flow()';
      case NodeViewType.WAIT:
        return 'wait()';
      case NodeViewType.WAITALL:
        return 'wait_all()';
      case NodeViewType.EXPRESS:
        return '';
      case NodeViewType.FUNCTION:
        return '';
      case NodeViewType.START:
        return 'Start';
      default:
        return 'new node';
    }
  }

  // 添加节点位置 = 初始位置 + 拖拽位移, 拖拽位移与scale有关
  appendNode(parentNode?: ChartNode, position?: Point, viewType: NodeViewType = NodeViewType.COMMON) {
    const text = this.initNodeText(viewType);
    // 确定位置
    let rect: Rect;
    const newNodewidth = this.nodesService.calculateNodeRenderWidth(text.length, viewType, text);
    if (position) {
      rect = Rect.createWithLeftTopVertex(position.x, position.y, newNodewidth, 38);
    } else {
      let cx: number;
      let cy: number;
      if (parentNode) {
        if (position) {
          cx = position.x
          cy = position.y;
        } else {
          cx = parentNode.rect.cx;
          cy = parentNode.rect.cy + 80;
        }
        rect = this.findIdleRect(Rect.createWithCenter(cx, cy, newNodewidth, 38));
      } else {
        const x = Number(this.menuLocation.x);
        const y = Number(this.menuLocation.y);
        rect = Rect.createWithLeftTopVertex(x, y, newNodewidth, 38);
      }
    }
    // 参数组装
    const newNode = new ChartNode();
    newNode.uid = UidUtils.generateUid();
    newNode.text = text;
    newNode.rect = rect;
    newNode.viewType = viewType;
    const args = new CreateNodeArgs(this.project.name, this.chart.uid, newNode);
    args.parent = parentNode;
    args.group = parentNode ? parentNode.group : null;
    this.nodesService.createNodes(this.chart, [args]).then(result => {
      if (result.code === ExecuteResultCode.OK) {
        this.expandCanvas([newNode]);
        this.editNode(newNode);
        this.judgeNodeInRect(newNode, this.transform, this.currentCanvasSize);
        if (parentNode.group) {
          const groupComp = this.groupComponent.find((gcmp, index) => gcmp.group === parentNode.group);
          groupComp.updateRect(this.selectedNodes, new Point(0, 0));
        }
      }
    })

    // const subscribeAppendNewNode = this.store.select(fromStore.getCreatedNewNodes).subscribe(nodes => {
    //   if (nodes) {
    //     // newNode是在本appendNode方法中添加的节点，与订阅到的新节点的uid进行比较
    //     // 如果一致，说明是通过appendNode方法添加的新节点，进入编辑状态；否则说明是通过粘贴等其他方法生成的新节点
    //     if (newNode.uid === nodes[0].uid) {
    //       // this.selectNodes(nodes);
    //       // 添加新节点后，判断是否需要扩张画布
    //       this.expandCanvas([newNode]);
    //       // 判断是否是通过撤销或重做生成的节点，若是，则不进入编辑状态
    //       if (nodes[0].source !== NodeSource.REDO_UNDO) {
    //         this.editNode(nodes[0]);
    //       }
    //       this.judgeNodeInRect(newNode, this.transform, this.currentCanvasSize);
    //     }
    //   }
    // });
    return newNode;
  }

  judgeNodeInRect(node: ChartNode, transform: Transform, size: Point) {
    if (node.rect.y + node.rect.h > transform.translateY + size.y / this.transform.scale) {
      this.transform.translateY = this.transform.translateY + 100;
      this.handleTransform();
      if (node.rect.y + node.rect.h > transform.translateY + size.y) {
        this.judgeNodeInRect(node, transform, size);
      }
    }
  }

  delete() {
    const startNode = this.selectedNodes.find(n => n.uid === UID_START);
    if (startNode) {
      this.outputService.error('选中节点中存在起始节点，不允许删除');
      return;
    }
    let connectors: Connector[] = this.selectedConnectors || [];
    if (this.selectedNodes) {
      const nodesId = this.selectedNodes.map(n => n.uid);
      connectors = this.chart.connectors
        .filter(conn => nodesId.includes(conn.start.uid) || nodesId.includes(conn.end.uid) || connectors.includes(conn));
    }
    // const conns = this.chart.connectors
    const items: { groups: Group[], connectors: Connector[], nodes: ChartNode[] } = {
      groups: this.selectedGroups, connectors, nodes: this.selectedNodes
    };
    this.commonMenuService.delete();
    if (this.selectedGroups.length === 0 && this.selectedNodes.length === 0 && connectors && connectors.length !== 0) {
      connectors.forEach(c => {
        this.layoutGroupCausedConnector(c.start.uid, c.end.uid);
      });
    }
    this.selectedNodes = [];
  }

  findChildren(parent: ChartNode) {
    if (this.chart && this.chart.connectors) {
      const conns = this.chart.connectors.filter(c => c.start.uid === parent.uid);
      const children = conns.map(c => c.end);
      return children;
    }
  }

  findIdleRect(rect: Rect) {
    let r = { ...rect };
    let overlapNode = this.chart.nodes.find(n => this.isOverLap(n.rect, r));
    while (overlapNode) {
      r = { ...rect, x: r.x + 40, cx: r.cx + 40 };
      overlapNode = this.chart.nodes.find(n => this.isOverLap(n.rect, r));
    }
    return r;
  }

  isOverLap(rect1: Rect, rect2: Rect) {
    if (Math.abs(rect1.cx - rect2.cx) < (rect1.w + rect2.w) / 2 && Math.abs(rect1.cy - rect2.cy) < (rect1.h + rect2.h) / 2) {
      return true;
    }
  }

  // 退出group
  removeNodeFromGroup() {
    console.log(this.chart.groups);
    // 记录退出前所在的group
    let changedGroups = this.selectedNodes.map(n => {
      return this.nodeGroupMap.get(n.uid);
    });
    this.store.dispatch(new fromStore.RemoveNodeFromGroup(this.selectedNodes));
    // 移除没有node的group
    this.chart.groups.forEach(g => {
      if (g.nodes.length === 0) {
        const items: { groups: Group[], connectors: Connector[], nodes: ChartNode[] } = { groups: [g], connectors: [], nodes: [] };
        this.store.dispatch(new fromStore.DeleteItems(items));
        changedGroups = changedGroups.filter(group => group.uid !== g.uid);
      }
    });
    // 对原group进行重新布局
    changedGroups.forEach(g => {
      if (g.nodes && g.nodes.length > 0 && g.layout === LayoutType.AUTO) {
        this.store.dispatch(new fromStore.LayoutGroup(g));
      }
    });
  }

