export class Mouse {
    mousedown: boolean;
    mousemove: boolean;
    mouseup: boolean;
    mousedrag: boolean;
    constructor(mousedown: boolean = false, mousemove: boolean = false, mouseup: boolean = true, mousedrag: boolean = false) {
        this.mousedown = mousedown;
        this.mousemove = mousemove;
        this.mouseup = mouseup;
        this.mousedrag = mousedrag;
    }
}
