import { DomSanitizer } from '@angular/platform-browser';
import { Pipe, PipeTransform } from '@angular/core';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { NodeTokenType } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.enums';

@Pipe({
  name: 'codeHighlight'
})
export class CodeHighlightPipe implements PipeTransform {

  constructor(private sanitizer: DomSanitizer) { }
  transform(text: string, ...args: any[]): any {
    const node = args[0];
    if (node.parseInfo && node.parseInfo.tokens) {
      let html = '';
      let renderedText = '';
      for (const token of node.parseInfo.tokens) {
        renderedText += token.text;
        switch (token.type) {
          case NodeTokenType.UNKOWN:
            html += token.text;
            break;
          case NodeTokenType.STRING:
            html += `<span style="color:purple">${token.text}</span>`;
            break;
          case NodeTokenType.NUMBER:
            html += `<span style="color:orange">${token.text}</span>`;
            break;
          case NodeTokenType.VARIABLE:
            html += `<span style="color:#6a9955">${token.text}</span>`;
            break;
          case NodeTokenType.NAME:
            html += `<span style="color:#68CEFE">${token.text}</span>`;
            break;
          case NodeTokenType.OPERATOR:
            html += `<span style="color:#ccc">${token.text}</span>`;
            break;
        }
      }

      if (renderedText !== node.text) {
        html += node.text.substr(renderedText.length);
      }
      return this.sanitizer.bypassSecurityTrustHtml(html);
    }
    return node.text;
  }

}
