import { deepCopy } from 'src/app/store/reducers/utilsFunction';
import { Store } from '@ngrx/store';
import { SimpleDialogBtnCode } from './../../../common/dialog/components/simple-dialog/simple-dialog.component';
import { TypeEditorComponent } from './../type-editor/type-editor.component';
import { PathService } from './../../../common/path-picker/path.service';
import { DialogService } from './../../../common/dialog/dialog.service';
import { slideIn } from './../../../common/anims/slide-in.anims';
import { CreateProject } from './../../../models/projects/project.folder.model';
import { Component, Input, OnInit } from '@angular/core';
import { SelectionModel } from '@angular/cdk/collections';
import { MatTableDataSource } from '@angular/material/table';
import { SimpleDialogBtnType } from 'src/app/common/dialog/components/simple-dialog/simple-dialog.component';
import * as fromStore from 'src/app/store/index';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/type.model';
import { Member } from 'libs/flowchart-ts/flowchart-core/src/lib/models/member.model';
import { Parameter } from 'libs/flowchart-ts/flowchart-core/src/lib/models/parameter.model';
@Component({
  selector: 'app-project-types-panel',
  templateUrl: './project-types-panel.component.html',
  styleUrls: ['./project-types-panel.component.css'],
  animations: [slideIn]
})
export class ProjectTypesPanelComponent implements OnInit {
  columns = ['select', 'name', 'description', 'members', 'operation'];
  @Input() project: CreateProject;

  dataSource = new MatTableDataSource<ChartType>();
  selection = new SelectionModel<ChartType>(true, []);

  types: ChartType[];
  editType: ChartType;
  constructor(
    private dialogService: DialogService,
    private pathService: PathService,
    private store: Store) { }

  ngOnInit() {
    this.initData();
  }

  initData() {
    this.dataSource.data = this.project.types;
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(tp?: ChartType): string {
    if (!tp) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(tp) ? 'deselect' : 'select'} row`;
  }


  //#region EventHandler
  onEditTypeClick($event, index: number) {
    this.dialogService.open(TypeEditorComponent, { type: this.project.types[index], editType: 'edit' }).subscribe(t => {
      if (t) {
        this.project.types[index] = t;
      }
    });
  }
  onAddTypeClick($event) {
    const newType = new ChartType();
    newType.name = '';
    this.dialogService.open(TypeEditorComponent, { type: newType, editType: 'new' }).subscribe(t => {
      if (t) {
        this.project.types.push(t);
      }
    });
  }

  onDeleteTypeClick($event, index: number) {
    this.dialogService.openSimple({ text: `是否确认删除Type[${this.project.types[index].name}]?`, btnType: SimpleDialogBtnType.YES_NO }).subscribe(btnCode => {
      if (btnCode === SimpleDialogBtnCode.CONFIRM) {
        this.project.types.splice(index, 1);
      }
    });
  }

  onImportFromJson($event) {
    this.pathService.showPathPicker({ path: 'D:\\02Repo\\flowcharts\\flowchart1203\\types', filter: { type: 'file', suffix: '.json' } }).subscribe(result => {
      this.pathService.getFileContent(result).then(content => {
        if (Array.isArray(content)) {

        } else {
          const newType = this.parseType(content);
          this.project.types.push(newType);
          this.dataSource.data = this.project.types;
        }
      }).catch(err => {

      });
    });
  }

  onRemoveMemberClick(type: ChartType, member: Member) {
    type.members.splice(type.members.indexOf(member), 1);
  }
  //#endregion

  private parseType(t: any) {
    const newType = new ChartType();
    newType.name = t.Name;
    newType.description = t.Description;
    newType.members = [];
    if (t.Members) {
      t.Members.forEach(m => {
        const member: Member = new Member();
        member.name = m.Name;
        member.memberType = m.member_type;
        member.type = m.type;

        if (m.Parameters) {
          member.parameters = [];
          m.Parameters.forEach(p => {
            const para = new Parameter();
            para.name = p.Name;
            para.type = p.type;
            member.parameters.push(para);
          });
        }
        newType.members.push(member);
      });
    }
    return newType;
  }

}
