export enum PathType {
    DRIVE = 0,
    DIR = 1,
    FILE = 2,
    ROOT = 99
}
export class PathInfo {
    name: string;
    path: string;
    parent: PathInfo;
    children: PathInfo[];
    type: PathType;

    static isDrive(path: string) {
        return /^[A-Z]\:\\$/g.test(path);
    }
}

export class PathDialogConfig {
    // 默认打开路径
    path?: string;
    filter?: { type?: 'path' | 'file'; prefix?: string, suffix?: string };
}
