import { CreateNodeArgs } from './../../models/request-arguments/node.op.arguments.model';
import { Injectable } from '@angular/core';
// import { Point } from 'src/app/canvas/models/point/point.types';
import { MessageService } from 'src/app/services/common/message.service';
// import { AppendNodeJson, RemoveNodeJson, ConnectorJson, DisConnectorJson } from 'src/app/canvas/models/canvas/HttpJson';
import { HttpClient } from '@angular/common/http';
import { Point } from '../../models/point.model';
import { Store } from '@ngrx/store';
import * as fromStore from '../../store';
import { CopyChartInformationSuccess, CreateConnectors } from '../../store';
import { CopyBuffer } from '../../models/buffer/copied-buffer';
import { UtilsService } from '../common/utils.service';
import { NodesService } from './nodes.service';
import { GroupService } from './group.service';
import { Transform } from 'src/app/models/canvas/Canvas';
import { UidUtils } from 'src/app/utils/uid.utils';
import { CreateConnectorArgs } from 'src/app/models/request-arguments/connector.op.args.model';
import { Connector } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { Group } from 'libs/flowchart-ts/flowchart-core/src/lib/models/group.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';

@Injectable({
  providedIn: 'root'
})
export class CanvasService {

  constructor(
    private messageService: MessageService,
    private http: HttpClient,
    private store: Store<fromStore.FlowchartState>,
    private utilsService: UtilsService,
    private nodeService: NodesService,
    private groupService: GroupService
  ) { }

  updateNode(newNode: ChartNode, local: boolean = true) {
    this.store.dispatch(new fromStore.UpdateNode(newNode));
  }

  getAllNodesCenter(nodes: ChartNode[]) {
    const allNodesRect = this.groupService.getNodesBorderRect(nodes);
    return new Point(allNodesRect.cx, allNodesRect.cy);
  }

  updateConnector(newConnector: Connector) {
    this.store.dispatch(new fromStore.UpdateConnector(newConnector));
  }

  updateGroup(newGroup: Group) {
    this.store.dispatch(new fromStore.UpdateGroup(newGroup));
  }

  updateChart(newChart: Chart) {
    this.store.dispatch(new fromStore.UpdateCurrentChart(newChart));
  }

  getTransformedCoordinate(coordinate: Point, transform: Transform) {
    return new Point(coordinate.x / transform.scale + transform.translateX, coordinate.y / transform.scale + transform.translateY);
  }

  // 判断一个点是否在一个rect内
  isInRect(point: Point, rect: Rect): boolean {
    if (point.x >= rect.x && point.x <= rect.x + rect.w && point.y >= rect.y && point.y <= rect.y + rect.h) {
      return true;
    }
    return false;
  }

  // 检查节点是否溢出画布, 返回适应节点的画布大小
  async asyncGetRectifiedCanvas(canvas: Rect): Promise<Rect> {
    const promise = new Promise<Rect>(resolve => {
      this.store.select(fromStore.getCurrentNodes).subscribe(data => {
        const nodesConatinerRect = this.groupService.getNodesBorderRect(data, 0);
        const x = Math.min(nodesConatinerRect.x, canvas.x);
        const y = Math.min(nodesConatinerRect.y, canvas.y);
        const newCanvas: Rect = Rect.createWithLeftTopVertex(x, y,
          Math.max(canvas.w, nodesConatinerRect.w),
          Math.max(canvas.h, nodesConatinerRect.h));
        resolve(newCanvas);
      });
    });
    return await promise;
  }

  // 返回节点的中心位置
  getCenterOfNode(node) {
    const x = node.rect.x + node.rect.w * 0.5;
    const y = node.rect.y + node.rect.h * 0.5;
    return new Point(x, y);
  }

  // canvasService封装异步获取当前chart
  async getCurrentChart() {
    const chart = new Promise<Chart>((resolve) => {
      this.store.select(fromStore.getCurrentChartDetails).subscribe(data => {
        resolve(data);
      });
    });
    return await chart;
  }

  removeNodeSelection(chart: Chart) {
    for (const item of chart.nodes) {
      if (item.isSelected) {
        item.isSelected = false;
      }
    }
  }

  removeConnectorEditing(chart: Chart) {

  }

  getSelectedNodesNum(nodes: ChartNode[]) {
    let selectedNum = 0;
    for (const item of nodes) {
      if (item.isSelected) {
        selectedNum++;
      }
    }
    return selectedNum;
  }

  copyNodes(chart: Chart, nodes: ChartNode[], groups?: Group[]) {
    let copiedConnectors = [];
    const copiedNodes = [];
    copiedConnectors = chart.connectors.filter(c => this.nodeService.isExistInNodeList(c.start, nodes) &&
      this.nodeService.isExistInNodeList(c.end, nodes) && c.start.uid !== '0' && c.end.uid !== '0');
    nodes.map(node => {
      if (node.uid === '0') { return; }
      const n = Object.assign({}, node);
      n.chart = null;
      n.copyId = node.uid;
      copiedNodes.push(n);
    });
    this.utilsService.deepCopy(copiedNodes);
    const copyBuffer = groups && groups.length === 0 ? new CopyBuffer(this.utilsService.deepCopy(copiedNodes), copiedConnectors) :
      new CopyBuffer(this.utilsService.deepCopy(copiedNodes), copiedConnectors, groups);
    this.store.dispatch(new CopyChartInformationSuccess(copyBuffer));
  }

  async pasteNodes(project: Project, chart: Chart, location?) {
    const nodesDetail = [];
    const promise = new Promise<CopyBuffer>((resolve) => {
      this.store.select(fromStore.getCopyChartDetails).subscribe(data => resolve(data));
    });
    const copyBuffer = await promise;
    if (!copyBuffer.nodes || copyBuffer.nodes.length === 0) { return; }
    // location为undefined时，表明通过工具栏进行粘贴，粘贴至被复制节点的右侧
    if (!location || location === undefined) {
      location = new Point(copyBuffer.nodes[0].rect.cx + copyBuffer.nodes[0].rect.w, copyBuffer.nodes[0].rect.cy);
    }

    const x = Number(location.left);
    const y = Number(location.top);
    const delta = new Point(x - copyBuffer.nodes[0].rect.cx, y - copyBuffer.nodes[0].rect.cy);
    for (const node of copyBuffer.nodes) {
      const text = node.text;
      const rect = Rect.createWithCenter(node.rect.cx + delta.x, node.rect.cy + delta.y, node.rect.w, node.rect.h);
      const copyNode = new ChartNode();
      copyNode.text = text;
      copyNode.rect = rect;
      copyNode.uid = UidUtils.generateUid();
      copyNode.viewType = node.viewType;
      copyNode.copyId = node.uid;
      const args = new CreateNodeArgs(project.name, chart.uid, copyNode);
      args.parent = undefined;
      nodesDetail.push(args);
    }
    this.store.dispatch(new fromStore.CreateNodes(nodesDetail));
    this.store.select(fromStore.getCreatedNewNodes).subscribe(data => {
      const pastedNodesId = [];
      nodesDetail.forEach(n => {
        pastedNodesId.push(n.node.uid);
      });
      if (data) {
        const newNodesId = data.map(n => n.uid);
        if (pastedNodesId.toString() === newNodesId.toString()) {
          this.getNodeGroupRelationship(copyBuffer.groups, copyBuffer.groupsNodesMap, data, chart);
        }
      }
    });

    const test = this.store.select(fromStore.getCurrentNodes).subscribe(data => {
      if (data.length !== chart.nodes.length) {
        let startNode;
        let endNode;
        const connectorsDetails = [];
        for (const c of copyBuffer.connectors) {
          data.forEach(node => {
            if (node.copyId === c.start.uid) { startNode = node; }
            if (node.copyId === c.end.uid) { endNode = node; }
          });
          if (startNode) {
            const connectorJson = new CreateConnectorArgs(UidUtils.generateUid(), project.name, chart.uid, startNode.uid, endNode.uid, c.type, c.shape);
            connectorsDetails.push(connectorJson);
          }
        }
        if (connectorsDetails && connectorsDetails.length !== 0) {
          this.store.dispatch(new CreateConnectors(connectorsDetails));
          test.unsubscribe();
        }
      }
    });
  }

  getNodeGroupRelationship(groups: Group[], relationship, newNodes: ChartNode[], chart: Chart) {
    console.log(relationship);
    const newCopiedGroups = [];
    relationship.forEach(copiedNodes => {
      const eachGroup = [];
      copiedNodes.forEach(cn => {
        newNodes.forEach(node => {
          if (node.copyId === cn.uid) {
            eachGroup.push(node);
          }
        });
      });
      newCopiedGroups.push(eachGroup);
    });
    newCopiedGroups.forEach((nodes, index) => {
      this.generateGroup(chart, groups[index], nodes);
    });
  }

  generateGroup(chart: Chart, group: Group, nodes: ChartNode[]) {
    if (nodes.length === 0) {
      return;
    }
    const judgeNodeInGroup = this.groupService.judgeNodeInGroup(chart, nodes);
    if (nodes.length === 0 || judgeNodeInGroup) {
      return;
    }
    const groupId = UidUtils.generateUid();
    const newGroup: Group = new Group(chart);
    newGroup.uid = groupId;
    newGroup.nodes = nodes;
    newGroup.name = 'group' + chart.groups.length;
    newGroup.layout = group.layout;
    newGroup.name = group.name;
    this.store.dispatch(new fromStore.CreateGroup(newGroup));
  }

  copyGroups(groups: Group[]) {
    let allGroupNodes = [];
    groups.forEach((g, i) => {
      allGroupNodes = allGroupNodes.concat(g.nodes);
    });
    groups.forEach(g => {
      this.copyNodes(g.chart, allGroupNodes, groups);
    });
  }

  // 判断节点是否完全处于当前画布内，用于判断是否需要扩张画布
  needExpand(nodes: ChartNode[], basePoint, canvasSize: Point, viewbowTranslate: Point): boolean {
    let needExpand = 'notExpand';
    nodes.forEach(n => {
      if (n.rect.x < basePoint.x || n.rect.y < basePoint.y
        || n.rect.x + n.rect.w > basePoint.x + canvasSize.x || n.rect.y + n.rect.h > basePoint.y + canvasSize.y) {
        needExpand = 'expand';
      }
    });

    return needExpand === 'expand' ? true : false;
  }

  directToNode(chart: Chart, currentNode: ChartNode, direction: 'left' | 'right' | 'up' | 'down') {
    return this.findNode(chart, currentNode, direction);
    let angle = 0;
    switch (direction) {
      case 'left':
        angle = 180;
        break;
      case 'right':
        angle = 0;
        break;
      case 'up':
        angle = 270;
        break;
      case 'down':
        angle = 90;
        break;
    }
    return this.findNeareatNode(chart, currentNode, angle);
  }

  private getAngle(start: Point, end: Point, dic: Point) {
    const v = new Point(end.x - start.x, end.y - start.y);
    return Math.atan(dic.y / dic.x) - Math.atan(v.y / v.x);
  }

  private findNearestNode1(chart: Chart, currentNode: ChartNode) {
    //
  }

  private findNeareatNode(chart: Chart, currentNode: ChartNode, angle: number) {
    const sin = Math.sin(angle);
    const cos = Math.cos(angle);
    const originPoint = new Point(0, 0);
    const directionVector = new Point(cos, sin);
    const factor = 2.6;
    const maxDistance = 1e100;

    const weightFunc = (x, y) => {
      let evaluation = Number.POSITIVE_INFINITY;
      const vector = sin < 0.01 ? new Point(x - currentNode.rect.cx, factor * (y - currentNode.rect.cy)) : new Point(factor * (x - currentNode.rect.cx), y - currentNode.rect.cy);
      const distance = Math.sqrt(vector.x * vector.x + vector.y * vector.y);
      if (distance < 0.001) {
        return Number.POSITIVE_INFINITY;
      }
      const ang = this.getAngle(originPoint, vector, directionVector);
      const cosAngle = Math.cos(ang);
      if (Math.abs(cosAngle) > 1e-6) {
        evaluation = distance / cosAngle;
        if (evaluation < 0) {
          evaluation += maxDistance;
        }
      }
      return evaluation;
    };
    let minWeight = Number.POSITIVE_INFINITY;
    // 先确定备选节点
    let substitutes: ChartNode[] = [];
    if (currentNode != null && chart.connectors != null) {

      if (angle === 90) {
        // down to children
        const childrenUids = chart.connectors.filter(c => c.start.uid === currentNode.uid).map(c => c.end.uid);
        substitutes = chart.nodes.filter(n => childrenUids.includes(n.uid));
      } if (angle === 270) {
        // up to parents
        const parentUids = chart.connectors.filter(c => c.end.uid === currentNode.uid).map(c => c.start.uid);
        substitutes = chart.nodes.filter(n => parentUids.includes(n.uid));
      }
    }
    let minWeightNode;
    // 计算备选节点权重，找出权重最大的节点
    substitutes.forEach(n => {
      const weight = weightFunc(n.rect.cx, n.rect.cy);
      if (weight < minWeight) {
        minWeightNode = n;
        minWeight = weight;
      }
    });
    if (minWeightNode != null) {
      return minWeightNode;
    }
    chart.nodes.forEach(n => {
      const weight = weightFunc(n.rect.cx, n.rect.cy);
      if (weight < minWeight) {
        minWeightNode = n;
        minWeight = weight;
      }
    });
    return minWeightNode;
  }

  private findNode(chart: Chart, currentNode: ChartNode, direction: 'left' | 'right' | 'up' | 'down') {
    let totalDistance = Number.POSITIVE_INFINITY;
    const factor = 0.6;
    let target: ChartNode = null;
    chart.nodes.forEach(n => {
      // if ((direction === 'left' && n.rect.cx < currentNode.rect.cx)) {

      // } else if ((direction === 'right' && n.rect.cx > currentNode.rect.cx)) {

      // }
      if ((direction === 'left' && n.rect.cx < currentNode.rect.cx) ||
        (direction === 'right' && n.rect.cx > currentNode.rect.cx)) {
        const offsetX = Math.abs(currentNode.rect.cx - n.rect.cx);
        const offsetY = Math.abs(currentNode.rect.cy - n.rect.cy);
        if ((offsetX * factor) > offsetY) {
          const dis = offsetX + offsetY;
          if (dis < totalDistance) {
            target = n;
            totalDistance = dis;
          }
        }
      }

      // if(direction === 'up' && n.rect.cy < currentNode.rect.cy) {
      //   const parentConns= chart.connectors.filter(c => c.end === currentNode.uid);
      //   if(parentConns && parentConns.length > 0) {
      //     const pUids = parentConns.map(c=>c.start);
      //   }
      // } else if(direction === 'down' && n.rect.cy > currentNode.rect.cy) {

      // }

      if ((direction === 'up' && n.rect.cy < currentNode.rect.cy) ||
        (direction === 'down' && n.rect.cy > currentNode.rect.cy)) {
        const offsetX = Math.abs(currentNode.rect.cx - n.rect.cx);
        const offsetY = Math.abs(currentNode.rect.cy - n.rect.cy);
        if (offsetX < (offsetY * factor)) {
          const dis = offsetX + offsetY;
          if (dis < totalDistance) {
            target = n;
            totalDistance = dis;
          }
        }
      }
    });
    return target;
  }
}

