import { ChartType } from "./../../../../flowchart-core/src/lib/models/type.model";
import { BinaryOpNode } from "./../../../../flowchart-parser/src/lib/nodes/ast-node.model";
import { AstNodeInfo, BaseGenerator } from "./base-generator";

export class PyGenerator extends BaseGenerator {

    readonly tag: string = 'py';
    getFileExtension() {
        return 'py';
    }

    fileHeaderLines() {
        return [
            "import asyncflow",
            ""
        ]
    }

    genEventCode(eventName: string, obj: string = null) {
        this.currentNode.parseInfo.setEventName(eventName);
        return `asyncflow.wait_event(${obj || 'self'}, asyncflow.EventId.${eventName})`;
    }

    generateCode(ni: AstNodeInfo) {
        if (!this.parseInfo.isControlNode) {
            this.codes = [];
            this.codes.push(`## ${this.currentNode.text}`);
            this.codes.push(`@asyncflow.func`);
            this.codes.push(`def ${ni.functionName}(self):`);
            if (ni.type.name === ChartType.BoolType.name) {
                this.codes.push(`    return ${ni.code === 'true' ? 'True' : 'False'}`);
            } else {
                this.codes.push(`    ret = ${ni.code}`);
                this.codes.push(`    return True`);
            }
            this.codes.push("");
        }
    }
    translateOp(node: BinaryOpNode) {

    }

}