import { CommonToastComponent } from './common-toast/common-toast.component';
import { Injectable } from '@angular/core';
import { MatSnackBar, MatSnackBarHorizontalPosition, MatSnackBarVerticalPosition } from '@angular/material';
import { ToastLevel } from './toast.constants';

@Injectable({
  providedIn: 'root'
})
export class ToastService {

  constructor(private snackBar: MatSnackBar) { }

  toast(msg: string, level: ToastLevel = ToastLevel.INFO, duration: number = 2, horPos: MatSnackBarHorizontalPosition = 'right', verPos: MatSnackBarVerticalPosition = 'bottom') {
    this.snackBar.openFromComponent(CommonToastComponent, {
      data: { msg, level },
      duration: duration * 1000,
      horizontalPosition: horPos,
      verticalPosition: verPos
    });
  }

  info(msg: string, duration: number = 2, horPos: MatSnackBarHorizontalPosition = 'center', verPos: MatSnackBarVerticalPosition = 'top') {
    this.toast(msg, ToastLevel.INFO, duration, horPos, verPos);
  }

  warn(msg: string, duration: number = 2, horPos: MatSnackBarHorizontalPosition = 'center', verPos: MatSnackBarVerticalPosition = 'top') {
    this.toast(msg, ToastLevel.WARNING, duration, horPos, verPos);
  }

  error(msg: string, duration: number = 2, horPos: MatSnackBarHorizontalPosition = 'center', verPos: MatSnackBarVerticalPosition = 'top') {
    this.toast(msg, ToastLevel.ERROR, duration, horPos, verPos);
  }
}
