import * as fromRecentProjectsList from '../../actions/projects/recent-projects-list.actions';
import { RecentProject } from '../../../models/project.model';

export interface RecentProjectsListState {
    data: RecentProject[];
    loaded: boolean;
    loading: boolean;
}

export const initialState: RecentProjectsListState = {
    data: [],
    loaded: false,
    loading: false,
};

export function reducer(
    state = initialState,
    action: fromRecentProjectsList.RecentProjectsListAction
): RecentProjectsListState {
    switch (action.type) {
        case fromRecentProjectsList.LOAD_RECENT_PROJECTS_LIST: {
            return {
                ...state,
                loading: true,
            };
        }

        case fromRecentProjectsList.LOAD_RECENT_PROJECTS_LIST_SUCCESS: {
            const result = action.payload;
            const reverseData = [...(result)].reverse();
            return {
                ...state,
                loading: false,
                loaded: true,
                data: reverseData
            };
        }

        case fromRecentProjectsList.LOAD_RECENT_PROJECTS_LIST_FAIL: {
            return {
                ...state,
                loading: false,
                loaded: false,
            };
        }
    }

    return state;
}

export const getRecentProjectsListLoading = (state: RecentProjectsListState) => state.loading;
export const getRecentProjectsListLoaded = (state: RecentProjectsListState) => state.loaded;
export const getRecentProjectsList = (state: RecentProjectsListState) => state.data;

