/**
 * 数据校验结果对象，校验成功（valid == true）时，msg为空，校验失败（valid == false）时，msg返回具体内容
 */
export class CheckResult {
    valid: boolean;
    msg: string;
    constructor(msg?: string) {
        if (msg) {
            this.valid = false;
            this.msg = msg;
        } else {
            this.valid = true;
        }
    }
}