import { Injectable } from '@angular/core';
import { Point } from 'src/app/models/point.model';
import { CONNECTOR_OFFSET } from 'src/app/constants/canvas/connector.constants';
import { Connector, ShapeType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';

@Injectable({
  providedIn: 'root'
})
export class ConnectorPathService {

  constructor() { }

  getConnectorPath(conn: Connector, start: Point, end: Point) {
    // const start = new Point(conn.start.rect.cx, conn.start.rect.h + conn.start.rect.y);
    // const end = new Point(conn.end.rect.cx, conn.end.rect.y)
    let isSelfConn = conn.start.uid === conn.end.uid;
    switch (conn.shape) {
      case ShapeType.LINE:
        if (isSelfConn) {
          return this.drawSelfConnectorPath(start, end, conn);
        } else {
          return `M ${start.x} ${start.y}, L ${end.x} ${end.y}`;
        }
      case ShapeType.POLYLINE:
        return this.drawPolyline2(start, end, conn);
      case ShapeType.CURVE:
        if (isSelfConn) {
          return this.drawSelfConnectorPath(start, end, conn);
        } else {
          return this.drawCurve(start, end, conn);
        }
    }
  }

  drawCurve(start: Point, end: Point, connector: Connector) {
    const startTransPos = this.getTransitionPos(start, 'bottom', 0);
    const endTransPos = this.getTransitionPos(end, 'top', 10);
    const offsetX = endTransPos.x - startTransPos.x;
    const offsetY = endTransPos.y - startTransPos.y;
    const gapX = Math.abs(offsetX / 3);
    const gapY = Math.abs(offsetY / 3);
    if (Math.abs(offsetX) < 5 && offsetY > connector.start.rect.h) {
      return `M${start.x},${start.y} L${end.x} ${end.y}`;
    } else if (offsetY < 0) {
      // return this.getPolylineUpwards(start, end, connector);
      return this.drawCurveUpwards(start, end, connector);
    } else {
      const secDot = `${startTransPos.x},${startTransPos.y + gapY}`;
      const thirdDot = `${endTransPos.x},${endTransPos.y - gapY}`;
      return `M${start.x},${start.y} L${startTransPos.x},${startTransPos.y} C${secDot} ${thirdDot} ${endTransPos.x},${endTransPos.y} L${end.x} ${end.y}`;
    }
  }

  drawPolyline(start: Point, end: Point, connector: Connector) {
    const offset = CONNECTOR_OFFSET.offset;
    const polylinePointArray: Point[] = [];
    let nodeHeight;
    let startNodeWidth;
    let endNodeWidth;
    if (connector !== null && connector !== undefined) {
      nodeHeight = connector.start.rect.h;
      startNodeWidth = connector.start.rect.w;
      endNodeWidth = connector.end.rect.w;
    } else {
      nodeHeight = 36;
      startNodeWidth = endNodeWidth = 144;
    }
    const nodeDeltaX = end.x - start.x;
    const nodeDeltaY = (end.y) - (start.y);
    const middleY = (end.y + start.y) * 0.5;
    const startPoint = new Point(start.x, start.y);
    const endPoint = new Point(end.x, end.y);
    if (nodeDeltaY >= 0) {
      // 终点在起点下方时
      const point1 = new Point(start.x, middleY);
      const point2 = new Point(end.x, middleY);
      polylinePointArray.push(startPoint, point1, point2, endPoint);
    } else if (nodeDeltaY < 0 && Math.abs(nodeDeltaX) > 0.5 * (startNodeWidth + endNodeWidth)) {
      // 终点在起点上方且二者水平方向相距大于各自节点的一半
      const isEndRight = nodeDeltaX / Math.abs(nodeDeltaX);
      const point1 = new Point(start.x, start.y + offset);
      const point2 = new Point(
        0.5 * (start.x + 0.5 * isEndRight * startNodeWidth + end.x - 0.5 * isEndRight * endNodeWidth),
        start.y + offset);
      const point3 = new Point(
        0.5 * (start.x + 0.5 * isEndRight * startNodeWidth + end.x - 0.5 * isEndRight * endNodeWidth),
        end.y - offset);
      const point4 = new Point(end.x, end.y - offset);
      polylinePointArray.push(startPoint, point1, point2, point3, point4, endPoint);
    } else if (Math.abs(nodeDeltaX) <= 0.5 * (startNodeWidth + endNodeWidth) && end.x > start.x) {
      const point1 = new Point(start.x, start.y + offset);
      const point2 = new Point(
        Math.min(start.x - 0.5 * startNodeWidth - offset, end.x - 0.5 * endNodeWidth - offset),
        start.y + offset);
      const point3 = new Point(
        Math.min(start.x - 0.5 * startNodeWidth - offset, end.x - 0.5 * endNodeWidth - offset),
        Math.min(start.y - nodeHeight - offset, end.y - offset));
      const point4 = new Point(end.x, Math.min(start.y - nodeHeight - offset, end.y - offset));
      polylinePointArray.push(startPoint, point1, point2, point3, point4, endPoint);
    } else if (Math.abs(nodeDeltaX) <= 0.5 * (startNodeWidth + endNodeWidth) && end.x <= start.x) {
      const point1 = new Point(start.x, start.y + offset);
      const point2 = new Point(
        Math.max(start.x + 0.5 * startNodeWidth + offset, end.x + 0.5 * endNodeWidth + offset),
        start.y + offset);
      const point3 = new Point(
        Math.max(start.x + 0.5 * startNodeWidth + offset, end.x + 0.5 * endNodeWidth + offset),
        Math.min(start.y - nodeHeight - offset, end.y - offset));
      const point4 = new Point(end.x, Math.min(start.y - nodeHeight - offset, end.y - offset));
      polylinePointArray.push(startPoint, point1, point2, point3, point4, endPoint);
    }
    let showPath = this.pointsToPath(polylinePointArray);
    return showPath;
  }

  drawPolyline2(start: Point, end: Point, connector: Connector) {
    let points = this.generatePolygonePoints(start, end, connector);
    let showPath = this.pointsToPath(points);
    return showPath;
  }

  /**
   * 生成向上连的折线关键点列
   * @param stRect 
   * @param enRect 
   */
  private genPolylinePointsUpward(start: Point, end: Point, stRect: Rect, enRect: Rect, offset: number) {
    // const start = { x: stRect.cx, y: stRect.y + stRect.h };
    // const end = { x: enRect.cx, y: enRect.y };
    let points = [];
    const p1 = new Point(start.x, start.y + offset);
    const p5 = new Point(end.x, end.y - offset);
    // 起始节点右端位于结束节点左端的左边
    if (stRect.x + stRect.w < enRect.x) {
      const x = (stRect.x + stRect.w + enRect.x) / 2;
      const p2 = new Point(x, start.y + offset);
      const p3 = new Point(x, (start.y + end.y) / 2);
      const p4 = new Point(x, end.y - offset);
      points = [start, p1, p2, p3, p4, p5, end];
    } else if (enRect.x + enRect.w < stRect.x) {
      const x = (stRect.x + enRect.w + enRect.x) / 2;
      const p2 = new Point(x, start.y + offset);
      const p3 = new Point(x, (start.y + end.y) / 2);
      const p4 = new Point(x, end.y - offset);
      points = [start, p1, p2, p3, p4, p5, end];
    } else {
      const left = Math.min(stRect.x, enRect.x);
      const right = Math.max(stRect.x + stRect.w, enRect.x + enRect.w);
      const side = Math.abs(start.x - left) > Math.abs(start.x - right) ? right + offset : left - offset;
      const p2 = new Point(side, start.y + offset);
      const p3 = new Point(side, (start.y + end.y) / 2);
      const p4 = new Point(side, end.y - offset);
      points = [start, p1, p2, p3, p4, p5, end];
    }
    return points;
  }

  generatePolygonePoints(start: Point, end: Point, connector: Connector) {
    let points = [];
    const offset = CONNECTOR_OFFSET.offset;
    let stRect: Rect, enRect: Rect;
    if (!connector) {
      stRect = Rect.createWithCenter(start.x, start.y - 18, 144, 36);
      enRect = Rect.createWithCenter(end.x, end.y + 18, 144, 36);
    } else {
      stRect = connector.start.rect;
      enRect = connector.end.rect;
    }
    if (connector) {
      if (start.y < end.y) {
        const middleY = (start.y + end.y) / 2;
        const p1 = new Point(start.x, middleY);
        const p2 = new Point(end.x, middleY);
        points = [start, p1, p2, end];
      } else {
        points = this.genPolylinePointsUpward(start, end, stRect, enRect, offset);
      }
    }
    return points;
  }

  pointsToPath(points: Point[]) {
    let path = '';
    let i = 0;
    for (const point of points) {
      if (i === 0) {
        path += `M${point.x},${point.y}`;
      } else {
        path += ` L${point.x},${point.y}`;
      }
      i++;
    }
    return path;
  }

  drawSelfConnectorPath(start: Point, end: Point, connector: Connector) {
    const rect = connector.start.rect;
    const halfWidth = rect.w / 2;
    // 三次贝塞尔曲线
    return `M${start.x + halfWidth},${start.y - 2} C${start.x + halfWidth} ${start.y - 2} ${start.x + halfWidth + 40} ${(start.y + end.y) / 2} ${end.x + halfWidth} ${end.y + 2} `;
    // 圆形
    // return this.drawArcByRadiusDeg(start.x + rect.w / 2 - 18, start.y, 36, 270, false);
  }

  //#region private methods
  private getTransitionPos(point: Point, anchorKey: 'top' | 'bottom', trans) {
    if (point != null) {
      const x: number = point.x;
      let y: number = point.y;
      switch (anchorKey) {
        case 'top':
          y = point.y - trans;
          break;
        case 'bottom':
          y = point.y + trans;
          break;
      }
      return { x, y };
    }
    return null;
  }

  /***
  * @param startX 起始点
  * @param startY 起始点
  * @param r 半径
  * @param deg 度数
  * @param clockwise 顺时针
  */
  private drawArcByRadiusDeg(startX: number, startY: number, r: number, deg: number, clockwise: boolean) {
    const cw = typeof clockwise ? 0 : 1;
    const x = startX + (r - r * Math.cos(deg * Math.PI / 180));
    const y = startY - (1 === cw ? 1 : -1) * r * Math.sin(deg * Math.PI / 180);
    const bigOrSmall = deg > 180 ? 1 : 0;
    return `M${startX} ${startY} A${r} ${r} 0 ${bigOrSmall} ${cw} ${x - r} ${y}`;
  }


  coor(point: Point) {
    return `${point.x} ${point.y}`;
  }

  curvV(start: Point, end: Point, isStartLeft: boolean, isEndLeft: boolean) {
    const offsetX = Math.abs(end.y - start.y) / 2;
    let p1, p2;
    if (isStartLeft) {
      p1 = new Point(start.x - offsetX, start.y);
    } else {
      p1 = new Point(start.x + offsetX, start.y);
    }
    if (isEndLeft) {
      p2 = new Point(end.x - offsetX, end.y);
    } else {
      p2 = new Point(end.x + offsetX, end.y);
    }
    return `C${this.coor(p1)} ${this.coor(p2)} ${this.coor(end)}`;
  }


  private drawCurveUpwards(start: Point, end: Point, connector: Connector) {
    const pnts = this.generatePolygonePoints(start, end, connector);
    pnts[4].y -= 20;
    pnts[5].y -= 20;
    const paths = [];
    pnts.forEach(p => paths.push(`${p.x} ${p.y}`));
    let path = `M${paths[0]} `;
    // path += `Q${paths[1]} ${paths[2]}`;
    // path += this.curvV(points[2], points[4], points[1].x > points[2].x, points[4].x < points[5].x);
    // path += `Q${paths[5]} ${paths[6]}`;
    path += `C${this.coor(pnts[1])} ${this.coor(pnts[2])} ${this.coor(pnts[3])}`;
    path += `C${this.coor(pnts[4])} ${this.coor(pnts[5])} ${this.coor(pnts[6])}`;
    return path;
  }

  private getPolylineUpwards(start: Point, end: Point, connector: Connector) {
    const startRect = connector.start.rect;
    const endRect = connector.end.rect;
    const hsw = startRect.w / 2;
    const hew = startRect.w / 2;
    const offset = 10;
    let verLineX;
    if (start.x < end.x) {
      if (startRect.x + startRect.w < endRect.x) {
        verLineX = (start.x + hsw + end.x - hew) / 2;
      } else {
        verLineX = Math.min(start.x - hsw, end.x - hew) - 5;
      }
    } else {
      if (start.x > end.x + endRect.w) {
        verLineX = (start.x - hsw + end.x + hew) / 2;
      } else {
        verLineX = Math.max(start.x + hsw, end.x + hew) + 5;
      }
    }
    return `M${start.x},${start.y} L${start.x},${start.y + offset} L${verLineX},${start.y + offset} L${verLineX},${end.y - offset} L${end.x},${end.y - offset} L${end.x},${end.y}`;
  }
  //#endregion
}
