import { NumberWidgetComponent } from './../number-widget/number-widget.component';
import { BoolWidgetComponent } from './../bool-widget/bool-widget.component';
import { VariableWidgetComponent } from './../variable-widget/variable-widget.component';
import { StringWidgetComponent } from '../string-widget/string-widget.component';
import { SLOT_CATEGORY } from './../slot.interface';
import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';
import { SlotBase } from '../../models/slot-base.model';

@Component({
  selector: 'app-slot',
  templateUrl: './slot.component.html',
  styleUrls: ['./slot.component.css']
})
export class SlotComponent extends SlotBase implements OnInit {

  isDragOver: boolean;
  component;

  ngOnInit() {
  }

  onSlotDragOver($event) {
    $event.preventDefault();
  }

  onSlotDragEnter($event) {
    $event.preventDefault();
    this.isDragOver = true;
  }

  onSlotDragLeave($event) {
    this.isDragOver = false;
  }

  onSlotDrop($event) {
    const text = $event.dataTransfer.getData("text");
    this.processDropMessage(text);
    this.isDragOver = false;
  }

  processDropMessage(text: string) {
    const data = JSON.parse(text);
    this.isEditing = true;
    switch(data.category) {
      case SLOT_CATEGORY.NUMBER:
        this.component = NumberWidgetComponent;
        break;
      case SLOT_CATEGORY.STRING:
        this.component = StringWidgetComponent;
        break;
      case SLOT_CATEGORY.VARIABLE:
        this.component = VariableWidgetComponent;
        break;
      case SLOT_CATEGORY.BOOL:
        this.component = BoolWidgetComponent;
        break;
    }
  }

}

