import { ChartService } from 'src/app/services/chart/chart.service';
import { ChartData } from 'src/app/core/models/chart-data.model';
import { OperationService } from './../../services/chart/operation.service';
import { CommonMenuService, MenuTarget } from './../../services/chart/common-menu.service';
import { NodeViewType } from './../../models/node.model';
import { Component, Input, OnInit, ViewChild } from '@angular/core';
import { Store } from '@ngrx/store';
import * as fromStore from 'src/app/store/index';
import { MatMenuTrigger } from '@angular/material';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { ConnectorType, ShapeType } from 'libs/flowchart-ts/flowchart-core/src/public-api';

@Component({
  selector: 'app-canvas-menu-panel',
  templateUrl: './canvas-menu-panel.component.html',
  styleUrls: ['./canvas-menu-panel.component.css']
})
export class CanvasMenuPanelComponent implements OnInit {
  @Input() project: Project;
  @Input() hasOpeningChart: boolean = false;
  chart: Chart;
  selectedItems: ChartData[];
  menuTarget: MenuTarget;
  hasGroups = false;
  hasNodes = false;
  copyNodes: Node[];
  copyInfo = null;
  @ViewChild('subNodeMenuBtn', { read: MatMenuTrigger, static: false }) subNodeMenuTrigger: MatMenuTrigger;
  @ViewChild('changeNodeTypeMenuBtn', { read: MatMenuTrigger, static: false }) changeNodeTypeMenuTrigger: MatMenuTrigger;
  constructor(
    private menuService: CommonMenuService,
    private operationService: OperationService,
    private store: Store<fromStore.FlowchartState>,
    private chartService: ChartService
  ) { }

  ngOnInit() {
    this.init();
    this.listenCopyDetails();

  }

  init() {
    this.menuService.selectedItems.subscribe(items => {
      this.selectedItems = items;
      this.menuTarget = this.menuService.menuTarget;
      this.hasGroups = this.menuService.hasGroups;
      this.hasNodes = this.menuService.hasNodes;
    });

    this.chartService.currentChart.subscribe(chart => this.chart = chart);
  }

  update() {

  }

  //#region menu item click hanlders

  onUndoClick() {
    this.operationService.undo(this.chart.uid);
  }

  onRedoClick() {
    this.operationService.redo(this.chart.uid);
  }

  onDeleteClick() {
    this.menuService.delete();
  }

  onCutClick() {
    this.menuService.cut();
  }

  onCopyClick() {
    this.menuService.copy();
  }

  listenCopyDetails() {
    this.store.select(fromStore.getCopyChartDetails).subscribe(data => {
      this.copyInfo = data;
      if (this.copyInfo) {
        this.copyNodes = this.copyInfo.nodes;
      }
    });
  }

  onPasteClick() {
    this.menuService.paste(this.project);
  }

  onAddChildNodeClick($event, viewType: NodeViewType) {
    $event.stopPropagation();
    this.subNodeMenuTrigger.closeMenu();
    this.menuService.addChild(viewType);
  }

  onChangeNodeTypeClick($event, nodeType: NodeViewType) {
    $event.stopPropagation();
    this.changeNodeTypeMenuTrigger.closeMenu();
    // this.menuService.changeNodeViewType(nodeType);
  }

  onAddGroupClick() {
    this.menuService.addGroup();
  }

  onRemoveGroupClick() {
    this.menuService.deleteGroup();
  }

  onChangeConnectorShapeClick(shape: ShapeType) {
    this.menuService.changeConnectorShape(shape);
  }

  onChangeConnectorTypeClick(type: ConnectorType) {
    this.menuService.changeConnectorType(type);
  }

  onDebugClick() {
    this.menuService.debug();
  }

  onFastDebugClick() {
    this.menuService.fastDebug();
  }

  onGenerateClick($event, build_all: boolean) {
    this.menuService.generate(build_all ? null : this.chart);
  }

  onSaveClick() {
    console.log(this.menuTarget);
    this.menuService.save();
  }

  //#endregion

}
