import { Component, ElementRef, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { KEY_CODE } from 'src/app/constants/common/key.code.constants';
import { StringUtils } from 'src/app/utils/string.utils';

@Component({
  selector: 'app-input-select',
  templateUrl: './input-select.component.html',
  styleUrls: ['./input-select.component.css']
})
export class InputSelectComponent implements OnInit, OnChanges {
  @Input() dataSource: any;
  @Input() value: any;
  @Input() width = 64;
  @Input() height = 24;
  @Input() showKey: string;
  @Input() valueKey: string;
  @Input() needSearch = true;
  @Input() placeholder = '请选择';
  @Input() optionsWidth: number;
  @Input() showValue: string;
  // 以index显示option
  @Input() useIndex = false;
  innerShowValue: string;
  options: any[];
  filterOptions: any[];
  isFold = true;
  isFocused = false;
  @Output() valueChange: EventEmitter<any> = new EventEmitter<any>();
  @Output() change: EventEmitter<any> = new EventEmitter<any>();
  @Output() input: EventEmitter<any> = new EventEmitter<any>();
  selectedIndex = -1;
  optionsIndex = -1;

  @ViewChild('optionspanel', {static: false}) optionsPanel: ElementRef;
  constructor() { }

  ngOnInit() {
    // this.initData();
  }

  ngOnChanges(changes: SimpleChanges) {
    // this.initOptions();
    if (changes.dataSource != null || changes.value != null) {
      this.initData();
    }
  }

  initData() {
    if (this.dataSource == null) {
      return;
    }
    this.initOptions();
    let match;
    if (StringUtils.isNullOrWhiteSpace(this.valueKey)) {
      match = this.options.find(o => this.valueEqual(o, this.value));

    } else {
      match = this.options.find(o => this.getValue(this.valueKey, o) == this.value);
    }
    if (match != null) {
      this.selectedIndex = this.options.indexOf(match);
      this.innerShowValue = this.getShowValue(this.showKey, this.options[this.selectedIndex]);
    } else {
      // this.value = null;
      this.innerShowValue = this.value;
    }
  }

  valueEqual(a, b) {
    if (a == null || b == null) {
      return false;
    }

    if (!StringUtils.isNullOrWhiteSpace(this.showKey)) {
      return a[this.showKey] == b[this.showKey];
    }
    return JSON.stringify(a) == JSON.stringify(b);
  }

  initOptions() {
    if (this.dataSource instanceof Array) {
      this.options = this.dataSource;
    } else if (this.dataSource instanceof Map) {
      this.options = Array.from(this.dataSource.keys());
    } else {
      this.options = [];
      // throw ("dataSource is not an array or iterable object.");
    }
    this.filterOptions = this.options;
  }


  onClick($event) {
    this.isFold = !this.isFold;
  }
  onDropDownClick($event) {
    this.isFold = !this.isFold;
  }
  onClickOutside($event) {
    this.isFold = true;
  }

  onInput($event) {
    this.isFold = false;
    const keyWord = $event.target.value;
    if (StringUtils.isNullOrWhiteSpace(keyWord)) {
      this.filterOptions = this.options.filter(o => true);
    } else {

      if (StringUtils.isNullOrWhiteSpace(this.showKey) && !this.useIndex) {
        this.filterOptions = this.options.filter(o => (o as string).includes(keyWord));
      } else {
        this.filterOptions = this.options.filter(o => {
          const showvalue = this.getShowValue(this.showKey, o) + '';
          if (!StringUtils.isNullOrWhiteSpace(showvalue) && showvalue.includes(keyWord)) {
            return true;
          }
          return false;
        });
      }
    }
  }

  onInputKeyDown($event) {
    switch ($event.keyCode) {
      case KEY_CODE.UP:
        this.minusOptionIndex();
        break;
      case KEY_CODE.DOWN:
        this.addOptionIndex();
        break;
      case KEY_CODE.ENTER:
        if (this.optionsIndex != -1) {
          this.selectOption(this.optionsIndex);
        } else {

        }
        break;
    }
  }

  onOptionClick($event, i) {
    $event.stopPropagation();
    this.selectOption(i);
  }


  selectOption(index: number) {
    this.isFold = true;
    if (this.selectedIndex == -1 || this.filterOptions[index] != this.options[this.selectedIndex]) {
      this.value = StringUtils.isNullOrWhiteSpace(this.valueKey) ? this.filterOptions[index] : this.getValue(this.valueKey, this.filterOptions[index]);
      this.innerShowValue = this.getShowValue(this.showKey, this.filterOptions[index]);
      this.selectedIndex = index;
      this.change.emit({ selectedIndex: this.selectedIndex, value: this.value });
      this.valueChange.emit(this.value);
    }
    this.optionsIndex = -1;
    // this.initOptions();
  }



  addOptionIndex() {
    if (this.optionsIndex >= this.filterOptions.length - 1) {
      this.optionsIndex = 0;
    } else {
      this.optionsIndex++;
    }
    this.autoJustOptionsPanelScroll();
  }

  minusOptionIndex() {
    if (this.optionsIndex <= 0) {
      this.optionsIndex = this.filterOptions.length - 1;
    } else {
      this.optionsIndex--;
    }
    this.autoJustOptionsPanelScroll();
  }

  autoJustOptionsPanelScroll() {
    if (this.optionsIndex >= 15) {
      this.optionsPanel.nativeElement.scrollTo(0, (this.optionsIndex - 14) * 20);
    } else {
      if (this.optionsPanel.nativeElement.scrollTop > 0) {
        this.optionsPanel.nativeElement.scrollTo(0, 0);
      }
    }
  }

  getValue(key: string, data: any) {
    if (!StringUtils.isNullOrWhiteSpace(key)) {
      let keys = [];
      if (key.includes('.')) {
        keys = key.split('.');
      } else {
        keys.push(key);
      }
      let v = data;
      for (const k of keys) {
        v = v[k];
      }
      return v;
    } else {
      return data;
    }
  }

  getShowValue(key: string, data: any) {
    if (key) {
      return this.getValue(key, data);
    }
    if (this.useIndex) {
      return this.options.indexOf(data);
    }
  }
}
