import { ExecuteResultCode } from './../../constants/command/command.constants';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';
import { COMMAND_NAME } from 'src/app/constants/command/command.constants';
import { Command, CommandExecuteResult } from "./command.model";


export class CreateFolderCommand extends Command {
    readonly name = COMMAND_NAME.CREATE_FOLDER;
    execute(): CommandExecuteResult {
        throw new Error("Method not implemented.");
    }
    revert(): CommandExecuteResult {
        throw new Error("Method not implemented.");
    }
}

export class UpdateFolderCommand extends Command {
    readonly name = COMMAND_NAME.UPDATE_FOLDER;
    project: Project;
    path: string;
    private prevPath: string;
    private updatedCharts: Chart[];

    constructor(project: Project, path: string, prevPath: string) {
        super();
        this.project = project;
        this.path = path;
        this.prevPath = prevPath;
        this.updatedCharts = [];
    }
    execute(): CommandExecuteResult {
        if (this.project && this.project.charts) {
            this.project.charts.forEach(c => {
                if (c.path.startsWith(this.prevPath + '.')) {
                    c.path = c.path.replace(this.prevPath + '.', this.path + '.');
                    this.updatedCharts.push(c);
                }
            });
        }
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
    revert(): CommandExecuteResult {
        if (this.updatedCharts) {
            this.updatedCharts.forEach(c => {
                c.path = c.path.replace(this.path + '.', this.prevPath + '.')
            });
        }
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}

export class RemoveFolderCommand extends Command {
    readonly name = COMMAND_NAME.REMOVE_FOLDER;
    project: Project;
    path: string;
    private removedChartsMap: Chart[];

    constructor(project: Project, path: string) {
        super();
        this.project = project;
        this.removedChartsMap = [];
        this.path = path;
    }

    execute(): CommandExecuteResult {
        if (this.project) {
            for (let i = this.project.charts.length - 1; i >= 0; i--) {
                const c = this.project.charts[i];
                if (c.path.startsWith(this.path + '.')) {
                    this.project.removeChart(c);
                    this.removedChartsMap[i] = c;
                } else {
                    this.removedChartsMap[i] = null;
                }
            }
        }
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
    revert(): CommandExecuteResult {
        for (let i = 0; i < this.removedChartsMap.length; i++) {
            if (this.removedChartsMap[i] != null) {
                this.project.charts.splice(i, 0, this.removedChartsMap[i]);
            }
        }
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }

}