import { LayoutGroupCommand } from './../../models/commands/group.commands';
import { UpdateConnectorCommand } from './../../models/commands/connector.commands';
import { CreateConnectorArgs } from './../../models/request-arguments/connector.op.args.model';
import { OperationService } from './operation.service';
import { COMMAND_NAME } from './../../constants/command/command.constants';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { catchError } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { Connector } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { Command } from 'src/app/models/commands/command.model';
import { CreateConnectorCommand, RemoveConnectorCommand } from 'src/app/models/commands/connector.commands';
import { Chart, LayoutType } from 'libs/flowchart-ts/flowchart-core/src/public-api';

@Injectable({
  providedIn: 'root'
})
export class ConnectorsService {

  constructor(private http: HttpClient,
    private operationService: OperationService) { }

  createConnector(chart: Chart, conn: Connector) {
    const cmds: Command[] = [];
    cmds.push(new CreateConnectorCommand(chart, conn));
    // if connector start and and belong to same auto-layout group, then the group need to layout again
    if (conn.start.group && conn.start.group === conn.end.group && conn.start.group.layout === LayoutType.AUTO) {
      cmds.push(new LayoutGroupCommand(chart, conn.start.group));
      const conns = chart.connectors.filter(c => conn.start.group.nodes.includes(c.start) || conn.start.group.nodes.includes(c.end));
      if (conns) {
        conns.forEach(c => {
          c.path = c.getLinePath();
        })
      }
    }
    return this.operationService.executeChartCommands(chart.uid, cmds);
  }

  removeConnector(chart: Chart, conn: Connector) {
    const cmds: Command[] = [];
    cmds.push(new RemoveConnectorCommand(chart, conn));
    // if connector start and and belong to same auto-layout group, then the group need to layout again
    if (conn.start.group && conn.start.group === conn.end.group && conn.start.group.layout === LayoutType.AUTO) {
      cmds.push(new LayoutGroupCommand(chart, conn.start.group));

      const conns = chart.connectors.filter(c => conn.start.group.nodes.includes(c.start) || conn.start.group.nodes.includes(c.end));
      if (conns) {
        conns.forEach(c => {
          c.path = c.getLinePath();
        })
      }
    }

    return this.operationService.executeChartCommands(chart.uid, cmds);
  }

  updateConnector(chart: Chart, conn: Connector, kv: Map<string, any>) {
    const cmd = new UpdateConnectorCommand(conn, kv);
    return this.operationService.executeChartCommands(chart.uid, [cmd])
  }

}
