import { Injectable } from '@angular/core';
import * as ToolbarAction from '../../actions/toolbar/toolbar.action';
import { Effect, Actions, ofType } from '@ngrx/effects';
import { pipe } from 'rxjs';

import { map } from 'rxjs/operators';

@Injectable()
export class ToolbarEffects {
    constructor(
        private actions$: Actions,
    ) { }

    @Effect()
    changeLatestConnectorShape$ = this.actions$.pipe(
        ofType(ToolbarAction.CHANGE_LATEST_CONNECTOR_SHAPE),
        map((action: ToolbarAction.ChangeLatestConnectorShape) => action.payload),
        pipe(
            map(data => new ToolbarAction.ChangeLatestConnectorShapeSuccess(data))
        )
    );

    @Effect()
    changeLatestConnectorType$ = this.actions$.pipe(
        ofType(ToolbarAction.CHANGE_LATEST_CONNECTOR_TYPE),
        map((action: ToolbarAction.ChangeLatestConnectorType) => action.payload),
        pipe(
            map(data => new ToolbarAction.ChangeLatestConnectorTypeSuccess(data))
        )
    );
}
