import { Component, Inject, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';

export class SimpleDialogConfig {
  text: string;
  btnType: SimpleDialogBtnType;
  title?: string;
  btn1Text?: string;
  btn2Text?: string;
  btn3Text?: string;
}

export enum SimpleDialogBtnType {
  OK_CANCEL,
  YES_NO,
  YES_NO_CANCEL
}

export enum SimpleDialogBtnCode {
  CONFIRM = 1,
  CODE2 = 2,
  CODE3 = 3
}


@Component({
  selector: 'app-simple-dialog',
  templateUrl: './simple-dialog.component.html',
  styleUrls: ['./simple-dialog.component.css']
})
export class SimpleDialogComponent implements OnInit {
  dialogTitle: string;
  dialogText: string;
  btn1Text: string;
  btn2Text: string;
  btn3Text: string;
  constructor(
    private dialogRef: MatDialogRef<SimpleDialogComponent>,
    @Inject(MAT_DIALOG_DATA) private data: SimpleDialogConfig) { }

  ngOnInit() {
    this.initDialog();
  }

  initDialog() {
    this.dialogText = this.data.text;
    this.dialogTitle = this.data.title || 'Tips';
    this.btn1Text = this.data.btn1Text || (this.data.btnType === SimpleDialogBtnType.OK_CANCEL) ? 'OK' : 'Yes';
    this.btn2Text = this.data.btn2Text || (this.data.btnType === SimpleDialogBtnType.OK_CANCEL) ? 'Cancel' : 'NO';
    this.btn3Text = this.data.btn3Text || (this.data.btnType === SimpleDialogBtnType.YES_NO_CANCEL) ? 'Cancel' : undefined;
  }

  onBtnClick($event, btnNo: number) {
    this.dialogRef.close(btnNo);
  }

}
