import { ProjectService } from './../../../services/project/project.service';
import { UidUtils } from './../../../utils/uid.utils';
import { APP_HTTP_RESULT_CODE } from './../../../constants/http/http.constants';
import { Store } from '@ngrx/store';
import { MatTableDataSource } from '@angular/material/table';

import { Component, Inject, Input, OnInit, ViewChildren } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { deepCopy } from 'src/app/store/reducers/utilsFunction';
import { EventEditorComponent } from '../event-editor/event-editor.component';
import { SelectionModel } from '@angular/cdk/collections';
import { InterfaceService } from 'src/app/services/interface/interface.service';
import * as fromStore from 'src/app/store/index';
import { OutputService } from 'src/app/services/common/output.service';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/type.model';
import { Member, MemberFrom, MemberType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/member.model';
import { Parameter } from 'libs/flowchart-ts/flowchart-core/src/lib/models/parameter.model';
import { CustomCodeEditorPanelComponent } from '../custom-code-editor-panel/custom-code-editor-panel.component';
import { TypeSource } from 'libs/flowchart-ts/flowchart-core/src/public-api';

@Component({
  selector: 'app-type-editor',
  templateUrl: './type-editor.component.html',
  styleUrls: ['./type-editor.component.css']
})
export class TypeEditorComponent implements OnInit {
  @ViewChildren('parameterNameInput') parameterNameInput;
  @ViewChildren('memberNameInput') memberNameInput;
  columns = ['select', 'memberType', 'name', 'source', 'description', 'type', 'parameters', 'op'];
  globalColumns = ['select', 'memberType', 'name', 'source', 'description', 'type', 'parameters', 'custom-code', 'op'];
  defaultColumns = ['select', 'memberType', 'name', 'source', 'description', 'type', 'parameters', 'op'];
  editType: ChartType;
  dialogTitle: string;
  project;
  members: Member[];
  nonFileMembers: Member[];
  memberDataSource = new MatTableDataSource<Member>([]);
  selection = new SelectionModel<Member>(true, []);

  chartTypes: ChartType[];
  types: ChartType[];
  constructor(
    public dialog: MatDialog,
    private dialogRef: MatDialogRef<EventEditorComponent>,
    @Inject(MAT_DIALOG_DATA) public data: ChartType,
    private typeService: InterfaceService,
    private store: Store,
    private outputService: OutputService,
    private projectService: ProjectService
  ) { }

  ngOnInit() {
    this.initData();
    this.initProject();
  }

  initData() {
    this.dialogTitle = !this.data ? 'add-type' : 'edit-type';
    const project = this.projectService.getCurrentProject();
    if (this.data) {
      // this.columns = this.data.name === 'Global' ? this.globalColumns : this.defaultColumns;
      // this.columns = this.defaultColumns;
      // this.editType = this.data;
      // Object.setPrototypeOf(this.editType, ChartType.prototype);
      // if (this.editType.members) {
      //   this.editType.members.forEach(m => Object.setPrototypeOf(m, Member.prototype));
      // }
      // this.columns = this.data.name === 'Global' ? this.globalColumns : this.defaultColumns;
      this.columns = this.defaultColumns;
      this.editType = new ChartType();
      this.editType.fromJson(this.data.toJson(), tname => project.getType.call(project, tname));
      this.members = this.editType.members.filter(m => m.from === MemberFrom.FILE);
      this.nonFileMembers = this.editType.members.filter(m => m.from !== MemberFrom.FILE);
    } else {
      this.editType = new ChartType();
      this.editType.uid = UidUtils.generateUid();
      this.editType.source = TypeSource.CUSTOM;
      this.members = [];
      this.nonFileMembers = [];
    }
    this.memberDataSource.data = this.members;
    this.types = this.typeService.getAllTypeList(true);
    this.chartTypes = project.types;
    this.typeService.chartTypes.subscribe(types => {
      if (types) {
        this.types = this.typeService.getAllTypeList(true);
      }
    });
    // this.store.select(fromStore.getTypes).subscribe(result => {
    //   this.chartTypes = result;
    //   this.types = this.typeService.getBasicTypeList(true).concat(this.chartTypes.map(c => c.name).concat(['List', 'Dict']));
    // });
  }

  async initProject() {
    this.project = this.projectService.getCurrentProject();
  }

  isValid() {
    if (this.chartTypes.find(c => c.name === this.editType.name && c.uid != this.editType.uid)) {
      return { isValid: false, msg: `Type ${this.editType.name} has already existed` };
    }
    return { isValid: true, msg: '' };
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.members.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.editType.memberMap.forEach(row => this.selection.select(row));
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(member?: Member): string {
    if (!member) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(member) ? 'deselect' : 'select'}`;
  }

  onMemberCategoryChanged($event, member: Member) {
    member.memberType = Number.parseInt($event.target.options[$event.target.selectedIndex].value);
  }

  onAddParameterClick($event, member: Member, memberNum: number) {
    if (!member.parameters) {
      member.parameters = [];
    }
    const newPara = new Parameter();
    newPara.name = '';
    newPara.type = ChartType.StringType;
    member.parameters.push(newPara);
    // 添加parameter后input自动聚焦
    setTimeout(() => {
      const newInputId = memberNum.toString() + (member.parameters.length - 1).toString();
      const target = this.parameterNameInput._results.find(elementRef => elementRef.nativeElement.id === newInputId);
      target.nativeElement.focus();
    }, 0);
  }

  onAddMemberClick($event) {
    if (!this.members) {
      this.members = [];
    }
    const newMember: Member = new Member();
    newMember.name = 'Property';
    newMember.memberType = MemberType.PROPERTY;
    newMember.type = ChartType.StringType;
    newMember.from = MemberFrom.FILE;
    this.editType.addMember(newMember);
    this.members.push(newMember);
    this.memberDataSource.data = this.members;
    // 添加成员自动聚焦
    setTimeout(() => {
      this.memberNameInput.last.nativeElement.focus();
    }, 0);
  }

  onDeleteMemberClick($event, index: number) {
    this.members.splice(index, 1);
    this.memberDataSource.data = this.members;
  }
  onDeleteParameterClick($event, i, j) {
    this.members[i].parameters.splice(j, 1);
  }

  checkName() {
    const nameRecord = new Map();
    let checked = true;
    this.editType.memberMap.forEach((m, index) => {
      if (!nameRecord.get(m.name)) {
        nameRecord.set(m.name, m);
      } else {
        checked = false;
        this.outputService.error(`成员名称${m.name}重复`);
      }
    });
    return checked;
  }

  onSaveClick($event) {
    if (!this.checkName()) {
      return;
    }
    if (this.isValid().isValid) {
      if (this.data) {
        const members = this.members.concat(this.nonFileMembers);
        this.data.members = [];
        this.data.memberMap.clear();
        members.forEach(m => this.data.addMember(m));
        this.data.name = this.editType.name;
        this.data.description = this.editType.description;
        this.project.saveType(this.editType);
        this.dialogRef.close(this.data);
      } else {
        this.typeService.createType(this.editType);
        this.project.saveType(this.editType, true);
        this.dialogRef.close(this.editType);
      }
    } else {
      // this.error
    }
  }

  onCancelClick($event) {
    this.dialogRef.close();
  }

  onParameterTypeChanged($event, i, j) {
    this.editType.memberMap[i].parameters[j].type = $event;
  }

  editCustomCode(member) {
    // console.log(member);
    const editorDialogRef = this.dialog.open(CustomCodeEditorPanelComponent, {
      width: '800px',
      height: '600px',
      data: member.code
    });
    editorDialogRef.componentInstance.codeChange.subscribe(code => {
      console.log(code);
      member.code = code;
    });
  }
}
