import { ProjectService } from './../../../services/project/project.service';
import { MemberEditorComponent } from './../../project/member-editor/member-editor.component';
import { TypeEditorComponent } from './../../project/type-editor/type-editor.component';
import { DialogService } from './../../../common/dialog/dialog.service';
import { Point } from './../../../models/point.model';
import { MatMenuTrigger } from '@angular/material/menu';
import { Component, OnInit, Input, ViewChild, AfterViewInit } from '@angular/core';
import { NestedTreeControl } from '@angular/cdk/tree';
import { MatTreeNestedDataSource } from '@angular/material/tree';
import { InterfaceService } from 'src/app/services/interface/interface.service';
import { Store } from '@ngrx/store';
import * as fromStore from '../../../store';
import { UtilsService } from 'src/app/services/common/utils.service';
import { ProjectPanelServiceService } from 'src/app/services/project/project-panel-service.service';
import { MatDialog } from '@angular/material';
import { APP_HTTP_RESULT_CODE } from 'src/app/constants/http/http.constants';
import { SimpleDialogBtnType } from 'src/app/common/dialog/components/simple-dialog/simple-dialog.component';
import { Member, MemberFrom } from 'libs/flowchart-ts/flowchart-core/src/lib/models/member.model';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/type.model';


export class ChartTypeTreeNode {
  name: string;
  data: Member | ChartType;
  children: ChartTypeTreeNode[];
  constructor(name: string, children: ChartTypeTreeNode[], data: Member | ChartType) {
    this.name = name;
    this.children = children;
    this.data = data;
  }
}

@Component({
  selector: 'app-interface-panel',
  templateUrl: './interface-panel.component.html',
  styleUrls: ['./interface-panel.component.css']
})
export class InterfacePanelComponent implements OnInit, AfterViewInit {
  @Input() projectName;

  treeControl = new NestedTreeControl<ChartTypeTreeNode>(node => node.children);
  dataSource = new MatTreeNestedDataSource<ChartTypeTreeNode>();

  tree: ChartTypeTreeNode[] = [];
  layer = 0;

  selectedNode: ChartTypeTreeNode;
  @ViewChild('typeMenuTrigger', { static: false }) typeMenuTrigger: MatMenuTrigger;
  @ViewChild('memberMenuTrigger', { static: false }) memberMenuTrigger: MatMenuTrigger;
  menuLocation: Point;
  constructor(
    public dialog: MatDialog,
    private interfaceService: InterfaceService,
    private store: Store<fromStore.FlowchartState>,
    private utilsService: UtilsService,
    private dialogService: DialogService,
    private projectPanelService: ProjectPanelServiceService,
    private typeService: InterfaceService,
    private projectService: ProjectService

  ) { }

  hasChild = (_: number, node: ChartTypeTreeNode) => !!node.children && node.children.length > 0;

  ngOnInit() {
    this.initData();
    this.renderTree();
  }

  ngAfterViewInit() {

  }

  initData() {
    this.menuLocation = new Point(0, 0);
    this.dataSource.data = this.transformDataToTreeNode(this.interfaceService.getAllCustomTypes());

  }

  transformDataToTreeNode(types: ChartType[]) {
    const treenodes = types.map(t => {
      if (t.members) {
        const memberNodes = [];
        t.members.forEach(m => {
          // m.belongType = t; TODO
          if (m.from === MemberFrom.FILE) {
            memberNodes.push(new ChartTypeTreeNode(m.name, [], m));
          }
        });
        return new ChartTypeTreeNode(t.name, memberNodes, t);
      }
      return new ChartTypeTreeNode(t.name, [], t);
    });
    this.projectPanelService.getLevel(treenodes);
    return treenodes;
  }

  renderTree() {
    // this.store.select(fromStore.getTypes).subscribe(result => {
    //   this.dataSource.data = this.transformDataToTreeNode(result);
    // });
    this.typeService.chartTypes.subscribe(types => {
      this.dataSource.data = this.transformDataToTreeNode(types);
    })
  }

  onNodeContextMenu($event, node: ChartTypeTreeNode, index: number) {
    $event.stopPropagation();
    $event.preventDefault();
    this.selectedNode = node;
    this.menuLocation = new Point($event.clientX, $event.clientY);
    if (node.data instanceof ChartType) {
      this.typeMenuTrigger.openMenu();
    } else {
      this.memberMenuTrigger.openMenu();
    }
  }

  onAddTypeClick($event) {
    this.dialogService.open(TypeEditorComponent, null).subscribe(tp => {
      // this.typeService.createType(tp);
    });
  }

  onEditTypeClick($event) {
    this.dialogService.open(TypeEditorComponent, this.selectedNode.data).subscribe(tp => {
      this.dataSource.data = this.transformDataToTreeNode(this.typeService.getAllCustomTypes());
    })
  }

  onEditMemberClick($event) {
    this.dialog.open(MemberEditorComponent, { data: this.selectedNode.data, width: '450px', height: '400px', disableClose: true })
      .afterClosed().subscribe((data) => {
        if (data) {
          const currentMember = this.selectedNode.data;
          const fatherTypeNode: ChartType = data.data.father;
          const members = fatherTypeNode.members.map(m => {
            if (m.name === currentMember.name) {
              return data.data;
            } else {
              return m;
            }
          });
          // const newTypeNode = ChartType.parseFromServer({...fatherTypeNode, members});
          // newTypeNode.project = fatherTypeNode.project;
          // this.typeService.updateType(newTypeNode as any).then(result => {
          //   if (result.code === APP_HTTP_RESULT_CODE.OK) {
          //     this.store.dispatch(new fromStore.UpdateTypeSuccess(newTypeNode as any));
          //   }
          // });
        }
      });
  }

  onAddTypeMemberClick($event) {
    this.dialogService.open(MemberEditorComponent).subscribe(result => {
      if (result) {
        this.store.dispatch(new fromStore.CreateMember(result));
      }
    });
  }

  onDeleteTypeClick($event) {

  }

  onDeleteMemberClick() {
    const deletedMember = this.selectedNode.data;
    this.dialogService.openSimple({ text: `是否确认删除成员${deletedMember.name}?`, btnType: SimpleDialogBtnType.YES_NO }).subscribe(btnCode => {
      if (btnCode === 1) {
        const currentMember: Member = this.selectedNode.data as Member;
        // const fatherTypeNode: ChartType = currentMember.belongType;
        // console.log(this.selectedNode.data);
        // const members = fatherTypeNode.members.filter(m => m.name !== currentMember.name);
        // const newTypeNode = ChartType.parseFromServer({...fatherTypeNode, members});
        // newTypeNode.project = fatherTypeNode.project;
        // this.typeService.updateType(newTypeNode as any).then(result => {
        //   if (result.code === APP_HTTP_RESULT_CODE.OK) {
        //     this.store.dispatch(new fromStore.UpdateTypeSuccess(newTypeNode as any));
        //   }
        // }); TODO
      }
    });
  }
}

