import { CSharpGenerator } from './csharp-generator';
import { ICodeGenerator } from "./base-generator";
import { LuaGenerator } from "./lua-generator";
import { PyGenerator } from './py-generator';
import { MinecraftGenerator } from './minecraft-generator';
import { JsGenerator } from './js-generator';
import { AntlrParser } from './../../../../flowchart-parser/src/lib/parser';



export class GeneratorManager {
    private static instance: GeneratorManager;

    generatorMap: Map<string, ICodeGenerator>;
    private constructor() {
        this.generatorMap = new Map<string, ICodeGenerator>();
    };
    static getInstance() {
        if (!GeneratorManager.instance) {
            GeneratorManager.instance = new GeneratorManager();
            GeneratorManager.instance.generatorMap.set('lua', new LuaGenerator(new AntlrParser()));
            GeneratorManager.instance.generatorMap.set('cs', new CSharpGenerator(new AntlrParser()));
            GeneratorManager.instance.generatorMap.set('js', new JsGenerator(new AntlrParser()));
            GeneratorManager.instance.generatorMap.set('py', new PyGenerator(new AntlrParser()));
            GeneratorManager.instance.generatorMap.set('mc', new MinecraftGenerator(new AntlrParser()));
        }
        return GeneratorManager.instance;
    }

    getGenerator(generatorTag: string) {
        const generator = this.generatorMap.get(generatorTag);
        return generator;
    }
}