import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { CanvasService } from '../chart/canvas.service';
import { ChangeFlowchart } from 'src/app/store';
import { Store } from '@ngrx/store';
import * as fromStore from '../../store';
import { HttpResult } from 'src/app/common/http/http.result.model';
import { APP_HTTP_RESULT_CODE } from 'src/app/constants/http/http.constants';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/type.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';


@Injectable({
  providedIn: 'root'
})
export class CurrentProjectService {

  constructor(
    private http: HttpClient,
    private canvasService: CanvasService,
    private store: Store<fromStore.FlowchartState>,
  ) { }


  async chooseTab(index: number) {
    this.store.dispatch(new ChangeFlowchart(index));
    // 切换流程图时要清空选中或编辑状态的节点和连线
    if (index !== -1) {
      const openingCharts = await this.asyncGetOpeningCharts();
      this.canvasService.removeNodeSelection(openingCharts[index]);
      this.canvasService.removeConnectorEditing(openingCharts[index]);
      return this.store.select(fromStore.getOpeningCharts);
    }
  }
  async asyncGetOpeningCharts() {
    const promise = new Promise<Chart[]>((resolve) => {
      this.store.select(fromStore.getOpeningCharts).subscribe(openingCharts => {
        resolve(openingCharts);
      });
    });
    const flowcharts = await promise;
    return flowcharts;
  }

  async asyncGetOpeningIndex() {
    const promise = new Promise<number>((resolve) => {
      this.store.select(fromStore.getCurrentChartIndex).subscribe(index => {
        resolve(index);
      });
    });
    const index = await promise;
    return index;
  }

  async getPathDefaultTypes(projectId: string) {
    const url = `api/project/default_types/${projectId}`;
    try {
      const result = await this.http.get<any>(url).toPromise();
      if (result.code === APP_HTTP_RESULT_CODE.OK) {
        const temp: Map<string, string> = result.data;
        result.data = new Map<string, ChartType>();
        temp.forEach((v, k) => {

        });
        return new HttpResult<Map<string, ChartType>>(APP_HTTP_RESULT_CODE.OK, '', result.data);
      }
      return new HttpResult<Map<string, ChartType>>(APP_HTTP_RESULT_CODE.ERROR, result.msg, null);
    } catch (error) {
      return new HttpResult<Map<string, ChartType>>(APP_HTTP_RESULT_CODE.ERROR, error.message, null);
    }
  }

  async save(projectId: string, chartId?: string) {
    const url = `api/project/save/${projectId}`;
    try {
      const result = await this.http.post<HttpResult<string>>(url, {}).toPromise();
      if (result.code === APP_HTTP_RESULT_CODE.OK) {
        return new HttpResult<string>(APP_HTTP_RESULT_CODE.OK, '', result.data);
      }
      return new HttpResult<string>(APP_HTTP_RESULT_CODE.ERROR, result.msg, null);
    } catch (error) {
      return new HttpResult<string>(APP_HTTP_RESULT_CODE.ERROR, error.message, null);
    }
  }
}
