import { APP_HTTP_RESULT_CODE } from './../../../constants/http/http.constants';
import { Injectable } from '@angular/core';

import { Effect, Actions, ofType } from '@ngrx/effects';
import { of } from 'rxjs';

import { map, switchMap, catchError, mergeMap } from 'rxjs/operators';

import * as currentProjectActions from '../../actions/projects/current-project.actions';
import { ProjectService } from 'src/app/services/project/project.service';

@Injectable()
export class CurrentProjectEffects {
    constructor(
        private actions$: Actions,
        private projectService: ProjectService
    ) { }

    @Effect()
    loadCurrentProject$ = this.actions$.pipe(
        ofType(currentProjectActions.LOAD_CURRENT_PROJECT),
        mergeMap((action: currentProjectActions.LoadCurrentProject) => this.projectService.getProjectInfo(action.payload).then(
            result => {
                if (result.code === APP_HTTP_RESULT_CODE.OK) {
                    return new currentProjectActions.LoadCurrentProjectSuccess(result.data);
                }
                return new currentProjectActions.LoadCurrentProjectFail(result.msg);
            }
        ).catch(error => {
            return new currentProjectActions.LoadCurrentProjectFail(error.message);
        }))
    );
}
