import { Component, EventEmitter, Inject, OnInit, Output } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Store } from '@ngrx/store';
import * as fromStore from '../../../store';
import { FormControl, Validators } from '@angular/forms';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/public-api';

@Component({
  selector: 'app-flowchart-editor',
  templateUrl: './flowchart-editor.component.html',
  styleUrls: ['./flowchart-editor.component.css']
})
export class FlowchartEditorComponent implements OnInit {
  @Output() save: EventEmitter<any> = new EventEmitter();

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    @Inject(MAT_DIALOG_DATA) public project: string,
    public dialogRef: MatDialogRef<FlowchartEditorComponent>,
    private store: Store<fromStore.FlowchartState>,
  ) { }

  error: boolean;
  errorInfo: string;
  folderName;
  folderPath: string;
  // 不带project的路径
  relativePath: string;
  typeSelectList = [];
  currentType: string;
  type: ChartType;

  nameControl = new FormControl('', [Validators.pattern('^[\-A-Za-z0-9_\u2E80-\u9FFF]*$')]);
  typeControl = new FormControl('', [Validators.required]);

  ngOnInit() {
    this.initTypeSelectList();
    this.initDefaultType();
    this.initName();
    this.generatePath();
  }

  initName() {
    if (this.data.newFolder) {
      return;
    }
    this.folderName = this.data.path.substring(this.data.path.lastIndexOf('.') + 1);
  }

  async generatePath() {
    if (this.data.path === '') {
      this.folderPath = this.data.project.name + '\\';
      this.relativePath = '';
      return;
    }
    this.relativePath = this.data.path.replace(/\./g, '\\');
    this.folderPath = this.data.project.name + '\\' + this.relativePath;
  }

  initTypeSelectList() {
    this.data.project.types.forEach(ct => {
      this.typeSelectList.push(ct.name);
    });
  }

  initDefaultType() {
    this.currentType = this.data.defaultType;
  }


  onCancelClick() {
    this.dialogRef.close();
  }

  checkName(e) {
    const name = e.target.value || e;
    if (!/^[a-zA-Z0-9_\-\u2E80-\u9FFF]*$/g.test(name)) {
      this.error = true;
      this.errorInfo = `${name}不合法，仅允许使用英文字母中文字符和下划线及连字符`;
    } else {
      this.errorInfo = null;
      this.error = false;
    }
  }

  checkArgs() {
    if ((!this.currentType || this.currentType === '') || (!this.folderName || this.folderName === '')) {
      this.error = true;
    } else {
      this.error = false;
    }
  }

  onSaveClick() {
    this.typeControl.markAsTouched();
    if (this.typeControl.hasError('required') || this.typeControl.hasError('pattern') ||
      this.nameControl.hasError('required') || this.nameControl.hasError('pattern')
    ) {
      return;
    }
    // this.data.type.name = this.folderName;
    console.log(this.folderName);
    this.save.emit(
      {
        folderName: this.folderName,
        type: this.currentType,
        path: this.folderPath,
        relativePath: this.relativePath,
        newChartType: this.data.type
      });
    this.dialogRef.close();
  }

  async getChartList() {
    const promise = new Promise(resolve => {
      this.store.select(fromStore.getChartListSelector).subscribe(data => {
        resolve(data);
      });
    });
    return await promise;
  }
}
