import { ChartType } from './../../../../flowchart-core/src/lib/models/type.model';
import { AstNodeInfo } from './base-generator';
import { LuaGenerator } from './lua-generator';
export class MinecraftGenerator extends LuaGenerator {
    readonly tag: string = 'mc';
    generateCode(ni: AstNodeInfo) {
        if (!this.parseInfo.isControlNode) {
            this.codes = [];
            this.codes.push(`function asycnflow.node_funcs.${ni.functionName}(slf)`);
            this.codes.push(`    return asyncflow.world:DoWithEntityByID(slf.id, function(self))`);
            this.codes.push(`        local ret = ${ni.code}`);
            if (ni.type === ChartType.BoolType) {
                this.codes.push(`    return ret`);
            } else if (ni.type === ChartType.NumberType) {
                this.codes.push(`    return ret ~=0`);
            } else {
                this.codes.push(`    return true`);
            }
            this.codes.push('end');
            this.codes.push("");
        }
    }
}