import { DEBUG_METHODS } from './../constants/debug-message.constants';

export enum DebugStartUpType {
    DEBUG = 'debug',
    QUICK_DEBUG = 'quick_debug',
    REPLAY = 'replay'
}

export class DebugStartUpInfo {
    type: DebugStartUpType;
    params: any;
    constructor(type: DebugStartUpType, params?: any) {
        this.type = type;
        this.params = params;
    }
}
