import { ParameterJsonKey } from "../constants/chart.constants";
import { Item } from "./item.model";
import { ChartType } from "./type.model";

/**
 * Parameter
 */
export class Parameter extends Item {
    default: string;
    count: number;

    fromJson(jobj: any, typeConverter: (t: string) => ChartType): boolean {
        if (!super.fromJson(jobj, typeConverter)) {
            return false;
        }
        this.default = jobj[ParameterJsonKey.Default];
        this.count = jobj[ParameterJsonKey.Count];
        return true;
    }

    toJson() {
        const p: any = {};
        p[ParameterJsonKey.Name] = this.name;
        p[ParameterJsonKey.Type] = this.type ? this.type.name : ChartType.UndefinedType.name;
        p[ParameterJsonKey.Description] = this.description;
        p[ParameterJsonKey.Default] = this.default;
        p[ParameterJsonKey.Count] = this.count;
        return p;
    }
}