import { ToastService } from './../../common/toast/toast.service';
import { OutputService } from './../common/output.service';
import { FileService } from 'src/app/services/common/file.service';
import { IpcService } from './../../common/ipc/services/ipc.service';
import { ActivatedRoute, Router } from '@angular/router';
import { APP_HTTP_RESULT_CODE } from './../../constants/http/http.constants';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HttpResult } from 'src/app/common/http/http.result.model';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';
import { ProjectConfig } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project-config.model';
import { Subject } from 'rxjs';
import { RecentProject } from 'src/app/models/project.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import * as JSON5 from 'json5';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/type.model';
import { EventType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/event.model';

export const ProjectBase = `api/project`;

@Injectable({
  providedIn: 'root'
})
export class ProjectService {
  private _backupCharts: Chart[];
  private _currentProject: Project;
  currentProject: Subject<Project>;



  constructor(private http: HttpClient,
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private ipcService: IpcService,
    private outputService: OutputService,
    private toastService: ToastService,
    private fileService: FileService) {
    this.currentProject = new Subject<Project>();
  }


  /**
   * Only for electron native
   * @param path 
   */
  async loadProject(path: string) {
    if (!Project.fs) {
      Project.fs = this.fileService;
    }
    return new Promise<Project>((resolve, reject) => {
      const project = new Project();
      project.load(path).then(results => {
        this.backupCharts(project);
        this._currentProject = project;
        this.currentProject.next(project);
        // this.recordProjectUseRecord(project);
        resolve(project);
      });
    });
  }

  recordProjectUseRecord(project: Project) {
    const path = './data/cache.json';
    this.ipcService.exists(path).then(exists => {
      if (exists) {
        // Read local records
        this.readLocalRecentUseRecord().then(projects => {
          const projectPath = project.path.replace(/\//g, '\\');
          const index = projects.findIndex(p => p.path.replace(/\//g, '\\') === projectPath);
          if (index > -1) {
            const updateProj = projects.splice(index, 1)
          }
        })
      } else {

      }
    })
  }

  readLocalRecentUseRecord() {
    const path = './data/cache.json'
    return new Promise<Project[]>((resolve, reject) => {
      this.ipcService.readFile(path).then(result => {
        const projects: Project[] = [];
        if (result.code === 0) {
          const content = JSON.parse(result.data);
          if (content.projects.recent) {
            content.projects.recent.forEach(p => {
              const proj = new Project();
              proj.uid = p.uid;
              proj.name = p.project;
              proj.path = p.path;
              projects.push(proj);
            });
            resolve(projects);
          }
        }
      });
    })
  }

  getCurrentProject() {
    return this._currentProject;
  }

  setCurrentProject(p: Project) {
    this._currentProject = p;
    this.currentProject.next(this._currentProject);
  }

  async getProjectInfo(project: string) {
    const url = `${ProjectBase}/info/${project}`;
    const httpResult: HttpResult<any> = await this.http.get<HttpResult<any>>(url).toPromise();
    if (httpResult.code === APP_HTTP_RESULT_CODE.OK) {
      const proj = new Project();
      proj.path = httpResult.data.path;
      proj.config = new ProjectConfig();
      proj.config.generator = httpResult.data.config.generator;
      proj.config.flowchartTypeDict = httpResult.data.config.flowchart_types;
      return new HttpResult<Project>(httpResult.code, 'success', proj);
    }
    return httpResult;

  }

  async open(path: string) {
    const url = `${ProjectBase}/open`;
    const result = await this.http.post<HttpResult<any>>(url, { path }).toPromise();
    return result;
  }

  routeToNewProject(project: string) {
    this.router.navigate(['project', project], { relativeTo: this.activatedRoute.root });
  }

  async create(project: Project) {
    const url = `${ProjectBase}/create`;
    const result = await this.http.post<HttpResult<any>>(url, { uid: project.uid, name: project.name, path: project.path, generator: project.config.generator }).toPromise();
    return result;
  }

  async update(project: Project) {
    const url = `${ProjectBase}/update`;
    const result = await this.http.post<HttpResult<any>>(url, { uid: project.uid, name: project.name, path: project.path, generator: project.config.generator }).toPromise();
    return result;
  }

  save(chart?: Chart) {
    if (chart) {
      const project: Project = { ...this._currentProject } as Project;
      Object.setPrototypeOf(project, Project.prototype);
      const charts = [...this._backupCharts];
      const index = charts.findIndex(c => c.uid === chart.uid);
      if (index > -1) {
        charts[index] = chart;
      } else {
        charts.push(chart);
      }
      project.charts = charts;
      project.save();
      this.backupCharts(project);
      this.outputService.log(`Save '${chart.path}' successfully`);
      return true;
    } else {
      this._currentProject.save();
      this.backupCharts(this._currentProject);
      this.toastService.info(`Save project successfully`);
      this.outputService.log(`Save project '${this._currentProject.path}' successfully`);
      return true;
    }
  }

  private backupCharts(project: Project) {
    const charts = [];
    project.charts.forEach(c => {
      const chart = new Chart();
      let chartJsonString = c.saveAsJson().join('\n').trim();
      chartJsonString = chartJsonString.substr(0, chartJsonString.length - 1);
      console.log('backup charts', chartJsonString)
      chart.fromJson(JSON5.parse(chartJsonString), project, (typename: string) => { return project.getType.call(project, typename) });
      charts.push(chart);
    })
    this._backupCharts = charts;
  }


  private loadFilesOfDir(dirPath: string, options?: { extension?: string, excludes?: string[] }) {
    return new Promise<string[]>((resolve, reject) => {
      this.ipcService.readDir(dirPath).then(result => {
        if (result.code === 0) {
          const files = result.args;
          if (files) {
            const promises: Promise<string>[] = [];
            files.forEach(file => {
              if (
                (!options.extension || file.endsWith(options.extension)) &&
                (!options.excludes || !options.excludes.includes(file))
              ) {
                const p = new Promise<string>((resol, rej) => {
                  this.ipcService.readFile(`${dirPath}\\${file}`).then(result => {
                    if (result.code === 0) {
                      resol(result.data);
                    } else {
                      rej(result);
                    }
                  })
                });
                promises.push(p);
              }
            })
            Promise.all(promises).then(results => {
              resolve(results);
            });
          }
        }
      })
    })
  }

  async fetchDefaultGlobalType() {
    const json = await this.http.get<any>('assets/data/Global.json').toPromise();
    const global = new ChartType();
    const toolProject = new Project();
    global.fromJson(json, typename => toolProject.getType.call(toolProject, typename));
    return global;
  }

  async fetchDefaultEvents() {
    const json = await this.http.get<any>('assets/data/event.json').toPromise();
    const toolProject = new Project();
    const events: EventType[] = [];
    json.forEach((ev: any) => {
      const event = new EventType();
      event.fromJson(ev, typename => { return toolProject.getType.call(toolProject, typename) });
      events.push(event);
    })
    return events;
  }
}
