import * as fromToolbar from '../../actions/toolbar/toolbar.action';
import { CONNECTOR_SHAPE, CONNECTOR_TYPE } from 'src/app/constants/canvas/connector.constants';
import { createReducer, on } from '@ngrx/store';
import { FIX_NODE_POSITION_SUCCESS } from '../../actions/toolbar/toolbar.action';
import { ConnectorType, ShapeType } from 'libs/flowchart-ts/flowchart-core/src/public-api';

export interface ToolbarState {
    connectorLatestShape: ShapeType;
    connectorLatestType: ConnectorType;
    fixNodePositionId: number;
}

export const initialState: ToolbarState = {
    connectorLatestShape: ShapeType.CURVE,
    connectorLatestType: ConnectorType.ALWAYS,
    fixNodePositionId: -1
};

export function reducer(
    state = initialState,
    action: fromToolbar.ToolbarAction
): ToolbarState {
    switch (action.type) {
        case fromToolbar.CHANGE_LATEST_CONNECTOR_SHAPE_SUCCESS: {
            const shape = action.shape;
            return {
                ...state,
                connectorLatestShape: shape
            };
        }
        case fromToolbar.CHANGE_LATEST_CONNECTOR_TYPE_SUCCESS: {
            const type = action.Type;
            return {
                ...state,
                connectorLatestType: type
            };
        }
        case fromToolbar.FIX_NODE_POSITION_SUCCESS: {
            const id = action.payload;
            return {
                ...state,
                fixNodePositionId: id
            };
        }
        case fromToolbar.FIX_CHART_POSITION_SUCCESS: {
            return {
                ...state
            };
        }
    }
    return state;
}

export const getLatestConnectorShape = (state: ToolbarState) => state.connectorLatestShape;
export const getLatestConnectorType = (state: ToolbarState) => state.connectorLatestType;
export const getFixedNodePosition = (state: ToolbarState) => state.fixNodePositionId;
export const fixToCurrentChart = (state: ToolbarState) => state;
