import { Component, Inject, OnInit, ViewChildren } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { Store } from '@ngrx/store';
import { Member } from 'libs/flowchart-ts/flowchart-core/src/lib/models/member.model';
import { Parameter } from 'libs/flowchart-ts/flowchart-core/src/lib/models/parameter.model';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/public-api';
import { CustomCode } from 'src/app/core/models/member/member.model';
import { InterfaceService } from 'src/app/services/interface/interface.service';
import * as fromStore from 'src/app/store/index';
import { deepCopy } from 'src/app/store/reducers/utilsFunction';
import { CustomCodeEditorPanelComponent } from '../custom-code-editor-panel/custom-code-editor-panel.component';

@Component({
  selector: 'app-member-editor',
  templateUrl: './member-editor.component.html',
  styleUrls: ['./member-editor.component.css']
})
export class MemberEditorComponent implements OnInit {
  constructor(
    public dialog: MatDialog,
    @Inject(MAT_DIALOG_DATA) public data: Member,
    private store: Store,
    private typeService: InterfaceService,
    private dialogRef: MatDialogRef<MemberEditorComponent>,
  ) { }
  @ViewChildren('parameterNameInput') parameterNameInput;
  member;
  parameters = [];
  code: CustomCode = null;
  chartTypes;
  types;

  ngOnInit() {
    this.member = { ...this.data };
    this.parameters = this.member.parameters ? deepCopy([...this.member.parameters]) : [];
    this.code = this.member.code ? deepCopy(this.member.code) : new CustomCode();
    // 只有Global具有用户自定义代码字段
    if (this.member.father.name !== 'Global') {
      this.code.hasCode = false;
    }
    this.store.select(fromStore.getTypes).subscribe(result => {
      this.chartTypes = result;
      this.types = this.typeService.getBasicTypeList(true).concat(this.chartTypes.map(c => c.name).concat(['List', 'Dict']));
    });
  }

  onMemberCategoryChanged($event) {
    this.member.memberType = Number.parseInt($event.target.options[$event.target.selectedIndex].value);
  }

  onDeleteParameterClick($event, j) {
    this.member.parameters.splice(j, 1);
  }

  onAddParameterClick() {
    const newPara = new Parameter();
    newPara.name = '';
    newPara.type = ChartType.StringType;
    this.parameters.push(newPara);
    this.member.parameters = this.parameters;
    // 添加parameter后input自动聚焦
    setTimeout(() => {
      const newInputId = (this.parameters.length - 1).toString();
      const target = this.parameterNameInput._results.find(elementRef => elementRef.nativeElement.id === newInputId);
      target.nativeElement.focus();
    }, 0);
  }

  onSaveClick($event) {
    this.member.parameters = this.parameters;
    this.dialogRef.close({ data: this.member });
  }

  editCustomCode(member) {
    const editorDialogRef = this.dialog.open(CustomCodeEditorPanelComponent, {
      width: '800px',
      height: '600px',
      data: this.code
    });
    editorDialogRef.componentInstance.codeChange.subscribe(code => {
      this.code = code;
      this.member.code = this.code;
    });
  }

  onCancelClick($event) {
    this.dialogRef.close();
  }


}
