    //                 return new currentChartActions.UpdateVariablesFailure(action.payload.variables);
    //             }
    //         }))
    // );

    @Effect()
    deleteItems = this.actions$.pipe(
        ofType(currentChartActions.DELETE_ITEMS),
        mergeMap((action: currentChartActions.DeleteItems) => this.chartService.deleteItems(action.payload).then(result => {
            if (result.code === APP_HTTP_RESULT_CODE.OK) {
                return new currentChartActions.DeleteItemsSuccess(action.payload);
            } else {
                this.outputService.error(`删除失败: ${result.msg}`);
                return new currentChartActions.DeleteItemsFailure(null);
            }
        }).catch(error => {
            this.outputService.error(`${error.error.message}`);
            return new currentChartActions.DeleteItemsFailure(null);
        }))
    );

