import { Injectable } from '@angular/core';
@Injectable({
  providedIn: 'root'
})
export class UtilsService {

  constructor() { }

  // 深拷贝, 数组/对象复制
  deepCopy(obj) {
    if (obj === null || typeof obj !== 'object') {
      return obj;
    }
    const copy = Array.isArray(obj) ? [] : {};
    Object.keys(obj).forEach(key => {
      copy[key] = this.deepCopy(obj[key]);
    });
    return copy;
  }

  generanteUrl(project: string, chartId: string) {
    const url = 'api/chart?project=' + project + '&chartId=' + chartId;
    return url;
  }

  transformObjectToArray(object: object) {
    const arr = [];
    // tslint:disable-next-line:forin
    for (const i in object) {
      const o = {};
      o[i] = object[i];
      arr.push(o[i]);
    }
    return arr;
  }
}
