import { EventEmitter } from '@angular/core';
import { Component, Inject, OnInit, Output } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { CustomCode } from 'src/app/core/models/member/member.model';

@Component({
  selector: 'app-custom-code-editor-panel',
  templateUrl: './custom-code-editor-panel.component.html',
  styleUrls: ['./custom-code-editor-panel.component.css']
})
export class CustomCodeEditorPanelComponent implements OnInit {
  @Output() codeChange = new EventEmitter();
  editorOptions = { theme: 'vs-dark', language: 'javascript' };
  code: string = '';
  language;
  lauguagesList =
    [
      { name: 'Javascript', value: 'javascript', localValue: 'js' },
      { name: 'Lua', value: 'lua', localValue: 'lua' },
      { name: 'C#', value: 'csharp', localValue: 'cs' },
      { name: 'Python', value: 'python', localValue: 'py' },
      { name: 'C++', value: 'cpp', localValue: 'cpp' },
    ];

  constructor(
    private dialogRef: MatDialogRef<CustomCodeEditorPanelComponent>,
    @Inject(MAT_DIALOG_DATA) public data: CustomCode,

  ) { }

  ngOnInit() {
    this.data.language = this.lauguagesList[0];
  }

  languageChange($event) {
    const target = $event.target;
    const lang = target.options[target.selectedIndex];
    this.editorOptions = {
      ...this.editorOptions,
      language: lang.value
    };
    this.data.language = this.lauguagesList[target.selectedIndex];
  }

  onCancelClick($event) {
    this.dialogRef.close();
  }

  onConfirmClick($event) {
    this.codeChange.emit(this.data);
    this.dialogRef.close();
  }

}
