import { ChartJsonKey, GroupJsonKey } from './../constants/chart.constants';
import { ChartType } from './type.model';
import { Chart } from './chart.model';
import { ChartNode } from './node.model';
import { Rect } from './../../../../flowchart-common/src/lib/models/rect.model';

export enum LayoutType {
    MANUAL,
    AUTO,
    TOP_DOWN,
    LEFT_RIGHT,
}

export class Group {
    /**
     * uid
     */
    uid: string;
    /**
     * group name
     */
    name: string;
    /**
     * group nodes
     */
    nodes: ChartNode[];
    /**
     * group layout type 
     */
    layout: LayoutType
    /**
     * belonged chart
     */
    chart: Chart;
    /**
     * description
     */
    description: string;

    /**
     * remarks
     */
    remarks: string[];
    /**
     * Group tag
     */
    readonly tag: string = 'group';

    //#region Constructor
    constructor(chart?: Chart) {
        if (chart) {
            this.chart = chart;
        }
        this.layout = LayoutType.MANUAL;
        this.remarks = [];
    }
    //#endregion


    //#region Methods

    /**
     * Add node to group
     * @param node Node to be added
     * @returns 
     */
    addNode(node: ChartNode, index?: number) {
        if (node.group === this) {
            return;
        }
        if (node.group != null) {
            node.group.removeNode(node.uid);
        }
        if (!this.nodes) {
            this.nodes = [];
        }
        node.setGroup(this);
        this.nodes.push(node);
    }

    /**
     * Remove node from group
     * @param node node to be removed
     * @returns 
     */
    removeNode(nodeId: string) {
        if (!this.nodes) {
            return;
        }
        const index = this.nodes.findIndex(n => n.uid === nodeId);
        if (index > -1) {
            // First clear group info in node
            this.nodes[index].group = null;
            // Then remove node from node list
            this.nodes.splice(index, 1);
        } else {
            return;
        }
    }

    getRect(padding: number = 15) {
        let x = Number.POSITIVE_INFINITY;
        let y = Number.POSITIVE_INFINITY;
        let right = Number.NEGATIVE_INFINITY;
        let bottom = Number.NEGATIVE_INFINITY;
        this.nodes.forEach(n => {
            x = Math.min(n.rect.x, x);
            y = Math.min(n.rect.y, y);
            right = Math.max(n.rect.x + n.rect.w, right);
            bottom = Math.max(n.rect.y + n.rect.h, bottom)
        })
        return Rect.createWithLeftTopVertex(x - padding, y - padding, right - x + padding * 2, bottom - y + padding * 2)
    }

    /**
     * Clear group nodes
     */
    clear() {
        this.nodes.forEach(n => n.setGroup(null));
        this.nodes = [];
    }

    //#endregion


    //#region Load and save
    fromJson(json: any, chart: Chart, typeConverter: (tname: string) => ChartType) {
        this.uid = json[GroupJsonKey.Uid];
        this.name = json[GroupJsonKey.Name];
        if (json[GroupJsonKey.Nodes]) {
            json[GroupJsonKey.Nodes].forEach((nuid: any) => {
                const node = chart.nodeMap.get(nuid)
                this.addNode(node);
            });
        }
        this.layout = json[GroupJsonKey.Layout];
        this.description = json[GroupJsonKey.Description];
        if (json[GroupJsonKey.Remarks]) {
            this.remarks = [];
            json[GroupJsonKey.Remarks].forEach((r: any) => this.remarks.push(r.replace(/\\n/g, '\n')));

        }
    }

    toJson() {
        const g: any = {};
        g[GroupJsonKey.Uid] = this.uid;
        g[GroupJsonKey.Name] = this.name;
        g[GroupJsonKey.Nodes] = this.nodes.map(n => n.uid);
        g[GroupJsonKey.Layout] = this.layout;
        g[GroupJsonKey.Remarks] = this.remarks;
        return g;
    }


}