import { Component, OnInit, Output, EventEmitter, Input, OnChanges, ViewChild, ElementRef, SimpleChanges, ChangeDetectionStrategy } from '@angular/core';
import { Store } from '@ngrx/store';
import * as fromStore from '../../../store';
import { Point } from 'src/app/models/point.model';
import { Drag } from 'src/app/models/canvas/Drag';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { Transform } from 'src/app/models/canvas/Canvas';
import { Canvas } from '@antv/g-canvas';
import { ConnectorPipe } from 'src/app/pipes/connector.pipe';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';

@Component({
  selector: 'app-mini-map',
  templateUrl: './mini-map.component.html',
  styleUrls: ['./mini-map.component.css'],
  animations: [
    trigger('fold', [
      state('collapsed', style({
        width: '20px',
        height: '20px'
      })),
      state('expand', style({

      })),
      transition('expand => fold', [animate('0.2s ease-in')]),
      transition('fold => expand', [animate('0.2s ease-out')])
    ])
  ],
  changeDetection: ChangeDetectionStrategy.OnPush

})
export class MiniMapComponent implements OnInit, OnChanges {
  @ViewChild('thumbnail', { static: false }) thumbnail: ElementRef;
  @Input() canvasContainerName = 'thumbnail-root';
  @Input() scale = 1;
  // 缩略图ViewBox需要的位移量，作用于viewBoxValue，改变小地图的偏移量
  @Input() thumbnailViewBoxTranslate: Point;
  @Input() canvas: Rect;
  // 当前translate在x、y轴的比例
  @Input() translateScale: Point;
  // 当前的视口大小
  @Input() currentCanvasSize: Point;
  @Input() chart: Chart;
  @Input() openingChartsLength: number;
  @Output() scaleChange: EventEmitter<number> = new EventEmitter();
  @Output() translateScaleChange: EventEmitter<Point> = new EventEmitter();
  @Output() changeCanvasSize: EventEmitter<string> = new EventEmitter();
  step = 0.02;
  isFold = false;
  // viewBoxValue是缩略图整体背景的偏移量，用于将流程图主体内容显示在缩略图中央
  viewBoxValue: string;
  mousedownOnThumbnail = false;
  positionRectSize = new Point(0, 0);
  positionRectPosition = new Point(0, 0);
  positionRectDelta = new Point(0, 0);
  locatorPosition = new Point(0, 0);
  // 缩略图大小与画布大小的比例
  ratio;
  drag = new Drag();
  thumbnailRootHeight: number;
  height = 115;
  width = 200;
  openingCharts = [];
  chartTranslateMap: Map<string, Transform> = new Map();
  canvasContainer;
  group;

  constructor(
    private store: Store<fromStore.FlowchartState>,
    private connectorPipe: ConnectorPipe
  ) { }

  ngOnInit() {
    this.initHeight();
    this.positionRectSize = new Point(this.currentCanvasSize.x / this.scale, this.currentCanvasSize.y / this.scale);
  }

  ngAfterViewInit(): void {
    this.initCanvasContainer();
    this.renderThumbnailBackground();
    this.getRatio();
  }

  getRatio() {
    this.ratio = this.canvas.w / this.width;
  }

  initCanvasContainer() {
    this.canvasContainer = new Canvas({
      id: 'canvas',
      container: this.canvasContainerName,
      width: this.width,
      height: this.height,
    });
    this.initGroup();
  }

  initGroup() {
    this.group = this.canvasContainer.addGroup();
  }

  // 渲染缩略图，包括背景颜色、节点和连线
  getThumbnail() {
    if (!this.canvasContainer) {
      return;
    }
    this.initHeight();
    this.canvasContainer.changeSize(this.width, this.height);
    this.group.clear();
    this.renderThumbnailBackground();
    const viewbox = new Point(this.thumbnailViewBoxTranslate.x * this.canvas.w, this.thumbnailViewBoxTranslate.y * this.canvas.h);
    this.renderNodes(viewbox);
    this.renderLines(viewbox);
  }

  // 渲染缩略图中节点
  renderNodes(viewbox: Point) {
    this.chart.nodes.forEach(n => {
      this.canvasContainer.addGroup();
      const element = this.group.addShape('rect', {
        attrs: {
          x: n.rect.x,
          y: n.rect.y,
          width: n.rect.w,
          height: n.rect.h,
          stroke: 'black',
          lineWidth: 2
        }
      });
      element.moveTo(n.rect.x - viewbox.x, n.rect.y - viewbox.y);
      element.scale(1 / this.ratio);
    });
  }

  // 渲染缩略图中连线
  renderLines(viewbox: Point) {
    this.chart.connectors.forEach(c => {
      this.canvasContainer.addGroup();
      const startPoint = new Point(c.start.rect.cx, c.start.rect.cy);
      const endPoint = new Point(c.end.rect.cx, c.end.rect.cy);
      const path = this.connectorPipe.transform(c.type, startPoint, endPoint, c);
      const element = this.group.addShape('path', {
        attrs: {
          path,
          stroke: 'black',
          lineWidth: 2,
        }
      });
      element.moveTo(-viewbox.x, -viewbox.y);
      element.scale(1 / this.ratio);
    });
  }

  renderGroups(viewbox: Point) {
  }

  renderThumbnailBackground() {
    this.group = this.canvasContainer.addGroup();
    this.group.addShape('rect', {
      attrs: {
        x: 0,
        y: 0,
        width: this.width,
        height: this.height,
        fill: 'white',
        fillOpacity: 0.25,
        zIndex: 0
      }
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (!this.isFold) {
      this.getThumbnail();
    }

    this.getRatio();
    this.getLocatorPosition();
    // 监听scale的变化
    if (changes.scale) {
      this.positionRectSize = new Point(this.currentCanvasSize.x / this.scale, this.currentCanvasSize.y / this.scale);
    }
    // 监听画布实际大小的变化，主要在画布扩大时响应
    if (changes.canvas) {
      const canvas = changes.canvas;
      if (canvas.previousValue && canvas.currentValue) {
        if (canvas.previousValue.w !== canvas.currentValue.w || canvas.previousValue.h !== canvas.currentValue.h) {
          this.initViewBoxValue(this.thumbnailViewBoxTranslate.x * this.canvas.w, this.thumbnailViewBoxTranslate.y * this.canvas.h);
        }
      }
    }
    // 监听当前视口大小变化
    if ((changes.currentCanvasSize && changes.currentCanvasSize.currentValue && changes.currentCanvasSize.previousValue)) {
      if (changes.currentCanvasSize.currentValue.x !== changes.currentCanvasSize.previousValue.x ||
        changes.currentCanvasSize.currentValue.y !== changes.currentCanvasSize.previousValue.y) {
        this.initHeight();
        this.thumbnailRootHeight = this.height + 24;
        this.positionRectSize = new Point(this.currentCanvasSize.x / this.scale, this.currentCanvasSize.y / this.scale);
        if (this.thumbnailViewBoxTranslate) {
          this.viewBoxValue = `${-this.currentCanvasSize.x}, ${-this.currentCanvasSize.y}, ${this.canvas.w}, ${this.canvas.h}`;
          this.initViewBoxValue(this.thumbnailViewBoxTranslate.x * this.canvas.w, this.thumbnailViewBoxTranslate.y * this.canvas.h);
        }
      }
    }
    // 监听流程图切换
    if (changes.chart && changes.chart.currentValue && changes.chart.previousValue
      && changes.chart.currentValue.uid !== changes.chart.previousValue.uid) {

      const currentChartUid = changes.chart.currentValue.uid;
      const transformBeforeLeave = this.chartTranslateMap.get(currentChartUid);
      if (transformBeforeLeave) {
        this.translateScaleChange.emit(new Point(transformBeforeLeave.translateX, transformBeforeLeave.translateY));
        this.scaleChange.emit(transformBeforeLeave.scale);
      }
      const previousChartUid = changes.chart.previousValue.uid;
      const previousScale = changes.scale ? changes.scale.previousValue : 1;
      const previousTranslateScale: Point = changes.translateScale.previousValue;
      this.chartTranslateMap.set(previousChartUid, new Transform(previousTranslateScale.x, previousTranslateScale.y, previousScale));
      this.viewBoxValue = `${-this.currentCanvasSize.x}, ${-this.currentCanvasSize.y}, ${this.canvas.w}, ${this.canvas.h}`;
      this.initViewBoxValue(this.thumbnailViewBoxTranslate.x * this.canvas.w, this.thumbnailViewBoxTranslate.y * this.canvas.h);
      // 监听已打开的流程图数量，如果某个流程图被关闭，则删除chartTranslateMap中的值
      if (changes.openingChartsLength) {
        if (changes.openingChartsLength.currentValue < changes.openingChartsLength.previousValue) {
          this.chartTranslateMap.delete(previousChartUid);
        }
      }
    }
    // 监听已打开流程图数量

  }

  getLocatorPosition() {
    const left = (this.translateScale.x - this.thumbnailViewBoxTranslate.x) * this.canvas.w / this.ratio;
    const top = (this.translateScale.y - this.thumbnailViewBoxTranslate.y) * this.canvas.h / this.ratio;
    this.locatorPosition = new Point(left, top);
  }

  // 根据canvas长宽，按比例计算实际位移
  scaleToValue(scale: Point): Point {
    return new Point(scale.x * this.canvas.x, scale.y * this.canvas.y);
  }

  // 缩略图的宽高比与实际画布保持一致
  initHeight() {
    this.height = this.currentCanvasSize.y / this.currentCanvasSize.x * this.width;
  }

  initViewBoxValue(deltaX: number, deltaY: number) {
    this.viewBoxValue = deltaX + ', ' + deltaY + ', ' + this.canvas.w + ',' + this.canvas.h;
  }

  changePositionRectPosition(deltaX: number, deltaY: number) {
    const left = this.translateScale.x - deltaX * this.ratio / this.canvas.w;
    const top = this.translateScale.y - deltaY * this.ratio / this.canvas.h;
    this.translateScale = new Point(left, top);
    const locatorRatio = 1 - this.positionRectSize.x / this.ratio / this.width;
    this.translateScaleChange.emit(
      new Point(
        Math.max(Math.min(this.translateScale.x, locatorRatio + this.thumbnailViewBoxTranslate.x), this.thumbnailViewBoxTranslate.x),
        Math.max(Math.min(this.translateScale.y, locatorRatio + this.thumbnailViewBoxTranslate.y), this.thumbnailViewBoxTranslate.y)));
  }


  getCurrentChart() {
    this.store.select(fromStore.getCurrentChartDetails).subscribe(data => {
      this.chart = data;
    });
  }

  formatLabel(scale: number = 1) {
    return scale * 100 + '%';
  }

  scaleValueChange() {
    this.scaleChange.emit(this.scale);
  }

  recordDragStart($event) {
    this.drag.start(new Point($event.offsetX, $event.offsetY));
  }

  mousedownThumbnail($event) {
    $event.stopPropagation();
    this.mousedownOnThumbnail = true;
    if (navigator.userAgent.indexOf('Edge') < 0) {
      $event.dataTransfer.setDragImage(new Image(), 0, 0);
    }
    this.drag.start(new Point($event.offsetX, $event.offsetY));
    this.positionRectPosition = new Point(this.translateScale.x * this.canvas.w, this.translateScale.y * this.canvas.h);
  }

  mousemoveThumbnail($event) {
    $event.stopPropagation();
    $event.preventDefault();
    if (this.mousedownOnThumbnail) {
      this.drag.move(new Point($event.offsetX, $event.offsetY));
      this.changePositionRectPosition(this.drag.delta.x, this.drag.delta.y);
    }
  }

  mouseupOnThumbnail($event) {
    this.mousedownOnThumbnail = false;
    this.positionRectPosition = new Point(this.translateScale.x * this.canvas.w, this.translateScale.y * this.canvas.h);
  }

  onThumbDrop($event) {
    $event.stopPropagation();
    $event.preventDefault();
  }

  onMouseLeaveMiniMap($event) {
    this.mousedownOnThumbnail = false;
  }

  // 移动缩略图的定位方块时，使方块以合适的delta移动
  transformToThumbnailMoveDelta(x, y): Point {
    const horizontalScale = this.canvas.w / this.thumbnail.nativeElement.clientWidth;
    const vertical = this.canvas.h / this.thumbnail.nativeElement.clientHeight;
    return new Point(x * horizontalScale, y * vertical);
  }

  // 点击缩略图中显示范围以外的区域时，定位至点击区域
  position(e) {
    if (e.target.localName !== 'canvas') {
      return;
    }
    e.stopPropagation(false);
    this.positionRectPosition = new Point(this.translateScale.x * this.canvas.w, this.translateScale.y * this.canvas.h);
    const centerPosition = new Point(
      e.offsetX - this.positionRectSize.x / this.ratio / 2, e.offsetY - this.positionRectSize.y / this.ratio / 2);
    const currentPosition = new Point(this.locatorPosition.x, this.locatorPosition.y);
    const delta = new Point(centerPosition.x - currentPosition.x, centerPosition.y - currentPosition.y);
    this.changePositionRectPosition(-delta.x, -delta.y);
  }

  zoomIn() {
    this.scale = Math.min(2, this.scale + 0.1);
    this.scaleValueChange();
  }

  zoomOut() {
    this.scale = Math.max(0.5, this.scale - 0.1);
    this.scaleValueChange();
  }

  reduceThumbnail() {
    this.width = Math.max(this.width - 100, 200);
    this.getRatio();
    this.getLocatorPosition();
    this.getThumbnail();
    this.thumbnailRootHeight = this.height + 24;

  }

  expandThumbnail() {
    this.width = this.width + 100;
    this.getRatio();
    this.getLocatorPosition();
    this.getThumbnail();
    this.thumbnailRootHeight = this.height + 24;
  }

  foldThumbnail() {
    this.isFold = !this.isFold;
    this.canvasContainer.destroy();
  }

  unfoldThumbnail() {
    this.isFold = !this.isFold;
    this.initCanvasContainer();
    this.getThumbnail();
  }

}
