import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'chartNamePipe'
})
export class ChartNamePipe implements PipeTransform {

  transform(chart: any, args?: any): any {
    if (chart.name !== null && chart.name !== undefined) {
      const showNameArray = chart.name.split('.');
      const showName = showNameArray[showNameArray.length - 1];
      return showName;
    } else if (typeof chart === 'string') {
      const showNameArray = chart.split('.');
      const showName = showNameArray[showNameArray.length - 1];
      return showName;
    }
  }

}
