import { Component, Input, OnInit } from '@angular/core';
import { Variable } from 'libs/flowchart-ts/flowchart-core/src/lib/models/variable.model';

@Component({
  selector: 'app-variable-widget',
  templateUrl: './variable-widget.component.html',
  styleUrls: ['./variable-widget.component.css']
})
export class VariableWidgetComponent implements OnInit {

  isEditing: boolean;
  text: string;
  varName: string;
  @Input() variableList: Variable[];
  constructor() {
    this.text = '';
    this.varName = '';
  }

  ngOnInit() {
    this.init();
  }

  init() {
    this.variableList = [];
    let i = 0;
    ['a', 'bc', 'def'].forEach(t => {
      const v = new Variable();
      v.name = t;
      v.index = i++;
      this.variableList.push(v);
    })
  }

  onClickoutside($event) {
    this.isEditing = false;
  }

  onViewDblClick($event) {
    $event.stopPropagation();
    if (!this.isEditing) {
      this.isEditing = true;
    }
  }

  onVariableChanged($event) {
    this.varName = $event.target.value;
    this.text = '$' + this.varName;
  }

}
