import { Chart } from "libs/flowchart-ts/flowchart-core/src/lib/models/chart.model";
import { Connector } from "libs/flowchart-ts/flowchart-core/src/lib/models/connector.model";
import { Group } from "libs/flowchart-ts/flowchart-core/src/lib/models/group.model";
import { ChartNode } from "libs/flowchart-ts/flowchart-core/src/lib/models/node.model";
import * as dagre from 'dagre';
import { Point } from "libs/flowchart-ts/flowchart-common/src/lib/models/point.model";
import { Rect } from "libs/flowchart-ts/flowchart-common/src/lib/models/rect.model";

export class LayoutUtils {

    private static getRect(rects: Rect[]) {
        let x = Number.POSITIVE_INFINITY;
        let y = Number.POSITIVE_INFINITY;
        let right = Number.NEGATIVE_INFINITY;
        let bottom = Number.NEGATIVE_INFINITY;
        rects.forEach(rect => {
            x = Math.min(rect.x, x);
            y = Math.min(rect.y, y);
            right = Math.max(rect.x + rect.w, right);
            bottom = Math.max(rect.y + rect.h, bottom)
        })
        return Rect.createWithLeftTopVertex(x, y, right - x, bottom - y)
    }

    static layout(nodes: ChartNode[], connectors: Connector[]) {
        const originGroupRect = LayoutUtils.getRect(nodes.map(n => n.rect));
        const g = new dagre.graphlib.Graph();
        g.setGraph({ rankdir: 'TB', ranksep: 80, nodesep: 40, marginx: 30, marginy: 50 });
        // g.setGraph({});
        g.setDefaultEdgeLabel(function () { return {}; });
        nodes.forEach(n => {
            g.setNode(n.uid, { label: n.text, width: n.rect.w, height: n.rect.h });
        });
        const nodesId = nodes.map(n => n.uid);
        if (connectors) {
            connectors.forEach(conn => {
                if (nodesId.includes(conn.start.uid) && nodesId.includes(conn.end.uid)) {
                    g.setEdge(conn.start.uid, conn.end.uid);
                }
            })
        }
        dagre.layout(g);
        const map = new Map<string, Point>();
        let right = Number.NEGATIVE_INFINITY;
        let bottom = Number.NEGATIVE_INFINITY;
        const newRects = g.nodes().map(n => {
            const node = g.node(n);
            return Rect.createWithLeftTopVertex(node.x, node.y, node.width, node.height);
        });
        const newGroupRect = LayoutUtils.getRect(newRects);
        const offset = { x: originGroupRect.cx - newGroupRect.cx, y: originGroupRect.cy - newGroupRect.cy }
        g.nodes().forEach(n => {
            const node = g.node(n);
            map.set(n, new Point(node.x + offset.x, node.y + offset.y))
        })
        return map;
    }

    static layoutGroup(group: Group, chart: Chart) {
        const map = this.layout(group.nodes, chart.connectors);
        return map;
    }
}