import { Action } from '@ngrx/store';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';

export const COPY_CHART_INFORMATION = '[Flowchart] Copy Chart Information';
export const COPY_CHART_INFORMATION_FAIL = '[Flowchart] Copy Chart Information Fail';
export const COPY_CHART_INFORMATION_SUCCESS = '[Flowchart] Copy Chart Information Success';

export class CopyChartInformation implements Action {
    readonly type = COPY_CHART_INFORMATION;
    constructor(public nodes: ChartNode[]) {
    }
}

export class CopyChartInformationFail implements Action {
    readonly type = COPY_CHART_INFORMATION_FAIL;
    constructor(public payload: any) { }
}

export class CopyChartInformationSuccess implements Action {
    readonly type = COPY_CHART_INFORMATION_SUCCESS;
    constructor(public payload: any) {
    }
}

// action types
export type CopyChartInformationAction =
    | CopyChartInformation
    | CopyChartInformationFail
    | CopyChartInformationSuccess;

