import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/type.model';
import { EventType } from 'libs/flowchart-ts/flowchart-core/src/public-api';
import * as fromInterface from '../../actions/interface/interfaces.actions';

export interface InterfaceState {
    allInterfaces: Array<ChartType>;
    data: ChartType[];
    events: EventType[];
    loaded: boolean;
    loading: boolean;
}

export const initialState: InterfaceState = {
    allInterfaces: [],
    data: null,
    events: [],
    loaded: false,
    loading: false,
};

export function reducer(
    state = initialState,
    action: fromInterface.ChartTypesAction
): InterfaceState {
    switch (action.type) {
        case fromInterface.LOAD_CHART_TYPES: {
            return {
                ...state,
                loading: true,
            };
        }

        case fromInterface.LOAD_CHART_TYPES_SUCCESS: {


            return {
                ...state,
                loading: false,
                loaded: true,
                allInterfaces: action.payload
            };
        }

        case fromInterface.LOAD_CHART_TYPES_FAIL: {
            return {
                ...state,
                loading: false,
                loaded: false,
            };
        }

        case fromInterface.LOAD_EVENTS_SUCCESS: {
            return {
                ...state,
                events: action.payload
            };
        }

        case fromInterface.UPDATE_EVENTS_SUCCESS: {
            return {
                ...state,
                events: action.payload.events
            };
        }

        case fromInterface.CREATE_TYPE_SUCCESS: {
            return {
                ...state,
                allInterfaces: [...state.allInterfaces, action.payload]
            };
        }
    }

    return state;
}

export const getInterfaceLoading = (state: InterfaceState) => state.loading;
export const getInterfaceLoaded = (state: InterfaceState) => state.loaded;
export const getInterfaceLoadState = (state: InterfaceState) => {
    return {
        loading: state.loading,
        loaded: state.loaded
    };
};
export const getInterface = (state: InterfaceState) => state.data;
export const getAllInterfaces = (state: InterfaceState) => state.allInterfaces;
