import { environment } from './../../../environments/environment.prod';
import { IpcService } from './../../common/ipc/services/ipc.service';
import { ActivatedRoute, Router } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { FileService } from 'src/app/services/common/file.service';

import { Store } from '@ngrx/store';
import * as fromStore from '../../store';
import { RecentProject } from 'src/app/models/project.model';
import { ProjectBasicConfigPanelComponent } from '../project/project-basic-config-panel/project-basic-config-panel.component';
import { DialogService } from 'src/app/common/dialog/dialog.service';
import { PathDialogConfig } from 'src/app/common/path-picker/path-info.model';
import { APP_HTTP_RESULT_CODE } from 'src/app/constants/http/http.constants';
import { ProjectService } from 'src/app/services/project/project.service';
import { PathService } from 'src/app/common/path-picker/path.service';
import { TranslateService } from '@ngx-translate/core';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';
import { forkJoin, Observable, of } from 'rxjs';
import { GeneratorManager } from 'libs/flowchart-ts/flowchart-generator/src/lib/generators/generator-manager';
import { BaseComponent } from '../base-component';


@Component({
  selector: 'app-begin-page',
  templateUrl: './begin-page.component.html',
  styleUrls: ['./begin-page.component.css']
})
export class BeginPageComponent extends BaseComponent implements OnInit {
  recentProjectsList$: Observable<RecentProject[]>;
  welcome: string;

  constructor(
    private fileService: FileService,
    private store: Store<fromStore.FlowchartState>,
    private route: ActivatedRoute,
    private router: Router,
    private dialogService: DialogService,
    private projectService: ProjectService,
    private pathService: PathService,
    private translateService: TranslateService,
    private ipcService: IpcService
  ) {
    super();
  }

  ngOnInit() {
    this.getRecentProjectList();
    Project.fs = this.fileService;
  }

  // 打开项目
  openProject($event) {
    if (environment.IS_WEB) {
      const pathPickerConfig = new PathDialogConfig();
      pathPickerConfig.filter = { type: 'path' };
      this.pathService.showPathPicker(pathPickerConfig).subscribe(path => {
        if (path) {
          this.projectService.open(path).then(result => {
            if (result.code === APP_HTTP_RESULT_CODE.OK) {
              const name = path.substring(path.lastIndexOf('\\') + 1);
              this.projectService.routeToNewProject(name);
            }
          });
        }
      });
    } else {
      this.ipcService.selectDir().then(result => {
        if (result.code === 0) {
          const path:string = result.args[0];
          this.loadProject(result.args[0]);
        }
      })
    }
  }

  onCreateProjectClick($event) {
    // this.router.navigate(['create/project']);
    this.dialogService.open(ProjectBasicConfigPanelComponent, null).subscribe(result => {
      if (result) {
        console.log(result);
      }
    });
  }

  // 获取最近使用的项目, 接口: api/project, api为本地运行时的跨域设置,代表http://ai-tool.fuxi.netease.com:4990
  async getRecentProjectList() {
    if (environment.IS_WEB) {
      this.store.select(fromStore.getAllRecentProjectsList).subscribe(data => {
        this.recentProjectsList$ = this.store.select(fromStore.getAllRecentProjectsList);
        this.store.dispatch(new fromStore.LoadRecentProjectsList());
        const promises: Promise<any>[] = [];
        this.recentProjectsList$.subscribe(project => {
          project.forEach(p => {
            const promise = new Promise(resolve => {
              resolve(this.projectService.open(p.path));
            });
            promises.push(promise);
          });
          forkJoin(promises).subscribe(projects => {
            const result = projects.map(p => {
              if (p.message === 'Success') {
                return true;
              }
              return false;
            });
            const existProjects = project.filter((p, index) => result[index]);
            this.recentProjectsList$ = of(existProjects);
          });
        });
      }).unsubscribe();
    } else {
      this.recentProjectsList$ = new Observable<RecentProject[]>(observer => {
        this.ipcService.readFile('./data/cache.json').then(result => {
          const projects: RecentProject[] = [];
          if (result.code === 0 && result.data) {
            const content = JSON.parse(result.data);
            if (content.projects.recent) {
              content.projects.recent.forEach(p => {
                const proj = new Project();
                proj.uid = p.uid;
                proj.name = p.project;
                proj.path = p.path;
                projects.push(proj);
              });
            }
          }
          observer.next(projects);
        });
      });
    }
  }


  onRecentProjectClick($event, recentProject: RecentProject) {
    this.loadProject(recentProject.path);
  }

  loadProject(path: string) {
    this.projectService.loadProject(path).then(result => {
      const project = this.projectService.getCurrentProject();
      GeneratorManager.getInstance().getGenerator(project.config.generator).build(project);
      console.log(project)
      this.router.navigate(['project', project.name])
    })
  }

}
