import { ChartJsonKey, VariableJsonKey, GroupJsonKey, ConnectorJsonKey } from './../constants/chart.constants';
import { Variable } from './variable.model';
import { Group } from './group.model';
import { Project } from './project.model';
import { CheckResult } from './check-result.model';
import { ChartType } from './type.model';
import { ChartNode } from './node.model';
import { UID_START } from '../constants/node.constants';
import { Connector } from './connector.model';
import { NodeViewType } from '../constants/node.enums';
import { MemberFrom, MemberType, Method } from './member.model';
import { Rect } from '../../../../flowchart-common/src/lib/models/rect.model';

/**
 * Flowchart
 */
export class Chart {
    /**
     * id
     */
    uid: string;
    /**
     * Flowchart name, see {@linkcode CHART_NAME_REGEX} for specific name rules
     */
    name: string;
    /**
     * Flowchart path
     */
    path: string;
    /**
     * Flowchart type
     */
    type: ChartType;
    /**
     * Flochart tag
     */
    readonly tag: string = 'chart';
    /**
     * Flowchart description
     */
    description: string;
    /**
     * Nodes list
     */
    nodes: ChartNode[];
    /**
     * Node map for find node faster
     */
    nodeMap: Map<string, ChartNode>;
    /**
     * Node connector list
     */
    connectors: Connector[];
    /**
     * Node groups list
     */
    groups: Group[];
    /**
     * Variables in this chart, all variables should be declared before use
     */
    variables: Variable[];
    /**
     * Parameters transfered to this chart(usually subchart), which is subset of variables
     */
    parameters: Variable[];
    /**
     * Belonged project
     */
    project: Project;
    /**
     * Whether the flowchart is subchart or not, subchart can be exported as function and called like functions in other flowcharts
     */
    private _isSubChart: boolean;
    get isSubChart(): boolean {
        return this._isSubChart;
    }
    set isSubChart(value: boolean) {
        if (this._isSubChart === value) return;
        if (!this.name) return;
        if (!this.project || !this.type) return;
        if (value) {
            this.type.addMember(this.toMethod());
        } else {
            this.type.removeMember(this.name);
        }
        this._isSubChart = value;
    }

    returnType: ChartType;

    constructor() {
        this.variables = [];
        this.nodes = [];
        this.nodeMap = new Map<string, ChartNode>();
        this.connectors = [];
        this.groups = [];
        this.parameters = [];
    }

    //#region methods
    /**
     * Data validation check
     * @returns CheckResult
     */
    check() {
        if (!this.uid) {
            return new CheckResult(`uid is required.`);
        }

        // if (CHART_NAME_REGEX.test(this.name)) {
        //     return new CheckResult(`Flowchart name '${this.name}' is not valid`);
        // }
        return new CheckResult();
    }

    addNode(node: ChartNode, index?: number) {
        if (!node) {
            throw new Error('Node is empty');
        }
        const check = node.check();
        if (!check.valid) {
            throw new Error(`Node data is not valid:[${check.msg}]`);
        }
        if (!this.nodeMap) {
            this.nodeMap = new Map<string, ChartNode>();
            this.nodes = [];
        }
        if (this.nodeMap.has(node.uid)) {
            throw new Error(`Node with uid ${node.uid} has already existed.`);
        }

        this.nodeMap.set(node.uid, node);
        if (index !== undefined && this.nodes.length > index && index > -1) {
            this.nodes.splice(index, 0, node);
        } else {
            this.nodes.push(node);
        }
        node.chart = this;
    }

    removeNode(nodeId: string) {
        this.groups.forEach(g => g.removeNode(nodeId));
        this.nodeMap.delete(nodeId);
        const index = this.nodes.findIndex(n => n.uid === nodeId);
        if (index > -1) {
            this.nodes.splice(index, 1);
        }
    }


    fromJson(json: any, project: Project, typeConverter: (tname: string) => ChartType) {
        this.uid = json[ChartJsonKey.Uid];
        this.name = json[ChartJsonKey.Name];
        this.path = json[ChartJsonKey.Path];
        if (!this.name && this.path) {
            this.name = this.path.substr(this.path.lastIndexOf('.') + 1);
        }
        this.project = project;
        if (json[ChartJsonKey.Type]) {
            this.type = project.getType(json[ChartJsonKey.Type]);
        }
        if (json[ChartJsonKey.Nodes]) {
            this.nodes = json[ChartJsonKey.Nodes].map((n: any) => {
                const node = new ChartNode();
                node.fromJson(n, this);
                return node;
            })
            if (this.nodes) {
                this.nodeMap = new Map<string, ChartNode>();
                this.nodes.forEach(n => this.nodeMap.set(n.uid, n));
            }
        }
        if (json[ChartJsonKey.Connectors]) {
            this.connectors = json[ChartJsonKey.Connectors].map((c: any) => {
                const connector = new Connector(null, null, null, null);
                connector.fromJson(c, this);
                return connector;
            })
        }
        if (json[ChartJsonKey.Groups]) {
            this.groups = json[ChartJsonKey.Groups].map((g: any) => {
                const group = new Group();
                group.fromJson(g, this, typeConverter);
                return group;
            });
        }
        if (json[ChartJsonKey.Variables]) {
            let i = 0;
            json[ChartJsonKey.Variables].forEach((v: any) => {
                const newV = new Variable();
                newV.fromJson(v, typeConverter);
                newV.index = i++;
                if (newV.isParameter) {
                    this.parameters.push(newV)
                } else {
                    this.variables.push(newV);
                }
            })
        }
    }

    saveAsJson() {
        const strs = [];
        strs.push('  {');
        strs.push(`    "${ChartJsonKey.Uid}": "${this.uid}",`);
        strs.push(`    "${ChartJsonKey.Path}": "${this.path}",`);
        strs.push(`    "${ChartJsonKey.Type}": "${this.type.name}",`);
        if (this.isSubChart) {
            strs.push(`    "${ChartJsonKey.IsSubChart}": "true",`);
        }
        if (this.returnType) {
            strs.push(`    "${ChartJsonKey.ReturnType}": "${this.returnType.name}",`);
        }
        const variables = this.parameters.concat(this.variables);
        if (variables.length > 0) {
            strs.push(`    "${ChartJsonKey.Variables}": [`);
            variables.forEach(v => {
                if (v.isParameter) {
                    strs.push(`      { "${VariableJsonKey.Name}": "${v.name}", "${VariableJsonKey.Type}": "${v.type.name}", "${VariableJsonKey.IsParameter}": "${v.isParameter}" },`);
                } else {
                    strs.push(`      { "${VariableJsonKey.Name}": "${v.name}", "${VariableJsonKey.Type}": "${v.type.name}" },`);
                }
            })
            this.removeTraillingComma(strs);
            strs.push(`    ],`);
        }

        const nodes = this.sortNodes();
        strs.push(`    "${ChartJsonKey.Nodes}": [`);
        nodes.forEach(node => {
            strs.push(`      {`);
            strs.push(`        "${ChartJsonKey.Uid}": "${node.uid}",`);
            strs.push(`        "${ChartJsonKey.Text}": "${node.text.replace(/"/g, '\\\"')}",`);
            var rect = node.rect;
            strs.push(`        "${ChartJsonKey.Rect}": {"x": ${rect.x}, "y": ${rect.y}, "w": ${rect.w}, "h": ${rect.h}},`);
            strs.push(`        "${ChartJsonKey.NodeViewType}": ${node.viewType} `);
            strs.push(`      },`);
        })
        this.removeTraillingComma(strs);
        strs.push(`    ],`);

        if (this.groups && this.groups.length > 0) {
            strs.push(`    "${ChartJsonKey.Groups}": [`);
            this.groups.forEach((group, index) => {
                strs.push(`      {`);
                strs.push(`        "${GroupJsonKey.Name}": "${group.name}",`);
                strs.push(`        "${GroupJsonKey.Layout}": ${group.layout},`);
                // remarks
                if (group.remarks && group.remarks.length > 0) {
                    strs.push(`        "${GroupJsonKey.Remarks}": [`)
                    group.remarks.forEach((remark, index1) => {
                        strs.push(`            "${remark.replace(/\n/g, '\\n')}"${index1 === group.nodes.length - 1 ? '' : ','}`)
                    })
                    strs.push(`        ],`);
                } else {
                    strs.push(`        "${GroupJsonKey.Remarks}": [],`)
                }
                // nodes
                strs.push(`        "${GroupJsonKey.Nodes}": [`);
                let i = 0;
                group.nodes.forEach(node => {
                    strs.push(`          "${node.uid}"${i === group.nodes.length - 1 ? '' : ','} // ${node.text}`);
                    i++;
                })
                strs.push(`        ]`);
                strs.push(`      }${index === this.groups.length - 1 ? '' : ','}`);
            })
            strs.push(`    ],`);
        }

        if (this.connectors && this.connectors.length > 0) {
            strs.push(`    "${ChartJsonKey.Connectors}": [`);
            this.connectors.forEach(conn => {
                strs.push(`      { "${ConnectorJsonKey.Start}": "${conn.start.uid}", "${ConnectorJsonKey.End}": "${conn.end.uid}", "${ConnectorJsonKey.Type}": ${conn.type}, "${ConnectorJsonKey.Shape}": ${conn.shape} },`);
            })
            this.removeTraillingComma(strs);
            strs.push(`    ],`);
        }
        this.removeTraillingComma(strs);
        strs.push("  },");
        return strs;
    }

    private removeTraillingComma(strs: string[]) {
        if (strs[strs.length - 1].endsWith(',')) {
            strs[strs.length - 1] = strs[strs.length - 1].substr(0, strs[strs.length - 1].length - 1);
        }
    }

    /**
     * Sort nodes using DFS(Depp first search);
     * @returns sorted node list
     */
    sortNodes() {
        const start = this.nodes.find(n => n.uid === UID_START);
        if (start !== null) {
            const sortedList = this.deepFirstSearch(start);
            const unsortedList = this.nodes.filter(n => sortedList.find(nn => nn.uid === n.uid) == null);
            if (unsortedList) {
                return sortedList.concat(unsortedList);
            }
            return sortedList;
        }
        return null;
    }

    calculateNodeRenderWidth?(textWidth: number, nodeViewType: NodeViewType, node: ChartNode) {
        switch (nodeViewType) {
            case NodeViewType.START:
                return 50 + 8.8 * textWidth;
            case NodeViewType.COMMON:
                return 50 + 8.8 * textWidth;
            case NodeViewType.WAITALL:
                return 147;
            case NodeViewType.WAIT:
                return 120 + 8.8 * (textWidth - 5);
            case NodeViewType.FUNCTION:
                return 66 + 6.578 * (textWidth - 2);
            case NodeViewType.STOP_FLOW:
            case NodeViewType.STOP_NODE:
                const stopNodeIds = this.getStopIdList(node.text);
                return 56 * stopNodeIds.length + 158;
            default:
                return 50 + 8.8 * textWidth;
        }
    }

    copyFrom(chart: Chart) {
        this.uid = chart.uid;
        this.name = chart.name;
        this.path = chart.path;
        this.project = chart.project;
        this.nodes = chart.nodes;
        this.nodeMap = chart.nodeMap;
        this.connectors = chart.connectors;
        this.groups = chart.groups;
        this.variables = chart.variables;
        this.parameters = chart.parameters;
        this.type = chart.type;
        this.isSubChart = chart.isSubChart;
        this.description = chart.description;
    }

    updateVariableIndex() {
        for (let i = 0; i < this.variables.length; i++) {
            this.variables[i].index = i + this.parameters.length;
        }
    }

    updateParameterIndex() {
        for (let i = 0; i < this.parameters.length; i++) {
            this.parameters[i].index = i;
        }
    }

    toMethod() {
        const method = new Method();
        method.name = this.name;
        method.type = this.returnType || ChartType.BoolType;
        method.source = `asyncflow.call_sub("${this.path}", $self${!this.parameters || this.parameters.length === 0 ? '' : ', $param'})`;
        method.from = MemberFrom.SUBCHART;
        method.parameters = this.parameters;
        method.memberType = MemberType.METHOD;
        return method;
    }

    initStart() {
        const start = new ChartNode();
        start.uid = UID_START;
        start.text = 'Start';
        start.rect = Rect.createWithLeftTopVertex(600, 200, 94, 40);
        this.nodeMap.set(UID_START, start);
        this.nodes.splice(0, 0, start);
    }
    //#endregion


    //#region Private methods
    /**
     * 
     * @param root Root node of this dfs
     * @returns 
     */
    private deepFirstSearch(root: ChartNode) {
        const sorted: ChartNode[] = [];
        if (root !== null) {
            const stack = [];
            stack.push(root);
            // Search for child node
            while (stack.length > 0) {
                const current = stack.pop();
                sorted.push(current);
                const children = this.findUnsortedChildren(current, sorted);
                if (children != null && children.length > 0) {
                    for (const child of children) {
                        if (!sorted.includes(child)) {
                            stack.push(child);
                        }
                    }
                }
            }
            return sorted;
        }
        return null;
    }

    private findUnsortedChildren(node: ChartNode, sorted: ChartNode[]) {
        let children: ChartNode[] = [];
        for (const conn of this.connectors) {
            if (conn.start.uid === node.uid && conn.end !== null) {
                const isSorted = sorted.find(n => n.uid === conn.end.uid);
                if (!isSorted) {
                    children.push(conn.end);
                }
            }
        }
        // Sort by left-right order(using x coordination)
        children = children.sort((a, b) => {
            return b.rect.x - a.rect.x;
        });
        return [...new Set(children)];
    }

    private getStopIdList?(text: string) {
        const rawListStr = /\((([\"]{1}[0-9a-z]+[\"]{1}[,][\s]{0,1})*[\"]{1}[0-9a-z]+[\"]{1})\)/g.exec(text);
        if (rawListStr && rawListStr.length > 0) {
            const stopIds = rawListStr[0].substr(1, rawListStr[0].length - 2).split(',').map((txt, index) => {
                return txt.substring(txt.indexOf('"') + 1, txt.lastIndexOf('"'));
            });
            return stopIds;
        }
        return [];
    }
    //#endregion
}