import { animate, state, style, transition, trigger } from '@angular/animations';
import { Component, Input, OnInit, EventEmitter, Output, ChangeDetectionStrategy } from '@angular/core';
import { Connector, ConnectorType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { Group } from 'libs/flowchart-ts/flowchart-core/src/lib/models/group.model';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';

// export type MenuBarComponent = NodeMenuBarComponent | ConnectorMenuBarComponent | GroupMenuBarComponent | ComplexMenuBarComponent;

@Component({
  selector: 'app-edit-bar',
  templateUrl: './edit-bar.component.html',
  styleUrls: ['./edit-bar.component.css'],
  animations: [
    trigger('expand', [
      state('collapsed', style({
        width: '32px',
      })),
      state('expanded', style({
      })),
      transition('expanded => collapsed', [animate('0.2s ease-in')]),
      transition('collapsed => expanded', [animate('0.2s ease-out')])
    ])
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class EditBarComponent implements OnInit {

  collapsed = false;

  // 当前连线类型
  @Input() connectorType = ConnectorType.ALWAYS;
  @Input() selectedNodes: ChartNode[];
  @Input() selectedGroups: Group[];
  @Input() selectedConnectors: Connector[];
  @Output() startConnect: EventEmitter<ConnectorType> = new EventEmitter<ConnectorType>();
  @Output() undo: EventEmitter<any> = new EventEmitter();
  @Output() redo: EventEmitter<any> = new EventEmitter();
  @Output() editbarStartDrag: EventEmitter<any> = new EventEmitter();
  @Output() delete: EventEmitter<void> = new EventEmitter();

  childComponent;

  constructor() { }

  judgeSelected() {
    return ((this.selectedNodes && this.selectedNodes.length > 0) || (this.selectedGroups && this.selectedGroups.length > 0)
    || (this.selectedConnectors && this.selectedConnectors.length > 0));
  }

  deleteItems() {
    if (this.judgeSelected) {
      this.delete.emit();
    }
  }

  ngOnInit() {
  }

  expand() {
    this.collapsed = false;
  }

  collapse() {
    this.collapsed = true;
  }

  onStartConnect(connType: ConnectorType) {
    if (connType !== this.connectorType) {
      this.connectorType = connType;
    }
    this.startConnect.emit(this.connectorType);
  }

  startDrag(e) {
    console.log(e);
    this.editbarStartDrag.emit(e);
  }

  undoClick() {
    this.undo.emit();
  }

  redoClick() {
    this.redo.emit();
  }

}
