import { Directive, ElementRef, OnInit, HostListener, Input, Renderer2 } from '@angular/core';
@Directive({
  selector: '[appDragResize]'
})
export class DragResizeDirective {

  constructor(
    private elr: ElementRef,
    private rd2: Renderer2
  ) { }
  @Input() borderSize = 4;
  @Input() resizeBorder: string;
  @Input() resizeDirection: string;
  mPosition;
  mousedown = false;
  mousemove = false;

  @HostListener('mousedown', ['$event'])
  handleMousedown(ev) {
    if (this.isBorderInDragArea(ev)) {
      if (this.resizeDirection === 'ew-resize') {
        this.mPosition = ev.x;
      } else {
        this.mPosition = ev.y;
      }
      this.mousedown = true;
    }
  }

  @HostListener('mouseup', ['$event'])
  handleMouseup() {
    this.mousedown = false;
    this.mPosition = 0;
  }

  @HostListener('mouseleave', ['$event'])
  handleMouseleave() {
    this.mousedown = false;
    this.setBorderDefaultStyle();

  }

  @HostListener('mousemove', ['$event'])
  handleMousemove(ev) {
    if (this.isBorderInDragArea(ev)) {
      this.setBorderHoverStyle();
    } else {
      this.setBorderDefaultStyle();
    }
    switch (this.resizeDirection) {
      case 'ew-resize': {
        if (this.mousedown) {
          const dx = ev.x - this.mPosition;
          this.mPosition = ev.x;
          this.elr.nativeElement.style.width = (parseInt(getComputedStyle(this.elr.nativeElement, '').width, 10)
            + (this.resizeBorder === 'border-right' ? dx : -dx)) + 'px';
        }
        break;
      }
      case 'ns-resize': {
        if (this.mousedown) {
          const dy = ev.y - this.mPosition;
          this.mPosition = ev.y;
          console.log(this.mPosition);
          this.elr.nativeElement.style.height = (parseInt(getComputedStyle(this.elr.nativeElement, '').height, 10)
            + (this.resizeBorder === 'border-top' ? -dy : dy)) + 'px';
        }
        break;
      }
    }
  }

  // 判断鼠标是否在border的可拖动区域
  isBorderInDragArea(ev) {
    switch (this.resizeDirection) {
      case 'ew-resize': {
        const width = this.elr.nativeElement.offsetWidth;
        if (Math.abs(ev.offsetX - width) <= this.borderSize && this.resizeBorder === 'border-right'
          || Math.abs(ev.offsetX - width) >= width && this.resizeBorder === 'border-left') {
          return true;
        } else {
          return false;
        }
      }
      case 'ns-resize': {
        const height = this.elr.nativeElement.offsetHeight;
        // console.log(ev.offsetY, height);
        if (Math.abs(ev.offsetY - height) <= this.borderSize && this.resizeBorder === 'border-bottom'
          || Math.abs(ev.offsetY - height) >= height && this.resizeBorder === 'border-top') {
          return true;
        } else {
          return false;
        }
      }
    }
  }

  // 当鼠标hover在border时border的样式
  setBorderHoverStyle() {
    this.rd2.setStyle(this.elr.nativeElement, this.resizeBorder, 'solid ' + this.borderSize + 'px ' + '#ccc');
    this.rd2.setStyle(this.elr.nativeElement, 'cursor', this.resizeDirection);
  }

  // 当鼠标离开在border后,恢复border的样式
  setBorderDefaultStyle() {
    this.rd2.setStyle(this.elr.nativeElement, this.resizeBorder, 'solid 1px #ccc');
    this.rd2.setStyle(this.elr.nativeElement, 'cursor', 'Default');
  }

}
