export class NodeProperty {
    name: string;
    description: string;
    priorityLevel: number;
    chlidNodeOrder: boolean;
    constructor(name: string, description: string, priorityLevel: number, childNodeOrder: boolean) {
        this.name = name;
        this.description = description;
        this.priorityLevel = priorityLevel;
        this.chlidNodeOrder = childNodeOrder;
    }
}

export class InterfaceProperty {
    name: string;
    description: string;
    notes: string;
    returnType: string;
    constructor(name: string, description: string, notes: string, returnType: string) {
        this.name = name;
        this.description = description;
        this.notes = notes;
        this.returnType = returnType;
    }
}

export class ChartProperty {
    name: string;
    description: string;
    path: string;
    type: string;
    export: boolean;
    constructor(name: string, description: string, path: string, type: string, Export: boolean) {
        this.name = name;
        this.description = description;
        this.path = path;
        this.type = type;
        this.export = Export;
    }
}

export class GroupProperty {
    name: string;
    description: string;
    constructor(name: string, description: string) {
        this.name = name;
        this.description = description;
    }
}

export enum PropertyType {
    NUMBER,
    STRING,
    BOOLEAN,
    ARRAY
}

export enum PropEditType {
    NONE,
    INPUT,
    CHECK,
    SELECT
}

export interface IProperty {
    name: string;
    nameEn: string;
    type: PropertyType;
    editType: PropEditType;
    selectList?: any[];
}
