import { Pipe, PipeTransform } from '@angular/core';
import { ShowingRunningStatus } from '../models/debug-node-status.model';

@Pipe({
  name: 'debugColor'
})
export class DebugColorPipe implements PipeTransform {

  async transform(status: ShowingRunningStatus, ...args: any[]) {
    // isSelected
    if (args[0]) {
      return '#0d0d0d';
    }
    // default color
    const defaultColor = args[1];
    switch (status) {
      case ShowingRunningStatus.FAILURE:
        return 'pink';
      case ShowingRunningStatus.SUCCESS:
        return '#9f9';
      case ShowingRunningStatus.IDLE:
        return defaultColor || '#0d0d0d';
      case ShowingRunningStatus.RUNNING:
        return '#3cf';
      default:
        return defaultColor || '#0d0d0d';
    }
  }

}
