import { HttpClient } from '@angular/common/http';
import { Router } from '@angular/router';
import { IpcService } from './../../../common/ipc/services/ipc.service';
import { environment } from './../../../../environments/environment.prod';
import { Store } from '@ngrx/store';
import { PathService } from './../../../common/path-picker/path.service';
import { ProjectService } from './../../../services/project/project.service';
import { UidUtils } from './../../../utils/uid.utils';
import { ChartFolder } from './../../../models/projects/project.folder.model';
import { PathPickerComponent } from '../../../common/path-picker/path-picker/path-picker.component';
import { DialogService } from './../../../common/dialog/dialog.service';
import { Component, Inject, Input, OnInit } from '@angular/core';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { PathDialogConfig } from 'src/app/common/path-picker/path-info.model';
import { APP_HTTP_RESULT_CODE } from 'src/app/constants/http/http.constants';
import { deepCopy } from 'src/app/store/reducers/utilsFunction';
import * as fromStore from 'src/app/store/index';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';
import { ProjectConfig } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project-config.model';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/type.model';
import { GeneratorManager } from 'libs/flowchart-ts/flowchart-generator/src/lib/generators/generator-manager';
@Component({
  selector: 'app-project-basic-config-panel',
  templateUrl: './project-basic-config-panel.component.html',
  styleUrls: ['./project-basic-config-panel.component.css']
})
export class ProjectBasicConfigPanelComponent implements OnInit {

  generators = [{ name: 'Python', value: 'py' }, { name: 'Lua', value: 'lua' }, { name: 'C#', value: 'cs' }, { name: 'C++', value: 'cpp' }, { name: 'Javascript', value: 'js' }];
  displayedFolderColumns = ['name', 'path', 'defaultType'];
  folderDataSource = new MatTableDataSource<ChartFolder>([]);
  @Input() project: Project;

  constructor(
    private dialogService: DialogService,
    private dialogRef: MatDialogRef<PathPickerComponent>,
    @Inject(MAT_DIALOG_DATA) public data: Project,
    private projectService: ProjectService,
    private pathService: PathService,
    private ipcService: IpcService,
    private router: Router,
    private store: Store,
    private http: HttpClient) { }

  ngOnInit() {
    this.initData();
  }

  initData() {

    if (this.data) {
      this.project = deepCopy(this.data);
    } else {
      this.project = new Project();
      this.project.uid = UidUtils.generateUid();
    }
    if (!this.project.config) {
      this.project.config = new ProjectConfig();
      this.project.config.generator = 'lua';
      this.project.config.flowchartTypeDict = new Map<string, ChartType>();
    }
    // this.folderDataSource.data = this.project.folders;
  }

  //#region EventHandlers
  onSelectPathClick($event) {
    if (environment.IS_WEB) {
      const pathPickerConfig = new PathDialogConfig();
      pathPickerConfig.filter = { type: 'path' };
      this.pathService.showPathPicker(pathPickerConfig).subscribe(result => {
        if (result !== undefined) {
          this.project.path = `${result}\\${this.project.name}`;
        }
      });
    } else {
      this.ipcService.selectDir().then(result => {
        if (!result.canceled) {
          this.project.path = `${result.args[0]}\\${this.project.name}`;
        }
      })
    }
  }

  onCancelClick($event) {
    this.dialogRef.close();
  }

  onConfirmClick($event) {
    if (environment.IS_WEB) {
      if (this.data) {
        this.projectService.update(this.project).then(result => {
          if (result.code === APP_HTTP_RESULT_CODE.OK) {
            this.store.dispatch(new fromStore.UpdateProjectSuccess(this.project));
            this.dialogRef.close();
          }
        });
      } else {
        this.projectService.create(this.project).then(result => {
          if (result.code === APP_HTTP_RESULT_CODE.OK) {
            this.projectService.open(this.project.path).then(openResult => {
              if (openResult.code === APP_HTTP_RESULT_CODE.OK) {
                this.projectService.routeToNewProject(this.project.name);
              }
            });
          }
        });
      }
    } else {
      if (this.data) {
        this.data.path = this.project.path;
        this.data.name = this.project.name;
        this.data.config.generator = this.project.config.generator;
        this.dialogRef.close();
      } else {
        Promise.all([this.projectService.fetchDefaultGlobalType(), this.projectService.fetchDefaultEvents()]).then(results => {
          const project = Project.createProject(results[0], results[1]);
          project.config.generator = this.project.config.generator;
          project.name = this.project.name;
          project.path = this.project.path;
          this.projectService.setCurrentProject(project);
          this.projectService.save();
          GeneratorManager.getInstance().getGenerator(project.config.generator).build(project);
          this.dialogRef.close();
          this.router.navigate(['project', this.project.name]);
        })
      }
    }
  }
  //#endregion
}
