import { Pipe, PipeTransform } from '@angular/core';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';

@Pipe({
  name: 'nodeUidToIndex'
})
export class NodeUidToIndexPipe implements PipeTransform {

  transform(uid: string, nodes: ChartNode[]): any {
    return nodes.findIndex(n => n.uid === uid);
  }

}
