import { createSelector } from '@ngrx/store';
import { getFlowChartState, FlowchartState } from '../../reducers';
import * as fromCopy from '../../reducers/common/copy.reducer';

export const getCopyState = createSelector(
    getFlowChartState,
    (state: FlowchartState) => state.copy
);

export const getCopyChartDetails = createSelector(getCopyState, fromCopy.getCopiedChartInformation);

