import { AstOperator, BinaryOpNode, ListNode } from './../../../../flowchart-parser/src/lib/nodes/ast-node.model';
import { IParser } from './../../../../flowchart-parser/src/lib/parser';
import { Context } from './generator-info.model';
import { AstNodeInfo, BaseGenerator } from './base-generator';
import { ChartType } from './../../../../flowchart-core/src/public-api';
export class LuaGenerator extends BaseGenerator {
    readonly tag: string = 'lua';
    parser: IParser;

    static NodeCommand: string = 'command';
    static NodeCondition: string = 'condition';
    constructor(parser: IParser) {
        super(parser);
        this.parser = parser;
        this.ctx = new Context();
    }
    varStartIndex = 0;
    generateCode(ni: AstNodeInfo) {
        if(!this.parseInfo.isControlNode) {
            this.codes = [];
            this.codes.push(`function asyncflow.node_funcs.${ni.functionName}(self)`);
            this.codes.push(`    local ret = ${ni.code}`);
            if(ni.type === ChartType.BoolType) {
                this.codes.push(`    return ${ni.code === 'true' ? 'true': 'false'}`);
            } else if(ni.type === ChartType.NumberType) {
                this.codes.push(`    return ret ~=0`);
            } else {
                this.codes.push(`    return true`);
            }
            this.codes.push('end');
            this.codes.push("");
        }
    }

    translateOp(node: BinaryOpNode) {
        if(node.left.type === ChartType.StringType && node.operator === AstOperator.Add) {
            node.operator = AstOperator.Strcat;
        }
    }

    getInvokeOperator() {
        return ':';
    }

    //#region VISITOR
    private visit_ListNode(node: ListNode) {
        const ni = super.visit(node);
        let code = ni.code;
        if(code.length >= 2) {
            code = '{' + code.substr(1, code.length -2) + '}'
        }
        ni.code = code;
        return ni;
    }
    visit(node: ListNode) {
        if(node instanceof ListNode) {
            return this.visit_ListNode(node);
        }
        return super.visit(node);
    }
}