export enum TypeSource {
    /**
     * Unknown source
     */
    UNKNOWN = 0,
    /**
     * Build-in types such as String, Boolean
     */
    BUILTIN = 1,
    /**
     * From custom type which is read from type.json
     */
    CUSTOM = 2,
    /**
     * Event type from event.json
     */
    EVENT = 3
}

