import { Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { KEY_CODE } from 'src/app/constants/common/key.code.constants';
import { SlotBase } from '../../models/slot-base.model';

@Component({
  selector: 'app-number-widget',
  templateUrl: './number-widget.component.html',
  styleUrls: ['./number-widget.component.css']
})
export class NumberWidgetComponent extends SlotBase implements OnInit {
  @ViewChild("input", {static: false}) editInput: ElementRef;

  ngOnInit() {
  }

  onViewDblClick($event) {
    $event.stopPropagation();
    if (!this.isEditing) {
      this.isEditing = true;
      setTimeout(() => {
        this.editInput.nativeElement.value = this.text.substr(1, this.text.length - 2);
      }, 10);
    }
  }

  onEditInput($event) {
    this.text = $event.target.value;
  }

  onEditKeydown($event) {
    switch ($event.keyCode) {
      case KEY_CODE.ENTER: {
        if ($event.target.value) {
          Number.parseFloat($event.target.value);
        }
        this.isEditing = false;
        break;
      }
    }
  }
}
