// import * as fromCopy from '../../actions/toolbar/toolbar.action';
import { Connector } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { Group } from 'libs/flowchart-ts/flowchart-core/src/lib/models/group.model';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import * as fromCopy from '../../actions/common/copy.actions';

export interface CopiedChartInformationState {
    copiedInformation: {
        nodes: ChartNode[];
        connectors: Connector[];
        groups: Group[];
        groupsNodesMap: Map<string, ChartNode[]>;
    };
}

export const initialState: CopiedChartInformationState = {
    copiedInformation: {
        nodes: null,
        connectors: null,
        groups: null,
        groupsNodesMap: new Map()
    }
};

export function reducer(
    state = initialState,
    action: fromCopy.CopyChartInformationAction
): CopiedChartInformationState {
    switch (action.type) {
        case fromCopy.COPY_CHART_INFORMATION_SUCCESS: {
            const copiedInformation = action.payload;
            return {
                ...state,
                copiedInformation: {
                    nodes: copiedInformation.nodes,
                    connectors: copiedInformation.connectors,
                    groups: copiedInformation.groups,
                    groupsNodesMap: copiedInformation.groupsNodesMap ? copiedInformation.groupsNodesMap : null
                }
            };
        }
    }
    return state;
}

export const getCopiedChartInformation = (state: CopiedChartInformationState) => state.copiedInformation;
