import { Command } from "./commands/command.model";

export class UndoRedoStack {
    past: Map<string, Command[][]>;
    future: Map<string, Command[][]>;
    preFuture: Map<string, Command[][]>;
    constructor(pastCmds: Map<string, Command[][]>, futureCmds: Map<string, Command[][]>, preFutureCmds: Map<string, Command[][]>) {
        this.past = pastCmds;
        this.future = futureCmds;
        this.preFuture = preFutureCmds;
    }
}
