import { Connector } from "libs/flowchart-ts/flowchart-core/src/lib/models/connector.model";
import { Group } from "libs/flowchart-ts/flowchart-core/src/lib/models/group.model";
import { ChartNode } from "libs/flowchart-ts/flowchart-core/src/lib/models/node.model";

export class Canvas {
    nodes: ChartNode[];
    connectors: Connector[];
    groups: Group[];
    chart: string;
}

export class Transform {
    translateX: number;
    translateY: number;
    scale: number;
    constructor(translateX: number, translateY: number, scale: number) {
        this.translateX = translateX;
        this.translateY = translateY;
        this.scale = scale;
    }
}

export class ViewBox {
    // 构造成svg viewBox的字符串
    viewBox: string;
    width: number;
    height: number;
    left: number;
    top: number;
    constructor(width: number, height: number, transform: Transform) {
        this.width = width / transform.scale;
        this.height = height / transform.scale;
        this.left = transform.translateX;
        this.top = transform.translateY;
        this.viewBox = transform.translateX + ', ' + transform.translateY + ', ' +
            width / transform.scale + ', ' + height / transform.scale;
    }
}
