import { Component, OnInit } from '@angular/core';
import { NodeViewType } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.enums';

@Component({
  selector: 'app-node-template-lib-panel',
  templateUrl: './node-template-lib-panel.component.html',
  styleUrls: ['./node-template-lib-panel.component.css']
})
export class NodeTemplateLibPanelComponent implements OnInit {
  nodesList = [
    {
      name: '普通节点',
      nameEn: 'common-node',
      index: NodeViewType.COMMON
    },
    {
      name: '函数节点',
      nameEn: 'function-node',
      index: NodeViewType.FUNCTION
    },
    {
      name: '等待节点',
      nameEn: 'wait-node',
      index: NodeViewType.WAIT
    },
    {
      name: '等待全部',
      nameEn: 'waitall-node',
      index: NodeViewType.WAITALL
    },
    {
      name: '停止节点',
      nameEn: 'stop-node',
      index: NodeViewType.STOP_NODE
    },
    {
      name: '停止流程',
      nameEn: 'stop-flow-node',
      index: NodeViewType.STOP_FLOW
    }
  ];

  constructor() { }

  ngOnInit() {
  }

  onNodeControlDragStart($event, viewType: NodeViewType) {
    $event.dataTransfer.setData('text', JSON.stringify({ offset: { x: $event.offsetX, y: $event.offsetY }, viewType }));
  }
}
