import { ChartJsonKey, TypeJsonKey } from './../constants/chart.constants';
import { Member, MemberFrom } from './member.model';
import { TypeSource } from './../constants/type.enums';
import { CheckResult } from './check-result.model';
import { Project } from './project.model';
import { StringUtils } from './../../../../flowchart-common/src/lib/utils/string.utils';

export class ChartType {

    uid: string;
    name: string;
    description?: string;
    inherits?: string[];
    source: TypeSource;
    members: Member[];
    memberMap: Map<string, Member>;
    project?: Project;

    //#region static members
    public static GlobalType: ChartType = null;
    public static AnyType: ChartType = null;
    public static VoidType: ChartType = null;
    public static ListType: ChartType = null;
    public static DictType: ChartType = null;
    public static StringType: ChartType = null;
    public static BoolType: ChartType = null;
    public static NumberType: ChartType = null;
    public static UndefinedType: ChartType = null;

    public static BASIC_TYPES: ChartType[];
    public static BASIC_TYPES_WITHOUT_VOID: ChartType[];
    //#endregion

    customAssignPredict: (t: ChartType) => boolean;
    //#region static property
    static builtInTypes: Map<string, ChartType> = null;
    constructor(data?: { uid?: string, name?: string, source?: TypeSource }) {
        // There is no static contructor in ts, so we need to initialize the built-in types before first call contructor
        if (!ChartType.inited) {
            ChartType.init();
        }
        this.name = data ? data.name : undefined;
        this.uid = data ? data.uid : undefined;
        this.source = data ? data.source : TypeSource.UNKNOWN;
        this.memberMap = new Map<string, Member>();
        this.members = [];
    }
    //#region Static methods
    static inited: boolean = false;
    /**
     * Initialize built-in types
     */
    static init() {
        ChartType.inited = true;
        ChartType.builtInTypes = new Map<string, ChartType>();
        ChartType.VoidType = new ChartType({ uid: '1', name: 'Void', source: TypeSource.BUILTIN });
        ChartType.GlobalType = new ChartType({ uid: '2', name: 'Global', source: TypeSource.CUSTOM });
        ChartType.AnyType = new ChartType({ uid: '3', name: 'Any', source: TypeSource.BUILTIN });
        ChartType.AnyType.customAssignPredict = t => true;
        ChartType.StringType = new ChartType({ uid: '4', name: 'String', source: TypeSource.BUILTIN });
        ChartType.BoolType = new ChartType({ uid: '5', name: 'Bool', source: TypeSource.BUILTIN });
        ChartType.NumberType = new ChartType({ uid: '6', name: 'Number', source: TypeSource.BUILTIN });
        // List type has a ForEach member, used to iterate members
        ChartType.ListType = new ChartType({ uid: '7', name: 'List', source: TypeSource.BUILTIN });
        const forEach = new Member();
        forEach.name = 'ForEach';
        forEach.source = 'ForEach';
        ChartType.ListType.addMember(forEach);
        ChartType.DictType = new ChartType({ uid: '8', name: 'Dict', source: TypeSource.BUILTIN });
        ChartType.UndefinedType = new ChartType({ uid: '9', name: "Undefined", source: TypeSource.BUILTIN });

        // Add to maps
        ChartType.builtInTypes.set(ChartType.VoidType.name, ChartType.VoidType);
        ChartType.builtInTypes.set(ChartType.AnyType.name, ChartType.AnyType);
        ChartType.builtInTypes.set(ChartType.StringType.name, ChartType.StringType);
        ChartType.builtInTypes.set(ChartType.BoolType.name, ChartType.BoolType);
        ChartType.builtInTypes.set(ChartType.NumberType.name, ChartType.NumberType);
        ChartType.builtInTypes.set(ChartType.ListType.name, ChartType.ListType);
        ChartType.builtInTypes.set(ChartType.DictType.name, ChartType.DictType);

        // 
        ChartType.BASIC_TYPES = [ChartType.AnyType, ChartType.BoolType, ChartType.NumberType, ChartType.StringType, ChartType.VoidType, ChartType.DictType, ChartType.ListType];
        ChartType.BASIC_TYPES_WITHOUT_VOID = [ChartType.AnyType, ChartType.BoolType, ChartType.NumberType, ChartType.StringType, ChartType.DictType, ChartType.ListType];
    }

    /**
     * Get Type object from project typedict by name
     * @param name Type name
     * @param project Project
     * @returns Type object
     */
    static getType(name: string, project: Project): ChartType {
        if (StringUtils.isNullOrWhiteSpace(name)) {
            return null;
        }
        name = name.trim();
        // Built-in type first
        if (ChartType.builtInTypes.has(name)) {
            return ChartType.builtInTypes.get(name);
        }
        // If typemap contains the type, return 
        if (project.typeMap.has(name)) {
            return project.typeMap.get(name);
        }
        // List<XXX> or Dict<XXX,XXX>
        if (name.includes('<') && name.endsWith('>')) {
            const genericTypeName = name.substring(0, name.indexOf('<'));
            const typeParamNames = name.substring(genericTypeName.length + 1, name.length - genericTypeName.length - 2).split(',');
            let genericType = ChartType.getType(genericTypeName, project);
            if (!(genericType instanceof GenericType)) {
                console.error(`Type '${genericTypeName}' is not a generic type in name`);
                return null;
            }

            const t = new GenericType();
            t.name = name;
            t.outType = genericType;
            t.typeParams = typeParamNames.map(tname => ChartType.getType(tname, project));
            project.addType(t);
            return t;
        } else {
            console.log(`Unknown type ${name}`);
            return null;
        }
    }
    //#endregion


    //#region Methods
    check(): CheckResult {
        // if (!this.uid) {
        //     return new CheckResult('uid is empty');
        // }
        if (!this.name) {
            return new CheckResult("Empty name");
        }
        // if (!TYPE_NAME_REGEX.test(this.name)) {
        //     return new CheckResult(`'${this.name}' is not a valid type name.`)
        // }
        return new CheckResult();
    }

    /**
     * Add member(method or property) to type
     * @param member Member to be added
     * @returns 
     */
    addMember(member: Member) {
        if (!member) {
            throw new Error('Member is empty');
        }
        if (StringUtils.isNullOrWhiteSpace(member.name)) {
            throw new Error('Member name is empty');
        }
        if (!this.memberMap) {
            this.memberMap = new Map<string, Member>();
        }
        this.memberMap.set(member.name, member);
        this.members.push(member);
        return true;
    }

    /**
     * Remove member from type by name
     * @param name name of member to be removed
     * @returns true for successfully removed
     */
    removeMember(name: string) {
        if (StringUtils.isNullOrWhiteSpace(name)) {
            return false;
        }
        if (this.memberMap.has(name)) {
            this.memberMap.delete(name);
        }
        return true;
    }

    /**
     * Get member from type by member name
     * @param name member name
     * @returns member
     */
    getMember(name: string): Member {
        const member = this.memberMap.get(name);
        if (!member) {
            // If not found, looking for member in parent type
            if (this.inherits && this.inherits.length > 0) {
                for (let inherit of this.inherits) {
                    const fatherType = ChartType.getType(inherit, this.project);
                    if (fatherType) {
                        return fatherType.getMember(name);
                    }
                }
            }
            // Special process for list.ForEach 
            if (name === 'ForEach') {
                return ChartType.ListType.getMember('ForEach');
            }
        }

        return member;
    }

    canAssignFrom(tp: ChartType) {
        if (tp === this || tp === ChartType.AnyType) {
            return true;
        }
        if (this.customAssignPredict) {
            return this.customAssignPredict(tp);
        }
        return false;
    }

    toName() {
        if (!StringUtils.isNullOrWhiteSpace(this.name)) {
            return this.name;
        }
        return 'Unknown';
    }

    fromJson(j: any, typeConverter: (typeName: string) => ChartType, preParse: boolean = false) {
        this.uid = j[TypeJsonKey.Uid];
        this.name = j[TypeJsonKey.Name];
        this.description = j[TypeJsonKey.Description];
        this.inherits = j[TypeJsonKey.Inherits];
        this.source = j[TypeJsonKey.Source];
        if(!preParse) {
            if (j[TypeJsonKey.Members]) {
                j[TypeJsonKey.Members].forEach((m: any) => {
                    const member = new Member();
                    member.fromJson(m, typeConverter);
                    this.addMember(member);
                })
            }
        }
    }

    toJson() {
        const t: any = {};
        t[TypeJsonKey.Uid] = this.uid;
        t[TypeJsonKey.Name] = this.name;
        t[TypeJsonKey.Description] = this.description;
        t[TypeJsonKey.Source] = this.source;
        const members: Member[] = [];
        this.members.forEach((v, k) => {
            if(v.from === MemberFrom.FILE) {
                members.push(v.toJson());
            }
        })
        t[TypeJsonKey.Members] = members;
        t[TypeJsonKey.Inherits] = this.inherits;
        return t;
    }

    refreshMap() {
        this.memberMap = new Map<string, Member>();
        if (this.members) {
            this.members.forEach(m => this.memberMap.set(m.name, m));
        }
    }

    //#endregion
}

/**
 * 
 */
export class GenericType extends ChartType {
    outType: ChartType;
    typeParams: ChartType[];
    typeDict: Map<string, ChartType>;

    constructor(data?: { uid?: string, name?: string, source?: TypeSource }) {
        super(data);
        this.typeParams = [];
    }

    toName(): string {
        if (this.outType && this.typeParams && this.typeParams.length > 0) {
            return `${this.outType.name}`
        }
    }

    static equals(t1: GenericType, t2: GenericType): boolean {
        if (t1.outType === t2.outType) {
            if (t1.typeParams.length === t2.typeParams.length) {
                for (let i = 0; i < t1.typeParams.length; i++) {
                    const sub1 = t1.typeParams[i];
                    const sub2 = t2.typeParams[i];
                    if (sub1 instanceof GenericType && sub2 instanceof GenericType) {
                        if (!GenericType.equals(sub1, sub2)) {
                            return false;
                        } else {
                            continue;
                        }
                    }
                    if (sub1 != sub2) {
                        return false;
                    }
                }
                return true;
            }
        }
        return false;
    }

}