import { createSelector } from '@ngrx/store';
import { getFlowChartState, FlowchartState } from '../../reducers';
import * as fromToolbar from '../../reducers/toolbar/toolbar.reducer';

export const getToolbarElementState = createSelector(
    getFlowChartState,
    (state: FlowchartState) => state.toolbar
);

export const getConnectorLatestShape = createSelector(
    getToolbarElementState,
    fromToolbar.getLatestConnectorShape
);

export const getConnectorLatestType = createSelector(
    getToolbarElementState,
    fromToolbar.getLatestConnectorType
);

export const getPositionFixedNodeId = createSelector(
    getToolbarElementState,
    fromToolbar.getFixedNodePosition
);
export const getFixToCurrnetMsg = createSelector(
    getToolbarElementState,
    fromToolbar.fixToCurrentChart
);
