import { InterfaceService } from 'src/app/services/interface/interface.service';
import { ProjectService } from './../../../services/project/project.service';
import { ToastService } from './../../../common/toast/toast.service';
import { EventsService } from './../../../services/project/events.service';
import { UidUtils } from './../../../utils/uid.utils';

import { EventEditorComponent } from './../event-editor/event-editor.component';
import { PathService } from './../../../common/path-picker/path.service';
import { DialogService } from './../../../common/dialog/dialog.service';
import { ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild } from '@angular/core';
import { SelectionModel } from '@angular/cdk/collections';
import { MatTableDataSource } from '@angular/material/table';
import { SimpleDialogBtnType } from 'src/app/common/dialog/components/simple-dialog/simple-dialog.component';
import * as fromStore from 'src/app/store/index';
import { Store } from '@ngrx/store';
import { deepCopy } from 'src/app/store/reducers/utilsFunction';
import { MatDialogRef, MatPaginator, PageEvent } from '@angular/material';
import { APP_HTTP_RESULT_CODE } from 'src/app/constants/http/http.constants';
import { HttpResult } from 'src/app/common/http/http.result.model';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';
import { EventType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/event.model';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/type.model';
import { Parameter } from 'libs/flowchart-ts/flowchart-core/src/lib/models/parameter.model';
import { EventParam } from 'libs/flowchart-ts/flowchart-core/src/lib/models/member.model';

@Component({
  selector: 'app-project-events-panel',
  templateUrl: './project-events-panel.component.html',
  styleUrls: ['./project-events-panel.component.css']
})
export class ProjectEventsPanelComponent implements OnInit {
  columns = ['select', 'name', 'description', 'classes', 'operation'];

  dataSource = new MatTableDataSource<EventType>();
  selection = new SelectionModel<EventType>(true, []);
  // @Input() project: CreateProject;
  project: Project;
  events: EventType[];
  types: ChartType[];
  pageEvent: PageEvent;
  // 当前分页索引
  currentIndex: number;
  numOfPages: number;
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild('indexInput', { static: false }) indexInput: ElementRef;

  constructor(
    private dialogService: DialogService,
    private dialogRef: MatDialogRef<ProjectEventsPanelComponent>,
    private pathService: PathService,
    private store: Store,
    private eventsService: EventsService,
    private cdref: ChangeDetectorRef,
    private projectService: ProjectService,
    private typeService: InterfaceService,
    private toastService: ToastService) { }

  ngOnInit() {
    this.initData();

  }

  ngAfterViewInit(): void {
    this.dataSource.paginator = this.paginator;
    this.getNumOfPages();
    this.initCurrentIndex();
    this.cdref.detectChanges();
  }

  initCurrentIndex() {
    this.currentIndex = this.paginator.pageIndex + 1;
    this.indexInput.nativeElement.value = this.currentIndex;

  }

  getNumOfPages() {
    let previous = this.numOfPages;
    this.numOfPages = Math.ceil(this.dataSource.data.length / this.paginator.pageSize);
    if (previous !== this.numOfPages) {
      this.currentIndex = this.currentIndex + (this.numOfPages - previous);
      previous = this.numOfPages;
      this.indexInput.nativeElement.value = this.currentIndex;
    }
  }

  goToPage($event) {
    this.getNumOfPages();
    const page = typeof ($event) === 'number' ? $event : $event.target.value;
    let delta = page - this.currentIndex;
    if (page < 1 || page > this.numOfPages) {
      return;
    }
    if (delta > 0) {
      while (delta--) {
        this.paginator.nextPage();
      }
    } else if (delta < 0) {
      while (delta++) {
        this.paginator.previousPage();
      }
    }
    this.initCurrentIndex();
  }

  initData() {
    this.project = this.projectService.getCurrentProject();
    this.types = this.typeService.getAllTypeList(true);
    this.events = this.project.events.map(e=>{
      const newEvent = new EventType();
      newEvent.fromJson(e.toJson(), this.project.getType.bind(this.project));
      return newEvent;
    })
    this.dataSource.data = this.events;
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(ev?: EventType): string {
    if (!ev) {
      return `${this.isAllSelected() ? 'select' : 'deselect'} all`;
    }
    return `${this.selection.isSelected(ev) ? 'deselect' : 'select'} row`;
  }


  //#region EventHandler
  onAddEventClick($event) {
    const newEvent = new EventType();
    newEvent.name = '';
    newEvent.uid = UidUtils.generateUid();
    // this.project.events.push(newEvent);
    // this.dataSource.data = this.project.events;
    this.dialogService.open(EventEditorComponent, { event: newEvent, editType: 'new', existEvents: this.events }).subscribe(event => {
      if (event) {
        if (!this.events) {
          this.events = [];
        }
        this.events.push(event);
        this.dataSource.data = this.events;
        this.getNumOfPages();
        this.initCurrentIndex();

      }
    });
  }

  onDeleteEventClick($event, ev: EventType) {
    this.dialogService.openSimple({ text: `是否确认删除事件${ev.name}?`, btnType: SimpleDialogBtnType.YES_NO }).subscribe(btnCode => {
      if (btnCode === 1) {
        this.events.splice(this.events.indexOf(ev), 1);
        this.dataSource.data = this.events;
        this.getNumOfPages();
        this.initCurrentIndex();
      }
    });
  }

  onDeleteAll() {
    console.log(this.selection.selected);
    const length = this.selection.selected.length;
    const nameMap = this.selection.selected.map(s => s.name);
    let nameList = '';
    this.selection.selected.forEach((selection, index) => {
      if (index <= 4) {
        nameList = nameList + selection.name + ', ';
      }
    });
    nameList = nameList.substring(0, nameList.length - 2);
    this.dialogService.openSimple({
      text: `是否确认删除${nameList}等全部${length}个事件`,
      btnType: SimpleDialogBtnType.YES_NO
    }).subscribe(btnCode => {
      if (btnCode === 1) {
        this.events = this.events.filter(ev => !nameMap.includes(ev.name));
        // 将选中的全部反选，对话框保存后才会删除，此时只是隐藏。
        this.selection.selected.forEach(s => {
          this.selection.toggle(s);
        });
        this.dataSource.data = this.events;
        this.getNumOfPages();
        this.initCurrentIndex();
      }
    });
  }

  onEditEventClick($event, event: EventType) {
    this.dialogService.open(EventEditorComponent, { event, editType: 'edit', existEvents: this.events }).subscribe(ev => {
      if (ev) {
        const index = this.events.indexOf(event);
        if (index > -1) {
          this.events[index] = ev;
          this.dataSource.data = this.events;
        }
      }
    });
  }

  onImportFromJson($event) {
    this.pathService.showPathPicker({ filter: { type: 'file', suffix: 'event.json' } }).subscribe(result => {
      this.pathService.getFileContent(result).then((result: HttpResult<JSON>) => {
        console.log(result, result.data);
        const eventList = result.data;
        if (eventList) {
          if (!this.events) {
            this.events = [];
          }
          this.events = this.events.concat(this.parseEvents(eventList));
          this.dataSource.data = this.events;
        }
      });
    });
  }

  onCancelClick($event) {
    this.dialogRef.close();
  }

  onSaveClick($event) {
    console.log(this.events);
    // this.store.dispatch(new fromStore.UpdateEvents({ project: this.project.name, events: this.events }));
    this.project.events = [];
    this.project.eventMap.clear();
    this.events.forEach(e => this.project.addEvent(e));
    this.project.saveEvents();
    this.dialogRef.close();
  }
  //#endregion

  //#region private methods
  parseEvents(arr) {
    const events: EventType[] = [];
    if (arr) {
      arr.forEach(e => {
        const ev: EventType = new EventType();
        ev.name = e.name;
        if (e.Parameters) {
          ev.parameters = [];
          e.Parameters.forEach(p => {
            const para = new EventParam();
            para.name = p.Name;
            para.type = p.type;
            ev.parameters.push(para);
          });
        }
        events.push(ev);
      });
    }
    return events;
  }
  //#endregion

}
