import { ProjectService } from './../../../services/project/project.service';
import { APP_HTTP_RESULT_CODE } from 'src/app/constants/http/http.constants';
import { ToastService } from './../../../common/toast/toast.service';
import { StringUtils } from './../../../utils/string.utils';
import { InterpageCommunicator } from './../../../common/interpage-communication/models/interpage-communicator.model';
import { DebugStartUpInfo, DebugStartUpType } from 'src/app/debug/models/debug-startup.model';
import { DebugRunningData } from './../../models/debug-node-status.model';
import { ChartService } from 'src/app/services/chart/chart.service';
import { RequireChartInfo } from 'src/app/models/chart.model';
import { slideIn } from './../../../common/anims/slide-in.anims';
import { MatTableDataSource } from '@angular/material/table';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DEBUG_METHODS } from './../../constants/debug-message.constants';
import { DebugMessage } from './../../models/debug-message.model';
import { DebugChartInfo } from './../../models/debug-chart-info.model';
import { Component, Inject, OnInit, ChangeDetectorRef } from '@angular/core';
import { Store } from '@ngrx/store';
import * as fromStore from 'src/app/store';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';

export enum DEBUG_COM_TYPE {
  WEB_SOCKET = 1,
  INTERPAGE_COM = 2
}

@Component({
  selector: 'app-debug-main',
  templateUrl: './debug-main.component.html',
  styleUrls: ['./debug-main.component.css'],
  animations: [slideIn]
})
export class DebugMainComponent implements OnInit {

  private _id = 0;
  socketMap: Map<number, WebSocket>;

  debugComType: DEBUG_COM_TYPE = DEBUG_COM_TYPE.WEB_SOCKET;

  // websocket 地址
  addr = 'ws://localhost';
  // 端口范围
  startPort = 8995;
  endPort = 9005;
  msgList: any[];

  // 运行流程图信息列表
  chartInfoList: DebugChartInfo[];

  // 广播实例(针对web类型的debug，页面间通信使用)
  com: InterpageCommunicator;

  // 表格相关
  chartInfoListDataSource;
  displayedColumns = ['chartName', 'agentId', 'objectName', 'ownerNodeName', 'port', 'op'];

  // 当前调试图的相关信息
  debuggingChartInfo: DebugChartInfo;
  debuggingChart: Chart;
  chartList: Chart[];

  runningPort = -1;
  runningAgentId: string;
  runningChartName: string;
  runningOwnerNodeAddr: string;
  runningDataMap: Map<number, DebugRunningData[]> = new Map<number, DebugRunningData[]>();
  debuggingChartRunningData: DebugRunningData[];
  constructor(
    private dialogRef: MatDialogRef<DebugMainComponent>,
    @Inject(MAT_DIALOG_DATA) public startupData: DebugStartUpInfo,
    private store: Store,
    private projectService: ProjectService,
    private chartService: ChartService,
    private toastService: ToastService,
    private cdRef: ChangeDetectorRef) { }

  ngOnInit() {
    this.initData();
  }

  initData() {
    this.chartInfoList = [];
    this.chartInfoListDataSource = new MatTableDataSource<DebugChartInfo>(this.chartInfoList);
    this.store.select(fromStore.getChartListSelector).subscribe(data => {
      // 直接此处赋值会变为不可变数据结构，后续getChart中的替换就无法进行，故先将数组替换一下
      this.chartList = [...data];
    });
    if (this.startupData.type === DebugStartUpType.QUICK_DEBUG) {
      this.debuggingChart = this.startupData.params.chart;
    }
  }

  initInterpageCommunicator() {
    this.com = new InterpageCommunicator('asyncflow');
    this.com.init();
    this.com.onmessage = e => {
      console.log('debugmain', e.data);
      if (!StringUtils.isNullOrWhiteSpace(e.data)) {
        const msg = JSON.parse(e.data);
        setTimeout(() => {
          console.log('from other page', msg);
          this.processMessage(-1, msg);
        }, 10);
      }
    };
  }

  //#region WebSocket Debug Mode methods
  initWebSocket() {
    this.socketMap = new Map<number, WebSocket>();
    this.msgList = [];
    this.checkWebSockets();
  }

  checkWebSockets() {
    let count = 0;
    for (let port = this.startPort; port <= this.endPort; port++) {
      try {
        const newAddr = this.addr + ':' + port;
        const ws = new WebSocket(newAddr);
        ws.onopen = ev => {
          // TODO 暂时针对多端口执行快速调试时，只以第一个为准
          if (this.runningPort === -1) {
            this.runningPort = port;
          }
          this.socketMap.set(port, ws);
          const msgId = this.generateMsgId();
          ws.onmessage = event => {
            const msg = JSON.parse(event.data);
            this.processMessage(port, msg);
          };
          ws.onclose = event => {
            this.removeSocket(port);
          };

          if (this.startupData && this.startupData.params && this.startupData.params.chart) {
            this.quickStart(this.startupData.params.chart);
          } else {
            this.getChartList(msgId, port);
          }

        };
      } catch (error) {
        count++;
      }

    }
  }
  ////#endregion

  //#region Common part
  parseChartInfo(chart_info, port: number) {
    const chartList: DebugChartInfo[] = [];
    for (const ci of chart_info) {
      const chart = new DebugChartInfo();
      chart.chartName = ci.chart_name;
      chart.agentId = ci.agent_id;
      chart.objectName = ci.object_name;
      chart.ownerNodeAddr = ci.owner_node_addr;
      chart.ownerNodeId = ci.owner_node_id;
      chart.ownerNodeName = ci.owner_chart_name;
      chart.port = port;
      chartList.push(chart);
    }
    return chartList;
  }

  onCategoryChanged($event) {
    const cate = Number.parseInt($event.target.value) as DEBUG_COM_TYPE;
    this.debugComType = cate;
  }


  onRefreshDebugDataClick($event) {
    if (this.addr) {
      if (this.debugComType == DEBUG_COM_TYPE.WEB_SOCKET) {
        this.initWebSocket();
      } else if (this.debugComType == DEBUG_COM_TYPE.INTERPAGE_COM) {
        if (!this.com) {
          this.initInterpageCommunicator();
        }
        const msgId = this.generateMsgId();
        this.getChartList(msgId);
      }
    }
  }

  onCloseClick($event) {
    this.dialogRef.close();
  }

  onStartDebugClick($event, i: number) {
    // alert(`start debug ${this.chartList[i].chartName}`);
    this.startDebug(i);
  }

  onBackClick($event) {
    this.stopDebug();
  }

  generateMsgId() {
    return this._id++;
  }

  getChartList(msgId: number, port: number = -1) {
    const getChartInfoList = new DebugMessage(msgId, DEBUG_METHODS.GET_CHART_LIST);
    getChartInfoList.params = { chart_name: '', obj_name: '' };
    this.sendMsg(getChartInfoList);
  }

  async quickStart(chart: Chart) {
    if (chart) {
      const msg = new DebugMessage(this.generateMsgId(), DEBUG_METHODS.QUICK_START);
      msg.params = { chart_name: chart.path };
      console.log(msg);
      this.sendMsg(msg);
    }
  }

  async startDebug(i: number) {
    const chart = this.chartInfoList[i];
    this.debuggingChartInfo = chart;
    this.runningPort = chart.port;
    this.runningAgentId = chart.agentId;
    this.debuggingChartRunningData = this.runningDataMap.get(chart.port);
    this.debuggingChart = this.projectService.getCurrentProject().charts.find(c => c.path === chart.chartName);
    if (!this.debuggingChart) {
      this.toastService.error(`Unable to debug chart ${chart.chartName} for fetching chart details failed.`);
      return;
    }
    this.cdRef.detectChanges();
    if (chart) {
      const msg = new DebugMessage(this.generateMsgId(), DEBUG_METHODS.DEBUG_CHART);
      msg.params = { chart_name: chart.chartName, agent_id: chart.agentId, owner_node_addr: chart.ownerNodeAddr };
      this.sendMsg(msg);
    }
  }

  stopDebug() {
    if (this.debuggingChart) {
      const msg = new DebugMessage<any>(this.generateMsgId(), DEBUG_METHODS.STOP_DEBUG);
      msg.params = { chart_name: this.debuggingChart.path || this.runningChartName };
      if (this.runningAgentId) {
        msg.params.agent_id = this.runningAgentId;
      } else if (this.debuggingChartInfo) {
        msg.params.agent_id = this.debuggingChartInfo.agentId;
      }
      if(this.debuggingChartInfo) {
        msg.params.owner_node_addr = this.debuggingChartInfo.ownerNodeAddr;
      }

      this.sendMsg(msg);
      this.debuggingChart = null;
      this.debuggingChartInfo = null;
    }
  }

  sendMsg(msg) {
    switch (this.debugComType) {
      case DEBUG_COM_TYPE.WEB_SOCKET: {
        // websocket连接可能会因为异常中断
        const socket = this.socketMap.get(this.runningPort);
        if (socket) {
          socket.send(JSON.stringify(msg));
        }
        break;
      }
      case DEBUG_COM_TYPE.INTERPAGE_COM: {
        if (this.com) {
          this.com.sendMsg(JSON.stringify(msg));
        }
        break;
      }
    }
  }

  removeSocket(port: number) {
    this.socketMap.delete(port);
    this.chartInfoList = this.chartInfoList.filter(c => c.port !== port);
    this.chartInfoListDataSource.data = this.chartInfoList;
    this.cdRef.detectChanges();
  }


  async getChart(name: string) {
    const chart = this.chartList.find(c => c.path === name);
    if (chart) {
      const result = await this.chartService.getChart(new RequireChartInfo(chart.project.name, chart.uid));
      if (result.code === APP_HTTP_RESULT_CODE.OK) {
        this.chartList[this.chartList.indexOf(chart)] = result.data;
        return result.data;
      }
    }
    return null;
  }

  processMessage(port: number, msg: DebugMessage<any>) {
    console.log('msg', msg);
    if (msg.method) {
      switch (msg.method) {
        case DEBUG_METHODS.GET_CHART_LIST:
          this.processGetChartListMsg(port, msg);
          break;
        case DEBUG_METHODS.DEBUG_CHART:
          this.processDegugChartMsg(port, msg);
          break;
        case DEBUG_METHODS.QUICK_START:
          this.processQuickDebugMsg(port, msg);
          break;
      }
    }
    if (msg.error) {

    }
  }

  processGetChartListMsg(port: number, msg: DebugMessage<any>) {
    if (msg && msg.result && msg.result.chart_info) {
      this.chartInfoList = [...this.parseChartInfo(msg.result.chart_info, port)];
      this.chartInfoListDataSource.data = this.chartInfoList;
      this.cdRef.detectChanges();
    }
  }

  processDegugChartMsg(port: number, msg: DebugMessage<any>) {
    if (msg.params) {
      this.processRunningData(port, msg.params.running_data);
    } else if (msg.result) {
      // 返回值包含图的基本信息，需要用来判断图的结构是否发生了变化，如果变化了则需要给用户以提示
    }
  }

  /**
   * 处理停止调试，有两种情况，一种是服务端发送过来的停止调试的信息  一种是前端用户发送的主动的停止调试消息
   * @param port 端口号
   * @param msg 消息内容
   */
  processStopDebugMsg(port: number, msg: DebugMessage<any>) {
    if (port !== this.runningPort) {
      return;
    }
    if (msg.result && msg.result.chart_name === this.debuggingChart.path) {
      this.debuggingChartInfo = null;
      this.debuggingChart = null;
    }
    if (msg.params && msg.params.chart_name === this.debuggingChart.path) {
      this.debuggingChartInfo = null;
      this.debuggingChart = null;
    }
  }

  processQuickDebugMsg(port: number, msg: DebugMessage<any>) {
    if (port !== this.runningPort) {
      return;
    }

    // 图开始运行时发送图信息等
    if (msg.params) {
      this.runningAgentId = msg.params.agent_id;
      this.runningChartName = msg.params.chart_name;
      this.debuggingChartInfo = new DebugChartInfo();
      this.debuggingChartInfo.agentId = msg.params.agent_id;
      this.debuggingChartInfo.chartName = msg.params.chart_name;
      this.debuggingChartInfo.ownerNodeAddr = msg.params.owner_node_addr;
      this.debuggingChartInfo.port = port;
    }

    // 发起快速调试的时候返回图结构
    if (msg.result) {

    }
  }

  processRunningData(port, runningData) {
    console.log('receive from server', runningData);
    if (this.debuggingChart && runningData) {
      this.runningDataMap.set(port, runningData.map(data => new DebugRunningData(data)));
      this.debuggingChartRunningData = this.runningDataMap.get(this.runningPort);
      this.cdRef.detectChanges();
    }
  }
}
