import { NodeTokenType, ParseInfoType, ParseResult } from './../constants/node.enums';
import { Variable } from './variable.model';

export class NodeToken {
    text: string;
    type: number;
}

export const INVALID_VAR_ID = -1;
export class NodeParseInfo {
    type: ParseInfoType;
    result: ParseResult;
    name: string;
    codes: string[];
    message: string;
    tokens: NodeToken[];

    setVarId: number;
    /**
     * 
     */
    get isEvent() { return this.type === ParseInfoType.EVENT };

    /**
     * Whether contains subchart call;
     */
    get isSubchartCall() { return this.type === ParseInfoType.SUBCHART }
    /**
     * is control node (stopnode stopflow)
     */
    get isControlNode() { return this.type === ParseInfoType.CONTROL }
    /**
     * isPlainNode
     */
    get isPlainNode() { return this.type === ParseInfoType.PLAIN };
    /**
     * 
     */
    get isError() { return this.result === ParseResult.ERROR }

    get isWarning() { return this.result === ParseResult.WARNING };


    constructor() {
        this.codes = [];
        this.type = ParseInfoType.PLAIN;
        this.result = ParseResult.NORMAL;
        this.setVarId = INVALID_VAR_ID;
    }

    //#region Methods
    /**
     * Clear parse information
     */
    clear() {
        this.codes = [];
        this.message = '';
        this.result = ParseResult.NORMAL;
        this.setVarId = INVALID_VAR_ID;
    }

    /**
     * set name of event which was referred as the name of parse info.
     * @param name event name
     */
    setEventName(name: string) {
        this.name = name;
        this.type = ParseInfoType.EVENT;
    }

    /**
     * set error message when there is error in the code
     * @param msg 
     */
    setErrorMessage(msg: string) {
        this.message = msg;
        this.result = ParseResult.ERROR;
    }

    /**
     * Set warning message
     * @param msg 
     */
    setWarningMessage(msg: string) {
        this.message = msg;
        this.result = ParseResult.WARNING;
    }
    //#endregion

}

export class ChartGenResult {
    chartId: string;
    nodeGenResultMap: Map<string, NodeParseInfo>;
    variables: Variable[];
    constructor(chartId: string, nodeGenResult?: Map<string, NodeParseInfo>) {
        this.chartId = chartId;
        this.nodeGenResultMap = nodeGenResult;
    }
}