import { DebugError } from './debug-error.model';
export class DebugMessage<T> {
    readonly jsonrpc: string = '2.0';
    method: string;
    id: number;
    params?: T;
    result?: T;
    error?: any;
    constructor(id: number, method: string) {
        this.id = id;
        this.method = method;
    }
}
