import { Pipe, PipeTransform } from '@angular/core';
import { MemberType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/member.model';

@Pipe({
  name: 'memberColor'
})
export class MemberColorPipe implements PipeTransform {

  transform(value: MemberType, ...args: any[]): any {
    switch (value) {
      case MemberType.EVENT:
        return 'yellow';
      case MemberType.METHOD:
        return 'green';
      case MemberType.PARAMETER:
        return 'lightblue';
      case MemberType.PROPERTY:
        return 'orange';
      case MemberType.UNKNOWN:
        return '#cdcdcd';
      default:
        return '#cdcdcd';
    }
  }

}
