import { EventParam } from 'libs/flowchart-ts/flowchart-core/src/lib/models/member.model';
import { InterfaceService } from './../../../services/interface/interface.service';
import { Component, Inject, OnInit, ViewChildren } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material';
import { deepCopy } from 'src/app/store/reducers/utilsFunction';
import { ChartType, EventType } from 'libs/flowchart-ts/flowchart-core/src/public-api';
import { Parameter } from 'libs/flowchart-ts/flowchart-core/src/lib/models/parameter.model';

@Component({
  selector: 'app-event-editor',
  templateUrl: './event-editor.component.html',
  styleUrls: ['./event-editor.component.css']
})
export class EventEditorComponent implements OnInit {
  @ViewChildren('eventNameInput') eventNameInput;
  event: EventType;
  dialogTitle: string;
  columns = ['name', 'type', 'description', 'op'];
  types;
  constructor(
    private dialogRef: MatDialogRef<EventEditorComponent>,
    @Inject(MAT_DIALOG_DATA) public data: { event: EventType, editType: 'new' | 'edit' },
    private interfaceService: InterfaceService) { }

  ngOnInit() {
    this.types = this.interfaceService.getAllTypeList();
    this.event = deepCopy(this.data.event);
    Object.setPrototypeOf(this.event, EventType.prototype);
    this.dialogTitle = this.data.editType === 'new' ? 'create-event' : 'edit-event';
  }

  onDeleteParamterClick($event, index: number) {
    this.event.parameters.splice(index, 1);
  }

  onAddParameterClick() {
    if (!this.event.parameters) {
      this.event.parameters = [];
    }
    const p = new EventParam();
    p.name = '';
    p.type = ChartType.StringType;
    this.event.parameters.push(p);
    setTimeout(() => {
      console.log(this.eventNameInput.last.nativeElement.focus());
    }, 0);
  }

  onCancelClick() {
    this.dialogRef.close();
  }

  onSaveClick() {
    this.data.event.name = this.event.name;
    this.data.event.description = this.event.description;
    this.data.event.memberMap.clear();
    this.data.event.members = [];
    this.event.members.forEach(m => this.data.event.addMember(m));
    this.dialogRef.close(this.data.event);
  }

  onParameterTypeChanged($event, p: Parameter) {
    p.type = $event;
  }

  onDeleteParameterClick($event, i) {
    this.event.parameters.splice(i, 1);
  }
}
