import { Chart } from "libs/flowchart-ts/flowchart-core/src/lib/models/chart.model";

export class TreeNode {
  name: string;
  data?: Chart;
  children?: TreeNode[];
  defaultType?: string;
  isFolder?: boolean;
  path?: string;
  level?: number;
  constructor(name: string, data?: Chart, children?: TreeNode[]) {
    this.name = name;
    this.data = data;
    this.children = children;
  }
}
