import { ChartType } from "./../../../../flowchart-core/src/lib/models/type.model";
import { IAstNodeVisitor } from "../ast-node-visitor.interface";

export enum AstOperator {
    Factor,         // '^'  : 乘方
    Multiplies,     // '*'  : 乘号
    Divided,        // '/'  : 除号
    Mod,            // '%'  : 取模
    FloorDiv,       // '//' : 取整余
    Add,            // '+'  : 加号
    Sub,            // '-'  : 减号 兼用负号
    Strcat,         // '..' : 字符串拼接
    RightShift,     // '>>' : 位右移
    LeftShift,      // '<<' : 位左移
    BitAnd,         // '&'  : 位And
    BitX,           // '~'  : 位异或
    BitOr,          // '|'  : 位或

    LessThan,       // '<'  : 小于
    MoreThan,       // '>'  : 大于
    LessEqualThan,  // '<=' : 小于等于
    MoreEqualThan,  // '>=' : 大于等于
    Equal,          // '==' : 等于
    NotEqual,       // '~=' : 不等于

    // 
    And,            // 'And', '&&': 不等于
    Or,             // 'Or', '||' : 不等于

    // 一元运算符
    Not,            // 'Not': 非
    Exclam,         // '!' 感叹号取非
    Sharp,          // '#': 井号

    UnknownOp
}

export abstract class AstNode {
    text: string;
    code: string;
    // Exception handling code before true logic code
    prefixCodes: string[];
    // Variables used in or express which is used for generating examine code
    varList: number[];
    type: ChartType;
    abstract Accept<T>(visitor: IAstNodeVisitor<T>): T;

    reset() {
        this.code = '';
        this.type = null;
    }
}

export class ExprNode extends AstNode {
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this);
    }
}

export class NameNode extends ExprNode {
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this);
    }
}

export class VarNode extends ExprNode {
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class ConstantNode<T> extends ExprNode {
    value: T;
}

export class NumberNode extends ConstantNode<number> {
    type = ChartType.NumberType;
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class StringNode extends ConstantNode<string> {
    type = ChartType.StringType;
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class FuncNode extends ExprNode {
    get isGlobal() {
        return this.caller == null;
    }

    caller: ExprNode;
    func: ExprNode;
    args: ArgListNode;
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class ArgNode extends ExprNode {
    get hasName() {
        return name === null || name === undefined;
    }

    isOptional: boolean;
    name: NameNode;
    value: ExprNode;
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class ArgListNode extends ExprNode {

    args: ArgNode[];
    get isEmpty() {
        return !this.args || this.args.length === 0;
    }

    constructor() {
        super();
        this.args = [];
    }

    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class UnaryOpNode extends ExprNode {
    operator: AstOperator;

    express: ExprNode;

    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class BinaryOpNode extends ExprNode {
    operator: AstOperator;
    left: ExprNode;
    right: ExprNode;

    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class MemberNode extends ExprNode {
    owner: ExprNode;
    member: NameNode;
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class ListNode extends ExprNode {
    items: ArgListNode;
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class SubscriptNode extends ExprNode {
    listExpr: ExprNode;
    index: ExprNode;
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class AssignNode extends ExprNode {
    left: VarNode;
    right: ExprNode;
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class SubscriptAssignNode extends ExprNode {
    left: SubscriptNode;
    right: ExprNode;
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class MemberAssignNode extends ExprNode {
    left: MemberNode;
    right: ExprNode;
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}

export class ParenthesisNode extends ExprNode {
    content: ExprNode;
    Accept<T>(visitor: IAstNodeVisitor<T>): T {
        return visitor.visit(this)
    }
}


export type VisitAstNode = ExprNode | NameNode | VarNode | StringNode | NumberNode | FuncNode | ArgNode | ArgListNode | UnaryOpNode | BinaryOpNode | MemberNode | ListNode | SubscriptNode | AssignNode | SubscriptAssignNode | MemberAssignNode | ParenthesisNode;