import { ChartJsonKey, MemberJsonKey } from './../constants/chart.constants';
import { ChartType } from "./type.model";
import { Item } from "./item.model";
import { Parameter } from './parameter.model';

export enum MemberType {
    UNKNOWN = 0,
    PROPERTY = 1,
    METHOD = 2,
    EVENT = 3,
    PARAMETER = 4
}

export enum MemberFrom {
    FILE = 0,
    SUBCHART = 1,
    // nsh specail type, from plan table
    IMPORT = 2,
    // built-in members such as wait_all()/stop_node();
    BUILT_IN = 3
}

export class CustomCode {
    hasCode: boolean;
    language: object;
    code: string;
    constructor() {
        this.hasCode = false;
        this.language = null;
        this.code = '';
    }
}

/**
 * Type member, it could be property or method.
 */
export class Member extends Item {
    memberType: MemberType;
    from: MemberFrom;
    source: string;
    parameters?: Parameter[];
    belongType?: ChartType;
    code?: CustomCode;
    fromJson(jobj: any, typeConverter: (t: string) => ChartType): boolean {
        if (super.fromJson(jobj, typeConverter)) {
            this.memberType = jobj[MemberJsonKey.MemberType];
            this.from = MemberFrom.FILE;
            this.source = jobj[MemberJsonKey.Source];
            if(jobj[MemberJsonKey.Parameters]) {
                this.parameters = jobj[MemberJsonKey.Parameters].map((p:any)=>{
                    const p1 = new Parameter();
                    p1.fromJson(p, typeConverter);
                    return p1;
                })
            }
            if (!this.code) {
                this.code = new CustomCode();
            }
            return true;
        }
        return false;
    }

    toJson() {
        const e: any = {};
        e[MemberJsonKey.Uid] = this.uid;
        e[MemberJsonKey.Name] = this.name;
		e[MemberJsonKey.Type] = this.type.name;
        e[MemberJsonKey.MemberType] = this.memberType;
        e[MemberJsonKey.From] = this.from;
        e[MemberJsonKey.Source] = this.source;
        e[MemberJsonKey.Code] = this.code;
        e[MemberJsonKey.Parameters] = this.parameters ? this.parameters.map(p => p.toJson()) : undefined;
        return e;
    }
}




/**
 * Property
 */
export class Property extends Member {
    memberType = MemberType.PROPERTY;
}

/**
 * Event parameter
 */
export class EventParam extends Property {
    index: number;
}

/**
 * Method object
 */
export class Method extends Member {
    /**
     * method parameters
     */
    parameters: Parameter[];
    constructor() {
        super();
        this.parameters = [];
        this.type = ChartType.VoidType;
        this.memberType = MemberType.METHOD;
    }

    fromJson(jobj: any, typeConverter: (t: string) => ChartType): boolean {
        if (super.fromJson(jobj, typeConverter)) {
            if (jobj[ChartJsonKey.Parameters]) {
                jobj[ChartJsonKey.Parameters].forEach((p: any) => {
                    const newP = new Parameter();
                    newP.fromJson(p, typeConverter);
                    this.parameters.push(newP);
                })
            }
            return true;
        }
        return false;
    }
}
