import { ConnectorType, ShapeType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { IChartOpArgs } from './chart.op.arguments.interface';
export class CreateConnectorArgs implements IChartOpArgs {
    project: string;
    chart: string;
    // connector uid
    uid: string;
    // startnode uid
    start: string;
    end: string;
    type: ConnectorType;
    shape: ShapeType;

    constructor(uid: string, project: string, chart: string, start: string, end: string, type: ConnectorType, shape: ShapeType) {
        this.uid = uid;
        this.project = project;
        this.chart = chart;
        this.start = start;
        this.end = end;
        this.type = type;
        this.shape = shape;
    }
}
