import { Connector } from "libs/flowchart-ts/flowchart-core/src/lib/models/connector.model";
import { Group } from "libs/flowchart-ts/flowchart-core/src/lib/models/group.model";
import { ChartNode } from "libs/flowchart-ts/flowchart-core/src/lib/models/node.model";

export class CopyBuffer {
    nodes: ChartNode[];
    connectors: Connector[];
    groups: Group[];
    groupsNodesMap: Map<string, ChartNode[]>;
    constructor(nodes: ChartNode[], connectors: Connector[], groups?: Group[]) {
        this.groupsNodesMap = new Map();
        this.nodes = nodes;
        this.connectors = connectors;
        this.groups = groups;
        if (groups && groups.length !== 0) {
            groups.map(g => {
                this.groupsNodesMap.set(g.uid, g.nodes);
            });
        }
    }
}
