export class Point {
    constructor(x?: number, y?: number) {
        this.x = x;
        this.y = y;
    }
    x: number;
    y: number;

    static equals(p1: Point, p2: Point) {
        return p1 && p2 && p1.x === p2.x && p1.y === p2.y;
    }

    assignedFrom(p: Point) {
        this.x = p.x;
        this.y = p.y;
    }

    add(p: Point): Point {
        this.x += p.x;
        this.y += p.y;
        return this;
    }
}
