import { APP_HTTP_RESULT_CODE } from './../../../constants/http/http.constants';
import { ProjectService } from './../../../services/project/project.service';
import { UidUtils } from './../../../utils/uid.utils';
import { PathService } from './../../../common/path-picker/path.service';
import { DialogService } from './../../../common/dialog/dialog.service';
import { CreateProject } from './../../../models/projects/project.folder.model';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { PathPickerComponent } from 'src/app/common/path-picker/path-picker/path-picker.component';

export type ProjectPanel = 'basics' | 'types' | 'events';

@Component({
  selector: 'app-project-editor',
  templateUrl: './project-editor.component.html',
  styleUrls: ['./project-editor.component.css']
})
export class ProjectEditorComponent implements OnInit {

  panel: ProjectPanel = 'basics';
  generators = [{ name: 'Python', value: 'py' }, { name: 'Lua', value: 'lua' }, { name: 'C#', value: 'cs' }, { name: 'C++', value: 'cpp' }];

  project: CreateProject;

  basicsFormGroup: FormGroup;
  typesFormGroup: FormGroup;
  eventsFormGroup: FormGroup;
  foldersFormGroup: FormGroup;
  constructor(
    private _formBuilder: FormBuilder,
    private dialogService: DialogService,
    private pathService: PathService,
    private projectService: ProjectService
  ) { }

  ngOnInit() {
    this.project = new CreateProject();
    this.basicsFormGroup = this._formBuilder.group({
      nameCtrl: ['', Validators.required],
      pathCtrl: ['', Validators.required],
      generatorCtrl: ['', Validators.required],
      descriptionCtrl: new FormControl()
    });
  }

  onSelectPathClick($event) {
    $event.stopPropagation();
    $event.preventDefault();
    this.basicsFormGroup.patchValue({
      pathCtrl: 'asdfasdf'
    });

    this.pathService.showPathPicker({ filter: { type: 'path' } }).subscribe(result => {
      if (result) {
        this.project.path = result;
      }
    });
  }

  onPanelChange(panel: ProjectPanel) {
    this.panel = panel;
  }

  onFinishClick($event) {
    this.project.uid = UidUtils.generateUid();
    // this.projectService.create(this.project).then(result => {
    //   if (result.code === APP_HTTP_RESULT_CODE.OK) {
    //     this.projectService.open(this.project.path).then(openResult => {
    //       if (openResult.code === APP_HTTP_RESULT_CODE.OK) {
    //         const name = this.project.name;
    //         window.location.href = "./project/" + name;
    //       }
    //     });
    //   }
    // })
  }

}
