import { LayoutUtils } from './../../utils/layout.utils';
import { ExecuteResultCode } from './../../constants/command/command.constants';
import { Chart, ChartNode, Group } from "libs/flowchart-ts/flowchart-core/src/public-api";
import { Command, CommandExecuteResult } from "./command.model";
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';
import { Point } from 'libs/flowchart-ts/flowchart-common/src/lib/models/point.model';

export class CreateGroupCommand extends Command {
    chart: Chart;
    group: Group;
    index: number;

    constructor(chart: Chart, group: Group, index?: number) {
        super();
        this.chart = chart;
        this.group = group;
        this.index = index;
    }

    execute(): CommandExecuteResult {
        this.group.nodes.forEach(n => n.group = this.group);
        if (!this.chart.groups) {
            this.chart.groups = [];
        }
        if (this.index === undefined) {
            this.index = this.chart.groups.length;
            this.chart.groups.push(this.group);
        }
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }

    revert(): CommandExecuteResult {

        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}

export class UpdateGroupCommand extends Command {
    group: Group;
    updateKeyValues: Map<string, any>;
    prevKeyValues: Map<string, any>;

    constructor(group: Group, kvPairs: Map<string, any>) {
        super();
        this.group = group;
        this.updateKeyValues = kvPairs;
        this.prevKeyValues = new Map<string, any>();
    }

    execute(): CommandExecuteResult {
        this.updateKeyValues.forEach((v, k) => {
            this.prevKeyValues.set(k, this.group[k]);
            this.group[k] = v;
        })
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }

    revert(): CommandExecuteResult {
        this.prevKeyValues.forEach((v, k) => {
            this.group[k] = v;
        })
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}

export class RemoveGroupCommand extends Command {
    chart: Chart;
    group: Group;
    index: number;

    constructor(chart: Chart, group: Group, index?: number) {
        super();
        this.chart = chart;
        this.group = group;
        this.index = index;
    }

    execute(): CommandExecuteResult {
        const index = this.chart.groups.indexOf(this.group);
        if (this.index === undefined) {
            this.index = index;
        }
        this.chart.groups.splice(this.index, 1);
        this.group.nodes.forEach(n => n.group = null);
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }

    revert(): CommandExecuteResult {

        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}


export class AddNodeToGroupCommand extends Command {
    group: Group;
    node: ChartNode;
    index: number;

    constructor(group: Group, node: ChartNode) {
        super();
        this.group = group;
        this.node = node;
    }

    execute(): CommandExecuteResult {
        this.group.addNode(this.node, this.index);
        this.group.nodes = [...this.group.nodes];
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
    revert(): CommandExecuteResult {
        this.group.removeNode(this.node.uid);
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }

}

export class RemoveNodeFromGroupCommand extends Command {
    group: Group;
    node: ChartNode;
    private index;

    constructor(group: Group, node: ChartNode) {
        super();
        this.group = group;
        this.node = node;
    }

    execute(): CommandExecuteResult {
        const index = this.group.nodes.findIndex(n => n === this.node);
        if (index > -1) {
            this.index = index;
            this.group.removeNode(this.node.uid);
            return new CommandExecuteResult(ExecuteResultCode.OK, '');
        } else {
            return new CommandExecuteResult(ExecuteResultCode.ERROR, `Group '${this.group.name}' does not contain node '${this.node.text}'`);
        }
    }
    revert(): CommandExecuteResult {
        throw new Error('Method not implemented.');
    }
}

export class LayoutGroupCommand extends Command {
    chart: Chart;
    group: Group;
    private rectMap: Map<string, Rect>;

    constructor(chart: Chart, group: Group) {
        super();
        this.chart = chart;
        this.group = group;
        this.rectMap = new Map<string, Rect>();
    }
    execute(): CommandExecuteResult {
        const rect = this.group.getRect();
        const center = new Point(rect.cx, rect.cy);
        const map = LayoutUtils.layoutGroup(this.group, this.chart);
        this.group.nodes.forEach(n => {
            const prevRect = n.rect;
            this.rectMap.set(n.uid, prevRect)
            const point = map.get(n.uid);
            if (point) {
                n.rect = Rect.createWithLeftTopVertex(point.x, point.y, prevRect.w, prevRect.h);
            }
        })

        if (this.chart.connectors) {
            const conns = this.chart.connectors.filter(c => this.group.nodes.includes(c.start) || this.group.nodes.includes(c.end))
            conns.forEach(c => c.path = c.getLinePath());
        }
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
    revert(): CommandExecuteResult {
        this.group.nodes.forEach(n => {
            n.rect = this.rectMap.get(n.uid);
        })
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }

}
