
export enum RESULT_CODE {
    OK = 0,
    ERROR = -1,
    PARAMETER_ERROR = 1001
}

export class HttpResult<T> {
    code: RESULT_CODE;
    msg: string;
    data?: T;

    constructor(code: RESULT_CODE, msg: string, data?: T) {
        this.code = code;
        this.msg = msg;
        this.data = data;
    }
}
