import { COMMAND_NAME, ExecuteResultCode } from './../../constants/command/command.constants';
import { Chart } from "libs/flowchart-ts/flowchart-core/src/lib/models/chart.model";
import { ChartNode } from "libs/flowchart-ts/flowchart-core/src/lib/models/node.model";
import { Command, CommandExecuteResult } from "./command.model";

export class CreateNodeCommand extends Command {
    readonly name = COMMAND_NAME.CREATE_NODE;
    chart: Chart;
    node: ChartNode;
    index: number;

    constructor(chart: Chart, node: ChartNode, index?: number) {
        super();
        this.chart = chart;
        this.node = node;
        this.index = index;
    }

    execute(): CommandExecuteResult {
        try {
            this.chart.addNode(this.node, this.index);
            this.chart.nodes = this.chart.sortNodes();
        } catch (error) {
            return new CommandExecuteResult(ExecuteResultCode.ERROR, error.message);
        }
        return new CommandExecuteResult(0, '');
    }

    revert(): CommandExecuteResult {
        this.chart.nodes = this.chart.sortNodes();
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}

export class UpdateNodeCommand extends Command {
    readonly name = COMMAND_NAME.UPDATE_NODE;
    node: ChartNode;
    udpateKeyValues: Map<string, any>;
    prevKeyValues: Map<string, any>;
    constructor(node: ChartNode, updateValues: Map<string, any>) {
        super();
        this.node = node;
        this.udpateKeyValues = updateValues;
        node.chart.nodes = node.chart.sortNodes();
    }

    execute(): CommandExecuteResult {
        this.udpateKeyValues.forEach((v, k) => {
            this.node[k] = v;
        });
        return new CommandExecuteResult(0, '', this.node);
    }

    revert(): CommandExecuteResult {

        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}

export class RemoveNodeCommand extends Command {
    readonly name = COMMAND_NAME.REMOVE_NODE;
    chart: Chart;
    node: ChartNode;
    index: number;
    constructor(chart: Chart, node: ChartNode, index?: number) {
        super();
        this.chart = chart;
        this.node = node;
        this.index = index;
    }

    execute(): CommandExecuteResult {
        try {
            if (this.index === undefined) {
                this.index = this.chart.nodes.findIndex(n => n === this.node);
            }
            this.chart.removeNode(this.node.uid);
            this.chart.nodes = this.chart.sortNodes();
        } catch (error) {
            return new CommandExecuteResult(ExecuteResultCode.ERROR, error.message);
        }
        return new CommandExecuteResult(0, '');
    }

    revert(): CommandExecuteResult {
        this.chart.nodes = this.chart.sortNodes();
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}
