import { ComponentType } from '@angular/cdk/portal';
import { Injectable } from '@angular/core';
import { MatDialog, MatDialogConfig } from '@angular/material/dialog';
import { SimpleDialogComponent, SimpleDialogConfig } from './components/simple-dialog/simple-dialog.component';

const DEFAULT_DIALOG_CONFIG: MatDialogConfig = {
  disableClose: true
};

@Injectable({
  providedIn: 'root'
})
export class DialogService {

  constructor(public dialog: MatDialog) { }

  open<T>(componentType: ComponentType<T>, data?: any, config: MatDialogConfig = DEFAULT_DIALOG_CONFIG) {
    const dialogRef = this.dialog.open(componentType, { ...config, data });
    return dialogRef.afterClosed();
  }

  openSimple(config: SimpleDialogConfig) {
    const dialogRef = this.dialog.open(SimpleDialogComponent, {data: config});
    return dialogRef.afterClosed();
  }
}
