import { UidUtils } from './../../../../flowchart-common/src/public-api';
import { EventJsonKey } from './../constants/chart.constants';
import { EventParam } from './member.model';
import { ChartType } from './type.model';


/**
 * 事件类型，继承自{@linkcode Type}
 */
export class EventType extends ChartType {
    /**
     * event parameters
     */
    id: number;
    parameters: EventParam[];
    belongTypes: ChartType[];

    fromJson(json: any, typeConverter: (tname: string) => ChartType) {
        if (json[EventJsonKey.Parameters]) {
            this.parameters = json[EventJsonKey.Parameters].map((p: any) => {
                const para = new EventParam();
                para.fromJson(p, typeConverter);
                para.index = this.members.length;
                this.addMember(para);
                return para;
            });
        }
        if (typeConverter && json[EventJsonKey.BelongTypes]) {
            this.belongTypes = json[EventJsonKey.BelongTypes].map((bt: string) => {
                return typeConverter(bt);
            });
        }

        this.name = json[EventJsonKey.Name];
        this.description = json[EventJsonKey.Description]
        this.uid = json[EventJsonKey.Uid] || UidUtils.generateUid();
    }

    toJson() {
        const e: any = {};
        e[EventJsonKey.Uid] = this.uid;
        e[EventJsonKey.Name] = this.name;
        e[EventJsonKey.Description] = this.description;
        if (this.parameters) {
            e[EventJsonKey.Parameters] = this.parameters.map(p => p.toJson());
        }
        if (this.belongTypes) {
            e[EventJsonKey.BelongTypes] = this.belongTypes.map(t => t.name);
        }
        return e;
    }
}