import { Component, OnInit, SimpleChanges } from '@angular/core';
import { SlotBase } from '../../models/slot-base.model';

@Component({
  selector: 'app-bool-widget',
  templateUrl: './bool-widget.component.html',
  styleUrls: ['./bool-widget.component.css']
})
export class BoolWidgetComponent extends SlotBase implements OnInit {

  isTrue: boolean;

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges) {
    if(changes.text) {
      if(changes.text.currentValue === 'true') {
        this.isTrue = true;
      } else {
        this.isTrue = false;
      }
    }
  }

  onDblClick($event) {
    this.isTrue = !this.isTrue;
    this.change.emit(this.isTrue? 'true': 'false');
  }

}
