import { ASYNCFLOW_DEBUG_TAG } from './../../../debug/constants/debug.constants';
import { UidUtils } from './../../../utils/uid.utils';
import { Observable, BehaviorSubject, Subscription } from 'rxjs';
export class InterpageCommunicator {
    private bc: BroadcastChannel;
    // 通信标识, 同样标识符的通信器才可相互通信
    tag: string;
    // 信源, 因为采用的是广播通信,需要区分信源，此处使用的是uid
    source: string;

    private message: BehaviorSubject<any>;
    onmessage: (this: InterpageCommunicator, event: InterpageCommEvent) => any;
    subscription: Subscription;
    constructor(tag: string) {
        this.tag = tag || ASYNCFLOW_DEBUG_TAG;
        this.source = UidUtils.generateUid();
    }

    init() {
        this.bc = new BroadcastChannel(this.tag);
        this.message = new BehaviorSubject<string>('');
        this.bc.onmessage = e => {
            // 回调可用并且信源并非自身（也即不是自己发出的广播）才可进行发送
            if (this.onmessage && e.data.source !== this.source) {
                const event = new InterpageCommEvent();
                event.data = e.data.msg;
                event.source = e.data.source;
                this.onmessage(event);
            }
        };
    }

    prepare() {
        const ifrm = new HTMLIFrameElement();
        ifrm.src = 'http://localhost:4200/debugdemo';
        ifrm.width = '0';
        ifrm.height = '0';
        document.body.append(ifrm);
        return new Promise<void>((resolve, reject) => {
            ifrm.onload = e => {
                resolve();
            };
        });
    }

    sendMsg(msg: string) {
        const message = { source: this.source, msg };
        this.bc.postMessage(message);
    }
}

export class InterpageCommEvent {
    data: any;
    source: string;
}
