import { OUTPUT_FLAG, OUTPUT_TAG } from './../../../services/common/output.service';
import { Component, OnInit } from '@angular/core';
import { MessageService } from 'src/app/services/common/message.service';
import { MSG_FLAG } from '../../../constants/common/message.flags.constants';
import { HttpClient } from '@angular/common/http';
import { Store } from '@ngrx/store';
import * as fromStore from '../../../store';
import { NORMAL, ERROR } from 'src/app/constants/common/http.response.constants';

@Component({
  selector: 'app-output',
  templateUrl: './output.component.html',
  styleUrls: ['./output.component.css']
})
export class OutputComponent implements OnInit {
  constructor(
    private messageService: MessageService,
    private http: HttpClient,
    private store: Store<fromStore.FlowchartState>,
  ) { }

  // 存放输出信息, 每条信息占数组的一项
  normalMessageOutputArray: Array<string> = [];
  errorMessageOutputArray: Array<string> = [];

  msgList: { msg: string, level: number, date: Date }[] = [];
  ngOnInit() {
    this.scrollBottom();
    this.subscribeMessage();
  }

  subscribeMessage() {
    this.messageService.subscribe(this, MSG_FLAG.PROJECT_LOADED, msg => {
      this.normalMessageOutputArray.push(this.getFormDate() + ' ' + MSG_FLAG.PROJECT_LOADED);
      this.scrollBottom();
    });

    this.messageService.subscribe(this, MSG_FLAG.SHOW_FLOWCHART, msg => {
      const chartName = msg.args.chartName;
      this.normalMessageOutputArray.push(this.getFormDate() + ' ' + MSG_FLAG.SHOW_FLOWCHART + ' ' + chartName);
      this.scrollBottom();
    });

    this.messageService.subscribe(this, OUTPUT_FLAG, msg => {
      msg.args.date = Date.now();
      this.msgList.push(msg.args);
      this.scrollBottom();
    }, OUTPUT_TAG);

    this.store.select(fromStore.getCurrentChartsLoadState).subscribe(state => {
      if (state.loading === false && state.loaded === true) {
        this.normalMessageOutputArray.push(this.getFormDate() + ' ' + NORMAL.LOAD_FLOWCHARTS_LIST_SUCCESS);
      } else if (state.loading === false && state.loaded === false) {
        this.errorMessageOutputArray.push(this.getFormDate() + ' ' + ERROR.LOAD_FLOWCHARTS_LIST_FAIL);
      }
    });

    this.store.select(fromStore.getInterfaceLoadState).subscribe(state => {
      if (state.loading === false && state.loaded === true) {
        this.normalMessageOutputArray.push(this.getFormDate() + ' ' + NORMAL.LOAD_INTERFACES_SUCCESS);
      } else if (state.loading === false && state.loaded === false) {
        this.errorMessageOutputArray.push(this.getFormDate() + ' ' + ERROR.LOAD_INTERFACES_FAIL);
      }
    });
  }

  getFormDate(): string {
    const date = new Date();
    const formatDate = date.toISOString().substring(0, 10) + ' ' + date.toTimeString().substring(0, 9);
    return formatDate;
  }

  // 让输出区域的滚动条自动处于底部
  scrollBottom() {
    const output = document.getElementById('outputArea');
    setTimeout(() => {
      output.scrollTop = output.scrollHeight;
    }, 10);
  }
}
