import { createSelector } from '@ngrx/store';
import { getFlowChartState, FlowchartState } from '../../reducers';
import * as fromInterface from '../../reducers/interfaces/interfaces.reducer';

export const getInterfaceState = createSelector(
    getFlowChartState,
    (state: FlowchartState) => state.interface
);

export const getAllInterfaces = createSelector(getInterfaceState, fromInterface.getAllInterfaces);


export const getTheInterfaceLoaded = createSelector(
    getInterfaceState,
    fromInterface.getInterfaceLoaded
);

export const getTheInterfaceLoading = createSelector(
    getInterfaceState,
    fromInterface.getInterfaceLoading
);

export const getInterfaceLoadState = createSelector(
    getInterfaceState,
    fromInterface.getInterfaceLoadState
);
