import { Injectable, Component } from '@angular/core';

export interface MenuItem {
  name: string;
  callback: (e?) => void;
}
@Injectable({
  providedIn: 'root'
})
export class MenuService {

  constructor() { }

  // 构建菜单内容
  generateMenuItem(name: string, callback: (e?) => void, scope?) {
    const item: MenuItem = {
      name: '',
      callback: (e?) => { }
    };
    item.name = name;
    item.callback = () => {
      callback(scope);
    };
    return item;
  }

}
