import { ConnectorsService } from 'src/app/services/chart/connectors.service';
import { UpdateConnectorCommand } from './../../models/commands/connector.commands';
import { OperationService } from './operation.service';
import { CreateGroupCommand, RemoveGroupCommand, LayoutGroupCommand } from './../../models/commands/group.commands';
import { ExecuteResultCode } from './../../constants/command/command.constants';
import { ProjectService } from './../project/project.service';
import { ChartService } from './chart.service';
import { ChartData } from 'src/app/core/models/chart-data.model';
import { BehaviorSubject } from 'rxjs';
import { CanvasService } from './canvas.service';
import { CodeGenerationService } from './../generation/code-generation.service';
import { CurrentProjectService } from './../project/current-project.service';
import { ToastService } from './../../common/toast/toast.service';
import { OutputService } from './../common/output.service';
import { Store } from '@ngrx/store';
import { Injectable, OnDestroy } from '@angular/core';
import * as fromStore from 'src/app/store/index';
import { APP_HTTP_RESULT_CODE } from 'src/app/constants/http/http.constants';
import { CreateNodeArgs } from 'src/app/models/request-arguments/node.op.arguments.model';
import { UidUtils } from 'src/app/utils/uid.utils';
import { GroupService } from './group.service';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';
import { Connector, ConnectorType, ShapeType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { Group, LayoutType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/group.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { NodeViewType } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.enums';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';
import { UID_START } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.constants';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { GeneratorManager } from 'libs/flowchart-ts/flowchart-generator/src/lib/generators/generator-manager';
import { NodesService } from './nodes.service';
import { Point } from 'libs/flowchart-ts/flowchart-common/src/lib/models/point.model';


export enum MenuTarget {
  None = 0,
  NodeOnly = 1,
  ConnectorOnly = 2,
  GroupOnly = 3,
  Complex = 4
}

@Injectable({
  providedIn: 'root'
})
export class CommonMenuService implements OnDestroy {
  chart: Chart;
  _selectedItems: ChartData[];
  selectedItems: BehaviorSubject<ChartData[]> = new BehaviorSubject<ChartData[]>(null);
  _selectedNodes: ChartNode[];
  _selectedConnectors: Connector[];
  _selectedGroups: Group[];

  // project: Project;
  private _menuTarget: MenuTarget;
  // 是否有Group被选中
  private _hasGroups: boolean;
  private _hasNodes: boolean;

  constructor(
    private store: Store,
    private currentProjectService: CurrentProjectService,
    private codeGenerationService: CodeGenerationService,
    private canvasService: CanvasService,
    private outputService: OutputService,
    private toastService: ToastService,
    private groupService: GroupService,
    private projectService: ProjectService,
    private chartService: ChartService,
    private nodeService: NodesService,
    private connectorService: ConnectorsService,
    private operationService: OperationService) {
    this.init();
  }

  init() {
    this.chartService.currentChart.subscribe(chart => {
      this.chart = chart;
    });
  }

  delete() {
    const startNode = this._selectedNodes.find(n => n.uid === UID_START);
    if (startNode) {
      this.outputService.error('Start node can not be deleted.');
      return;
    }
    let connectors: Connector[] = this._selectedConnectors || [];
    if (this._selectedNodes) {
      const nodesId = this._selectedNodes.map(n => n.uid);
      connectors = this.chart.connectors
        .filter(conn => nodesId.includes(conn.start.uid) || nodesId.includes(conn.end.uid) || connectors.includes(conn));
    }
    const items: { groups: Group[], connectors: Connector[], nodes: ChartNode[] } = { groups: this._selectedGroups, connectors, nodes: this._selectedNodes };
    this.chartService.deleteItems(items).then(result => {
      if (result.code === ExecuteResultCode.OK) {
        this._selectedConnectors = [];
        this._selectedNodes = [];
        this._selectedGroups = [];
        this._selectedItems = [];
        this.selectedItems.next(this._selectedItems)
      } else {
        this.toastService.error(`Delete failed: ${result.msg}`);
      }
    })

    // if (this.selectedGroups.length === 0 && this.selectedNodes.length === 0 && connectors && connectors.length !== 0) {
    //   connectors.forEach(c => {
    //     this.layoutGroupCausedConnector(c.start.uid, c.end.uid);
    //   });
    // }
  }

  cut() {
    this.copy();
    this.delete();
  }

  copy() {
    if (this._selectedGroups && this._selectedGroups.length > 0) {
      this.canvasService.copyGroups(this._selectedGroups);
      return;
    }
    this.canvasService.copyNodes(this.chart, this._selectedNodes);

  }

  paste(project: Project) {
    // this.canvasService.pasteNodes(this.project, this.chart, this.menuLocation);
    this.canvasService.pasteNodes(project, this.chart);
  }

  addGroup() {
    const judgeNodeInGroup = this.groupService.judgeNodeInGroup(this.chart, this.selectedNodes);
    if (this.selectedNodes.length === 0 || judgeNodeInGroup) {
      return;
    }
    const group = new Group();
    group.uid = UidUtils.generateUid();
    group.nodes = this._selectedNodes;
    group.chart = this.chart;
    group.layout = LayoutType.AUTO
    group.chart = this.chart;
    const cmd = new CreateGroupCommand(this.chart, group);
    const layout = new LayoutGroupCommand(this.chart, group);
    this.operationService.executeChartCommands(this.chart.uid, [cmd, layout]);
  }

  deleteGroup() {
    const cmds = [];
    this._selectedGroups.forEach(g => {
      const cmd = new RemoveGroupCommand(this.chart, g);
      cmds.push(cmd);
    });
    this.operationService.executeChartCommands(this.chart.uid, cmds);
  }

  changeConnectorShape(shape: ShapeType) {
    if (this._selectedConnectors[0]) {
      this.connectorService.updateConnector(this.chart, this._selectedConnectors[0], new Map<string, any>([['shape', shape]]));
    }
  }

  changeConnectorType(type: ConnectorType) {
    if (this._selectedConnectors[0]) {
      this.connectorService.updateConnector(this.chart, this._selectedConnectors[0], new Map<string, any>([['type', type]]));
    }
  }

  addChild(viewType: NodeViewType, menuLocation?: Point) {
    const text = 'new node';
    const parentNode = this._selectedNodes[0];
    // 确定位置
    let cx: number;
    let cy: number;
    if (parentNode) {
      cx = parentNode.rect.cx;
      cy = parentNode.rect.cy + 80;
    } else {
      cx = Number(menuLocation.x);
      cy = Number(menuLocation.y);
    }
    // const rect = this.findIdleRect(Rect.createWithCenter(cx, cy, 126, 38));
    const rect = Rect.createWithCenter(cx, cy, 126, 38);
    // 参数组装
    const newNode = new ChartNode();
    newNode.uid = UidUtils.generateUid();
    newNode.text = text;
    newNode.rect = rect;
    const args = new CreateNodeArgs(this.chart.project.name, this.chart.uid, newNode);
    args.parent = parentNode;
    if (args.parent) {
      args.group = this.chart.groups.find(g => g.nodes.find(n => n.uid === args.parent.uid));
    }
    // 默认文本
    if (viewType) {
      newNode.viewType = viewType;
      switch (viewType) {
        case NodeViewType.COMMON:
          newNode.text = '';
          break;
        case NodeViewType.STOP_NODE:
          newNode.text = 'stopnode()';
          break;
        case NodeViewType.STOP_FLOW:
          newNode.text = 'stopflow()';
          break;
        case NodeViewType.WAIT:
          newNode.text = 'wait()';
          break;
        case NodeViewType.WAITALL:
          newNode.text = 'wait_all()';
          break;
        case NodeViewType.EXPRESS:
          newNode.text = '';
          break;
        case NodeViewType.FUNCTION:
          newNode.text = '';
          break;
        case NodeViewType.START:
          break;
      }
    }
    this.nodeService.createNodes(this.chart, [args])
  }

  debug() {

  }

  fastDebug() {

  }

  save() {
    this.projectService.save();
  }

  generate(chart?: Chart) {
    try {
      const project = this.projectService.getCurrentProject();
      const generator = GeneratorManager.getInstance().getGenerator(project.config.generator);
      if (!chart) {
        generator.build(project);
      } else {
        generator.handle(chart);
        generator.save(project);
      }
      this.outputService.log(`${chart ? 'Chart' : 'Project'} has been parsed successfully`);
    } catch (error) {
      this.outputService.error(`Error occured when parse charts: [${error.message}]`);
    }
  }

  get selectedNodes() {
    return this._selectedNodes;
  }

  get selectedConnectors() {
    return this._selectedConnectors;
  }

  get selectedGroups() {
    return this._selectedGroups;
  }

  get hasGroups() {
    return this._hasGroups;
  }

  get hasNodes() {
    return this._hasNodes;
  }

  get menuTarget() {
    return this._menuTarget;
  }

  //#region private methods
  private updateSelectedData() {
    this._selectedNodes = [];
    this._selectedConnectors = [];
    this._selectedGroups = [];
    this._hasGroups = false;
    this._hasNodes = false;
    if (this._selectedItems) {
      this._selectedItems.forEach((item, index) => {
        if (item instanceof ChartNode) {
          this._selectedNodes.push(item);
        } else if (item instanceof Connector) {
          this._selectedConnectors.push(item);
        } else if (item instanceof Group) {
          this._selectedGroups.push(item);
        }
      });
      if (this._selectedItems.length === 1) {
        if (this._selectedItems[0] instanceof ChartNode) {
          this._menuTarget = MenuTarget.NodeOnly;
        } else if (this._selectedItems[0] instanceof Connector) {
          this._menuTarget = MenuTarget.ConnectorOnly;
        } else if (this.selectedItems[0] instanceof Group) {
          this._menuTarget = MenuTarget.GroupOnly;
        }
      } else if (this._selectedItems.length > 1) {
        this._menuTarget = MenuTarget.Complex;
      } else {
        this._menuTarget = MenuTarget.None;
      }
    }
    if (this._selectedGroups.length > 0) {
      this._hasGroups = true;
    }
    if (this._selectedNodes.length > 0) {
      this._hasNodes = true;
    }

  }

  setSelectedItems(items: ChartData[]) {
    this._selectedItems = items;
    this.updateSelectedData();
    this.selectedItems.next(this._selectedItems);
  }

  ngOnDestroy() {
    this.selectedItems.unsubscribe();
  }
}
