import { WaitallNodeComponent } from './waitall-node/waitall-node.component';
import { INodeComponent } from './node-component.interface';
import { ShowingRunningStatus } from './../../../debug/models/debug-node-status.model';
import { FunctionNodeComponent } from './function-node/function-node.component';
import { ExpressNodeComponent } from './express-node/express-node.component';
import { StartNodeComponent } from './start-node/start-node.component';
import { WaitNodeComponent } from './wait-node/wait-node.component';
import { StopNodeComponent } from './stop-node/stop-node.component';
import { CommonNodeComponent } from './common-node/common-node.component';
import {
  Component, OnInit, Input, ViewChild, Output, EventEmitter, OnChanges, ElementRef, AfterViewInit,
  ChangeDetectorRef,
  SimpleChanges,
  ViewContainerRef,
  ComponentRef,
  ChangeDetectionStrategy
} from '@angular/core';
import { Point } from 'src/app/models/point.model';
import { CanvasService } from 'src/app/services/chart/canvas.service';
import { MessageService } from 'src/app/services/common/message.service';
import { NodesService } from 'src/app/services/chart/nodes.service';
import { Store } from '@ngrx/store';
import * as fromStore from '../../../store';
import { Group } from 'libs/flowchart-ts/flowchart-core/src/lib/models/group.model';
import { ConnectorType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { NodeViewType } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.enums';
import { UID_START } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.constants';

@Component({
  // tslint:disable-next-line:component-selector
  selector: '[app-node]',
  templateUrl: './node.component.html',
  styleUrls: ['./node.component.css'],
  // changeDetection: ChangeDetectionStrategy.OnPush

})
export class NodeComponent implements OnInit, OnChanges, AfterViewInit, INodeComponent {
  // tslint:disable-next-line:variable-name
  _results: any;

  constructor(
    private canvasService: CanvasService,
    private nodeService: NodesService,
    private messageService: MessageService,
    private cdref: ChangeDetectorRef,
    private store: Store<fromStore.FlowchartState>
  ) { }
  @ViewChild('textInput', { static: false }) textInput;
  @ViewChild('nodeText', { static: false }) nodeText: ElementRef;
  @ViewChild('nodeRect', { static: false }) nodeRect: ElementRef;
  @Input() delta: Point = new Point(0, 0);
  @Input() node: ChartNode;
  @Input() isEditing: boolean;
  @Output() isEditingChange: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Input() index: number;
  @Output() nodeClick: EventEmitter<any> = new EventEmitter<any>();
  temporary: Point;
  get x() {
    return this.node.rect.x - (this.delta && this.isSelected ? this.delta.x : 0);
  }

  get y() {
    return this.node.rect.y - (this.delta && this.isSelected ? this.delta.y : 0);
  }
  toolIconHeight = 24;
  propertyHeight = 20;
  // 节点是否在编辑
  isInputting = false;
  // 当前输入框的宽度
  inputWidth: number;
  // 未加入group的节点正hover于某个group上方
  hoveringGroup: Group = null;

  // 输入框临时变量
  inputValue: string;

  // 当前节点是否被选中
  @Input() isSelected = false;
  @Input() isCurrentFocus = false;
  // 抛出点击位置
  @Input() mousedownLocation: Point;
  @Output() mousedownLocationChange: EventEmitter<Point> = new EventEmitter();
  // 抛出右键事件
  @Output() nodeContextEvent: EventEmitter<Point> = new EventEmitter();
  // 抛出更新选中节点列表的事件
  @Output() updateSelectedNodesList: EventEmitter<void> = new EventEmitter();

  // 节点的三种操作：添加子节点、连线、编辑节点
  @Output() appendChild: EventEmitter<ChartNode> = new EventEmitter();
  @Output() iconToConnect: EventEmitter<[number, ConnectorType]> = new EventEmitter();
  @Output() updateNode = new EventEmitter<{ node: ChartNode, local: boolean, textChanged: boolean }>();

  @ViewChild('hiddenText', { static: false }) textEl: ElementRef;


  @Output() tempCoordinateChange: EventEmitter<Point> = new EventEmitter();

  @Output() nodeKeyDown: EventEmitter<any> = new EventEmitter<any>();

  @ViewChild('nodeComp', { static: false, read: ViewContainerRef }) public nodeComponent: ViewContainerRef;
  componentRef: ComponentRef<any>;
  viewInited: boolean;

  childComponent;

  @Input() runningStatus: ShowingRunningStatus = ShowingRunningStatus.IDLE;
  isTransitingColor = false;
  ngOnInit() {
    this.initNode();
  }

  initNode() {
    this.temporary = new Point(this.node.rect.x, this.node.rect.y);
  }

  ngAfterViewInit(): void {
    this.updateNodeView();
    this.cdref.detectChanges();
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.isEditing) {
      if (changes.isEditing.currentValue) {
        this.editNode(false);
      }
    }
    if (changes.node && changes.node.previousValue && changes.node.currentValue) {
      if (changes.node.currentValue.viewType !== changes.node.previousValue.viewType) {
        this.nodeService.updateRealNodeView(this.node);
      }
    }
  }

  updateNodeView() {
    let compType;
    if (!this.node.viewType) {
      // this.node.viewType = this.judgeViweType(this.node.text);
    }
    switch (this.node.viewType) {
      case NodeViewType.WAIT:
        compType = WaitNodeComponent;
        break;
      case NodeViewType.WAITALL:
        compType = WaitallNodeComponent;
        break;
      case NodeViewType.START:
        compType = StartNodeComponent;
        break;
      case NodeViewType.STOP_FLOW:
      case NodeViewType.STOP_NODE:
        compType = StopNodeComponent;
        break;
      case NodeViewType.EXPRESS:
        compType = ExpressNodeComponent;
        break;
      case NodeViewType.FUNCTION:
        compType = FunctionNodeComponent;
        break;
      default:
        if (this.node.text === 'Start' && this.node.uid === UID_START) {
          compType = StartNodeComponent;
        } else {
          compType = CommonNodeComponent;
        }
        break;
    }
    this.childComponent = compType;
  }

  judgeViweType(text: string) {
    const lowText = text.toLocaleLowerCase();
    if (lowText === 'wait' || lowText === 'waitall') {
      return NodeViewType.WAIT;
    } else if (lowText.startsWith('stopnode(')) {
      return NodeViewType.STOP_NODE;
    } else if (lowText.startsWith('stopflow(')) {
      return NodeViewType.STOP_FLOW;
    } else if (lowText === 'start') {
      return NodeViewType.START;
    }
    return NodeViewType.COMMON;
  }


  MoveNode(x: number, y: number) {
    console.log('node move', x, y);
    this.temporary.x += x
    this.temporary.y += y
  }

  openNodeMenu($event) {
    if (!$event.ctrlKey) {
      // this.isSelected = true;
      const menuLocation = new Point($event.clientX, $event.clientY);
      this.nodeContextEvent.emit(menuLocation);
    }
  }

  /**
   *
   * @param emit 是否向外传递事件
   * @returns
   */
  editNode(emit: boolean = true) {
    if (this.node.uid === '0' || this.node.viewType === NodeViewType.WAITALL) { return; }
    if (emit) {
      this.setEdit();
      return;
    }
  }

  completeInput($event) {
    if ($event.key === 'Enter') {
      this.textInput.nativeElement.blur();
    }
  }

  onNodeClick($event) {
    this.nodeClick.emit($event);
  }

  iconEdit($event) {
    $event.stopPropagation();
    this.editNode();
  }

  iconAdd($event) {
    $event.stopPropagation();
    this.appendChild.emit(this.node);
  }

  iconConnect($event) {
    $event.stopPropagation();
    this.iconToConnect.emit([this.node.id, null]);
  }

  onShowCodeClick($event) {

  }

  setEdit() {
    this.isEditingChange.emit(true);
  }

  onAnchorMouseDown($event, connectorType: ConnectorType) {
    $event.stopPropagation();
    this.iconToConnect.emit([this.node.id, connectorType]);
  }

  onDblClick($event) {
    if (!this.isEditing) {
      this.editNode();
    }
  }
}

