import { DomService } from './../../../services/common/dom.service';
import { IpcService } from './../../../common/ipc/services/ipc.service';
import { environment } from './../../../../environments/environment.prod';
import { ProjectEventsPanelComponent } from './../../project/project-events-panel/project-events-panel.component';
import { ProjectBasicConfigPanelComponent } from './../../project/project-basic-config-panel/project-basic-config-panel.component';
import { PathService } from './../../../common/path-picker/path.service';
import { DebugStartUpType } from './../../../debug/models/debug-startup.model';
import { DebugMainComponent } from './../../../debug/components/debug-main/debug-main.component';
import { Router } from '@angular/router';
import { APP_HTTP_RESULT_CODE } from './../../../constants/http/http.constants';
import { DialogService } from './../../../common/dialog/dialog.service';
import { Component, Input, OnInit, Output, ViewChild } from '@angular/core';
import { PathPickerComponent } from 'src/app/common/path-picker/path-picker/path-picker.component';
import { PathDialogConfig } from 'src/app/common/path-picker/path-info.model';
import { ProjectService } from 'src/app/services/project/project.service';
import { DebugStartUpInfo } from 'src/app/debug/models/debug-startup.model';
import { Store } from '@ngrx/store';
import * as fromStore from '../../../store';
import { MatMenuTrigger } from '@angular/material';
import { TranslateService } from '@ngx-translate/core';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';
import { GeneratorManager } from 'libs/flowchart-ts/flowchart-generator/src/lib/generators/generator-manager';
import { HttpClient } from '@angular/common/http';
// import themeCss from '../../../../theme.css';
import { deepCopy } from 'src/app/store/reducers/utilsFunction';
import { colors } from '../../../../theme';
import { MessageService } from 'src/app/services/common/message.service';
import { MSG_FLAG } from 'src/app/constants/common/message.flags.constants';
import { EventEmitter } from '@angular/core';
import html2canvas from 'html2canvas';
import { CurrentProjectService } from 'src/app/services/project/current-project.service';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { ToastService } from 'src/app/common/toast/toast.service';
import { OutputService } from 'src/app/services/common/output.service';

@Component({
  selector: 'app-header-menu',
  templateUrl: './header-menu.component.html',
  styleUrls: ['./header-menu.component.css']
})
export class HeaderMenuComponent implements OnInit {
  @ViewChild('languageTrigger', { static: false }) language: MatMenuTrigger;
  @Input() canvasViewBox;
  @Input() project: Project;
  @Output() openNewProject = new EventEmitter();
  charts: Chart[];
  index: number;
  themeList = [];
  languageList = [
    {
      value: null,
    },
    {
      value: null,
    }
  ];

  // project: Project;
  constructor(
    private dialogService: DialogService,
    private projectService: ProjectService,
    private pathService: PathService,
    private router: Router,
    private ipcService: IpcService,
    public translateService: TranslateService,
    private store: Store,
    private http: HttpClient,
    private messageService: MessageService,
    private currentProjectService: CurrentProjectService,
    private toastService: ToastService,
    private domService: DomService,
    private outputService: OutputService,
  ) { }

  ngOnInit() {
    this.generateThemeChoice();
    this.initLanguage();
    this.loadTheme();

  }

  loadTheme() {
    const theme = localStorage.getItem('theme');
    if (theme) {
      this.changeTheme(Number(theme));
    }
  }

  //#region event handlers
  onCreateNewProjectClick($event) {
    this.dialogService.open(ProjectBasicConfigPanelComponent, null).subscribe(result => {
      if (result) {
        this.openNewProject.emit();
        this.projectService.routeToNewProject(result);
      }
    });
  }

  onOpenProjectClick($event) {
    if (environment.IS_WEB) {
      const pathPickerConfig = new PathDialogConfig();
      pathPickerConfig.filter = { type: 'path' };
      this.pathService.showPathPicker(pathPickerConfig).subscribe(path => {
        if (path) {
          this.projectService.open(path).then(result => {
            if (result.code === APP_HTTP_RESULT_CODE.OK) {
              const name = path.substring(path.lastIndexOf('\\') + 1);
              this.projectService.routeToNewProject(name);
            }
          });
        }
      });
    } else {
      this.ipcService.selectDir().then(result => {
        if (result.code === 0) {
          const path: string = result.args[0];
          this.loadProject(result.args[0]);
        }
      })
    }
  }

  onEditProjectClick($event) {
    if (this.project) {
      this.dialogService.open(ProjectBasicConfigPanelComponent, this.project);
    }
  }

  onExportScreenshot($event) {
    html2canvas(document.getElementById('canvas-container'),
      {
        foreignObjectRendering: true,
        x: 0,
        y: 0,
        scale: 6,
      }).then(async (canvas) => {
        this.charts = await this.currentProjectService.asyncGetOpeningCharts();
        this.index = await this.currentProjectService.asyncGetOpeningIndex();
        const name = this.charts[this.index].project + '.' + this.charts[this.index].path;
        this.downloadPng(canvas, name);
      });
  }

  onExitClick($event) {
    this.ipcService.exitApp();
  }

  onSaveProject() {
    this.projectService.save();
  }

  downloadPng(canvas, name: string) {
    const a = document.createElement('a');
    a.href = canvas.toDataURL('image/png');
    a.download = `${name}.png`;
    a.click();
  }



  onDebugClick($event) {
    this.dialogService.open(DebugMainComponent, new DebugStartUpInfo(DebugStartUpType.DEBUG));
  }

  onEventManagementClick($event) {
    this.dialogService.open(ProjectEventsPanelComponent);
  }
  //#endregion


  initLanguage() {
    this.translateService.get('chooseLanguage.Chinese').subscribe(data => {
      this.languageList[0].value = data;
    });

    this.translateService.get('chooseLanguage.English').subscribe(data => {
      this.languageList[1].value = data;
    });
  }


  chooseLang(index: number) {
    const lang = index === 0 ? 'zh' : 'en';
    this.translateService.use(lang);
    this.initLanguage();
  }

  loadProject(path: string) {
    this.projectService.loadProject(path).then(result => {
      const project = this.projectService.getCurrentProject();
      GeneratorManager.getInstance().getGenerator(project.config.generator).build(project);
      console.log(project)
      this.router.navigate(['project', project.name])
    })
  }

  translateColorToKeywords(data, colorMap) {
    Object.keys(colorMap).forEach(key => {
      const value = colorMap[key];
      data = data.replace(new RegExp(key, 'ig'), value);
    });
    return data;
  }

  translateKeywordsToColor(data, colors) {
    // console.log(colors);
    Object.keys(colors).forEach(key => {
      // console.log(colors[key]);
      data = data.replace(new RegExp(key, 'ig'), colors[key]);
    });
    return data;
  }

  changeTheme(index: number) {
    const colorMap = this.generataColorMap();
    this.http.get('assets/theme.css', { responseType: 'text' }).subscribe(data => {
      data = this.translateColorToKeywords(data, colorMap);
      // console.log(data);

      data = this.translateKeywordsToColor(data, colors[index]);
      // console.log(data);
      const style = document.createElement('style');
      style.appendChild(document.createTextNode(data));
      document.head.appendChild(style);
      localStorage.setItem('theme', index.toString());
      this.messageService.publish({ flag: MSG_FLAG.CHANGE_THEME, content: '', args: { data: { theme: colors[index].theme } } });

    });
  }

  chooseTheme(index: number) {
    this.changeTheme(index);
  }

  generataColorMap(index = 0) {
    const colorMap = {};
    Object.keys(colors[0]).forEach(k => {
      colorMap[colors[0][k]] = k;
    });
    return colorMap;
  }

  generateThemeChoice() {
    colors.forEach(c => {
      this.themeList.push(c.theme);
    });
  }


  onHelpEmailClick($event) {
    if(this.domService.copy($event.target.innerText)) {
      this.toastService.info(`Email address has been copied`);
    } else {
      this.toastService.info(`Copy email failed`);
    }
  }
}
