import { ProjectService } from 'src/app/services/project/project.service';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { TreeNode } from 'src/app/models/treenode.model';
import { HttpResult } from 'src/app/common/http/http.result.model';
import { BUILD_IN_TYPES } from 'src/app/core/constants/build-in-types.constants';
import { APP_HTTP_RESULT_CODE } from 'src/app/constants/http/http.constants';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/type.model';
import { Subject } from 'rxjs';



@Injectable({
  providedIn: 'root'
})
export class InterfaceService {

  private _chartTypes: ChartType[];

  chartTypes: Subject<ChartType[]>;
  constructor(private http: HttpClient,
    private projectService: ProjectService) {
    this.chartTypes = new Subject<ChartType[]>();
    this.projectService.currentProject.subscribe(p => {
      this._chartTypes = p.types;
      this.chartTypes.next(this._chartTypes);
    })
  }

  async getTypes(projectId: string) {
    const url = `api/type/${projectId}`;
    let result;
    try {
      result = await this.http.get<HttpResult<ChartType[]>>(url).toPromise();
      if (result.code === APP_HTTP_RESULT_CODE.OK) {
        result = new HttpResult<ChartType[]>(APP_HTTP_RESULT_CODE.OK, '', result.data);
        result = result.data.map(t => {
          const tt = t.fromJson();
          tt.project = projectId;
          return tt;
        });
        return result;
      }
      result = new HttpResult<null>(APP_HTTP_RESULT_CODE.ERROR, result.msg, null);
    } catch (error) {
      result = new HttpResult<null>(APP_HTTP_RESULT_CODE.ERROR, error.message, null);
    }
  }

  parseFromServer(tp) {
    const t = new ChartType();
    t.uid = tp.uid;
    t.name = tp.name;
    t.description = tp.description;
    t.source = tp.source;
    if (tp.members) {
      t.memberMap = tp.members.map(m => {
        return m.fromJson(m);
      });
    }
    return t;
  }

  async createType(chartType: ChartType) {
    this.projectService.getCurrentProject().addType(chartType);
    this.chartTypes.next(this.projectService.getCurrentProject().types);
  }

  async deleteType(chartType: ChartType) {
    const url = `api/type/delete/${chartType.name}`;
    const postData = chartType.toJson();
    const result = await this.http.post<HttpResult<boolean>>(url, postData).toPromise();
    return result;
  }

  async updateType(chartType: ChartType) {
    const url = `api/type/update/${chartType.project}`;
    const postData = chartType.toJson();
    const result = await this.http.post<HttpResult<boolean>>(url, postData).toPromise();
    return result;
  }

  getBasicTypeList(needVoid = true) {
    return needVoid ? ChartType.BASIC_TYPES : ChartType.BASIC_TYPES_WITHOUT_VOID;
  }

  getAllTypeList(needVoid = true) {
    return [... (needVoid ? ChartType.BASIC_TYPES : ChartType.BASIC_TYPES_WITHOUT_VOID), ...this._chartTypes];
  }

  getAllCustomTypes() {
    return this._chartTypes;
  }
}
