import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import * as fromCurrentChartsList from '../../actions/charts/current-charts-list.actions';

export interface CurrentChartsListState {
    data: Chart[];
    loaded: boolean;
    loading: boolean;
}

export const initialState: CurrentChartsListState = {
    data: [],
    loaded: false,
    loading: false,
};

export function reducer(
    state = initialState,
    action: fromCurrentChartsList.CurrentChartsListAction
): CurrentChartsListState {
    switch (action.type) {
        case fromCurrentChartsList.LOAD_CURRENT_CHARTS_LIST: {
            return {
                ...state,
                loading: true,
            };
        }

        case fromCurrentChartsList.LOAD_CURRENT_CHARTS_LIST_SUCCESS: {
            const list = action.payload;
            return {
                ...state,
                loading: false,
                loaded: true,
                data: list
            };
        }

        case fromCurrentChartsList.LOAD_CURRENT_CHARTS_LIST_FAIL: {
            return {
                ...state,
                loading: false,
                loaded: false,
            };
        }

        case fromCurrentChartsList.CREATE_CHART_SUCCESS: {
            return {
                ...state,
                data: [...state.data, action.payload]
            };
        }

        case fromCurrentChartsList.UPDATE_CHART_SUCCESS: {
            return {
                ...state,
                data: updateChart(state.data, action.payload)
            };
        }

        case fromCurrentChartsList.UPDATE_CHART_PROPERTY_SUCCESS:
            return {
                ...state,
                data: updateChart(state.data, action.payload.chart)
            };

        case fromCurrentChartsList.REMOVE_CHART_SUCCESS:
            return {
                ...state,
                data: removeChart(state.data, action.payload)
            };
    }

    return state;
}

export const getCurrentChartsListLoadState = (state: CurrentChartsListState) => {
    return {
        loading: state.loading,
        loaded: state.loaded
    };
};
export const getCurrentChartsList = (state: CurrentChartsListState) => state.data;

export function updateChart(chartList: Chart[], chart: Chart) {
    return chartList.map((c, index) => {
        if (c.uid !== chart.uid) {
            return c;
        } else {
            return chart;
        }
    });
}

export function removeChart(chartList: Chart[], chart: Chart) {
    const newChartList = [];
    for (const c of chartList) {
        if (c.uid !== chart.uid) {
            newChartList.push(c);
        }
    }
    return newChartList;
}
