import { INodeComponent } from './../node-component.interface';
import { AfterViewInit, Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild, SimpleChanges } from '@angular/core';
import { Store } from '@ngrx/store';
import { KEY_CODE } from 'src/app/constants/common/key.code.constants';
import { ShowingRunningStatus } from 'src/app/debug/models/debug-node-status.model';
import { NodesService } from 'src/app/services/chart/nodes.service';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { NodeViewType } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.enums';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';
const NODE_SIZE = { w: 36, h: 36 };
@Component({
  selector: 'app-wait-node',
  templateUrl: './wait-node.component.html',
  styleUrls: ['./wait-node.component.css']
})
export class WaitNodeComponent implements OnInit, INodeComponent {

  constructor(
    private store: Store,
    private nodesService: NodesService
  ) { }
  @Input() index: number;
  @Input() isSelected: boolean;
  @Input() isEditing: boolean;
  @Input() node: ChartNode;
  @Output() isEditingChange: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() updateNode = new EventEmitter<{ node: ChartNode, local: boolean, textChanged: boolean }>();
  @Input() runningStatus: ShowingRunningStatus;


  @ViewChild('waitNodeRoot', { static: false, read: ElementRef }) root: ElementRef;
  @ViewChild('durationInput', { static: false }) durationInput: ElementRef;
  duration: string;
  ngOnInit() {
    this.init();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.node && (changes.node.currentValue == null || changes.node.previousValue == null ||
      (changes.node.currentValue.uid !== changes.node.previousValue.uid ||
        changes.node.currentValue.text !== changes.node.previousValue.text))) {
      this.duration = this.parseDuration();
    }
    if (changes.isEditing) {
      if (this.isEditing) {
        setTimeout(() => {
          this.durationInput.nativeElement.focus();
          this.durationInput.nativeElement.selectionStart = 0;
          this.durationInput.nativeElement.selectionEnd = this.duration.length;
        }, 10);
      }
    }
  }

  init() {
  }

  parseDuration() {
    if (this.node.text.startsWith('wait(')) {
      return this.node.text.substring(5, this.node.text.length - 1);
    }
  }

  update() {
    const newText = this.node.viewType === NodeViewType.WAIT ? `wait(${this.duration})` : 'wait_all()';
    const nodeview = document.getElementById(this.node.uid);
    const curHeight = nodeview.clientHeight;
    const newRect = Rect.createWithLeftTopVertex(this.node.rect.x, this.node.rect.y,
      this.nodesService.calculateNodeRenderWidth(newText.length, NodeViewType.WAIT, newText), curHeight);
    this.nodesService.updateNode(this.node, new Map<string, any> ([['text', newText], ['rect', newRect]]));
    this.updateNode.emit({node: this.node, local: true, textChanged: true});
  }

  check() {
    let err;
    if (this.duration == undefined || this.duration == null) {
      err = 'wait time not set';
    } else if (this.duration.startsWith('$')) {
      const va = this.node.chart.variables.find(v => v.name === this.duration.substr(1));
      if (!va) {
        err = `${this.duration} undefined`;
      } else if (va.type.name != 'Number') {
        err = `${this.duration} is not number`;
      }
    }
    return err;
  }

  save() {
    const err = this.check();
    if (err) {
      return;
    }
    this.update();
    this.isEditing = false;
    this.isEditingChange.emit(false);
  }


  onDurationInputKeyDown($event) {
    $event.stopPropagation();
    switch ($event.keyCode) {
      case KEY_CODE.ENTER:
        this.save();
        break;
    }
  }

  onDurationInputBlur($event) {
    $event.stopPropagation();
    this.save();
  }
}
