import { GeneratorManager } from './generators/generator-manager';
import { ICodeGenerator } from './generators/base-generator';
import { Chart, Connector, ChartNode, ChartType, Project, MemberType, MemberFrom, TypeSource } from "../../../flowchart-core/src/public-api";

export enum CompletionItemType {
    VAR = 1,
    EVENT = 2,
    FUNCTION = 3,
    PROPERTY = 4,
    SUBCHART
}

export class CompletionItem {
    type: CompletionItemType;
    text: string;
    weight: number;
    priority: number;
    replaceStr: string;

    constructor(type: CompletionItemType, text: string) {
        this.type = type;
        this.text = text;
    }
}

export class SplitResult {
    leftStr: string;
    rightStr: string;
    ownerStr: string;
    ownerType: string;
    preStr: string;
    replaceStr: string;

    constructor(text: string, cursor: number) {
        this.leftStr = text.substr(0, cursor);
        this.rightStr = text.substr(cursor);
        this.splitInputString(this.leftStr);
    }

    private splitInputString(leftCmd: string) {
        let leftLength = leftCmd.length;
        this.preStr = null;

        // Search for the first symbol from cusor position
        let i = 0;
        for (i = leftLength - 1; i >= 0; i--) {
            const char = leftCmd[i];
            if (this.isSymbol(char)) {
                if (char == '$') i--;
                break;
            }
        }

        // No symbol found
        if (i < 0) {
            this.preStr = '';
            this.replaceStr = leftCmd;
            this.ownerStr = '';
            return;
        }

        // Symbol found
        this.preStr = leftCmd.substr(0, i + 1);
        this.replaceStr = leftCmd.substr(i + 1);
        // if seperator is '.', then parse string segment before '.'
        if (leftCmd[i] === '.') {
            this.ownerStr = this.getOwnerString(leftCmd.substr(0, i));
        } else {
            this.ownerStr = '';
        }
    }

    private getOwnerString(leftCmd: string) {
        let leftLenght = leftCmd.length;
        let rightParenthesisCount = 0;
        let inParenthesis = false;
        let charList = [];
        for (let i = leftLenght - 1; i >= 0; i--) {
            const c = leftCmd[i];
            if (c === ')') {
                rightParenthesisCount++;
                if (!inParenthesis) {
                    charList.push(c);
                    inParenthesis = true;
                }
            } else if (c === '(') {
                if (rightParenthesisCount === 0) break;
                rightParenthesisCount--;
                if (rightParenthesisCount === 0) {
                    inParenthesis = false;
                }
            } else if (',=><+=*/'.includes(c) && !inParenthesis) {
                break;
            }

            if (!inParenthesis) {
                charList.push(c);
            }
        }
        charList.reverse();
        return charList.join();
    }

    private isSymbol(c: string) {
        // is letter
        if (c.match(/[a-zA-Z0-9_]/g)) {
            return false;
        }
    }
}

export class AutoComplete {
    private lastOwnerType: ChartType;
    private lastOwnerString: string;
    private project: Project;
    private chart: Chart;
    private generator: ICodeGenerator;
    private chartType: ChartType;
    private lastCompletionItems: CompletionItem[];
    constructor(chart: Chart) {
        this.project = chart.project;
        this.chart = chart;
        this.generator = GeneratorManager.getInstance().getGenerator(this.project.config.generator);
        this.lastCompletionItems = [];
        this.chartType = this.project.config.getChartType(this.chart.path);
    }

    prepareCompletionItems(text: string, cursor: number) {
        let items: CompletionItem[] = null;
        const split = new SplitResult(text, cursor);
        let ownerStr: string = split.ownerStr;
        let ownerType: ChartType = null;

        // Optimization: if there is no change for ownerStr or no change for ownerType, we can use the last result;
        if (ownerStr === this.lastOwnerString) {
            items = this.lastCompletionItems;
        } else {
            ownerType = this.generator.getTextType(ownerStr);
        }

        // Other situations
        items = this.prepareCompletionItemsForType(ownerType);
        if (items) {
            items.forEach(item => item.replaceStr = split.replaceStr);
        }
        this.sort(items, split.replaceStr);
        return items;
    }

    prepareCompletionItemsForType(tp: ChartType) {
        let items: CompletionItem[] = [];
        if (!tp) {
            this.addVariableItems(items);
            this.addEventItems(items);
            this.addTypeMembers(items, this.chartType);
            this.addTypeMembers(items, ChartType.GlobalType);
        } else if (tp.source === TypeSource.EVENT) {
            this.addTypeMembers(items, tp)
        } else {
            this.addEventItems(items);
            this.addTypeMembers(items, tp);
        }
        this.lastCompletionItems = items;
        return items;
    }

    addVariableItems(items: CompletionItem[]) {
        const variables = this.chart.parameters.concat(this.chart.variables);
        variables.forEach(v => items.push(new CompletionItem(CompletionItemType.VAR, v.name)))
    }

    addEventItems(items: CompletionItem[]) {
        this.project.events.forEach(ev => items.push(new CompletionItem(CompletionItemType.EVENT, ev.name)));
        this.project.events.forEach(ev => items.push(new CompletionItem(CompletionItemType.EVENT, `On${ev.name}`)));
    }

    addTypeMembers(items: CompletionItem[], type: ChartType) {
        for (let member of type.members) {
            switch (member.memberType) {
                case MemberType.METHOD:
                    if (member.from === MemberFrom.SUBCHART) {
                        items.push(new CompletionItem(CompletionItemType.SUBCHART, member.name));
                    } else {
                        items.push(new CompletionItem(CompletionItemType.FUNCTION, member.name));
                    }
                    break;
                case MemberType.PROPERTY:
                    items.push(new CompletionItem(CompletionItemType.PROPERTY, member.name));
            }
        }
    }


    calcItemWeight(text: string, head: string) {
        let matchCount = 0;
        let nameLength = text.length;
        let headLength = head.length;
        let length = Math.min(nameLength, headLength);
        for (let i = 0; i < length; i++) {
            if (text[i] === head[i]) {
                matchCount++;
            } else {
                break;
            }
        }

        // for cases which are fully matched, smaller index of head has larger weight
        let headIndex = text.indexOf(head);
        if (headIndex === -1) {
            headIndex = 100;
        }
        return matchCount - headIndex;
    }

    sort(items: CompletionItem[], head: string) {
        const h = head.toLowerCase();
        if (items) {
            items.forEach(item => item.weight = this.calcItemWeight(item.text.toLowerCase(), head));
        }
        items.sort((a, b) => a.weight > b.weight ? -1 : 1);
    }
}