import { PathType } from './path-info.model';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'fileIcon'
})
export class FileIconPipe implements PipeTransform {

  transform(pathType: PathType, ...args: any[]): any {
    switch (pathType) {
      case PathType.DIR:
        return 'assets/images/file/folder.svg';
      case PathType.DRIVE:
        return 'assets/images/file/drive.svg';
      case PathType.FILE:
        return 'assets/images/file/file.svg';
    }
  }

}
