import { ChartService } from 'src/app/services/chart/chart.service';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Subject } from 'rxjs';
import { UtilsService } from 'src/app/services/common/utils.service';
import { TreeNode } from '../../models/treenode.model';
import { Store } from '@ngrx/store';
import * as fromStore from '../../store';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/public-api';

@Injectable({
  providedIn: 'root'
})

export class ProjectPanelServiceService {

  constructor(private http: HttpClient, 
    private utilsService: UtilsService, 
    private store: Store<fromStore.FlowchartState>,
    private chartService: ChartService
  ) { }
  messageSource = new Subject<any>();
  message$ = this.messageSource.asObservable();

  initialData;
  treeNode: TreeNode[] = [];
  transformTreenodeJson(data1: Chart[], defaultTypeMap: Map<string, string>) {
    const data = data1.slice().sort((a, b) => {
      if (a.path > b.path) {
        return 1;
      } else {
        return -1;
      }
    });
    const resultJson = [];
    // tslint:disable-next-line:forin
    for (const item in data) {
      const array = data[item].path.split('.');
      // 从最后一项开始填充tempJson：tempJson是一个由name和children组成的json，只有一个根节点
      // 从叶子节点开始遍历，到上一层，拷贝叶子节点作为这一层children的value
      const tempJson = [];
      for (let i = array.length - 1; i >= 0; i--) {
        if (tempJson.length !== 0) {
          const copy = this.utilsService.deepCopy(tempJson);
          copy[0].path = array.join('.');
          copy[0].uid = data[item].uid;
          tempJson[0].name = array[i];
          tempJson[0].children = copy;
        }
        if (tempJson.length === 0) {
          const item1 = {
            name: array[i],
            defaultType: defaultTypeMap[array[i]]
          };
          tempJson.push(item1);
        }
      }

      if (resultJson.length === 0 || resultJson[resultJson.length - 1].name !== tempJson[0].name) {
        resultJson.push(tempJson[0]);
      } else if (resultJson[resultJson.length - 1].name === tempJson[0].name) {
        resultJson[resultJson.length - 1].children.push(this.utilsService.deepCopy(tempJson[0].children)[0]);
      }
      for (const item of resultJson) {
        item.defaultType = defaultTypeMap[item.name];
      }
    }
    return resultJson;
  }

  transformTreeNodeData(chartList: Chart[], defaultTypeMap: Map<string, ChartType>) {
    const treeNodes: TreeNode[] = [];
    const sortedList = chartList.slice().sort((a, b) => {
      if (a.path > b.path) {
        return 1;
      } else {
        return -1;
      }
    });
    const treeNodeMap = new Map<string, TreeNode>();
    for (const item of sortedList) {
      const paths = item.path.split('.');
      for (let i = 0; i < paths.length; i++) {
        const parentPath = i > 0 ? paths.slice(0, i).join('.') : null;
        const path = paths.slice(0, i + 1).join('.');
        if (!treeNodeMap.has(path)) {
          const newNode = new TreeNode(paths[i], item, []);
          treeNodeMap.set(path, newNode);
          // find parent node
          if (parentPath !== null && treeNodeMap.has(parentPath)) {
            const pNode = treeNodeMap.get(parentPath);
            if (!pNode.children.includes(newNode)) {
              pNode.path = parentPath;
              if (newNode.name === '') {
                pNode.isFolder = true;
              } else {
                pNode.data = null;
                pNode.children.push(newNode);
              }
            }
          } else {
            // 没有父路径的，则认为是一级菜单
            treeNodes.push(newNode);
            newNode.defaultType = defaultTypeMap.get(path).name;
          }
        }
      }
    }
    this.getLevel(treeNodes);
    return treeNodes;
  }

  getLevel(treeNodes: any, level = 0, depth = 0) {
    let maxDepth = 0;
    treeNodes.forEach(t => {
      t.level = level;
      if (t.children && t.children.length > 0) {
        maxDepth++;
        this.getLevel(t.children, level + 1, depth + 1);
      }
    });
    return (treeNodes);
  }

  async getPath(fatherFolderName: string) {
    const getChartList: Chart[] = this.chartService.getCurrentChartList();
    let path = '';
    for (let i = 0; i < getChartList.length; i++) {
      const index = getChartList[i].path.indexOf(fatherFolderName);
      if (index !== -1) {
        path = getChartList[i].path.slice(0, index + fatherFolderName.length);
        break;
      }
    }
    return path;
  }

  async getChartList() {
    const promise = new Promise(resolve => {
      this.store.select(fromStore.getChartListSelector).subscribe(data => {
        resolve(data);
      });
    });
    return await promise;
  }
}




