import { DebugType, RunningStatus, ShowingRunningStatus } from "../constants/node.enums";
import { ChartNode } from "./node.model";
import { Parameter } from "./parameter.model";


export class NodeRunningCounter {
    success: number;
    failure: number;
    get total() {
        return this.success + this.failure;
    }

    constructor() {
        this.success = 0;
        this.failure = 0;
    }
}


export class NodeRunningStatus {
    node: ChartNode;
    oldStatus: RunningStatus;
    status: RunningStatus;
    showingStatus: ShowingRunningStatus;
    success: boolean;
    counter: NodeRunningCounter;

    constructor() {
        this.status = RunningStatus.IDLE;
        this.showingStatus = ShowingRunningStatus.IDLE;
    }
}

export class DebugRunningData {
    type: DebugType;
    id: number;
    nodeUid: string;
    oldStatus?: RunningStatus;
    newStatus?: RunningStatus;
    result?: boolean;
    eventId?: string;
    argCount?: number;
    eventParams?: Parameter[];
    variableName?: string;
    oldValue?: string;
    newValue?: string;

    constructor(data?: any) {
        if (data) {
            if (data.type !== undefined) {
                this.type = data.type;
            }
            if (data.id !== undefined) {
                this.id = data.id;
            }
            if (data.node_uid !== undefined) {
                this.nodeUid = data.node_uid;
            }
            if (data.old_status !== undefined) {
                this.oldStatus = data.old_status;
            }
            if (data.new_status !== undefined) {
                this.newStatus = data.new_status;
            }
            if (data.result !== undefined) {
                this.result = data.result;
            }
            if (data.event_id !== undefined) {
                this.eventId = data.event_id;
            }
            if (data.arg_count !== undefined) {
                this.argCount = data.arg_count;
            }
            if (data.event_params !== undefined) {
                this.eventParams = data.event_params;
            }
            if (data.variable_name !== undefined) {
                this.variableName = data.variable_name;
            }
            if (data.old_value !== undefined) {
                this.oldValue = data.old_value;
            }
            if (data.new_value !== undefined) {
                this.newValue = data.new_value;
            }
        }
    }
}