export const getCurrentChart = (state: CurrentChartState) => state.currentChart;
export const getCurrentChartVariables = (state: CurrentChartState) => state.variables;
export const getCurrentChartParameters = (state: CurrentChartState) => state.parameters;
export const getCurrentChartIndex = (state: CurrentChartState) => state.chartIndex;
export const getOpeningCharts = (state: CurrentChartState) => state.openingFlowCharts;
export const getStoredCharts = (state: CurrentChartState) => state.storedFlowCharts;
export const getCurrentNodes = (state: CurrentChartState) => state.currentChart.nodes;
export const getCurrentChartLoaded = (state: CurrentChartState) => state.loaded;
export const getCreatedNewNodes = (state: CurrentChartState) => state.newNodes;
export const getCreatedNewParameter = (state: CurrentChartState) => state.newParameter;
export const getCreatedNewVariable = (state: CurrentChartState) => state.newVariable;
export const getSelectedItems = (state: CurrentChartState) => state.selectedItems;
