import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { ExpressPiece } from 'src/app/models/express-piece.model';
import { INodeComponent } from '../node-component.interface';
import { NodesService } from 'src/app/services/chart/nodes.service';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';

@Component({
  selector: 'app-express-node',
  templateUrl: './express-node.component.html',
  styleUrls: ['./express-node.component.css']
})
export class ExpressNodeComponent implements OnInit, INodeComponent {
  @Input() index: number;
  @Input() isSelected: boolean;
  @Input() isEditing: boolean;
  @Input() node: ChartNode;
  @Output() isEditingChange: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() updateNode: EventEmitter<{node: ChartNode, local: boolean, textChanged: boolean}> = new EventEmitter<{node: ChartNode, local: boolean, textChanged: boolean}>();

  operators = [
    { n: '+' },
    { n: '-' },
    { n: '×', v: '*' },
    { n: '÷', v: '/' },
    { n: '==' },
    { n: '>' },
    { n: '>=' },
    { n: '<' },
    { n: '<=' }];
  expressPieces: ExpressPiece[];

  constructor(
    private nodesService: NodesService
  ) { }

  ngOnInit() {
  }

  ngAfterViewInit(): void {
    this.nodesService.updateRealNodeView(this.node);
  }
  onAddPieceClick($event) {
  }

  onAddPieceTypeChange($event) {
    if (this.expressPieces === undefined) {
      this.expressPieces = [];
    }
    const newPiece = new ExpressPiece();
    newPiece.text = '';
    newPiece.type = $event.target.value;
    this.expressPieces.push(newPiece);
  }

}
