import { ChartType, GenericType } from './../../../../flowchart-core/src/lib/models/type.model';
import { AssignNode, BinaryOpNode, ListNode, VisitAstNode } from './../../../../flowchart-parser/src/lib/nodes/ast-node.model';
import { Context } from './generator-info.model';
import { IParser } from './../../../../flowchart-parser/src/lib/parser';
import { AstNodeInfo, BaseGenerator } from "./base-generator";

export class CSharpGenerator extends BaseGenerator {
    readonly tag = 'cs';

    unitVarTypeCount: Map<string, number>;

    constructor(parser: IParser) {
        super(parser);
        this.parser = parser;
        this.ctx = new Context();
        this.unitVarTypeCount = new Map<string, number>();
        this.unitVarTypeCount.set('Bool', 0);
        this.unitVarTypeCount.set('Int', 0);
        this.unitVarTypeCount.set('Double', 0);
        this.unitVarTypeCount.set('', 0);
    }

    translateOp(node: BinaryOpNode) {

    }

    toCsharpType(tp: ChartType): string {
        if (!tp) {
            return 'object';
        }
        if (tp === ChartType.NumberType) {
            return 'double';
        }

        if (tp === ChartType.StringType) {
            return 'string';
        }

        if (tp === ChartType.BoolType) {
            return 'bool';
        }
        if (tp === ChartType.AnyType) {
            return 'object';
        }

        if (tp.name.startsWith('List')) {
            if (tp.name === 'List') {
                return 'List<object>';
            }
            return `List<${this.toCsharpType(this.project.getType(tp.name.substr(4, tp.name.length - 5)))}>`;
        }
        const category = this.project.getType(tp.name);
        if (category) {
            return category.name;
        }
        return "I" + tp.name;
    }

    generateCode(ni: AstNodeInfo) {
        this.codes = [];
        this.codes.push(`node.${ni.isAction ? "Command" : "Condition"} = new Predicate<object>(delegate(object _self) `);
        this.codes.push("{");
        this.codes.push(`    ${this.toCsharpType(this.currentChartType)} self = (${this.toCsharpType(this.currentChartType)})_self;`);

        //this.codes.AddRange(ni.node.PrefixCodes);

        if (ni.isAction || this.parseInfo.isSubchartCall) {
            this.codes.push("    " + ni.code + ";");
            this.codes.push("    return true;");
        }
        else {
            // 根据不同的类型，来决定是返回true or false 避免装箱操作
            if (ni.type == ChartType.NumberType) {
                this.codes.push(`    return ${ni.code} != 0.0;`);
                this.codes.push(`    return ((${this.toCsharpType(ni.type)})(__ret__)) != 0;`);
            }
            else if (ni.type == ChartType.BoolType) {
                this.codes.push(`    return ${ni.code};`);
                this.codes.push(`    return ((${this.toCsharpType(ni.type)})(__ret__));`);
            }
            else if (ni.type == ChartType.StringType) {
                this.codes.push(`    string __ret__ = ${ni.code} as string;`);
                this.codes.push(`    return string.IsNullOrEmpty(__ret__);`);
            }
            else {
                this.codes.push(`    object __ret__ = ${ni.code}`);
                this.codes.push(`    return __ret__ != null;`);
            }
        }
        this.codes.push("});");
        this.codes.push("");
    }

    //#region VISITOR
    visit_AssignNode(node: AssignNode): AstNodeInfo {
        const ni = node.left.Accept<AstNodeInfo>(this);
        const varName = node.left.text;
        const v = this.ctx.getVariable(varName);
        const expNi = node.right.Accept<AstNodeInfo>(this);

        if (!ni || !ni.type || !expNi) {
            return null;
        }
        if (ni.type === ChartType.UndefinedType) {
            ni.type = expNi.type;
            v.type = ni.type;
        } else {
            const typeCheck = this.checkType(ni.type, expNi.type);
            if (!typeCheck) {
                this.error(`left type of assign operator is different from right type`);
                return null;
            }
        }
        ni.code = `fc_set(${v.index}, ${expNi.code})`;
        console.log(`Assigned variable type is ${ni.type.name}`);
        return ni;

    }

    visit_ListNode(node: ListNode) {
        const ni = super.visit(node);
        ni.code = `new List<${this.toCsharpType((ni.type as GenericType).typeParams[0])}>(${ni.code})`;
        return ni;
    }

    visit(node: VisitAstNode) {
        if (node instanceof AssignNode) {
            return this.visit_AssignNode(node);
        } else if (node instanceof ListNode) {
            return this.visit_ListNode(node);
        } else {
            return super.visit(node);
        }
    }
    //#endregion
}