import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/public-api';
import { Component, OnInit, Input, SimpleChanges, OnChanges } from '@angular/core';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';

import { ChartData } from 'src/app/core/models/chart-data.model';

@Component({
  selector: 'app-right-slide-panel',
  templateUrl: './right-slide-panel.component.html',
  styleUrls: ['./right-slide-panel.component.css']
})
export class RightSlidePanelComponent implements OnInit, OnChanges {

  @Input() chart: Chart;
  @Input() data: ChartData;
  get isChart(){
    return !this.data || this.data instanceof Chart;
  }

  get isNode() {
    return this.data && this.data instanceof ChartNode;
  }
  constructor() { }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges) {
  }

}
