import { CreateNodeArgs } from './../../../models/request-arguments/node.op.arguments.model';
import { Action } from '@ngrx/store';
import { Connector } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';

// load nodes
export const LOAD_NODES = '[Flowchart] Load Nodes';
export const LOAD_NODES_FAIL = '[Flowchart] Load Nodes Fail';
export const LOAD_NODES_SUCCESS = '[Flowchart] Load Nodes Success';

export class LoadNodes implements Action {
    readonly type = LOAD_NODES;
    constructor(public nodes: ChartNode[]) {
    }
}

export class LoadNodesFail implements Action {
    readonly type = LOAD_NODES_FAIL;
    constructor(public payload: any) { }
}

export class LoadNodesSuccess implements Action {
    readonly type = LOAD_NODES_SUCCESS;
    constructor(public payload: ChartNode[]) {
    }
}

// create nodes
export const CREATE_NODES = '[Flowchart] Create Nodes';
export const CREATE_NODES_FAIL = '[Flowchart] Create Nodes Fail';
export const CREATE_NODES_SUCCESS = '[Flowchart] Create Nodes Success';

export class CreateNodes implements Action {
    readonly type = CREATE_NODES;
    constructor(public payload: CreateNodeArgs[]) {
    }
}

export class CreateNodesFail implements Action {
    readonly type = CREATE_NODES_FAIL;
    constructor() {
    }
}

export class CreateNodesSuccess implements Action {
    readonly type = CREATE_NODES_SUCCESS;
    constructor(public payload: {nodes: ChartNode[], conns: Connector[], args: CreateNodeArgs[], isUndoRedo: boolean}) {
    }
}

// update nodes
export const UPDATE_NODE = '[Flowchart] Update ChartNode';
export const UPDATE_NODE_FAIL = '[Flowchart] Update ChartNode Fail';
export const UPDATE_NODE_SUCCESS = '[Flowchart] Update ChartNode Success';

export class UpdateNode implements Action {
    readonly type = UPDATE_NODE;
    constructor(public payload: ChartNode | ChartNode[]) {
    }
}

export class UpdateNodeFail implements Action {
    readonly type = UPDATE_NODE_FAIL;
    constructor(public payload: ChartNode | ChartNode[]) { }
}

export class UpdateNodeSuccess implements Action {
    readonly type = UPDATE_NODE_SUCCESS;
    constructor(public payload: ChartNode | ChartNode[]) {
    }
}


// 一些本地数据更新时，比如说选中状态，移动过程中的节点坐标等，不执行
export const UPDATE_NODE_LOCAL = '[Flowchart] Update ChartNode Local';
export const UPDATE_NODE_LOCAL_FAIL = '[Flowchart] Update ChartNode Local Fail';
export const UPDATE_NODE_LOCAL_SUCCESS = '[Flowchart] Update ChartNode Local Success';

export class UpdateNodeLocal implements Action {
    readonly type = UPDATE_NODE_LOCAL;
    constructor(public payload: ChartNode) {
    }
}

export class UpdateNodeLocalFail implements Action {
    readonly type = UPDATE_NODE_LOCAL_FAIL;
    constructor(public payload: ChartNode) { }
}

export class UpdateNodeLocalSuccess implements Action {
    readonly type = UPDATE_NODE_LOCAL_SUCCESS;
    constructor(public payload: ChartNode) {
    }
}

// move nodes
export const MOVE_NODE = '[Flowchart] Move ChartNode';
export const MOVE_NODE_FAIL = '[Flowchart] Move ChartNode Fail';
export const MOVE_NODE_SUCCESS = '[Flowchart] Move ChartNode Success';

export class MoveNode implements Action {
    readonly type = MOVE_NODE;
    constructor(public payload: ChartNode) { }
}

export class MoveNodeFail implements Action {
    readonly type = MOVE_NODE_FAIL;
    constructor(public payload: ChartNode) { }
}

export class MoveNodeSuccess implements Action {
    readonly type = MOVE_NODE_SUCCESS;
    constructor(public payload: ChartNode) {
    }
}

// remove nodes
export const REMOVE_NODE = '[Flowchart] Remove ChartNode';
export const REMOVE_NODE_FAIL = '[Flowchart] Remove ChartNode Fail';
export const REMOVE_NODE_SUCCESS = '[Flowchart] Remove ChartNode Success';

export class RemoveNode implements Action {
    readonly type = REMOVE_NODE;
    constructor(public payload: ChartNode[], public relatedConns: Connector[]) {
    }
}

export class RemoveNodeFail implements Action {
    readonly type = REMOVE_NODE_FAIL;
    constructor(public payload: ChartNode[]) { }
}

export class RemoveNodeSuccess implements Action {
    readonly type = REMOVE_NODE_SUCCESS;
    constructor(public payload: ChartNode[]) {
    }
}


// action types
export type NodesAction =
    | LoadNodes
    | LoadNodesFail
    | LoadNodesSuccess
    | CreateNodes
    | CreateNodesFail
    | CreateNodesSuccess
    | UpdateNode
    | UpdateNodeFail
    | UpdateNodeSuccess
    | UpdateNodeLocal
    | UpdateNodeLocalFail
    | UpdateNodeLocalSuccess
    | RemoveNode
    | RemoveNodeFail
    | RemoveNodeSuccess;

