import { Pipe, PipeTransform } from '@angular/core';
import { ConnectorType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';

@Pipe({
  name: 'connectorColor'
})
export class ConnectorColorPipe implements PipeTransform {

  transform(type: ConnectorType, ...args: any[]): any {
    switch (type) {
      case ConnectorType.ALWAYS:
        return '#00a6ff';
      case ConnectorType.FAIL:
        return 'red';
      case ConnectorType.SUCCESS:
        return 'green';
      default:
        return '#757575';
    }
  }

}
