import { Pipe, PipeTransform } from '@angular/core';
import { CompletionItemType } from 'libs/flowchart-ts/flowchart-generator/src/lib/auto-complete';

@Pipe({
  name: 'hintItemColor'
})
export class HintItemColorPipe implements PipeTransform {

  transform(type: CompletionItemType, ...args: any[]): any {
    switch (type) {
      case CompletionItemType.EVENT:
        return 'brown';
      case CompletionItemType.FUNCTION:
        return 'orange';
      case CompletionItemType.PROPERTY:
        return '#06c';
      case CompletionItemType.VAR:
        return '#cc0';
      case CompletionItemType.SUBCHART:
        return 'green';
    }
  }

}
