import { KEY_CODE } from '../../../../../../constants/common/key.code.constants';
import { Component, OnInit, ViewChildren, ElementRef, ViewChild } from '@angular/core';
import { SlotBase } from '../../models/slot-base.model';

@Component({
  selector: 'app-string-widget',
  templateUrl: './string-widget.component.html',
  styleUrls: ['./string-widget.component.css']
})
export class StringWidgetComponent extends SlotBase implements OnInit {

  @ViewChild("input", { static: false }) editInput: ElementRef;
  constructor() {
    super();
    this.text = "“”";
  }

  ngOnInit() {
  }

  onViewDblClick($event) {
    $event.stopPropagation();
    if (!this.isEditing) {
      this.isEditing = true;
      setTimeout(() => {
        this.editInput.nativeElement.value = this.text.substr(1, this.text.length - 2);
      }, 10);
    }
  }

  onEditInput($event) {
    this.text = $event.target.value;
  }

  onEditKeydown($event) {
    switch ($event.keyCode) {
      case KEY_CODE.ENTER: {
        if ($event.target.value) {
          this.text = `“${$event.target.value}”`;
        }
        this.isEditing = false;
        break;
      }
    }
  }

}
