import { ChartData } from 'src/app/core/models/chart-data.model';
import * as fromCurrentProject from '../../actions/projects/current-project.actions';
import * as fromCurrentChart from '../../actions/charts/current-chart.actions';
import * as fromInterface from '../../actions/interface/interfaces.actions';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/type.model';
import { EventType } from 'libs/flowchart-ts/flowchart-core/src/public-api';

export interface CurrentProjectState {
    project: Project;
    entities: {
        name: string;
        currentChart?: Chart;
        openingCharts?: Chart[];
    };
    loaded: boolean;
    loading: boolean;
    selectedItem: ChartData;
    types: ChartType[];
    events: EventType[];
}

export const initialState: CurrentProjectState = {
    project: null,
    entities: {
        name: '',
        currentChart: null,
        openingCharts: []
    },
    loaded: false,
    loading: false,
    selectedItem: null,
    types: [],
    events: []
};

export function reducer(
    state = initialState,
    action: fromCurrentProject.CurrentProjectAction | fromCurrentChart.CurrentChartAction | fromInterface.ChartTypesAction
): CurrentProjectState {
    switch (action.type) {
        case fromCurrentProject.LOAD_CURRENT_PROJECT: {
            return {
                ...state,
                loading: true,
            };
        }

        case fromCurrentProject.LOAD_CURRENT_PROJECT_SUCCESS: {
            const data = action.payload;
            console.log('LOAD_CURRENT_PROJECT_SUCCESS');
            return {
                ...state,
                loading: false,
                loaded: true,
                project: action.payload
            };
        }

        case fromCurrentProject.LOAD_CURRENT_PROJECT_FAIL: {
            return {
                ...state,
                loading: false,
                loaded: false,
            };
        }

        case fromCurrentProject.SELECT: {
            return {
                ...state,
                selectedItem: action.payload
            };
        }

        case fromCurrentChart.LOAD_CURRENT_CHART_SUCCESS:
            return {
                ...state,
                selectedItem: null
            };

        case fromInterface.LOAD_CHART_TYPES_SUCCESS:
            return {
                ...state,
                types: action.payload
            };

        case fromInterface.CREATE_TYPE_SUCCESS:
            return {
                ...state,
                types: [...state.types, action.payload]
            };

        case fromInterface.UPDATE_TYPE_SUCCESS:
            const newTypes = [...state.types];
            const index = newTypes.findIndex(t => t.uid === action.payload.uid);
            if (index > -1) {
                newTypes[index] = action.payload;
            }
            return {
                ...state,
                types: newTypes
            };

        case fromInterface.LOAD_EVENTS_SUCCESS:
            return {
                ...state,
                events: action.payload
            };

        case fromInterface.UPDATE_EVENTS_SUCCESS:
            return {
                ...state,
                events: action.payload.events
            };

        case fromCurrentProject.UPDATE_PROJECT_SUCCESS:
            return {
                ...state,
                project: action.payload
            };
    }

    return state;
}

export const getRecentProjectsListLoading = (state: CurrentProjectState) => state.loading;
export const getRecentProjectsListLoaded = (state: CurrentProjectState) => state.loaded;
export const getRecentProjectsList = (state: CurrentProjectState) => state.entities;

export const getCurrentProject = (state: CurrentProjectState) => state.project;
export const getSelectedItem = (state: CurrentProjectState) => state.selectedItem;
export const getTypes = (state: CurrentProjectState) => state.types;
export const getEvents = (state: CurrentProjectState) => state.events;

