import { DEBUG_METHODS } from './../../constants/debug-message.constants';
import { DebugMessage } from './../../models/debug-message.model';
import { InterpageCommunicator } from './../../../common/interpage-communication/models/interpage-communicator.model';
import { UidUtils } from './../../../utils/uid.utils';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { Component, OnInit } from '@angular/core';

const GET_CHART_LIST_MSG: DebugMessage<any> = {
  jsonrpc: '2.0',
  method: DEBUG_METHODS.GET_CHART_LIST,
  id: 0,
  result: [
    {

    }
  ]
};

@Component({
  selector: 'app-debugdemo',
  templateUrl: './debugdemo.component.html',
  styleUrls: ['./debugdemo.component.css']
})
export class DebugdemoComponent implements OnInit {

  urlString: string;
  url: SafeResourceUrl;

  communicator: InterpageCommunicator;
  sendMsg: string;
  recvMsg: string;
  constructor(private sanitizer: DomSanitizer) {
    this.url = this.sanitizer.bypassSecurityTrustResourceUrl('http://www.163.com');
   }

  ngOnInit() {
    this.init();
  }

  init() {
    this.communicator = new InterpageCommunicator('asyncflow');
    this.communicator.init();
    // 处理来自流程图的BroadcastChannel信息
    this.communicator.onmessage = e => {
      console.log('debugdemo', e.data);
      this.recvMsg = e.data;
      if (window.parent) {
        window.parent.postMessage(e.data, '*');
      }
    };

    // 监听来自父窗体的消息
    window.addEventListener('message', e => {
      // 如果通信消息是调试信息
      if (e.data.tag === 'asyncflow') {
        this.communicator.sendMsg(e.data.msg);
      }
    });

  }

  onSendBroadcastClick($event) {
    this.communicator.sendMsg(this.sendMsg);
  }

  onUrlInput($event) {
    this.urlString = $event.target.value;
  }

  onGoClick($event) {
    this.url = this.sanitizer.bypassSecurityTrustResourceUrl(this.urlString);
  }
}
