import { Injectable } from '@angular/core';
import { RecentProject } from 'src/app/models/project.model';
import { HttpClient } from '@angular/common/http';
import { HttpResult } from 'src/app/common/http/http.result.model';
import { APP_HTTP_RESULT_CODE } from 'src/app/constants/http/http.constants';

@Injectable({
  providedIn: 'root'
})
export class RecentProjectsListService {

  constructor(private http: HttpClient) { }

  async getRecentProjectsList() {
    const url = 'api/project';
    try {
        const result = await this.http.get<HttpResult<RecentProject[]>>(url).toPromise();
        if (result.code === APP_HTTP_RESULT_CODE.OK) {
          console.log(result);
          return new HttpResult<RecentProject[]>(APP_HTTP_RESULT_CODE.OK, '', result.data);
        }
        return new HttpResult<string>(APP_HTTP_RESULT_CODE.ERROR, result.msg, null);
      } catch (error) {
        return new HttpResult<string>(APP_HTTP_RESULT_CODE.ERROR, error.message, null);
      }
  }

}
