import {
    ActionReducerMap,
    createFeatureSelector,
} from '@ngrx/store';

import * as fromRecentProjectsList from './projects/recent-projects-list.reducer';
import * as fromCurrentProject from './projects/current-project.reducer';
import * as fromCurrentChartsList from './charts/current-charts-list.reducer';
import * as fromCurrentChart from './charts/current-chart.reducer';
import * as fromInterface from './interfaces/interfaces.reducer';
import * as fromToolbar from './toolbar/toolbar.reducer';
import * as fromCopy from './common/copy.reducer';

export interface FlowchartState {
    recentProjecetsList: fromRecentProjectsList.RecentProjectsListState;
    currentProject: fromCurrentProject.CurrentProjectState;
    currentChartsList: fromCurrentChartsList.CurrentChartsListState;
    currentChart: fromCurrentChart.CurrentChartState;
    interface: fromInterface.InterfaceState;
    toolbar: fromToolbar.ToolbarState;
    copy: fromCopy.CopiedChartInformationState;
}

export const reducers: ActionReducerMap<FlowchartState> = {
    recentProjecetsList: fromRecentProjectsList.reducer,
    currentProject: fromCurrentProject.reducer,
    currentChartsList: fromCurrentChartsList.reducer,
    currentChart: fromCurrentChart.reducer,
    interface: fromInterface.reducer,
    toolbar: fromToolbar.reducer,
    copy: fromCopy.reducer
};

export const getFlowChartState = createFeatureSelector<FlowchartState>('getFlowChartState');
