import { UidUtils } from './../../../../flowchart-common/src/public-api';
import { VariableJsonKey } from '../constants/chart.constants';
import { Item } from './item.model';
import { ChartType } from './type.model';
import { Parameter } from './parameter.model';
export class Variable extends Parameter {
    /**
     * variable index
     */
    index: number;
    /**
     * If variable is parameter of this subchart
     */
    isParameter: boolean;



    //#region Methods
    fromJson(jobj: any, typeConverter: (typeName: string) => ChartType) {
        if (super.fromJson(jobj, typeConverter)) {
            this.isParameter = jobj[VariableJsonKey.IsParameter];
            this.default = jobj[VariableJsonKey.Default];
            this.count = jobj[VariableJsonKey.Count];
            this.uid = jobj[VariableJsonKey.Uid] || UidUtils.generateUid();
            return true;
        }
        return false;
    }

    toJson() {
        const v: any = {};
        v[VariableJsonKey.Uid]= this.name;
        v[VariableJsonKey.Name] = this.uid;
        v[VariableJsonKey.IsParameter] = this.isParameter;
        v[VariableJsonKey.Count]=this.count;
        v[VariableJsonKey.Default] = this.default;
    }
}