import { Injectable } from '@angular/core';
import { environment } from 'src/environments/environment';


let electron;
if (!environment.IS_WEB) {
  electron = (<any>window).require('electron');
} else {
  electron = null;
}


@Injectable({
  providedIn: 'root'
})
export class IpcService {
  private _ipc;
  constructor() {
    if (electron) {
      this._ipc = electron.ipcRenderer;
    }
  }

  readFile(path: string): Promise<any> {
    return this._ipc.invoke('read-file', path);
  }

  readDir(path: string, options?: any): Promise<any> {
    return this._ipc.invoke('read-dir', path);
  }

  selectDir(): Promise<any> {
    return this._ipc.invoke('select-dir');
  }

  writeFile(path: string, content: string): Promise<any>  {
    return this._ipc.invoke('write-file', path, content);
  }

  exists(path: string): Promise<any>  {
    return this._ipc.invoke('exists', path);
  }

  createDir(path: string): Promise<any>  {
    return this._ipc.invoke('create-dir', path);
  }

  deleteFile(path: string): Promise<any>  {
    return this._ipc.invoke('delete-file', path);
  }

  renameFile(path: string, newName:string): Promise<any>  {
    return this._ipc.invoke('rename-file', path, newName);
  }

  exitApp(): Promise<any> {
    return this._ipc.invoke('exit-app');
  }

}
