import { AstGenerator } from './../../../../flowchart-parser/src/lib/ast-generator';
import { UndefinedTypeError } from './generator-info.model';
import { IParser } from './../../../../flowchart-parser/src/lib/parser';
import { Context } from './generator-info.model';
import { CommonTokenStream } from 'antlr4ts';
import { StringUtils } from './../../../../flowchart-common/src/public-api';
import { Parameter } from './../../../../flowchart-core/src/lib/models/parameter.model';
import { IAstNodeVisitor } from './../../../../flowchart-parser/src/lib/ast-node-visitor.interface';
import { Token, TokenType } from './../../../../flowchart-parser/src/lib/tokens';
import { Chart } from './../../../../flowchart-core/src/lib/models/chart.model';
import { Project } from './../../../../flowchart-core/src/lib/models/project.model';
import { ChartType, GenericType } from './../../../../flowchart-core/src/lib/models/type.model';
import { ArgListNode, ArgNode, AssignNode, AstNode, AstOperator, BinaryOpNode, FuncNode, ListNode, MemberAssignNode, MemberNode, NameNode, NumberNode, ParenthesisNode, StringNode, SubscriptAssignNode, SubscriptNode, UnaryOpNode, VarNode, VisitAstNode } from './../../../../flowchart-parser/src/lib/nodes/ast-node.model';
import { ChartNode } from './../../../../flowchart-core/src/lib/models/node.model';
import { EventParam, Member, MemberFrom, MemberType, Method, Property } from './../../../../flowchart-core/src/lib/models/member.model';
import { ParseInfoType } from './../../../../flowchart-core/src/lib/constants/node.enums';
import { NodeParseInfo, NodeToken } from './../../../../flowchart-core/src/lib/models/parse-info.model';

export interface ICodeGenerator {
    handle(chart: Chart): void;

    build(project: Project): void;

    save(project: Project): boolean;

    getFileExtension(): string;

    fileHeaderLines(): string[];

    getTextType(text: string): ChartType;
}

export class AstNodeInfo {
    code: string;
    type: ChartType;
    node: AstNode;
    get isAction() {
        return this.type === ChartType.VoidType;
    }

    functionName: string;
}


export abstract class BaseGenerator implements ICodeGenerator, IAstNodeVisitor<AstNodeInfo> {

    private static _inited: boolean;

    static tokenMap: Map<number, TokenType>;
    abstract tag: string;
    parser: IParser;
    project: Project;
    currentChart: Chart;
    currentNode: ChartNode;
    currentChartType: ChartType;
    ctx: Context;

    varStartIndex: number = 0;
    get parseInfo() {
        return this.currentNode.parseInfo;
    }

    get codes() {
        return this.parseInfo.codes;
    }
    set codes(v: string[]) {
        this.parseInfo.codes = v;
    }
    constructor(parser: IParser) {
        if (!BaseGenerator._inited) {
            BaseGenerator.tokenMap = new Map<number, TokenType>();
            BaseGenerator.tokenMap.set(39, TokenType.UNKOWN);
            BaseGenerator._inited = true;
        }
        this.parser = parser;
        this.ctx = new Context();
    }

    //#region VISITORS
    visit(node: VisitAstNode): AstNodeInfo {
        if (node instanceof NameNode) {
            return this.visitNameNode(node);
        }
        if (node instanceof NumberNode) {
            return this.visitNumberNode(node);
        }

        if (node instanceof StringNode) {
            return this.visitStringNode(node);
        }

        if (node instanceof VarNode) {
            return this.visitVarNode(node)
        }

        if (node instanceof FuncNode) {
            return this.visitFuncNode(node);
        }

        if (node instanceof ArgNode) {
            return this.visitArgNode(node);
        }

        if (node instanceof ArgListNode) {
            return this.visitArgListNode(node);
        }

        if (node instanceof UnaryOpNode) {
            return this.visitUnaryOpNode(node);
        }

        if (node instanceof BinaryOpNode) {
            return this.visitBinaryOpNode(node);
        }

        if (node instanceof MemberNode) {
            return this.visitMemberNode(node);
        }

        if (node instanceof MemberAssignNode) {
            return this.visitMemberAssignNode(node);
        }

        if (node instanceof SubscriptAssignNode) {
            return this.visitSubscriptAssignNode(node);
        }

        if (node instanceof AssignNode) {
            return this.visitAssignNode(node);
        }

        if (node instanceof ListNode) {
            return this.visitListNode(node);
        }

        if (node instanceof SubscriptNode) {
            return this.visitSubscriptNode(node);
        }

        if (node instanceof ParenthesisNode) {
            return this.visitParenthesisNode(node);
        }
        return null;
    }

    visitNameNode(node: NameNode) {
        const nodeInfo = new AstNodeInfo();
        nodeInfo.code = node.text;
        nodeInfo.type = ChartType.AnyType;

        if (node.text === "true" || node.text === 'false') {
            nodeInfo.type = ChartType.BoolType;
            return nodeInfo;
        }

        // const tp = this.project.getEvent(node.text);
        // if (tp) {
        //     nodeInfo.code = this.genEventCode(tp.name);
        //     nodeInfo.type = tp;
        //     return nodeInfo;
        // }

        let member = this.currentChartType.getMember(node.text);
        if (member && member.memberType === MemberType.PROPERTY) {
            nodeInfo.code = member.source || member.name;
            nodeInfo.type = member.type;
            return nodeInfo;
        }
        member = ChartType.GlobalType.getMember(node.text);
        if (member && member.memberType === MemberType.PROPERTY) {
            nodeInfo.code = member.source;
            nodeInfo.type = member.type;
            return nodeInfo;
        }
        return nodeInfo;
    }

    visitSingleNameNode(node: NameNode) {
        const nodeInfo = new AstNodeInfo();
        nodeInfo.code = node.text;
        nodeInfo.type = ChartType.AnyType;

        // check if is event;
        const tp = this.project.getEvent(node.text);
        if (tp) {
            nodeInfo.code = this.genEventCode(tp.name);
            nodeInfo.type = tp;
            return nodeInfo;
        }

        const member = this.currentChartType.getMember(node.text);
        if (member instanceof Property) {
            nodeInfo.code = member.source;
            nodeInfo.type = member.type;
            return nodeInfo;
        }

        if(node.text.toLowerCase() === 'true' || node.text.toLowerCase() === 'false') {
            nodeInfo.code = node.text.toLowerCase();
            nodeInfo.type = ChartType.BoolType;
            return nodeInfo;
        }

        return nodeInfo;
    }

    visitNumberNode(node: NumberNode) {
        const ni = new AstNodeInfo();
        ni.type = ChartType.NumberType;
        ni.code = node.text;
        return ni;
    }

    visitStringNode(node: StringNode) {
        const ni = new AstNodeInfo();
        ni.type = ChartType.StringType;
        ni.code = node.text;
        return ni;
    }

    visitVarNode(node: VarNode) {
        const v = this.ctx.getVariable(node.text);
        if (!v) {
            this.error(`Undefined variable '${node.text}', please check if it has been declared in variable list already.`);
            return null;
        }
        const ni = new AstNodeInfo();
        ni.code = this.genGetVarCode(v.index);
        ni.type = v.type;
        return ni;
    }

    visitFuncNode(node: FuncNode) {
        const caller = node.caller;
        let callerInfo: AstNodeInfo = null;
        let funcName = node.func.text;
        let func = null;
        if (!caller) {
            if (this.currentChart.type) {
                func = this.currentChartType.getMember(funcName);
            }
            if (!func) {
                const chartType = this.project.config.getChartType(this.currentChart.path);
                if (chartType) {
                    func = chartType.getMember(funcName);
                }
            }
            if (!func) {
                func = ChartType.GlobalType.getMember(funcName);
                if (func) {
                    callerInfo = new AstNodeInfo();
                    callerInfo.code = '';
                    callerInfo.type = ChartType.GlobalType;
                } else {
                    this.error(`${funcName} was not found`);
                    return null;
                }
            } else {
                callerInfo = new AstNodeInfo();
                callerInfo.code = 'self';
                callerInfo.type = this.currentChartType;
            }
        } else {
            callerInfo = caller.Accept(this);
            if (callerInfo.type.name === 'Undefined') {
                this.error(`Try to find member from undefined type`);
            }
            func = callerInfo.type.getMember(funcName);
            if (!func) {
                this.error(`${funcName} was not found`)
            }
        }

        if (func.memberType !== MemberType.METHOD) {
            return null;
        }

        const method = func as Method;
        const args = node.args.Accept<AstNodeInfo>(this);
        console.log(`Func name ${func.name}`);
        // Check if the arguments is valid
        if (!this.checkFunctionParameter(node.args.args, method.parameters)) {
            return null;
        }

        //do some custom parse and generation
        this.handleFunction(func, args, node.args);

        let result;
        let source = func.source || func.name;
        if (source.includes("$")) {
            // if source is template, then we should replace $self, $class $params $name
            result = source.replace("$param", args.code)
                .replace("$self", callerInfo.code.endsWith(".ForEach") ? callerInfo.code.substr(9, callerInfo.code.length - 8) : callerInfo.code)
                .replace("$name", func.name);
        } else {
            const callerCode = StringUtils.isNullOrWhiteSpace(callerInfo.code) ? "" : `${callerInfo.code}${this.getInvokeOperator()}`;
            result = `${callerCode}${source}(${args.code})`;
        }

        if (func.from === MemberFrom.SUBCHART) {
            this.parseInfo.type = ParseInfoType.SUBCHART;
        }

        // return result
        const ni = new AstNodeInfo();
        ni.type = func.type;
        ni.code = result;
        return ni;
    }

    visitArgNode(node: ArgNode) {
        return node.value.Accept<AstNodeInfo>(this);
    }

    visitArgListNode(node: ArgListNode) {
        const argsInfo = node.args.map(arg => arg.Accept<AstNodeInfo>(this));
        const ni = new AstNodeInfo();
        ni.code = argsInfo.map(arg => arg.code).join(", ");
        ni.node = node;
        return ni;
    }

    visitUnaryOpNode(node: UnaryOpNode) {
        const expr = node.express.Accept<AstNodeInfo>(this);
        const ni = new AstNodeInfo();
        ni.code = `${AstGenerator.OperatorReverseDict.get(node.operator), expr.code}`;
        ni.type = expr.type;
        return ni;
    }

    visitBinaryOpNode(node: BinaryOpNode) {
        const left = node.left.Accept<AstNodeInfo>(this);
        const right = node.right.Accept<AstNodeInfo>(this);
        if (!left || !right) {
            return null;
        }

        const typeCheck = this.checkType(left.type, right.type);
        if (!typeCheck) {
            // special process for +
            if (!(node.operator === AstOperator.Add && left.type === ChartType.StringType)) {
                this.error(`Type error, left of ${node.operator} is ${left.type.name} but right is ${right.type.name}`);
                return null;
            }
        }

        // translate operator in different language
        this.translateOp(node);
        const ni = new AstNodeInfo();
        ni.code = `${left.code} ${AstGenerator.OperatorReverseDict.get(node.operator)} ${right.code}`;
        ni.type = left.type;

        if (node.operator === AstOperator.Or || node.operator === AstOperator.And || node.operator === AstOperator.Equal
            || node.operator === AstOperator.MoreThan || node.operator === AstOperator.MoreEqualThan
            || node.operator === AstOperator.LessEqualThan || node.operator === AstOperator.LessThan) {
            ni.type = ChartType.BoolType;
        }

        return ni;
    }

    visitMemberNode(node: MemberNode) {
        const owner = node.owner;
        const ownerInfo = owner.Accept<AstNodeInfo>(this);
        const memberInfo = node.member.Accept<AstNodeInfo>(this);
        const ni = new AstNodeInfo();
        if (owner instanceof NameNode) {
            const tp = this.project.getEvent(owner.text);
            if (tp) {
                const member = tp.getMember(node.member.text) as EventParam;
                if (member) {
                    ni.type = member.type;
                    ni.code = this.genEventParamCode(tp.name, member.index);
                    return ni;
                }
            }

            if (owner.text === 'self') {
                let member = this.currentChartType.getMember(node.member.text);
                if (!member) {
                    member = ChartType.GlobalType.getMember(node.member.text);
                }
                if (member) {
                    ni.type = member.type;
                    ni.code = member.source || member.name;
                    return ni;
                }
            }
            this.error('Not member');
            return null;
        } else {
            const member = ownerInfo.type.getMember(memberInfo.code);
            if (!member) {
                const ev = this.project.getEvent(memberInfo.code);
                if (ev) {
                    ni.code = this.genEventCode(ev.name, ownerInfo.code);
                    ni.type = ev;
                    return ni;
                }
                this.error("Member not found");
                return null;
            }

            const genType = ownerInfo.type as GenericType;
            if (member && member.name === 'ForEach') {
                if (genType && genType.typeParams) {
                    member.type = genType.typeParams[0];
                }
            }
            ni.type = member.type;
            ni.code = `${genType.typeParams[0]}.${member.source}`;
        }
    }

    visitAssignNode(node: AssignNode) {
        // parse right express first
        const expNi = node.right.Accept<AstNodeInfo>(this);
        const ni = node.left.Accept<AstNodeInfo>(this);
        const varName = node.left.text;
        const v = this.ctx.getVariable(varName);
        if (!expNi) {
            this.error(`Invalid express ${node.right.text}`);
            return null;
        }
        if (!v) {
            this.error(`Undefined variable ${varName}`);
            return null;
        }
        if (ni.type === ChartType.UndefinedType) {
            // if variable is undefined type, then we need assign type to both ni and v with type from expNi
            ni.type = expNi.type;
            v.type = ni.type;
        } else {
            // for variable with specific type (not undefined) , we need to check if the value is assignabl
            if (!this.checkType(ni.type, expNi.type)) {
                this.error(`Type error: ${ni.type.name} ${expNi.type.name}`);
                return null;
            }
        }

        // special process for subchart call node
        if (this.parseInfo.isSubchartCall) {
            this.parseInfo.setVarId = v.index + this.varStartIndex;
            return expNi;
        }

        ni.code = this.genSetVarCode(v.index, expNi.code);
        return ni;
    }

    visitMemberAssignNode(node: MemberAssignNode) {
        const ni = new AstNodeInfo();
        const left = node.left.Accept<AstNodeInfo>(this);
        const right = node.right.Accept<AstNodeInfo>(this);
        if (!this.checkType(left.type, right.type)) {
            this.error(`Type error: ${left.type.name} ${right.type.name}`);
            return null;
        }
        ni.code = `${node.left.code} = ${node.right.code}`;
        return ni;
    }

    visitSubscriptAssignNode(node: SubscriptAssignNode) {
        const left = node.left.Accept<AstNodeInfo>(this);
        const right = node.right.Accept<AstNodeInfo>(this);
        if (!this.checkType(left.type, right.type)) {
            this.error(`Type error: ${left.type.name} ${right.type.name}`);
            return null;
        }
        const ni = new AstNodeInfo();
        ni.code = `${node.left.code} = ${node.right.code}`;
        return ni;
    }

    visitListNode(node: ListNode) {
        const itemsInfo = node.items.args.map(item => item.Accept<AstNodeInfo>(this));
        let innerType = ChartType.AnyType;
        if (itemsInfo.length > 0) {
            // if there is element has different type from the first element, throw error
            const index = itemsInfo.findIndex(item => itemsInfo[0].type != null && !itemsInfo[0].type.canAssignFrom(item.type));
            if (index > 0) {
                this.error(`Type error: the element at index ${index} has different type from the first one`);
                return null;
            }
        }
        innerType = itemsInfo[0].type;
        const ni = new AstNodeInfo();
        const gType = new GenericType();
        gType.outType = ChartType.ListType;
        gType.typeParams = [innerType];
        ni.type = gType;
        ni.code = `[${itemsInfo.map(ni => ni.code).join(', ')}]`;
        return ni;
    }

    visitSubscriptNode(node: SubscriptNode) {
        const listInfo = node.listExpr.Accept<AstNodeInfo>(this);
        const indexInfo = node.index.Accept<AstNodeInfo>(this);
        let tp = ChartType.AnyType;
        if (listInfo.type instanceof GenericType) {
            const genType = listInfo.type as GenericType;
            if (genType.outType === ChartType.ListType) {
                const tps = genType.typeParams;
                if (tps && tps.length > 0) {
                    tp = tps[0];
                }
            } else if (genType.outType === ChartType.DictType) {
                const tps = genType.typeParams;
                if (tps && tps.length > 1) {
                    tp = tps[1];
                }
            }
        }
        const ni = new AstNodeInfo();
        ni.type = tp;
        ni.code = `${listInfo.code}[${indexInfo.code}]`;
        return ni;
    }

    visitParenthesisNode(node: ParenthesisNode) {
        const ni = node.content.Accept<AstNodeInfo>(this);
        if (!ni) {
            return null;
        }
        ni.code = `(${ni.code})`;
        return ni;
    }

    //#endregion


    handle(chart: Chart): void {
        this.project = chart.project;
        this.currentChart = chart;
        this.currentChartType = chart.type;
        this.ctx.reset();
        chart.parameters.forEach(p => this.ctx.addVariable(p));
        chart.variables.forEach(v => this.ctx.addVariable(v));
        const nodes = chart.sortNodes();
        try {
            const handleNodes = nodes.slice(1);
            for (let node of handleNodes) {
                this.handleNode(node);
            }
        } catch (error) {
            console.error(error);
            error(`Error occured when parsing chart ${this.currentChart.path}: ${error.message}`);
            return;
        }

        // chart.parameters = chart.variables.filter(v => v.isParameter);
        if (!this.ctx.chartReturnType) {
            chart.isSubChart = true;
            chart.returnType = this.ctx.chartReturnType;
        }
    }
    build(project: Project): void {
        for (const chart of project.charts) {
            this.handle(chart);
        }
        this.save(project);
        project.hasBuilt = true;
    }
    save(project: Project): boolean {
        this.saveJson(project);
        this.saveCode(project);
        this.saveEvent(project);
        return true;
    }
    getFileExtension(): string {
        return 'lua';
    }
    fileHeaderLines(): string[] {
        return [
            "if asyncflow.node_funcs == nil then asyncflow.node_funcs = {} end",
            "local EventId = asyncflow.EventId"
        ]
    };
    getTextType(text: string): ChartType {
        if (StringUtils.isNullOrWhiteSpace(text)) {
            return null;
        }
        const str = text;
        const parseResult = this.parser.parse(str);
        const tokens = parseResult.tokenStream;
        const astTree = parseResult.node;

        // if asttree is empty, the syntax is not valid, return 
        if (!astTree) {
            return null;
        }

        try {
            let result: AstNodeInfo = null;
            if (astTree instanceof NameNode) {
                result = this.visitSingleNameNode(astTree);
            } else {
                result = astTree.Accept(this);
            }
            return result.type;
        } catch (error) {
            return null;
        }
    }


    saveJson(project: Project) {
        const files = project.getFileList();
        files.forEach((chart, key) => {
            const path = `${project.config.outputPath || (project.path + '/generate')}/Flowchart_${key}.json`;
            const lines = Project.exportChartsJson(chart);
            // TODO we need to set abstract file system
            if (Project.fs) {
                Project.fs.writeFile(path, lines.join('\n'));
            }
        })
    }

    saveCode(project: Project) {
        if (!Project.fs) {
            console.error(`File system not set yet`);
            return;
        }
        const outputPath = project.config.outputPath || `${project.path}\\generate`;
        Project.fs.exists(outputPath).then(exists => {
            if (!exists) {
                Project.fs.createDir(outputPath).then(result => {
                    if (result.success) {
                        this.saveCodeFiles(project, outputPath);
                    }
                })
            } else {
                this.saveCodeFiles(project, outputPath);
            }
        })
    }

    private saveCodeFiles(project: Project, outputPath: string) {
        const files = project.getFileList();
        files.forEach((charts, key) => {
            const path = `${outputPath}\\Flowchart_${key}.${this.getFileExtension()}`;
            const lines = [];
            lines.push(...this.fileHeaderLines());
            charts.forEach(chart => {
                chart.nodes.forEach(node => {
                    const pi = node.parseInfo;
                    // control node has no need to generate code
                    if (pi && !pi.isControlNode) {
                        lines.push(...pi.codes);
                    }
                });
            })
            Project.fs.writeFile(path, lines.join('\r\n'));
        });
        return true;
    }

    saveEvent(project: Project) {
        const path = (project.config.outputPath || `${project.path}\\generate`) + "\\event_info.json";
        const lines: string[] = [];
        let i = 1;
        lines.push('[');
        project.events.forEach(ev => {
            const paraCount = ev.parameters ? ev.parameters.length : 0;
            lines.push(`    {"id": ${i++}, "name": "${ev.name}", "arg_count": ${paraCount}},`);
        })
        if (lines[lines.length - 1].endsWith(',')) {
            lines[lines.length - 1] = lines[lines.length - 1].substr(0, lines[lines.length - 1].length - 1);
        }
        lines.push(']');
        Project.fs.writeFile(path, lines.join('\r\n'));

    }

    error(msg: string) {
        this.parseInfo.setErrorMessage(msg);
    }

    parseStartNode(startCommand: string) {
        if (startCommand.toLowerCase() === 'start') {
            return true;
        }

        const newParams: Parameter[] = [];
        const reg = new RegExp(`^(\w+)\((.*)\)$`);
        if (reg.test(startCommand)) {

        }
        return true;
    }

    handleNode(node: ChartNode) {
        this.currentNode = node;
        this.currentNode.parseInfo = new NodeParseInfo();
        const str = node.text;
        if (StringUtils.isNullOrWhiteSpace(str)) return true;

        const result = this.parser.parse(str);
        const astTreeNode = result.node;
        const tokens = this.handleTokens(str, result.tokenStream);
        tokens.forEach(Token.print);
        this.parseInfo.tokens = tokens.map(tk => {
            const token = new NodeToken();
            token.text = tk.text;
            token.type = tk.type;
            return token;
        });
        if (!astTreeNode) {
            this.error(`'${str}' parse errorr occured, unable to identify`);
            return false;
        }

        try {
            let result: AstNodeInfo;
            if (astTreeNode instanceof NameNode) {
                result = this.visitSingleNameNode(astTreeNode as NameNode);
            } else {
                result = astTreeNode.Accept(this);
            }

            if (!result) {
                return false;
            }

            // Encapsulate codes to function named with unique path;
            result.functionName = `${node.chart.path.replace('.', '_')}_id_${node.uid}`;
            this.generateCode(result);
        } catch (error) {
            if (error instanceof UndefinedTypeError) {
                this.ctx.undefinedVarNode.push(this.currentNode);
            }
            this.currentNode.parseInfo.setErrorMessage(error.message);
            return false;
        }
    }

    handleTokens(text: string, tokenStream: CommonTokenStream) {
        const tokens: Token[] = [];
        let pos = 0;
        const tks = tokenStream.getTokens();
        for (let token of tks) {
            if (token.type < 0) {
                continue;
            }
            if (token.startIndex > pos) {
                const newToken = new Token();
                newToken.text = text.substr(pos, token.startIndex - pos);
                newToken.type = - TokenType.UNKOWN;
                tokens.push(newToken);
                pos = token.startIndex;
            }
            const newToken = new Token();
            newToken.text = token.text;
            newToken.type = Token.getTokenType(token.type);
            tokens.push(newToken);
            pos = token.stopIndex + 1;
        }
        return tokens;
    }


    genSetVarCode(varId: number, code: string) {
        return `asyncflow.set_var(${varId + this.varStartIndex}, ${code})`;
    }

    genGetVarCode(varId: number) {
        return `asyncflow.get_var(${varId + this.varStartIndex})`;
    }

    genEventCode(eventName: string, obj: string = null) {
        this.currentNode.parseInfo.setEventName(eventName);
        return `asyncflow.wait_event(${obj || 'self'}, EventId.${eventName})`;
    }

    genEventParamCode(eventName: string, paramIndex: number) {
        return `asyncflow.get_event_param(EventId.${eventName}, ${paramIndex})`;
    }

    getInvokeOperator() {
        return '.'
    }

    checkFunctionParameter(args: ArgNode[], parametersDef: Parameter[]) {
        let hasChangableLen = false;
        let hasDefaultCount = 0;

        // check null
        if ((!args || args.length === 0) && (!parametersDef || parametersDef.length === 0)) {
            return true;
        }

        // 1. No more than one length-changable parameter
        // 2. Length-changable parameter must be the last one
        // 3. Parameter with default value must occur after non-default value parameter
        if (parametersDef.length > 0) {
            // check if there is length-changable parameter(count == -1)
            hasChangableLen = parametersDef[parametersDef.length - 1].count === -1;
            const defaultParams = parametersDef.filter(p => (p.default != undefined && p.default != null));
            if (defaultParams) {
                hasDefaultCount = defaultParams.length - (hasChangableLen ? 1 : 0);
            }
        }

        if (args.length < parametersDef.length - hasDefaultCount - (hasChangableLen ? 1 : 0)) {
            this.error("Lack of necessary parameters");
        }

        if (!hasChangableLen && args.length > parametersDef.length) {
            this.error("Too many parameters");
        }

        // Check parameter type
        let argIndex = 0, defIndex = 0;
        // 
        while (defIndex < parametersDef.length && argIndex < args.length) {
            const param = parametersDef[defIndex];
            const arg = args[argIndex].Accept<AstNodeInfo>(this);
            if (!arg) {
                this.error('Unable to identify parameter type');
                return false;
            }
            var argType = arg.type;
            if (!argType) {
                this.error('Unable to identify parameter type');
                return false;
            }

            const defType = param.type;
            if (!this.checkType(argType, defType)) {
                this.error(`Parameter type not match, expect ${defType.name} but get ${argType.name}`);
            }

            argIndex++;
            if (param.count != -1) {
                defIndex++;
            }
        }
        return true;
    }

    checkType(tp1: ChartType, tp2: ChartType) {

        return true;
    }

    handleFunction(func: Member, argsInfo: AstNodeInfo, args: ArgListNode): void {
        let funcName = func.name.toLowerCase().replace("_", "");
        if (funcName === 'waitall') {
            this.parseInfo.type = ParseInfoType.CONTROL;
            this.parseInfo.name = 'wait_all';
            this.parseInfo.codes = [];
            const endConns = this.currentChart.connectors.filter(conn => conn.end === this.currentNode);
            endConns.forEach(conn => this.parseInfo.codes.push(conn.start.uid));
        } else if (funcName === 'stopnode' || funcName === 'stopflow') {
            this.parseInfo.type = ParseInfoType.CONTROL;
            this.parseInfo.name = funcName === 'stopnode' ? 'stop_node' : 'stop_flow';
            this.parseInfo.codes = [];
            this.parseInfo.codes.push(...argsInfo.code.split(',').map(s => {
                let str = s.trim();
                if (str.startsWith('"')) {
                    str = str.substr(1);
                }
                if (str.endsWith('"')) {
                    str = str.substr(0, str.length - 1);
                }
                return str;
            }))
        } else if (funcName === 'return') {
            const returnType = args.args.length === 0 ? ChartType.BoolType : args.args[0].type;
            if (!this.ctx.chartReturnType) {
                this.ctx.chartReturnType = returnType;
            } else {
                if (this.ctx.chartReturnType != returnType) {
                    this.error(`Chart has 2 return node, return different type ${this.ctx.chartReturnType} - ${returnType}`)
                }
            }
        }
    }

    abstract generateCode(ni: AstNodeInfo): void;

    abstract translateOp(node: BinaryOpNode): void;
}