import { Injectable } from '@angular/core';
import { Effect, Actions, ofType } from '@ngrx/effects';
import { mergeMap } from 'rxjs/operators';

import * as recentProjectsListActions from '../../actions/projects/recent-projects-list.actions';

import { RecentProjectsListService } from 'src/app/services/project/recent-projects-list.service';
import { APP_HTTP_RESULT_CODE } from 'src/app/constants/http/http.constants';

@Injectable()
export class RecentProjectsListEffects {
    constructor(
        private actions$: Actions,
        private recentProjectService: RecentProjectsListService
    ) { }

    @Effect()
    loadRecentProjectsList$ = this.actions$.pipe(
        ofType(recentProjectsListActions.LOAD_RECENT_PROJECTS_LIST),
        mergeMap(() => this.recentProjectService.getRecentProjectsList().then(
            (result: any) => {
                if (result.code === APP_HTTP_RESULT_CODE.OK) {
                    return new recentProjectsListActions.LoadRecentProjectsListSuccess(result.data);
                }
                return new recentProjectsListActions.LoadRecentProjectsListFail(result.msg);
            }
        ).catch(error => {
            return new recentProjectsListActions.LoadRecentProjectsListFail(error.message);
        }))
    );
}
