import { CommonMenuService } from 'src/app/services/chart/common-menu.service';
import { MenuService } from './../../../../services/common/menu.service';
import { ChartService } from './../../../../services/chart/chart.service';
import { IProperty } from './../../../../models/property.model';
import { Component, Input, OnInit, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { Store } from '@ngrx/store';
import * as fromStore from '../../../../store';
import { OutputService } from 'src/app/services/common/output.service';
import { ChartData } from 'src/app/core/models/chart-data.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { BaseComponent } from 'src/app/components/base-component';


@Component({
  selector: 'app-property-table',
  templateUrl: './property-table.component.html',
  styleUrls: ['./property-table.component.css']
})
export class PropertyTableComponent extends BaseComponent implements OnInit {

  @Input() chart: Chart;
  @Output() datachange: EventEmitter<Map<string, any>> = new EventEmitter<Map<string, any>>();
  @Input() properties: IProperty[];
  chartList;
  chartNameList;

  @ViewChild('editInput', { static: false }) editInput: ElementRef;
  constructor(
    private store: Store<fromStore.FlowchartState>,
    private outputService: OutputService,
    private chartService: ChartService,
    private commonMenuService: CommonMenuService
  ) {
    super();
  }

  ngOnInit() {
    this.initData();
  }


  initData() {
    // this.store.select(fromStore.getCurrentChartDetails).subscribe(data => {
    //   this.chart = data;
    // });
    // this.store.select(fromStore.getChartListSelector).subscribe(chartList => {
    //   this.chartList = chartList;
    //   this.chartNameList = this.chartList.map(cl => cl.name);
    // });
    // this.addSubscription(this.chartService.currentChart.subscribe(chart => {
    //   this.chart = chart;
    // }));
    // this.addSubscription(this.commonMenuService.selectedItems.subscribe(data=>{

    // }))
    this.chartNameList = this.chartService.getCurrentChartList().map(c => c.name);
    this.addSubscription(this.chartService.charts.subscribe(charts => {
      this.chartNameList = charts.map(c => c.name);
    }))
  }

  update(prop: IProperty, value: any) {
    const newData: any = { ...this.chart };
    newData[prop.nameEn] = value;
    const map = new Map<string, any>([[prop.nameEn, value]]);
    this.datachange.emit(map);
  }

  saveData(prop) {
    const currentChart = this.chart as Chart;
    const input = document.getElementById(prop.name) as HTMLInputElement;
    if (prop.nameEn === 'name') {
      const currentChartName = currentChart.name;
      const newName = input.value;
      const nameHasExisted = this.chartNameList.filter(nl => nl !== currentChartName).indexOf(input.value);
      if (nameHasExisted === -1 && input && input.value !== currentChartName) {
        this.update(prop, input.value);
      } else if (nameHasExisted !== -1) {
        this.outputService.error(input.value + '已存在，请更换名称');
      }
    } else {
      if (input) {
        const newChart: any = { ...currentChart, [prop.nameEn]: input.value };
        Object.setPrototypeOf(newChart, Chart.prototype);
        this.store.dispatch(new fromStore.UpdateChartProperty({ chart: newChart, props: ['path'] }));
        this.update(prop, input.value);
      }
    }
  }

  onDataCheckChanged($event, prop: IProperty) {
    const currentChart = this.chart as Chart;
    const newChart: any = { ...currentChart, [prop.nameEn]: $event.checked };
    Object.setPrototypeOf(newChart, Chart.prototype);
    this.store.dispatch(new fromStore.UpdateChartProperty({ chart: newChart, props: ['path'] }));
    this.update(prop, $event.checked);
  }

  onDataSelectionChanged($event, prop: IProperty) {
    const newValue = $event.target.options[$event.target.selectedIndex].value;
    const currentChart = this.chart as Chart;
    const newChart: any = { ...currentChart, [prop.nameEn]: newValue };
    Object.setPrototypeOf(newChart, Chart.prototype);
    this.store.dispatch(new fromStore.UpdateChartProperty({ chart: newChart, props: ['path'] }));
    this.update(prop, newValue);
  }


}
