import { ExecuteResultCode } from './../../constants/command/command.constants';
import { Chart, Variable } from "libs/flowchart-ts/flowchart-core/src/public-api";
import { Command, CommandExecuteResult } from "./command.model";

export class CreateVariableCommand extends Command {
    chart: Chart;
    v: Variable;
    index: number;
    constructor(chart: Chart, v: Variable) {
        super();
        this.chart = chart;
        this.v = v;
        this.index = v.index;
    }

    execute(): CommandExecuteResult {
        if (this.v.isParameter) {
            this.chart.parameters.splice(this.index, 0, this.v)
            for (let i = 0; i < this.chart.parameters.length; i++) {
                this.chart.parameters[i].index = i;
            }
        } else {
            this.chart.variables.splice(this.index - this.chart.parameters.length, 0, this.v);
            for (let i = 0; i < this.chart.variables.length; i++) {
                this.chart.variables[i].index = i + this.chart.parameters.length;
            }
        }
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }

    revert(): CommandExecuteResult {
        this.chart.variables.splice(this.index, 1);
        if (this.v.isParameter) {
            this.chart.parameters.splice(this.index, 1);
            for (let i = 0; i < this.chart.parameters.length; i++) {
                this.chart.parameters[i].index = i;
            }
        } else {
            this.chart.variables.splice(this.index - this.chart.parameters.length, 1);
            for (let i = 0; i < this.chart.variables.length; i++) {
                this.chart.variables[i].index = i + this.chart.parameters.length;
            }
        }
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }

}

export class UpdateVariableCommand extends Command {
    v: Variable;
    updateKeyValues: Map<string, any>;
    prevKeyValues: Map<string, any>;
    constructor(v: Variable, kv: Map<string, any>) {
        super();
        this.v = v;
        this.updateKeyValues = kv;
        this.prevKeyValues = new Map<string, any>();
    }

    execute(): CommandExecuteResult {
        this.updateKeyValues.forEach((v, k) => {
            this.prevKeyValues.set(k, this.v[k]);
            this.v[k] = v;
        })
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }

    revert(): CommandExecuteResult {

        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}

export class RemoveVariableCommand extends Command {
    chart: Chart;
    v: Variable;
    index: number;
    constructor(chart: Chart, v: Variable) {
        super();
        this.chart = chart;
        this.v = v;
        this.index = v.index;
    }

    execute(): CommandExecuteResult {
        if (this.v.isParameter) {
            this.chart.parameters.splice(this.index, 1);
            for (let i = this.index; i < this.chart.parameters.length; i++) {
                this.chart.parameters[i].index = i;
            }

        } else {
            this.chart.variables.splice(this.index - this.chart.parameters.length, 1);
        }
        for (let i = 0; i < this.chart.variables.length; i++) {
            this.chart.variables[i].index = i + this.chart.parameters.length;
        }
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }

    revert(): CommandExecuteResult {

        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}