export class EditBar {
    left: number;
    top: number;
    mouseLocationX: number;
    mouseLocationY: number;
    mousedown: boolean;
    mousemove: boolean;
    constructor(left: number, top: number, mousedown: boolean, mousemove: boolean, layerX?: number, layerY?: number) {
        this.left = left;
        this.top = top;
        this.mousedown = mousedown;
        this.mousemove = mousemove;
        this.mouseLocationX = layerX;
        this.mouseLocationY = layerY;
    }
}
