import { Action } from '@ngrx/store';
import { RecentProject } from '../../../models/project.model';

export const LOAD_RECENT_PROJECTS_LIST = '[Flowchart] Load Recent Projects List';
export const LOAD_RECENT_PROJECTS_LIST_FAIL = '[Flowchart] Load Recent Projects List Fail';
export const LOAD_RECENT_PROJECTS_LIST_SUCCESS = '[Flowchart] Load Recent Projects List Success';

export class LoadRecentProjectsList implements Action {
    readonly type = LOAD_RECENT_PROJECTS_LIST;
}

export class LoadRecentProjectsListFail implements Action {
    readonly type = LOAD_RECENT_PROJECTS_LIST_FAIL;
    constructor(public payload: any) { }
}

export class LoadRecentProjectsListSuccess implements Action {
    readonly type = LOAD_RECENT_PROJECTS_LIST_SUCCESS;
    constructor(public payload: RecentProject[]) { }
}

export type RecentProjectsListAction =
    | LoadRecentProjectsList
    | LoadRecentProjectsListFail
    | LoadRecentProjectsListSuccess;
