import { APP_HTTP_RESULT_CODE } from './../../constants/http/http.constants';
import { BaseComponent } from 'src/app/components/base-component';
import { CommonMenuService } from 'src/app/services/chart/common-menu.service';
import { environment } from './../../../environments/environment.prod';
import { InterfaceService } from './../../services/interface/interface.service';
import { EventsService } from './../../services/project/events.service';
import { ChartService } from 'src/app/services/chart/chart.service';
import { ProjectService } from 'src/app/services/project/project.service';
import { StringUtils } from './../../utils/string.utils';
import { ChartData } from 'src/app/core/models/chart-data.model';
import { Component, OnInit, ViewChild, ElementRef, HostListener, AfterViewInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { MessageService } from 'src/app/services/common/message.service';
import { MSG_FLAG } from '../../constants/common/message.flags.constants';
import { Store } from '@ngrx/store';
import * as fromStore from '../../store';
import { CloseAllFlowchartsSuccess, CloseFlowchartSuccess } from '../../store';
import { Point } from 'src/app/models/point.model';
import { CurrentProjectService } from 'src/app/services/project/current-project.service';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { MatMenuTrigger } from '@angular/material';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { KEY_CODE } from 'src/app/constants/common/key.code.constants';
@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css'],
  animations: [
    trigger('right-slide-in', [
      state('collapsed', style({
        width: '0',
        opacity: 0
      })),
      state('visible', style({
        // width: '400px'
      })),
      transition('visible => collapsed', [animate('0.2s ease-in')]),
      transition('collapsed => visible', [animate('0.2s ease-out')])
    ]),
  ]
})
export class MainComponent extends BaseComponent implements OnInit, AfterViewInit {
  constructor(
    private activatedRoute: ActivatedRoute,
    private router: Router,
    private messageService: MessageService,
    private store: Store<fromStore.FlowchartState>,
    private currentProjectService: CurrentProjectService,
    private projectService: ProjectService,
    private chartService: ChartService,
    private typeService: InterfaceService,
    private eventService: EventsService,
    private commonMenuService: CommonMenuService
  ) {
    super();
  }
  @ViewChild('contextMenuTrigger', { static: false }) contextMenu: MatMenuTrigger;

  @ViewChild('canvasContainer', { static: false }) canvasContainer: ElementRef;
  // 已经打开的所有流程图
  openingCharts: Chart[];
  // 当前流程图
  currentChart: Chart = null;
  // project信息 包括名称、当前流程图信息、打开的所有流程图
  project: Project;
  // 当前选中的流程图的index
  // selectedIndex = 0;
  currentCanvasSize = new Point(0, 0);
  selectedIndex = -1;

  outputBar = ['output'];
  barSelectIndex = 0;

  selectedItem: ChartData;
  rightPanelFold = false;
  canvasViewbox = null;
  @HostListener('window:resize', ['$event'])
  onResize(event) {
    this.getCurrentCanvasSize();
  }

  ngOnInit(): void {
    this.init();
    this.subscribeData();
    this.initGlobalKeyEvent();
  }

  ngAfterViewInit(): void {
    this.getCurrentCanvasSize();
  }

  init() {
    this.project = this.projectService.getCurrentProject();
    if (environment.IS_WEB) {
      this.activatedRoute.params.subscribe(params => {
        const projectId = params.projectId;
        if (!StringUtils.isNullOrWhiteSpace(projectId)) {
          Promise.all([
            this.projectService.getProjectInfo(projectId),
            this.chartService.getCurrentChartsList(projectId),
            this.typeService.getTypes(projectId),
            this.eventService.getEvents(projectId)
          ]).then(result => {
            this.project = new Project();
            this.project.charts = result[1].data;
            this.project.types = result[2].data;
            this.project.events = result[3].data;
          });
          this.openingCharts = [];
        }
      });
    } else {
      this.openingCharts = [];
      this.currentChart = null;
      this.selectedIndex = -1;
      this.addSubscription(this.projectService.currentProject.subscribe(p => this.project = p));
      this.addSubscription(this.chartService.openningCharts.subscribe(charts => {
        this.openingCharts = charts;
        this.selectedIndex = this.openingCharts.findIndex(c => c.uid === this.currentChart.uid);
      }));
      this.addSubscription(this.chartService.currentChart.subscribe(chart => {
        this.currentChart = chart;
        this.selectedIndex = this.openingCharts.findIndex(c => c.uid === this.currentChart.uid);
      }));
      this.addSubscription(this.commonMenuService.selectedItems.subscribe(items => {
        this.selectedItem = items && items.length > 0 ? items[0] : null;
      }));

    }
  }

  initGlobalKeyEvent() {
    window.addEventListener('keydown', this.processKeyDown.bind(this));
  }

  processKeyDown($event) {
    switch ($event.keyCode) {
      case KEY_CODE.S:
        if ($event.ctrlKey) {
          this.commonMenuService.save();
        }
        break;
    }
  }

  onOpenNewProject() {
    this.store.dispatch(new CloseAllFlowchartsSuccess(0));
    this.currentChart = null;
    this.openingCharts = [];
  }

  getCurrentCanvasSize() {
    this.currentCanvasSize = new Point(this.canvasContainer.nativeElement.offsetWidth, this.canvasContainer.nativeElement.offsetHeight);
  }

  dataListening() {

  }

  subscribeData() {

  }


  getIndexOfOpeningCharts(chart: Chart) {
    let index = 0;
    const result = this.openingCharts.find((c, i) => {
      if (chart.uid === c.uid) {
        index = i;
        return true;
      }
    });
    return result === undefined ? -1 : index;
  }

  chooseTab(index: number) {
    if (index === -1) {
      this.openingCharts = [];
      this.chartService.setOpenningCharts(this.openingCharts);
      this.currentChart = null;
    } else {
      this.currentChart = this.openingCharts[index];
      this.chartService.setCurrentChart(this.openingCharts[index]);
    }
  }

  async asyncGetOpeningCharts() {
    const promise = new Promise<Chart[]>((resolve) => {
      this.store.select(fromStore.getOpeningCharts).subscribe(openingCharts => {
        resolve(openingCharts);
      });
    });
    const flowcharts = await promise;
    return flowcharts;
  }

  closeChartBeforeRemove(chart) {
    const index = this.getIndexOfOpeningCharts(chart);
    if (index !== -1) {
      this.closeChart(index);
    }
  }


  closeChart(index: number, $event?) {
    if ($event) {
      $event.stopPropagation();
    }
    const length = this.openingCharts.length;
    this.openingCharts.splice(index, 1);
    if (index > this.selectedIndex) {
      // 如果关闭当前流程图之后的流程图
      this.chooseTab(this.selectedIndex);
    } else if (index < this.selectedIndex) {
      // 如果关闭当前流程图之前的流程图
      this.chooseTab(this.selectedIndex - 1);
    } else if (index === this.selectedIndex && length !== 0 && this.selectedIndex !== length - 1) {
      // 如果关闭的是正在打开的流程图，且不是最后一个
      this.chooseTab(index);
    } else if (this.selectedIndex === length - 1) {
      // 如果关闭的是正在打开的流程图，且是最后一个
      this.chooseTab(length - 2);
    }
    this.chartService.setOpenningCharts(this.openingCharts);
  }

  changeRightPanelFoldStatus() {
    this.rightPanelFold = !this.rightPanelFold;
  }

  changeBar(i) {
    this.barSelectIndex = i;
  }

  onChartClickFromTree($event) {
    const uid = $event;
    const index = this.openingCharts ? this.openingCharts.findIndex(c => c.uid === uid) : -1;
    if (index > -1) {
      this.selectedIndex = index;
      this.currentChart = this.openingCharts[index];
      this.chartService.setCurrentChart(this.currentChart);
    } else {
      this.chartService.getChart({ project: this.project.uid, chartId: uid }).then(result => {
        if (result.code === APP_HTTP_RESULT_CODE.OK) {
          this.currentChart = result.data;
          this.chartService.setCurrentChart(this.currentChart);
          this.selectedIndex = this.openingCharts.indexOf(this.currentChart);
        }
      })
    }
  }

  ngOnDestroy() {
    super.ngOnDestroy();
    window.removeEventListener('keydown', this.processKeyDown);
  }
}
