import { PathPickerComponent } from 'src/app/common/path-picker/path-picker/path-picker.component';
import { DialogService } from './../dialog/dialog.service';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { StringUtils } from '../../utils/string.utils';
import { Injectable } from '@angular/core';
import { PathInfo, PathDialogConfig } from './path-info.model';
import { HttpResult } from '../http/http.result.model';
import { APP_HTTP_RESULT_CODE } from 'src/app/constants/http/http.constants';

@Injectable({
  providedIn: 'root'
})
export class PathService {

  constructor(
    private http: HttpClient,
    private dialogService: DialogService
    ) { }

  public async getChildrenPathList(path: string) {
    const url = 'api/ppp';
    const data: any = {};
    if (!StringUtils.isNullOrWhiteSpace(path)) {
      data.path = path;
    }
    try {
      const result = await this.http.post<HttpResult<PathInfo[]>>(url, data).toPromise();
      if (result.code === APP_HTTP_RESULT_CODE.OK) {
        return new HttpResult<PathInfo[]>(APP_HTTP_RESULT_CODE.OK, result.msg, result.data);
      }
      return new HttpResult<PathInfo[]>(APP_HTTP_RESULT_CODE.ERROR, result.msg, null);
    } catch (error) {
      return new HttpResult<PathInfo[]>(APP_HTTP_RESULT_CODE.ERROR, error.message, null);
    }
  }

  public showPathPicker(config: PathDialogConfig) {
    return this.dialogService.open(PathPickerComponent, config);
  }

  public async getFileContent(path: string) {
    const url = 'api/ppp/file';
    const data = { path };
    try {
      const result = await this.http.post<HttpResult<string>>(url, data).toPromise();
      console.log(result);
      if (result.code === APP_HTTP_RESULT_CODE.OK) {
        return new HttpResult<JSON>(APP_HTTP_RESULT_CODE.OK, result.msg, JSON.parse(result.data));
      }
      return new HttpResult<JSON>(APP_HTTP_RESULT_CODE.ERROR, result.msg, null);
    } catch (error) {
      return new HttpResult<JSON>(APP_HTTP_RESULT_CODE.ERROR, error.message, null);
    }
  }
}
