import { ProjectConfig } from './project-config.model';
import { Chart } from "./chart.model";
import { EventType } from "./event.model";
import { ChartType, GenericType } from "./type.model";

import * as Json5 from 'json5';
import { ChartNode } from './node.model';
import { UID_START } from '../constants/node.constants';
import { NodeViewType } from '../constants/node.enums';
import { Rect, UidUtils } from './../../../../flowchart-common/src/public-api';
import { Member, Method, MemberFrom } from './member.model';
import { TypeSource } from '../constants/type.enums';
import { Parameter } from './parameter.model';
import { Connector, ConnectorType, ShapeType } from './connector.model';
import { INVALID_VAR_ID } from './parse-info.model';
import { IFileSystem, ReadFileResult } from './fs-interface';
import { Group, LayoutType } from './group.model';
/**
 * Flowhcart project
 */
export class Project {
    /**
     *  project unique id 
     */
    uid: string;
    /**
     *  Project name 
     */
    name: string;
    /**
     * Project path
     */
    path: string;
    /**
     * Output path for code generation
     */
    outputPath: string;
    /**
     * Flowcharts
     */
    charts: Chart[];
    // 
    /**
     * Flowchart chart map, using uid as key for search
     */
    chartMap: Map<string, Chart>;
    /**
     * Types 
     */
    types: ChartType[];
    /**
     * Types map
     */
    typeMap: Map<string, ChartType>;
    /**
     * Events
     */
    events: EventType[];
    /**
     * Events map
     */
    eventMap: Map<string, EventType>;
    /**
     * Config information for project
     */
    config: ProjectConfig;

    /**
     * Has this project ever been fully built
     */
    hasBuilt: boolean;

    /**
     * Current processing project
     */
    static current: Project = null;
    /**
     * Constructor
     */

    static fs: IFileSystem;
    constructor() {
        this.charts = [];
        this.chartMap = new Map<string, Chart>();
        this.types = [];
        this.typeMap = new Map<string, ChartType>();
        this.events = [];
        this.eventMap = new Map<string, EventType>();
    }

    //#region Methods 
    /**
     * Load project from json 
     * @param json json string
     * @returns return true after successfully loaded
     */
    async load(path: string) {
        path = path.replace(/\//g, '\\');
        if (!ChartType.inited) {
            ChartType.init();
        }
        if (Project.fs) {
            // types
            const typesPath = `${path}\\types`;
            const typeFiles = await this.loadFolders(typesPath, { extension: '.json', excludes: ["event.json"] });
            if (typeFiles) {
                typeFiles.forEach(file => this.loadType(file, true));
                typeFiles.forEach(file => this.loadType(file, false))
            }
            const event = await Project.fs.read(typesPath + "\\event.json");
            if (event.success) {
                this.loadEvents(event.data);
            }
            const config = await Project.fs.read(path + "\\config.json");
            if (config.success) {
                this.loadConfig(config.data);
            }
            const chartsPath = `${path}\\charts`;
            const chartFiles = await this.loadFolders(chartsPath, { extension: ".json" });
            if (chartFiles) {
                chartFiles.forEach(file => this.loadChart(file));
            }
            this.path = path;
            this.name = path.substr(path.lastIndexOf('\\') + 1);
            return true;
        } else {
            return false;
        }
    }

    loadConfig(confStr: string) {
        let json = Json5.parse(confStr);
        const config = new ProjectConfig();
        config.generator = json.generator;
        config.flowchartTypeDict = new Map<string, ChartType>();
        Object.keys(json.flowchart_type).forEach(k => {
            config.flowchartTypeDict.set(k, this.getType(json.flowchart_type[k]));
        })
        config.outputPath = json.output_path;
        this.config = config;
    }

    loadEvents(eventStr: string) {
        const events = Json5.parse(eventStr);
        events.forEach((ev: any) => {
            const event = new EventType();
            event.fromJson(ev, typename => { return this.getType.call(this, typename) });
            this.addEvent(event);
        })
    }

    loadType(typeStr: string, preParse: boolean) {
        const json = Json5.parse(typeStr);
        const tp = new ChartType();
        tp.fromJson(json, typename => { return this.getType.call(this, typename) }, preParse);
        const index = this.types.findIndex(t => t.name === tp.name);
        if (index > -1) {
            this.types[index].members = tp.members;
            this.types[index].memberMap = tp.memberMap;
            if (tp.name === 'Global') {
                ChartType.GlobalType.members = tp.members;
                ChartType.GlobalType.memberMap = tp.memberMap;
            }
        } else {
            this.addType(tp);
        }
    }

    loadChart(chartStr: string) {
        const jsonArr = Json5.parse(chartStr);
        jsonArr.forEach((json: any) => {
            const chart = new Chart();
            chart.fromJson(json, this, typename => { return this.getType.call(this, typename) });
            this.addChart(chart);
        })
    }

    /**
     * Add type to project
     * @param t Type to be added
     */
    addType(t: ChartType) {
        if (!this.typeMap) {
            this.typeMap = new Map<string, ChartType>();
            this.types = [];
        }
        if (!t) {
            throw `Type data is empty`;
        }

        const check = t.check();
        if (check.valid) {
            this.typeMap.set(t.name, t);
            this.types.push(t);
        } else {

        }

        if (t.name === 'Global') {
            ChartType.GlobalType.members = [];
            ChartType.GlobalType.memberMap = new Map<string, Member>();
            if (t.members) {
                t.members.forEach(m => ChartType.GlobalType.addMember(m));
            }
            ChartType.GlobalType.description = t.description;
        }
    }

    /**
     * 添加事件
     * @param ev 待添加事件
     */
    addEvent(ev: EventType) {
        if (!this.eventMap || !this.events) {
            this.eventMap = new Map<string, EventType>();
            this.events = [];
        }
        if (!ev) {
            throw 'Event data is empty';
        }
        const check = ev.check();
        if (check.valid) {
            this.eventMap.set(ev.name, ev);
            this.events.push(ev)
        } else {
            check.msg;
        }
    }

    /**
     * 添加流程图
     * @param chart Chart to be added
     */
    addChart(chart: Chart, replace: boolean = true) {
        if (!this.charts || !this.chartMap) {
            this.charts = [];
            this.chartMap = new Map<string, Chart>();
        }
        if (!chart) {
            throw 'Flowchart is empty.';
        }
        const check = chart.check();
        if (check.valid) {
            if (this.chartMap.has(chart.uid) && !replace) {
                throw `Flowchart with uid ${chart.uid} has already existed.`;
            } else {
                this.chartMap.set(chart.uid, chart);
                this.charts.push(chart);
                chart.project = this;
            }
        }

        // if (chart.isSubChart) {
        //     const tp = this.config.getChartType(chart.path);
        //     tp.addMember(chart.toMethod());
        // }
    }

    removeChart(chart: Chart) {
        this.chartMap.delete(chart.uid);
        this.charts.splice(this.charts.findIndex(c => c.uid === chart.uid), 1);
        chart.project = null;
    }

    getType(name: string): ChartType {
        if (!this.typeMap || !this.types) {
            return null;
        }
        if (!name) {
            return ChartType.UndefinedType;
        }
        const tp = this.typeMap.get(name) || ChartType.builtInTypes.get(name);
        if (!tp) {
            if (name.startsWith('List')) {
                return this.getListType(name);
            } else if (name.startsWith('Dict')) {
                return this.getDictType(name);
            }
        }
        return tp;
    }

    getEvent(name: string) {
        if (!this.eventMap || !this.events) {
            return undefined;
        }
        if (name.startsWith("On")) {
            name = name.substr(2);
        }
        return this.eventMap.get(name);
    }

    toJson() {

    }

    getFileList(): Map<string, Chart[]> {
        const files = new Map<string, Chart[]>();
        for (let chart of this.charts) {
            const filename = chart.path.split('.')[0];
            if (!files.has(filename)) {
                files.set(filename, [chart]);
            } else {
                files.get(filename).push(chart);
            }
        }
        return files;
    }

    /**
     * 
     * @param writeFileFunc method for write file locally
     */
    async save() {
        await this.prepareDir(this.path);
        const chartDirPath = `${this.path}\\charts`;
        await this.prepareDir(chartDirPath);
        const fileDict = this.getFileList();
        fileDict.forEach((v, k) => {
            v.sort((a, b) => a.path > b.path ? 1 : -1);
            const filePath = `${chartDirPath}\\${k}.json`;
            const strs = [];
            strs.push('[');
            v.forEach(chart => strs.push(...chart.saveAsJson()));
            if (strs[strs.length - 1].endsWith(',')) {
                strs[strs.length - 1] = strs[strs.length - 1].substr(0, strs[strs.length - 1].length - 1);
            }
            strs.push(']');
            Project.fs.writeFile(filePath, strs.join('\n'));
        })

        // save types 
        const typeDir = `${this.path}\\types`;
        await this.prepareDir(typeDir);
        this.types.forEach(t => {
            this.saveType(t, false);
        })

        // save events
        this.saveEvents();

        this.saveConfig();
        return true;
    }

    saveConfig() {
        Project.fs.writeFile(`${this.path}\\config.json`, JSON.stringify(this.config.toJson(), null, 4));
    }

    async saveType(tp: ChartType, needPrepare: boolean = false) {
        const typeDir = `${this.path}\\types`;
        if (needPrepare) {
            await this.prepareDir(typeDir);
        }
        const filePath = `${typeDir}\\${tp.name}.json`;
        if (tp.source === TypeSource.CUSTOM) {
            Project.fs.writeFile(filePath, JSON.stringify(tp.toJson(), null, 4));
        }
    }

    saveEvents() {
        const typeDir = `${this.path}\\types`;
        if (this.events) {
            const events = this.events.map(e => e.toJson());
            Project.fs.writeFile(`${typeDir}\\event.json`, JSON.stringify(events, null, 4));
        } else {
            Project.fs.writeFile(`${typeDir}\\event.json`, '[]');
        }
    }

    private async prepareDir(path: string) {
        const exists = await Project.fs.exists(path);
        if (!exists) {
            Project.fs.createDir(path);
        }
    }

    private async loadFolders(dirPath: string, options?: { extension?: string, excludes?: string[] }) {
        const result = await Project.fs.readDir(dirPath);
        if (result.success) {
            const promises: Promise<ReadFileResult>[] = [];
            result.files.forEach(file => {
                if (
                    (!options.extension || file.endsWith(options.extension)) &&
                    (!options.excludes || !options.excludes.includes(file))
                ) {
                    const pr = Project.fs.read(`${dirPath}/${file}`);
                    promises.push(pr);
                }
            });
            const filesResult = await Promise.all(promises);
            const sucResults = filesResult.filter(r => r.success);
            return sucResults.map(r => r.data);
        }
        return null;
    }

    private getListType(name: string) {
        if (name.startsWith('List')) {
            const sub = name.substr(5, name.length - 6);
            const t = new GenericType();
            t.uid = UidUtils.generateUid();
            t.name = name;
            t.outType = ChartType.ListType;
            t.typeParams = [this.getType(sub)];
            return t;
        }
        return null;
    }

    private getDictType(name: string) {
        if (name.startsWith('Dict')) {
            const commaIndex = name.indexOf(',');
            const k = name.substring(5, commaIndex - 1);
            const v = name.substring(commaIndex + 1);
            const t = new GenericType();
            t.uid = UidUtils.generateUid();
            t.name = name;
            t.outType = ChartType.DictType;
            t.typeParams = [this.getType(k), this.getType(v)];
            return t;
        }
        return null;
    }

    //#endregion

    //#region Static methods
    static createProject(global?: ChartType, events?: EventType[]) {
        const p = new Project();
        p.config = new ProjectConfig();
        p.config.generator = 'lua';
        p.config.flowchartTypeDict = new Map<string, ChartType>();
        const character = new ChartType();
        character.uid = UidUtils.generateUid();
        character.name = 'Character';
        character.memberMap = new Map<string, Member>();
        character.source = TypeSource.CUSTOM;
        p.config.flowchartTypeDict.set('AI', character);
        const say = new Method();
        say.name = 'Say';
        say.type = ChartType.VoidType;
        say.from = MemberFrom.FILE;
        say.parameters = [];
        const parameter = new Parameter();
        parameter.name = 'str';
        parameter.type = ChartType.StringType;
        parameter.uid = UidUtils.generateUid();
        say.parameters.push(parameter);
        character.addMember(say);
        p.addType(character)
        if (global) {
            p.addType(global);
        }
        if (events) {
            events.forEach(e => p.addEvent(e));
        }
        const node1 = new ChartNode();
        node1.uid = UID_START;
        node1.text = 'Start';
        node1.viewType = NodeViewType.COMMON;
        node1.rect = Rect.createWithCenter(400, 200, 100, 36);
        const node2 = new ChartNode();
        node2.uid = UidUtils.generateUid();
        node2.text = 'Say("hello world")';
        node2.viewType = NodeViewType.COMMON;
        node2.rect = Rect.createWithCenter(375, 400, 200, 36);
        const chart = new Chart();
        chart.uid = UidUtils.generateUid();
        chart.name = 'helloworld';
        chart.path = 'AI.helloworld';
        chart.type = character;
        chart.addNode(node1);
        chart.addNode(node2);
        chart.groups = [];
        chart.nodeMap = new Map<string, ChartNode>();
        chart.connectors = [];
        const conn = new Connector(node1, node2, ConnectorType.ALWAYS, ShapeType.CURVE);
        chart.connectors.push(conn);
        p.addChart(chart)
        p.config.flowchartTypeDict.set('AI', character);
        return p;

    }


    static exportChartsJson(charts: Chart[]) {
        const output: string[] = [];
        output.push('[');
        charts.forEach(chart => {
            const nodes = chart.sortNodes();
            output.push('  {');
            output.push(`    "name": "${chart.name}",`);
            output.push(`    "path": "${chart.path}",`);
            const variables = chart.parameters.concat(chart.variables);
            if (variables && variables.length > 0) {
                output.push(`    "varCount": ${variables.length},`);
                output.push(`    "variables": [`);
                variables.forEach(v => {
                    if (v.isParameter) {
                        output.push(`      { "name": "${v.name}", "type": "${v.type.name}", "is_param": true },`);
                    } else {
                        output.push(`      { "name": "${v.name}", "type": "${v.type.name}", "is_param": false },`);
                    }
                })
                // remove trailling comma
                output[output.length - 1] = output[output.length - 1].substr(0, output[output.length - 1].length - 1);
                output.push('    ],')
            } else {
                // output.push(`    "varCount": 0,`);
                // output.push(`    "variables": []`);
            }
            // start information
            if (chart.connectors && chart.connectors.length > 0) {
                output.push(`    "start": [`);
                chart.connectors.forEach(conn => {
                    if (conn.start.uid === UID_START) {
                        output.push(`      "${conn.end.uid}", // ${conn.end.text}`);
                    }
                })
                output.push("    ],");
            }
            // nodes
            if (nodes && nodes.length > 1) {
                output.push('    "nodes": [');
                const nodesOutput = [];
                nodes.slice(1).forEach(node => output.push(...Project.exportNodeJson(node)));
                // remove trailling comma
                if (output[output.length - 1].endsWith(',')) {
                    output[output.length - 1] = output[output.length - 1].substr(0, output[output.length - 1].length - 1);
                }
                output.push('    ]')
            }
            output.push("  },")
        })
        // remove trailling comma
        if (output[output.length - 1].endsWith(',')) {
            output[output.length - 1] = output[output.length - 1].substr(0, output[output.length - 1].length - 1);
        }
        output.push(']');
        return output;
    }

    static exportNodeJson(node: ChartNode) {
        let nodeCategory = null;
        if (node.parseInfo.isControlNode) {
            nodeCategory = 'control';
        } else if (node.parseInfo.isEvent) {
            nodeCategory = 'event';
        } else {
            nodeCategory = 'function';
        }
        let funcName = node.parseInfo.isControlNode ? node.parseInfo.name : `${node.chart.path.replace(".", "_")}_id_${node.uid}`;
        const output = [];
        output.push("      {");
        output.push(`        "uid": "${node.uid}", //${node.text}`);
        output.push(`        "text": "${node.text.replace(/\"/g, '\\\"')}",`);
        output.push(`        "type": "${nodeCategory}",`);
        output.push(`        "funcName": "${funcName}",`);
        if (node.parseInfo && node.parseInfo.setVarId != INVALID_VAR_ID) {
            output.push(`        "subChartVar": ${node.parseInfo.setVarId},`);
        }

        if (node.parseInfo.isControlNode) {
            if (node.parseInfo.codes) {
                output.push(`        "param": [`);
                node.parseInfo.codes.forEach(code => output.push(`          "${code}",`));

                const len = output.length;
                const last = output[len - 1];
                if (last.endsWith(',')) {
                    output[len - 1] = last.substr(0, last.length - 1);
                }
                output.push(`        ],`)
            }
        }
        // nodes uid of the end of connectors with type success
        output.push(`        "success": [`);
        const sucChildren = node.chart.connectors.filter(conn => conn.start.uid === node.uid && (conn.type === ConnectorType.ALWAYS || conn.type === ConnectorType.SUCCESS));
        if (sucChildren) {
            sucChildren.sort((c1, c2) => c1.end.rect.x - c2.end.rect.x);
            sucChildren.forEach(conn => {
                output.push(`          "${conn.end.uid}", // ${conn.end.text}`);
            });
        }
        output.push(`        ],`)

        // nodes uid of the end of connectors with type fail
        output.push(`        "fail": [`);
        const failChildren = node.chart.connectors.filter(conn => conn.start.uid === node.uid && (conn.type === ConnectorType.ALWAYS || conn.type === ConnectorType.FAIL));
        if (failChildren) {
            failChildren.sort((c1, c2) => c1.end.rect.x - c2.end.rect.x);
            failChildren.forEach(conn => {
                output.push(`          "${conn.end.uid}", // ${conn.end.text}`);
            });
        }
        output.push(`        ],`)
        // last close bracket
        output.push(`      },`);
        return output;
    }
    //#endregion
}