import { INodeComponent } from './../node-component.interface';
import { Component, EventEmitter, Input, OnInit, Output, ViewChild, ElementRef, OnChanges, SimpleChanges } from '@angular/core';
import { Store } from '@ngrx/store';
import * as fromStore from 'src/app/store';
import { ShowingRunningStatus } from 'src/app/debug/models/debug-node-status.model';
import { ChangeDetectorRef } from '@angular/core';
import { NodesService } from 'src/app/services/chart/nodes.service';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { NodeViewType } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.enums';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';

@Component({
  selector: 'app-stop-node',
  templateUrl: './stop-node.component.html',
  styleUrls: ['./stop-node.component.css']
})
export class StopNodeComponent implements INodeComponent, OnInit, OnChanges {
  @Input() node: ChartNode;
  @Input() index: number;
  @Input() isSelected: boolean;
  @Input() isEditing: boolean;
  @Output() isEditingChange: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() updateNode = new EventEmitter<{ node: ChartNode, local: boolean, textChanged: boolean }>();
  @Input() runningStatus: ShowingRunningStatus;

  @ViewChild('stopNodeRoot', { static: false, read: ElementRef }) root: ElementRef;
  stopNodeIds: string[];

  currentNodes: ChartNode[];
  nodesmap: Map<number, ChartNode>;
  constructor(
    private store: Store,
    private cdref: ChangeDetectorRef,
    private nodesService: NodesService,
  ) { }

  ngOnInit() {
    this.init();
  }

  ngOnChanges(changes: SimpleChanges) {

    if (changes.node && (changes.node.currentValue == null || changes.node.previousValue == null ||
      (changes.node.currentValue.uid !== changes.node.previousValue.uid ||
        changes.node.currentValue.text !== changes.node.previousValue.text))) {
      this.stopNodeIds = this.getStopIdList(this.node.text);
    }
  }



  init() {
    this.currentNodes = this.node.chart.nodes;
  }

  getStopIdList(text: string) {
    const rawListStr = /\((([\"]{1}[0-9a-z]+[\"]{1}[,][\s]{0,1})*[\"]{1}[0-9a-z]+[\"]{1})\)/g.exec(text);
    if (rawListStr && rawListStr.length > 0) {
      const stopIds = rawListStr[0].substr(1, rawListStr[0].length - 2).split(',').map((txt, index) => {
        return txt.substring(txt.indexOf('"') + 1, txt.lastIndexOf('"'));
      });
      return stopIds;
    }
    return [];
  }

  update() {
    const funcName = this.node.viewType === NodeViewType.STOP_NODE ? 'stop_node' : 'stop_flow';
    const newtext = `${funcName}(${this.stopNodeIds.map((id, index) => index === this.stopNodeIds.length - 1 ? `"${id}"` : `"${id}"` + ', ').join('')})`;
    const nodeview = document.getElementById(this.node.uid);
    const curHeight = nodeview.clientHeight;
    const newRect = Rect.createWithLeftTopVertex(this.node.rect.x, this.node.rect.y,
      this.nodesService.calculateNodeRenderWidth(newtext.length, NodeViewType.STOP_FLOW, newtext), curHeight)
    this.nodesService.updateNode(this.node, new Map<string, any>([['text', newtext], ['rect', newRect]]));
    this.updateNode.emit({node: this.node, local: true, textChanged: true});
  }

  onStopIdChanged($event, index: number) {
    this.stopNodeIds[index] = $event;
    this.stopNodeIds = [...this.stopNodeIds];
  }

  onAddStopNodeIdClick($event) {
    this.stopNodeIds.push('1');
  }
  onDeleteStopIdClick($event, index: number) {
    this.stopNodeIds.splice(index, 1);
    this.update();
  }

  onClickOutside($event) {
    if (this.isEditing) {
      this.update();
      this.isEditing = false;
      this.isEditingChange.emit(false);
      // setTimeout(() => {
      // }, 100);
    }
  }

  clickToSave($event) {
    const selectId = $event.path[1].id;
    const addNodeId = $event.path[0].id;
    if (selectId !== 'select' && addNodeId !== 'addNode') {
      this.onClickOutside($event);
    }
  }
}
