import { Group } from 'libs/flowchart-ts/flowchart-core/src/public-api';
import { AddNodeToGroupCommand, LayoutGroupCommand } from './../../models/commands/group.commands';
import { CreateNodeArgs } from './../../models/request-arguments/node.op.arguments.model';
import { AddNodeToGroupArgs } from './../../models/request-arguments/group.request.args.model';
import { UidUtils } from './../../utils/uid.utils';
import { ConnectorJson } from './../../models/canvas/HttpJson';
import { OperationService } from './operation.service';
import { COMMAND_NAME, ExecuteResultCode } from './../../constants/command/command.constants';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { AppendNodeJson, RemoveNodeJson } from 'src/app/models/canvas/HttpJson';
import { HttpResult, RESULT_CODE } from 'src/app/common/http/http.result.model';
import { Store } from '@ngrx/store';
import * as fromStore from '../../store';
import { Connector, ConnectorType, ShapeType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { NodeViewType } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.enums';
import { Command } from 'src/app/models/commands/command.model';
import { CreateNodeCommand, RemoveNodeCommand, UpdateNodeCommand } from 'src/app/models/commands/node.commands';
import { CreateConnectorCommand, RemoveConnectorCommand } from 'src/app/models/commands/connector.commands';
import { LayoutType } from 'libs/flowchart-ts/flowchart-core/src/public-api';
import { Point, Rect } from 'libs/flowchart-ts/flowchart-common/src/public-api';

@Injectable({
  providedIn: 'root'
})
export class NodesService {

  constructor(
    private http: HttpClient,
    private operationService: OperationService,
    private store: Store<fromStore.FlowchartState>
  ) { }

  isExistInNodeList(node: ChartNode, nodeList: ChartNode[]): boolean {
    const exist = nodeList.find(n => n.uid === node.uid);
    if (exist === undefined) {
      return false;
    } else {
      return true;
    }
  }

  async createNode(details): Promise<any> {
    const appendNodeJson = new AppendNodeJson(details.projectName, details.chartPath, details.newNode, details.parent);
    const promise = new Promise((resolve) => {
      this.http.post<any>('api/chart/addnode', appendNodeJson).subscribe(data => {
        resolve(data);
      });
    });
    console.log(await promise);
    return await promise;
  }

  async createNodes(chart: Chart, nodeDetails: CreateNodeArgs[]) {
    const cmds = [];
    for (const n of nodeDetails) {
      const cmd = new CreateNodeCommand(chart, n.node);
      cmds.push(cmd);
      // // 有parentUid的需要新增创建connector的命令，且需要放在创建节点命令之后
      // if (n.parent) {
      //   const newConn = new Connector(n.parent, n.node, ConnectorType.ALWAYS, ShapeType.CURVE);
      //   const createConnCmd = new CreateConnectorCommand(chart, newConn);
      //   cmds.push(createConnCmd);
      // }

      // 有group信息，则需要将add node to group
      if (n.group) {
        const addNodeToGroup = new AddNodeToGroupCommand(n.group, n.node);
        cmds.push(addNodeToGroup);
        if (n.group.layout === LayoutType.AUTO) {
          const layout = new LayoutGroupCommand(chart, n.group);
          cmds.push(layout);
        }
      }
    }
    const result = await this.operationService.executeChartCommands(chart.uid, cmds);
    return result;
  }

  async removeNode(removedNodes: ChartNode[], relatedConns: Connector[]) {
    const cmds = [];
    if (relatedConns) {
      for (const conn of relatedConns) {
        const cmd = new RemoveConnectorCommand(conn.start.chart, conn);
        cmds.push(cmd);
      }
    }
    for (const node of removedNodes) {
      const cmd = new RemoveNodeCommand(node.chart, node)
      cmds.push(cmd);
    }
    const result = await this.operationService.executeChartCommands(removedNodes[0].chart.uid, cmds);
    return result;
  }

  updateNode(node: ChartNode, updateValues: Map<string, any>) {
    const cmd = new UpdateNodeCommand(node, updateValues);
    return this.operationService.executeChartCommands(node.chart.uid, [cmd]);
  }

  async updateNodes(nodes: ChartNode[], undoRedo = false) {
    const cmds = [];
    for (const node of nodes) {
      if (node && node.chart) {
        const updateKeyValues = new Map<string, any>();
        updateKeyValues.set('text', node.text);
        updateKeyValues.set('rect', node.rect);
        cmds.push(new UpdateNodeCommand(node, updateKeyValues));
      }
    }
    return await this.operationService.executeChartCommands(nodes[0].chart.uid, cmds);
  }
  // 返回传入的流程图已选中节点的个数
  getSelectedNodesNum(chart: Chart): number {
    const selectedNodes = chart.nodes.filter(n => n.isSelected);
    return selectedNodes.length;
  }

  calculateNodeRenderWidth(textWidth: number, nodeView: NodeViewType = NodeViewType.COMMON, text: string) {
    const startNodeView = document.getElementById('0');
    const startNodeIndexBoxView = document.getElementById('start-index-box');
    const startNodeText = document.getElementById('start-text');
    if (!startNodeView || !startNodeText) {
      return 50 + 8.8 * textWidth;
    }
    // start节点的index-box宽度
    const startIndexBoxWidth = startNodeIndexBoxView.clientWidth;
    // start节点每个字母的宽度
    const fontSizePixel = startNodeText.clientWidth / 5;
    // node.text外长度为固定的
    const regularWidth = startNodeView.clientWidth - startNodeText.clientWidth;
    const sideMargin = (regularWidth - startIndexBoxWidth) / 2;
    switch (nodeView) {
      case NodeViewType.COMMON:
        return regularWidth + fontSizePixel * textWidth;
      case NodeViewType.WAITALL:
        return 147 * (startIndexBoxWidth / 40);
      case NodeViewType.WAIT:
        return sideMargin * 2 + (textWidth + 1 - 6) * fontSizePixel + (startIndexBoxWidth / 40) * (38 + 32) + startIndexBoxWidth;
      case NodeViewType.FUNCTION:
        const functionFontSizePixel = 6.57 * (startIndexBoxWidth / 40);
        return 26 * (startIndexBoxWidth / 40) + (functionFontSizePixel * (textWidth - 2)) + startIndexBoxWidth;
      case NodeViewType.STOP_FLOW:
      case NodeViewType.STOP_NODE:
        const stopNodeIds = this.getStopIdList(text);
        return (56 * stopNodeIds.length + 158) * (startIndexBoxWidth / 40);
      default:
        return regularWidth + fontSizePixel * textWidth;
    }
  }

  getStopIdList?(text: string) {
    const rawListStr = /\((([\"]{1}[0-9a-z]+[\"]{1}[,][\s]{0,1})*[\"]{1}[0-9a-z]+[\"]{1})\)/g.exec(text);
    if (rawListStr && rawListStr.length > 0) {
      const stopIds = rawListStr[0].substr(1, rawListStr[0].length - 2).split(',').map((txt, index) => {
        return txt.substring(txt.indexOf('"') + 1, txt.lastIndexOf('"'));
      });
      return stopIds;
    }
    return [];
  }

  updateRealNodeView(node: ChartNode) {
    const nodeview = document.getElementById(node.uid);
    const curWidth = nodeview.clientWidth;
    const curHeight = nodeview.clientHeight;
    const newRect = Rect.createWithLeftTopVertex(node.rect.cx - curWidth / 2, node.rect.cy - curHeight / 2, curWidth, curHeight);
    return this.updateNode(node, new Map<string, any>([['rect', newRect]]));
  }

  updateNodePosition(nodes: ChartNode[], delta: Point) {
    const cmds: Command[] = [];
    nodes.forEach(n => {
      const newRect = Rect.createWithLeftTopVertex(n.rect.x - delta.x, n.rect.y - delta.y, n.rect.w, n.rect.h);
      const cmd = new UpdateNodeCommand(n, new Map<string, any>([['rect', newRect]]));
      cmds.push(cmd)
    })
    return this.operationService.executeChartCommands(nodes[0].chart.uid, cmds);
  }

  insertNode(chart: Chart, conn: Connector, newNode: ChartNode, group: Group) {
    const cmds: Command[] = [];
    cmds.push(new RemoveConnectorCommand(chart, conn));
    cmds.push(new CreateNodeCommand(chart, newNode))
    cmds.push(new CreateConnectorCommand(chart, new Connector(conn.start, newNode, conn.type, conn.shape)))
    cmds.push(new CreateConnectorCommand(chart, new Connector(newNode, conn.end, conn.type, conn.shape)))
    if (group) {
      cmds.push(new AddNodeToGroupCommand(group, newNode));
      if(conn.start.group.layout === LayoutType.AUTO) {
        cmds.push(new LayoutGroupCommand(chart, conn.start.group));
      }
    }
    return this.operationService.executeChartCommands(chart.uid, cmds);
  }

}
