import { NodeJsonKey } from './../constants/chart.constants';
import { CheckResult } from './check-result.model';
import { NodeParseInfo } from './parse-info.model';
import { Chart } from './chart.model';
import { NodeSource, NodeViewType } from "../constants/node.enums";
import { Group } from './group.model';
import { NodeRunningStatus } from './node.running-status.model';
import { Rect } from './/../../../../flowchart-common/src/lib/models/rect.model';

export class ChartNode {
    /**
     * Unique id 
     */
    uid: string;
    /**
     * index id of node
     */
    id: number;
    /**
     * node type 
     */
    type: string;
    /**
     * Node content text
     */
    text: string;
    /**
     * node tag
     */
    readonly tag: string = 'node';
    /**
     * codes snippets generated by code generator
     */
    codes?: string[];
    /**
     * View type of node
     */
    viewType: NodeViewType;
    /**
     * Belonged flowchart
     */
    chart: Chart;
    /**
     * Belonged group
     */
    group: Group;
    /**
     * Syntax parser result
     */
    parseInfo?: NodeParseInfo;
    /**
     * Node rect information which contains the position and size of the node;
     */
    rect: Rect;
    /**
     * Priority of node
     */
    priority: number;
    /**
     * Status when running in asyncflow
     */
    status?: NodeRunningStatus;
    /**
     * Node source where it was created. 
     */
    source: NodeSource;
    /**
     * Uid of node which this one copy from;
     */
    copyId?: string;

    isSelected?: boolean;
    isEditing?: boolean;
    //#region Methods

    /**
     * set belong group
     * @param group 
     * @returns 
     */
    setGroup(group: Group) {
        if (group === this.group) {
            return;
        }
        this.group = group;
    }
    /**
     * Parse node from json object
     * @param json JSON object
     */
    fromJson(json: any, chart: Chart): void {
        this.uid = json[NodeJsonKey.Uid];
        this.text = json[NodeJsonKey.Text];
        this.rect = json[NodeJsonKey.Rect];
        this.rect.cx = this.rect.x + this.rect.w / 2;
        this.rect.cy = this.rect.y + this.rect.h / 2;
        this.type = json[NodeJsonKey.Type];
        this.viewType = json[NodeJsonKey.ViewType];
        this.chart = chart;
    }

    toJson() {
        const n: any = {};
        n[NodeJsonKey.Uid] = this.uid;
        n[NodeJsonKey.Text] = this.text;
        n[NodeJsonKey.Rect] = this.rect;
        n[NodeJsonKey.Type] = this.type;
        n[NodeJsonKey.ViewType] = this.viewType;
    }

    generateStartNode(chart: Chart) {
        this.uid = '0';
        this.text = 'Start';
        this.rect = Rect.createWithLeftTopVertex(0, 0, 95, 38);
        this.viewType = 1;
        this.chart = chart;
        // this.type = chart.type.name;
        return this;
    }

    /**
     * Check if the node data is valid
     */
    check(): CheckResult {
        return new CheckResult();
    }
    //#endregion
}