import { CreateChartCommand, UpdateChartCommand } from './../../models/commands/chart.command';
import { RemoveGroupCommand, LayoutGroupCommand, RemoveNodeFromGroupCommand } from './../../models/commands/group.commands';
import { environment } from './../../../environments/environment.prod';
import { ProjectService } from 'src/app/services/project/project.service';
import { Store } from '@ngrx/store';
import * as fromStore from '../../store';
import { APP_HTTP_RESULT_CODE } from './../../constants/http/http.constants';
import { HttpResult } from './../../common/http/http.result.model';
import { GroupService } from './group.service';
import { OperationService } from './operation.service';
import { Injectable } from '@angular/core';
import { RequireChartInfo } from 'src/app/models/chart.model';
import { HttpClient } from '@angular/common/http';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { NodeViewType } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.enums';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';
import { Connector } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';
import { Group, LayoutType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/group.model';
import { Variable } from 'libs/flowchart-ts/flowchart-core/src/lib/models/variable.model';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { Subject } from 'rxjs';
import { RemoveConnectorCommand } from 'src/app/models/commands/connector.commands';
import { RemoveNodeCommand } from 'src/app/models/commands/node.commands';
import { RemoveChartCommand } from 'src/app/models/commands/chart.command';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';

@Injectable({
  providedIn: 'root'
})
export class ChartService {
  currentChart: Subject<Chart>;
  openningCharts: Subject<Chart[]>;
  charts: Subject<Chart[]>;

  private _currentChart: Chart;
  private _openningCharts: Chart[];
  private _charts: Chart[];
  constructor(
    private http: HttpClient,
    private operationService: OperationService,
    private groupService: GroupService,
    private store: Store<fromStore.FlowchartState>,
    private projectService: ProjectService
  ) {
    this.currentChart = new Subject<Chart>();
    this.openningCharts = new Subject<Chart[]>();
    this.charts = new Subject<Chart[]>();
    this.projectService.currentProject.subscribe(p => {
      this._charts = p.charts;
      this.charts.next(p.charts);
      this._currentChart = null;
      this.currentChart.next(null);
      this._openningCharts = [];
      this.openningCharts.next([]);
    })
  }

  getNodeGroupMap(chart: Chart) {
    return this.groupService.getNodeGroupMap(chart);
  }

  async getCurrentChartsList(project: string) {
    const url = 'api/project/charts?project=' + project;
    // const result = await this.http.get<Chart[]>(url).toPromise();
    try {
      const result = await this.http.get<HttpResult<Chart[]>>(url).toPromise();
      if (result.code === APP_HTTP_RESULT_CODE.OK) {
        result.data.forEach(item => item.project = this.projectService.getCurrentProject());
        this._charts = result.data;
        this.charts.next(this._charts);
        this._openningCharts = [];
        this.openningCharts.next(this._openningCharts);
        return new HttpResult<Chart[]>(APP_HTTP_RESULT_CODE.OK, '', result.data);
      }
      return new HttpResult<Chart[]>(APP_HTTP_RESULT_CODE.ERROR, result.msg, null);
    } catch (error) {
      return new HttpResult<Chart[]>(APP_HTTP_RESULT_CODE.ERROR, error.message, null);
    }
  }

  async getChart(requireChartInfo: RequireChartInfo): Promise<HttpResult<Chart>> {
    if (environment.IS_WEB) {
      const url = 'api/chart?project=' + requireChartInfo.project + '&chartId=' + requireChartInfo.chartId;
      try {
        const result = await this.http.get<HttpResult<any>>(url).toPromise();
        if (result.code === APP_HTTP_RESULT_CODE.OK) {
          const chart = new Chart();
          chart.uid = result.data.uid;
          chart.name = result.data.name;
          chart.path = result.data.path;
          chart.project = this.projectService.getCurrentProject();
          this._currentChart = chart;
          this.currentChart.next(this._currentChart);
          const index = this._openningCharts.findIndex(c => c.uid === chart.uid);
          if (index > -1) {
            this._openningCharts[index] = chart;
          } else {
            this._openningCharts.push(chart)
          }
          this.openningCharts.next(this._openningCharts);
          return new HttpResult<Chart>(APP_HTTP_RESULT_CODE.OK, '', chart);
        }
        return new HttpResult<Chart>(APP_HTTP_RESULT_CODE.ERROR, result.msg, null);
      } catch (error) {
        return new HttpResult<Chart>(APP_HTTP_RESULT_CODE.ERROR, error.message, null);
      }
    } else {
      const project = this.projectService.getCurrentProject();
      if (project) {
        this._currentChart = project.chartMap.get(requireChartInfo.chartId);
        this.currentChart.next(this._currentChart);
        if (this._openningCharts) {
          const index = this._openningCharts.findIndex(c => c.uid === requireChartInfo.chartId);
          if (index > -1) {
            this._openningCharts[index] = this._currentChart;
          } else {
            this._openningCharts.push(this._currentChart);
          }
        } else {
          this._openningCharts = [this._currentChart];
        }
        this.openningCharts.next(this._openningCharts);
      }
      return new HttpResult<Chart>(APP_HTTP_RESULT_CODE.OK, '', this._currentChart);
    }
  }

  parseChart(data) {
    const chart = new Chart();
    chart.uid = data.uid;
    chart.name = data.name;
    chart.path = data.path;
    chart.type = data.type;
    chart.description = data.description;
    chart.isSubChart = data.export;
    if (data) {
      if (data.nodes) {
        chart.nodes = data.nodes.map((node, index) => {
          if (node !== undefined && node != null) {
            const newNode = new ChartNode();
            newNode.uid = node.uid;
            newNode.text = node.text;
            newNode.chart = chart;
            newNode.rect = Rect.createWithCenter(newNode.rect.cx, newNode.rect.cy,
              chart.calculateNodeRenderWidth(newNode.text.length, node.viewType, node), 40);
            newNode.viewType = node.viewType || NodeViewType.COMMON;
            newNode.group = node.group;
            return newNode;
          }
        });
      }
      if (data.connectors) {
        const newConnectors = [];
        for (const conn of data.connectors) {
          const startNode = chart.nodeMap.get(conn.start);
          const endNode = chart.nodeMap.get(conn.end);
          if (startNode !== undefined && endNode !== undefined) {
            newConnectors.push(new Connector(startNode, endNode, conn.type, conn.shape));
          }
        }
        chart.connectors = newConnectors;
      }
      chart.nodes = chart.sortNodes();
      if (data.groups) {
        chart.groups = data.groups.map(g => {
          const newG: Group = { ...g };
          newG.chart = chart;
          newG.nodes = chart.nodes.filter(n => g.nodes.includes(n.uid))
          g.nodes.forEach((uid: string) => {
            const match = chart.nodeMap.get(uid);
            if (match) {
              newG.nodes.push(match);
            }
          });
          return newG;
        });
      }
      if (data.variables) {
        chart.parameters = [];
        chart.variables = [];
        const variables: Variable[] = data.variables.map(p => {
          const v = new Variable();
          v.name = p.name;
          v.index = p.idx;
          v.count = p.Count;
          v.description = p.description;
          v.default = p.default;
          v.type = p.type;
        });
        let index = 0;
        variables.forEach(v => {
          v.index = index++;
          if (v.isParameter) {
            chart.parameters.push(v);
          } else {
            chart.variables.push(v);
          }
        });
      }
    }
  }


  async createChart(project: Project, chart: Chart) {
    const cmd = new CreateChartCommand(project, chart);
    return await this.operationService.executeProjectCommands([cmd]);
  }

  async updateChart(chart: Chart, kv: Map<string, any>) {
    const cmd = new UpdateChartCommand(chart, kv);
    this.operationService.executeChartCommands(chart.uid, [cmd]);
  }

  async removeChart(project: Project, chart: Chart) {
    const cmd = new RemoveChartCommand(project, chart);
    return await this.operationService.executeProjectCommands([cmd]);
  }

  judgeNodeInGroup(chart: Chart, node: ChartNode) {
    return this.groupService.judgeNodeInGroup(chart, [node]);
  }

  async deleteItems(items: { groups: Group[], connectors: Connector[], nodes: ChartNode[] }) {
    const cmds = [];
    // 用于存储后续被影响需要重新布局的group
    const effectedGroups: Group[] = [];
    // 被影响的剩余需要重新计算路径的connector
    const effectedConns: Connector[] = [];
    if (items.groups) {
      items.groups.forEach(group => {
        const cmd = new RemoveGroupCommand(this._currentChart, group);
        cmds.push(cmd);
      });
    }
    // 再删除选中的连线
    if (items.connectors) {
      items.connectors.forEach(conn => {
        const cmd = new RemoveConnectorCommand(this._currentChart, conn);
        cmds.push(cmd);
      });
    }
    // 最后删除节点
    if (items.nodes) {
      const chart = this.getCurrentChart();
      items.nodes.forEach(node => {
        // 记录受影响的自动布局的group
        if (node.group && items.groups.includes(node.group) && node.group.layout === LayoutType.AUTO) {
          effectedGroups.push(node.group);
        }
        if (items.groups.length === 0 && this.judgeNodeInGroup(chart, node)) {
          const cmd = new RemoveNodeFromGroupCommand(node.group, node)
          cmds.push(cmd);
        }

        const cmd = new RemoveNodeCommand(this._currentChart, node);
        cmds.push(cmd);
      });
    }

    // 重新布局自动布局内容
    effectedGroups.forEach(g => {
      cmds.push(new LayoutGroupCommand(this._currentChart, g));
    })


    const result = this.operationService.executeChartCommands(this._currentChart.uid, cmds);
    return result;
  }

  async createParameter(parameter: Variable, chart: Chart) {
    const url = `api/chart/parameters/create/${chart.project}/${chart.uid}`;
    const result = await this.http.post<HttpResult<boolean>>(url, parameter).toPromise();
    return result;
  }

  async updateParameter(parameter: Variable, chart: Chart) {
    const url = `api/chart/parameters/update/${chart.project}/${chart.uid}`;
    const result = await this.http.post<HttpResult<boolean>>(url, parameter).toPromise();
    return result;
  }

  async deleteParameter(parameter: Variable, chart: Chart) {
    const url = `api/chart/parameters/delete/${chart.project}/${chart.uid}/${parameter.index}`;
    const result = await this.http.post<HttpResult<boolean>>(url, parameter).toPromise();
    return result;
  }

  getCurrentChart() {
    // const promise = new Promise<Chart>((resolve) => {
    //   this.store.select(fromStore.getCurrentChartDetails).subscribe(data => {
    //     resolve(data);
    //   });
    // });
    // return await promise;
    return this._currentChart;
  }

  setCurrentChart(chart: Chart) {
    this._currentChart = chart;
    this.currentChart.next(this._currentChart);
    if (!this._openningCharts.includes(this._currentChart)) {
      this._openningCharts.push(this._currentChart);
      this.openningCharts.next(this._openningCharts);
    }
  }

  setOpenningCharts(charts: Chart[]) {
    this._openningCharts = charts;
    this.openningCharts.next(this._openningCharts);
  }

  getCurrentChartList() {
    return this._charts;
  }
}
