import { Chart, ChartNode } from "libs/flowchart-ts/flowchart-core/src/public-api";

export abstract class Command {
    name: string;
    abstract execute(): CommandExecuteResult;
    abstract revert(): CommandExecuteResult;
}


function doExecute(cmds: Command[]) {
    cmds.forEach(cmd => {
        cmd.execute();
    });
}

export class CommandExecuteResult {
    code: number;
    msg: string;
    args: any;
    constructor(code: number, msg: string, args?: any) {
        this.code = code;
        this.msg = msg;
        this.args = args;
    }
}