import { OutputService } from 'src/app/services/common/output.service';
import { VariableService } from './../../../../services/chart/variable.service';
import { ProjectService } from 'src/app/services/project/project.service';
import { VariableRequestInfo } from './../../../../core/models/variable.model';
import { InterfaceService } from 'src/app/services/interface/interface.service';
import { IProperty } from './../../../../models/property.model';
import { CHART_PROPERTY_LIST } from './../../../../constants/property/property.constants';
import { Component, Input, OnInit, OnChanges, SimpleChanges, ViewChild, ViewChildren } from '@angular/core';
import { Subject } from 'rxjs';
import { Store } from '@ngrx/store';
import * as fromStore from '../../../../store';
import { UtilsService } from 'src/app/services/common/utils.service';
import { CHART_PROPERTY } from 'src/app/constants/property/property.constants';
import { distinctUntilChanged, debounceTime } from 'rxjs/operators';
import { CanvasService } from 'src/app/services/chart/canvas.service';
import { ChartService } from 'src/app/services/chart/chart.service';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { Variable } from 'libs/flowchart-ts/flowchart-core/src/lib/models/variable.model';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/public-api';
import { BaseComponent } from 'src/app/components/base-component';

@Component({
  selector: 'app-chart-property-table',
  templateUrl: './chart-property-table.component.html',
  styleUrls: ['./chart-property-table.component.css']
})
export class ChartPropertyTableComponent extends BaseComponent implements OnInit, OnChanges {
  @ViewChildren('variableInput') variableInput;
  @ViewChildren('parameterInput') parameterInput;
  @ViewChild('variableContainer', { static: false }) variableContainer;
  @ViewChild('parameterContainer', { static: false }) parameterContainer;
  editedChart: Chart;
  variables: Variable[];
  @Input() chart: Chart;

  types: ChartType[];
  customTypes: ChartType[];
  constructor(
    private store: Store<fromStore.FlowchartState>,
    private utilsService: UtilsService,
    private canvasService: CanvasService,
    private interfaceService: InterfaceService,
    private typeService: InterfaceService,
    private chartService: ChartService,
    private variableService: VariableService,
    private projectService: ProjectService,
    private outputService: OutputService
  ) {
    super();
  }

  trackList(index: number) {
    return index;
  }
  ngOnInit() {
    this.initEditedChart();
    this.initChartPropertyArray();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.chart) {
      this.editedChart = this.chart;
    }
  }


  initEditedChart() {
    this.editedChart = this.chartService.getCurrentChart();
    this.types = this.typeService.getAllTypeList(true);
    this.customTypes = this.typeService.getAllCustomTypes();
  }

  initChartPropertyArray() {
    // this.chartPropertyArray = this.utilsService.transformObjectToArray(CHART_PROPERTY);
    // this.store.select(fromStore.getTypes).subscribe(chartTypes => {
    //   this.chartPropertyArray.find(p => p.name === '类型').selectList = chartTypes.map(ct => ct.name);
    // });
    // this.store.select(fromStore.getCurrentChartDetails).subscribe(chart => {
    //   this.editedChart = chart;
    //   this.chart = chart;
    // });
    this.addSubscription(this.typeService.chartTypes.subscribe(types => {
      this.types = this.typeService.getAllTypeList(true);
      this.customTypes = this.typeService.getAllCustomTypes()
    }));

    this.addSubscription(this.chartService.currentChart.subscribe(chart => {
      this.editedChart = chart;
      this.chart = chart;
    }))
  }

  saveData(propName: string, value: any) {

    if (propName === 'name') {
      const currentChartName = this.chart.name;
      const nameExistedIndex = this.chartService.getCurrentChartList().findIndex(c => c.name === value && c.uid != this.chart.uid)
      if (nameExistedIndex === -1) {
        this.chartService.updateChart(this.chart, new Map<string, any>([[propName, value]]));
      } else if (nameExistedIndex !== -1) {
        this.outputService.error(value + ' already exists, please change a different name');
      }
    } else {
      this.chartService.updateChart(this.chart, new Map<string, any>([[propName, value]]));
    }
  }

  getDefaultName(input: string, exists: string[]) {
    const origin = input;
    let i = 1;
    while (exists.includes(input)) {
      input = origin + i++;
    }
    return input;
  }

  onChartDataChanged($event) {
    // this.store.dispatch(new fromStore.UpdateChart($event));
    this.chartService.updateChart(this.chart, $event);
  }

  onAddVariableClick(isPara: boolean) {
    console.log(this.chart.variables);
    const p = new Variable();
    p.name = this.getDefaultName('variable', this.chart.variables.map(p => p.name).concat(this.chart.parameters.map(p => p.name)));
    p.type = ChartType.StringType;
    p.isParameter = isPara;
    p.index = this.chart.variables.length + this.chart.parameters.length;
    this.variableService.createVariable(p, this.chart);
  }

  onAddParameterClick($event) {
    const p = new Variable();
    p.name = this.getDefaultName('variable', this.chart.variables.map(p => p.name).concat(this.chart.parameters.map(p => p.name)));
    p.type = ChartType.StringType;
    p.isParameter = true;
    p.index = this.chart.parameters.length;
    this.variableService.createVariable(p, this.chart);
  }

  subscribeNewParameter() {
    const initialLength = this.variableInput.length;
    this.store.select(fromStore.getCreatedNewParameter).subscribe(data => {
      if (this.variableInput) {
        setTimeout(() => {
          if (initialLength === this.variableInput.length || !this.variableInput._results) { return; }
          const newInputRef = this.variableInput._results[this.variableInput.length - 1].nativeElement;
          newInputRef.focus();
          // 参数区滚动条置底
          this.variableContainer.nativeElement.scrollTop = this.variableContainer.nativeElement.scrollHeight;
        }, 0);
      }
    });
  }

  subscribeNewVariable() {
    this.store.select(fromStore.getCreatedNewVariable).subscribe(data => {
      const initialLength = this.parameterInput.length;
      if (this.parameterInput) {
        setTimeout(() => {
          if (initialLength === this.parameterInput._results || !this.parameterInput._results) { return; }
          const newInputRef = this.parameterInput._results[this.parameterInput.length - 1].nativeElement;
          newInputRef.focus();
          // 变量区滚动条置底
          this.parameterContainer.nativeElement.scrollTop = this.parameterContainer.nativeElement.scrollHeight;
        }, 0);
      }
    });
  }

  onParameterNameInputBlur($event, parameter: Variable) {
    const newName = $event.target.value;
    const nameStringList = this.chart.variables.map(p => p.name).concat(this.chart.parameters.map(p => p.name));
    if (newName === parameter.name) {
      return;
    }
    this.variableService.updateVariable(parameter, new Map<string, any>([['name', $event.target.value]]), this.chart)
  }

  onParameterTypeChanged($event, parameter: Variable) {
    if ($event === parameter.type) {
      return;
    }
    this.variableService.updateVariable(parameter, new Map<string, any>([['type', $event]]), this.chart);
  }

  onDeleteParameterClick($event, parameter: Variable, i: number) {
    this.variableService.removeVariable(parameter, this.chart);
  }

  onVariableTypeChanged($event, variable: Variable) {
    if ($event === variable.type) {
      return;
    }
    this.variableService.updateVariable(variable, new Map<string, any>([['type', $event]]), this.chart)
  }

  checkNameExist(name: string, list: Array<string>): boolean {
    let exist = false;
    list.forEach(l => {
      if (l === name) {
        exist = true;
      }
    });
    return exist;
  }
}
