import { ArgListNode, ArgNode, AssignNode, AstNode, AstOperator, BinaryOpNode, ExprNode, FuncNode, ListNode, MemberAssignNode, MemberNode, NameNode, NumberNode, ParenthesisNode, StringNode, SubscriptAssignNode, SubscriptNode, UnaryOpNode, VarNode } from "./nodes/ast-node.model";
import { NodeCommandVisitor } from "../antlr/NodeCommandVisitor";
import { NormalStringContext, CharStringContext, IntContext, HexContext, FloatContext, HexFloatContext, Exp_varContext, Exp_nameContext, Exp_nodeRefContext, Exp_numberContext, Exp_stringContext, Exp_atomContext, Exp_memberContext, Exp_globalFuncCallContext, Exp_funcCallContext, Exp_listContext, Exp_subscriptContext, Exp_parenthesisContext, Exp_powerContext, Exp_unaryContext, Exp_mulDivContext, Exp_addSubContext, Exp_compareContext, Exp_strcatContext, Exp_bitwiseMoveContext, Exp_bitwiseAndContext, Exp_bitwiseXContext, Exp_bitwiseOrContext, Exp_andContext, Exp_orContext, Exp_assignmentContext, Exp_subscriptAssignContext, Exp_memberAssignContext, NodeContext, ExpContext, Atom_expContext, ArgContext, ArgListContext, FunctionContext, ListContext, FieldsepContext, OperatorUnaryContext, OperatorPowerContext, OperatorMulDivModContext, OperatorAddSubContext, OperatorStrcatContext, OperatorBitwiseMoveContext, OperatorBitwiseAndContext, OperatorBitwiseXContext, OperatorBitwiseOrContext, OperatorComparisonContext, OperatorAndContext, OperatorOrContext, NumberContext, StringContext } from "../antlr/NodeCommandParser";

import { AbstractParseTreeVisitor } from 'antlr4ts/tree/AbstractParseTreeVisitor';
import { ChartType } from "./../../../flowchart-core/src/public-api";

export class AstGenerator extends AbstractParseTreeVisitor<AstNode> implements NodeCommandVisitor<AstNode> {
    protected defaultResult(): AstNode {
        return null;
    }

    static OperatorDict: Map<string, AstOperator>;

    static OperatorReverseDict: Map<AstOperator, string>;

    constructor() {
        super();
        AstGenerator.OperatorDict = new Map<string, AstOperator>();
        AstGenerator.OperatorDict.set("^", AstOperator.Factor);
        AstGenerator.OperatorDict.set("*", AstOperator.Multiplies);
        AstGenerator.OperatorDict.set("/", AstOperator.Divided);
        AstGenerator.OperatorDict.set("%", AstOperator.Mod);
        AstGenerator.OperatorDict.set("//", AstOperator.FloorDiv);
        AstGenerator.OperatorDict.set("+", AstOperator.Add);
        AstGenerator.OperatorDict.set("-", AstOperator.Sub);
        AstGenerator.OperatorDict.set("..", AstOperator.Strcat);
        AstGenerator.OperatorDict.set(">>", AstOperator.RightShift);
        AstGenerator.OperatorDict.set("<<", AstOperator.LeftShift);
        AstGenerator.OperatorDict.set("&", AstOperator.BitAnd);
        AstGenerator.OperatorDict.set("~", AstOperator.BitX);
        AstGenerator.OperatorDict.set("|", AstOperator.BitOr);
        AstGenerator.OperatorDict.set("<", AstOperator.LessThan);
        AstGenerator.OperatorDict.set(">", AstOperator.MoreThan);
        AstGenerator.OperatorDict.set("<=", AstOperator.LessEqualThan);
        AstGenerator.OperatorDict.set(">=", AstOperator.MoreEqualThan);
        AstGenerator.OperatorDict.set("==", AstOperator.Equal);
        AstGenerator.OperatorDict.set("~=", AstOperator.NotEqual);
        AstGenerator.OperatorDict.set("and", AstOperator.And);
        AstGenerator.OperatorDict.set("or", AstOperator.Or);
        AstGenerator.OperatorDict.set("not", AstOperator.Not);
        AstGenerator.OperatorDict.set("!", AstOperator.Exclam);
        AstGenerator.OperatorDict.set("#", AstOperator.Sharp);

        AstGenerator.OperatorReverseDict = new Map<AstOperator, string>();
        AstGenerator.OperatorDict.forEach((v, k) => {
            AstGenerator.OperatorReverseDict.set(v, k);
        })

        AstGenerator.OperatorDict.set("&&", AstOperator.And);
        AstGenerator.OperatorDict.set("||", AstOperator.And);
        AstGenerator.OperatorDict.set("!=", AstOperator.NotEqual);
    }

    visitNormalString?: (ctx: NormalStringContext) => AstNode;
    visitCharString?: (ctx: CharStringContext) => AstNode;
    visitInt?: (ctx: IntContext) => AstNode;
    visitHex?: (ctx: HexContext) => AstNode;
    visitFloat?: (ctx: FloatContext) => AstNode;
    visitHexFloat?: (ctx: HexFloatContext) => AstNode;
    visitExp_var(ctx: Exp_varContext): AstNode {
        const node = new VarNode();
        node.text = ctx.text;
        return node;
    }
    visitExp_name(ctx: Exp_nameContext): AstNode {
        const node = new NameNode();
        node.text = ctx.text;
        return node;
    }
    visitExp_nodeRef?: (ctx: Exp_nodeRefContext) => AstNode;
    visitExp_number(ctx: Exp_numberContext): AstNode {
        const node = new NumberNode();
        node.text = ctx.text;
        return node;
    };
    visitExp_string(ctx: Exp_stringContext): AstNode {
        const node = new StringNode();
        node.text = ctx.text;
        return node;
    };
    visitExp_atom?: (ctx: Exp_atomContext) => AstNode;
    visitExp_member(ctx: Exp_memberContext): AstNode {
        const node = new MemberNode();
        node.owner = this.visit(ctx.exp()) as ExprNode;
        node.member = new NameNode();
        node.member.text = ctx.NAME().text;
        return node;
    }
    visitExp_globalFuncCall(ctx: Exp_globalFuncCallContext): AstNode {
        const node = this.visitFunction(ctx.function()) as FuncNode;
        return node;
    }
    visitExp_funcCall(ctx: Exp_funcCallContext): AstNode {
        const node = this.visit(ctx.function()) as FuncNode;
        node.caller = this.visit(ctx.exp());
        return node;
    }
    visitExp_list?: (ctx: Exp_listContext) => AstNode;
    visitExp_subscript(ctx: Exp_subscriptContext): AstNode {
        const node = new SubscriptNode();
        node.listExpr = this.visit(ctx.exp()[0]) as ExprNode;
        node.index = this.visit(ctx.exp()[1]) as ExprNode;
        return node;
    };
    visitExp_parenthesis(ctx: Exp_parenthesisContext): AstNode {
        const node = new ParenthesisNode();
        node.content = this.visit(ctx.exp()) as ExprNode;
        return node;
    }
    visitExp_power(ctx: Exp_powerContext): AstNode {
        return this.BinaryOperation(ctx.exp(), ctx.operatorPower().text);
    }
    visitExp_unary(ctx: Exp_unaryContext): AstNode {
        const node = new UnaryOpNode();
        const op = ctx.operatorUnary().text;
        node.operator = AstGenerator.OperatorDict.get(op) || AstOperator.UnknownOp;
        node.express = this.visit(ctx.exp()) as ExprNode;
        return node;
    };
    visitExp_mulDiv(ctx: Exp_mulDivContext): AstNode {
        return this.BinaryOperation(ctx.exp(), ctx.operatorMulDivMod().text);
    };
    visitExp_addSub(ctx: Exp_addSubContext): AstNode {
        return this.BinaryOperation(ctx.exp(), ctx.operatorAddSub().text);
    }
    visitExp_compare(ctx: Exp_compareContext): AstNode {
        return this.BinaryOperation(ctx.exp(), ctx.operatorComparison().text);
    }
    visitExp_strcat(ctx: Exp_strcatContext): AstNode {
        return this.BinaryOperation(ctx.exp(), ctx.operatorStrcat().text);
    }
    visitExp_bitwiseMove(ctx: Exp_bitwiseMoveContext): AstNode {
        return this.BinaryOperation(ctx.exp(), ctx.operatorBitwiseMove().text);
    }
    visitExp_bitwiseAnd(ctx: Exp_bitwiseAndContext): AstNode {
        return this.BinaryOperation(ctx.exp(), ctx.operatorBitwiseAnd().text);
    }
    visitExp_bitwiseX(ctx: Exp_bitwiseXContext): AstNode {
        return this.BinaryOperation(ctx.exp(), ctx.operatorBitwiseX().text);
    }
    visitExp_bitwiseOr(ctx: Exp_bitwiseOrContext): AstNode {
        return this.BinaryOperation(ctx.exp(), ctx.operatorBitwiseOr().text);
    }
    visitExp_and(ctx: Exp_andContext): AstNode {
        return this.BinaryOperation(ctx.exp(), ctx.operatorAnd().text);
    }
    visitExp_or(ctx: Exp_orContext): AstNode {
        return this.BinaryOperation(ctx.exp(), ctx.operatorOr().text);
    }
    visitExp_assignment(ctx: Exp_assignmentContext): AstNode {
        const node = new AssignNode();
        node.left = new VarNode();
        node.left.text = ctx.VAR().text;
        node.right = this.visit(ctx.exp()) as ExprNode;
        return node;
    }
    visitExp_subscriptAssign(ctx: Exp_subscriptAssignContext): AstNode {
        const node = new SubscriptAssignNode();
        const exp = ctx.exp();
        node.left = new SubscriptNode();
        node.left.listExpr = this.visit(exp[0]) as ExprNode;
        node.left.index = this.visit(exp[1]) as ExprNode;
        node.right = this.visit(exp[2]) as ExprNode;
        return node;
    }
    visitExp_memberAssign(ctx: Exp_memberAssignContext): AstNode {
        const node = new MemberAssignNode();
        node.left = new MemberNode();
        node.left.owner = this.visit(ctx.exp()[0]) as ExprNode;
        node.left.member = new NameNode;
        node.left.member.text = ctx.NAME().text;
        node.right = this.visit(ctx.exp()[1]);
        return node;
    }
    visitNode?: (ctx: NodeContext) => AstNode;
    visitExp?: (ctx: ExpContext) => AstNode;
    visitAtom_exp?: (ctx: Atom_expContext) => AstNode;
    visitArg(ctx: ArgContext): AstNode {
        const node = new ArgNode();
        const name = ctx.NAME();
        if (name) {
            node.name = new NameNode();
            node.name.text = name.text;
        }
        node.value = this.visit(ctx.exp()) as ExprNode;
        return node;
    };
    visitArgList(ctx: ArgListContext): AstNode {
        const node = new ArgListNode();
        const args = ctx.arg();
        for (let argCtx of args) {
            node.args.push(this.visitArg(argCtx) as ArgNode)
        }
        return node;
    };
    visitFunction(ctx: FunctionContext): AstNode {
        const node = new FuncNode();
        node.func = new NameNode();
        node.func.text = ctx.NAME().text;
        node.args = this.visit(ctx.argList()) as ArgListNode;
        return node;
    };
    visitList(ctx: ListContext): AstNode {
        const node = new ListNode();
        node.items = this.visit(ctx.argList()) as ArgListNode;
        return node;
    }
    visitFieldsep?: (ctx: FieldsepContext) => AstNode;
    visitOperatorUnary?: (ctx: OperatorUnaryContext) => AstNode;
    visitOperatorPower?: (ctx: OperatorPowerContext) => AstNode;
    visitOperatorMulDivMod?: (ctx: OperatorMulDivModContext) => AstNode;
    visitOperatorAddSub?: (ctx: OperatorAddSubContext) => AstNode;
    visitOperatorStrcat?: (ctx: OperatorStrcatContext) => AstNode;
    visitOperatorBitwiseMove?: (ctx: OperatorBitwiseMoveContext) => AstNode;
    visitOperatorBitwiseAnd?: (ctx: OperatorBitwiseAndContext) => AstNode;
    visitOperatorBitwiseX?: (ctx: OperatorBitwiseXContext) => AstNode;
    visitOperatorBitwiseOr?: (ctx: OperatorBitwiseOrContext) => AstNode;
    visitOperatorComparison?: (ctx: OperatorComparisonContext) => AstNode;
    visitOperatorAnd?: (ctx: OperatorAndContext) => AstNode;
    visitOperatorOr?: (ctx: OperatorOrContext) => AstNode;
    visitNumber?: (ctx: NumberContext) => AstNode;
    visitString?: (ctx: StringContext) => AstNode;


    private BinaryOperation(exp: ExpContext[], op: string) {
        console.assert(exp.length === 2, "Binary operation arguments length is not valid");
        const node = new BinaryOpNode();
        node.operator = AstGenerator.OperatorDict.get(op) || AstOperator.UnknownOp;
        node.left = this.visit(exp[0]) as ExprNode;
        node.right = this.visit(exp[1]) as ExprNode;
        node.type = ChartType.BoolType;
        return node;
    }

}