import { UtilsService } from './../../../services/common/utils.service';
import { Transform, ViewBox } from './../../../models/canvas/Canvas';
import { Component, Input, OnInit, ViewChild, ElementRef, AfterViewInit, SimpleChanges, OnChanges, ChangeDetectorRef } from '@angular/core';
import { Point } from 'src/app/models/point.model';
import { trigger, state, style, transition, animate } from '@angular/animations';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';
import { DebugRunningData, NodeRunningCounter, NodeRunningStatus } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.running-status.model';
import { DebugType, RunningStatus, ShowingRunningStatus } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.enums';
import { MatMenuTrigger } from '@angular/material';
import { CanvasService } from 'src/app/services/chart/canvas.service';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';

@Component({
  selector: 'app-debug-canvas',
  templateUrl: './debug-canvas.component.html',
  styleUrls: ['./debug-canvas.component.css'],
  animations: [
    trigger('color-trans', [
      state('running', style({
        stroke: 'purple',
        fill: 'purple'
      })),
      state('success', style({
        stroke: 'green',
        fill: 'green'
      })),
      state('failure', style({
        stroke: 'red',
        fill: 'red'
      })),
      state('idle', style({
        stroke: 'transparent',
        fill: 'transparent'
      })),
      transition('running => success', [animate('0.5s ease-in')]),
      transition('running => failure', [animate('0.5s ease-in')]),
      transition('idle => success', [animate('0.5s ease-in')]),
      transition('idle => failure', [animate('0.5s ease-in')]),
    ])
  ]
})
export class DebugCanvasComponent implements OnInit, OnChanges, AfterViewInit {

  @Input() chart: Chart;
  @Input() runningData: DebugRunningData[];
  canvasViewBox;
  thumbnailViewBoxTranslate;
  canvasRect: Rect;
  translateScale;
  transform: Transform;
  translate: Point;
  // 相对原点
  relativeBasepoint = new Point(0, 0);
  initNodesCenter;
  currentCanvasSize: Point;
  expand = 2;
  // 菜单位置
  menuLocation = new Point(0, 0);
  // 正在操作的节点
  operatingNode: ChartNode = null;
  // 节点运行状态map， key为节点uid value为运行状态
  runningMap = new Map<string, NodeRunningStatus>();
  nodesRunningData: NodeRunningStatus[];
  // 变量列表
  variableMap: Map<string, any> = new Map<string, any>();
  // 断点节点列表
  breakpointNodeList: ChartNode[] = [];
  get keys() {
    return Array.from(this.variableMap.keys());
  }
  varList;
  @ViewChild('contextMenuTrigger', { static: false })
  contextMenu: MatMenuTrigger;

  @ViewChild('root', { static: false, read: ElementRef }) root: ElementRef;
  constructor(
    private utilsService: UtilsService,
    private cdRef: ChangeDetectorRef,
    private canvasService: CanvasService,
  ) { }

  ngOnInit() {
    this.initCanvas();
    this.initData();
  }

  ngOnChanges(changes: SimpleChanges) {
    // if (changes.chart) {
    //   this.nodes = changes.chart.currentValue ? this.chart.nodes : [];
    // }
    if (changes.runningData && changes.runningData.currentValue) {
      this.processRunningData(changes.runningData.currentValue);
    }
  }

  ngAfterViewInit() {
    this.canvasViewBox = new ViewBox(this.currentCanvasSize.x, this.currentCanvasSize.y, this.transform).viewBox;
    this.moveToCanvasCenter();
    this.expandCanvas(this.chart.nodes);
  }

  initCanvas() {
    if (this.transform === undefined) {
      this.transform = new Transform(0, 0, 1);
    }
    this.translate = new Point(0, 0);
    this.canvasRect = Rect.createWithLeftTopVertex(0, 0, 1000 * 2, 600 * 2);
    this.currentCanvasSize = new Point(1000, 600);
  }

  initData() {
    this.runningMap = new Map<string, NodeRunningStatus>();
    if (this.chart && this.chart.nodes) {
      let i = 0;
      this.chart.nodes.forEach(n => {
        const newStatus = new NodeRunningStatus();
        newStatus.node = n;
        newStatus.status = RunningStatus.IDLE;
        newStatus.counter = new NodeRunningCounter();
        this.runningMap.set(n.uid, newStatus);
        i++;
      });
    }
  }

  moveToCanvasCenter() {
    const canvasCenter = new Point(this.currentCanvasSize.x / 2, this.currentCanvasSize.y / 2);
    const nodesCenter = this.canvasService.getAllNodesCenter(this.chart.nodes);
    this.initNodesCenter = nodesCenter;
    const delta = new Point(nodesCenter.x - canvasCenter.x, nodesCenter.y - canvasCenter.y);
    this.transform.translateX = delta.x;
    this.transform.translateY = delta.y;
    this.canvasViewBox = new ViewBox(this.currentCanvasSize.x, this.currentCanvasSize.y, this.transform).viewBox;
    this.relativeBasepoint = new Point(nodesCenter.x - this.currentCanvasSize.x, nodesCenter.y - this.currentCanvasSize.y);
    this.handleTransform();
    this.handleThumbnailViewboxChange();
  }

  handleThumbnailViewboxChange() {
    // 当前视口大小
    const positionRectSize = new Point(this.currentCanvasSize.x / this.transform.scale, this.currentCanvasSize.y / this.transform.scale);
    // 当视口位于中央时边缘对于左上角的偏移百分比
    const widthScale = 0.5 - positionRectSize.x / 2 / this.canvasRect.w;
    const heightScale = 0.5 - positionRectSize.y / 2 / this.canvasRect.h;
    const canvasCenter = new Point(this.currentCanvasSize.x / 2, this.currentCanvasSize.y / 2);
    const xScaleDelta = (this.initNodesCenter.x - canvasCenter.x) / this.canvasRect.w - widthScale;
    const yScaleDelta = (this.initNodesCenter.y - canvasCenter.y) / this.canvasRect.h - heightScale;
    this.thumbnailViewBoxTranslate = new Point(xScaleDelta, yScaleDelta);
  }

  processDebugMessage(msg: string) {
    const msgJ = JSON.parse(msg);
    if (msgJ.type) {
      // switch(msgJ.type) {
      //   case
      // }
    }
  }

  // 扩大画布
  expandCanvas(nodes: ChartNode[]) {
    const canvasSize = new Point(this.currentCanvasSize.x * this.expand, this.currentCanvasSize.y * this.expand);
    // this.handleThumbnailViewboxChange();
    const expand = this.canvasService.needExpand(nodes, this.relativeBasepoint, canvasSize, this.thumbnailViewBoxTranslate);
    if (expand) {
      this.expand = this.expand + 1;
      this.relativeBasepoint = new Point(this.relativeBasepoint.x - this.currentCanvasSize.x / 2
        , this.relativeBasepoint.y - this.currentCanvasSize.y / 2);
      this.canvasRect = Rect.createWithLeftTopVertex(0, 0, this.currentCanvasSize.x * this.expand, this.currentCanvasSize.y * this.expand);
      this.handleTransform();
      this.handleThumbnailViewboxChange();
      this.expandCanvas(nodes);
    }
  }

  handleTransform() {
    this.canvasViewBox = new ViewBox(this.currentCanvasSize.x, this.currentCanvasSize.y, this.transform).viewBox;
    this.translateScale = new Point((this.transform.translateX) / this.canvasRect.w, this.transform.translateY / this.canvasRect.h);
  }
  handleThumbnailTransform(ThumbnailTranslate: Point) {
    this.transform = new Transform
      (ThumbnailTranslate.x * this.canvasRect.w, ThumbnailTranslate.y * this.canvasRect.h, this.transform.scale);
    this.handleTransform();
  }

  onMapTranslateChanged($event) {
    this.transform.translateX = $event.x * this.canvasRect.w;
    this.transform.translateY = $event.y * this.canvasRect.h;
    this.handleTransform();
  }

  onMapScaleChanged($event) {
    this.transform.scale = $event;
    this.handleTransform();
  }

  onMousewheel($event) {
    // 按住ctrl后，不能通过滚轮使页面滚动
    if ($event.ctrlKey || $event.metaKey) {
      $event.preventDefault();
      $event.stopPropagation();
      let preScale = Math.max(this.transform.scale, 0.5);
      if ($event.deltaY < 0 && ($event.ctrlKey || $event.metaKey) && this.transform.scale < 2) {
        preScale = this.transform.scale;
        this.transform.scale = this.transform.scale + 0.02;
      }
      if ($event.deltaY > 0 && ($event.ctrlKey || $event.metaKey) && this.transform.scale >= 0.5) {
        preScale = this.transform.scale;
        this.transform.scale = this.transform.scale - 0.02;
      }
      const x = ($event.clientX - 460) / this.transform.scale;  // 鼠标位置换算到相对原点的坐标
      const y = ($event.clientY - 388) / this.transform.scale;
      const offsetX = x * (this.transform.scale - preScale) / preScale;  // x * 绝对缩放率 得到位移
      const offsetY = y * (this.transform.scale - preScale) / preScale;
      this.transform = new Transform(this.transform.translateX + offsetX, this.transform.translateY + offsetY, this.transform.scale);
    } else {
      // 滚轮向下滚动，画布向下位移最大值为画布高度 - 视口高度
      if ($event.deltaY >= 0) {
        this.transform = new Transform(
          this.transform.translateX,
          Math.min(this.transform.translateY + $event.deltaY
            , this.canvasRect.h - this.currentCanvasSize.y / this.transform.scale + this.thumbnailViewBoxTranslate.y * this.canvasRect.h),
          this.transform.scale);
      } else if ($event.deltaY < 0) {
        // 滚轮向上滚动，画布向上不位移不能超过画布的纵坐标
        this.transform = new Transform(
          this.transform.translateX,
          Math.max(this.transform.translateY + $event.deltaY
            , this.canvasRect.y + this.thumbnailViewBoxTranslate.y * this.canvasRect.h),
          this.transform.scale);
      }
    }
    this.handleTransform();
  }

  //#region debug
  processRunningData(runningData: DebugRunningData[]) {
    let i = 0;
    for (const item of runningData) {
      switch (item.type) {
        case DebugType.NODE:
          this.updateNodeStatus(item);
          break;
        case DebugType.VARIABLE:
          this.updateVariable(item);
      }
      i++;
    }
    const dataList = [];
    this.runningMap.forEach((v, k) => {
      dataList.push(v);
    });
    this.nodesRunningData = dataList;
    console.log(this.runningMap);
  }

  updateNodeStatus(runningData: DebugRunningData) {
    if (this.runningMap === undefined) {
      this.runningMap = new Map<string, NodeRunningStatus>();
    }
    const uid = runningData.nodeUid;
    const node = this.chart.nodes.find(n => n.uid === uid);
    if (!node) {
      return;
    }
    let newStatus: NodeRunningStatus;
    if (node.status === undefined) {
      newStatus = new NodeRunningStatus();
      newStatus.counter = new NodeRunningCounter();
      newStatus.status = RunningStatus.IDLE;
      node.status = newStatus;
    } else {
      newStatus = this.utilsService.deepCopy(node.status);
    }
    newStatus.status = runningData.newStatus;
    newStatus.oldStatus = runningData.oldStatus;
    newStatus.success = runningData.result === true;
    newStatus.showingStatus = this.parseStatusToShowingStatus(newStatus);
    node.status = newStatus;
    if (newStatus.showingStatus === ShowingRunningStatus.SUCCESS || newStatus.showingStatus === ShowingRunningStatus.FAILURE) {
      setTimeout(() => {
        newStatus.showingStatus = ShowingRunningStatus.IDLE;
        newStatus.status = RunningStatus.IDLE;
        this.cdRef.detectChanges();
      }, 500);
    }
    // const status = this.runningMap.get(uid);
    // status.oldStatus = runningData.oldStatus;
    // status.status = runningData.newStatus;
    if (runningData.oldStatus !== RunningStatus.END_RUNNING && runningData.newStatus === RunningStatus.END_RUNNING) {
      try {
        this.count(node.status, runningData.result === true);
      } catch (error) {

      }
    }
    this.cdRef.detectChanges();
  }

  updateVariable(runningData: DebugRunningData) {
    if (runningData.variableName) {
      this.variableMap.set(runningData.variableName, runningData.newValue);
    }
  }

  parseStatusToShowingStatus(status: NodeRunningStatus) {
    switch (status.status) {
      case RunningStatus.IDLE:
        return ShowingRunningStatus.IDLE;
      case RunningStatus.RUNNING:
        return ShowingRunningStatus.RUNNING;
      case RunningStatus.END_RUNNING:
        if (status.success) {
          return ShowingRunningStatus.SUCCESS;
        } else {
          return ShowingRunningStatus.FAILURE;
        }
      default: return ShowingRunningStatus.FAILURE;
    }
  }

  setBreakpoint(isSet: boolean) {
    if (!this.operatingNode) { return; }
    if (isSet) {
      const isExised = this.breakpointNodeList.find(n => n.uid === this.operatingNode.uid);
      if (!isExised) {
        this.breakpointNodeList.push(this.operatingNode)
      }
    } else {
      this.breakpointNodeList = this.breakpointNodeList.filter(n => n.uid !== this.operatingNode.uid);
    }
    console.log(this.breakpointNodeList);
  }

  openDebugNodeMenu(e, node: ChartNode) {
    e.preventDefault();
    this.operatingNode = node;
    console.log(e, node);
    this.menuLocation = new Point(node.rect.x + e.offsetX, node.rect.y + e.offsetY);
    this.contextMenu.openMenu();
  }

  debugNodeMenuClosed() {
    this.operatingNode = null;
  }

  /**
   * 节点运行计数
   * @param uid
   * @param success
   */
  count(status: NodeRunningStatus, success: boolean) {

    if (success) {
      status.counter.success++;
    } else {
      status.counter.failure++;
    }
    status.success = success;
  }
  //#endregion
}
