import { CreateVariableCommand, RemoveVariableCommand, UpdateVariableCommand } from './../../models/commands/variable.commands';
import { VariableRequestInfo } from './../../core/models/variable.model';
import { OperationService } from './operation.service';
import { COMMAND_NAME } from './../../constants/command/command.constants';
import { Injectable } from '@angular/core';
import { Variable } from 'libs/flowchart-ts/flowchart-core/src/lib/models/variable.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';

@Injectable({
  providedIn: 'root'
})
export class VariableService {

  constructor(private operationService: OperationService) { }

  createVariable(variable: Variable, chart: Chart) {
    const cmd = new CreateVariableCommand(chart, variable)
    const result = this.operationService.executeChartCommands(chart.uid, [cmd]);
    return result;
  }

  updateVariable(variable: Variable, kv: Map<string, any>, chart: Chart) {
    const cmd = new UpdateVariableCommand(variable, kv)
    const result = this.operationService.executeChartCommands(chart.uid, [cmd]);
    return result;
  }

  removeVariable(variable: Variable, chart: Chart) {
    console.log(variable, chart);
    const cmd = new RemoveVariableCommand(chart, variable);
    const result = this.operationService.executeChartCommands(chart.uid, [cmd]);
    return result;
  }
}
