import { EventEmitter } from '@angular/core';
import { Component, Input, OnInit, Output } from '@angular/core';

@Component({
  selector: 'app-img-button',
  templateUrl: './img-button.component.html',
  styleUrls: ['./img-button.component.css']
})
export class ImgButtonComponent implements OnInit {

  @Input() name;
  @Input() src;
  @Input() disabled: boolean;
  @Input() selected: boolean;

  @Input() viewBox = '0 0 1024 1024';
  @Input() isSvg = false;
  @Input() isImg = false;
  @Input() isTransferY = false;
  @Input() showMore = false;

  @Output() cmd: EventEmitter<any> = new EventEmitter<any>();
  constructor() { }

  ngOnInit() {
  }

  onClick($event) {
    if (this.disabled || this.selected) {
      return;
    }
    this.cmd.emit($event);
  }

}
