import { animate, state, style, transition, trigger } from '@angular/animations';

export const debugColorTrans =  trigger('debug-olor-trans', [
      state('running', style({
        stroke: '#0ff',
        background: '#0ff'
      })),
      state('success', style({
        stroke: 'lightgreen',
        background: 'lightgreen'
      })),
      state('failure', style({
        stroke: 'pink',
        background: 'pink'
      })),
      state('idle', style({
        stroke: '#bae7f5',
        background: '#bae7f5'
      })),
      transition('running => success', [animate('0.5s ease-in')]),
      transition('running => failure', [animate('0.5s ease-in')]),
      transition('running => idle', [animate('0.5s ease-in')]),
      transition('idle => success', [animate('0.5s ease-in')]),
      transition('idle => failure', [animate('0.5s ease-in')]),
      transition('idle => running', [animate('0.5s ease-in')]),
      transition('success => idle', [animate('0.5s ease-in')]),
      transition('failure => idle', [animate('0.5s ease-in')]),
      transition('running => idle', [animate('0.5s ease-in')]),
    ]);
