import { CreateConnectorArgs } from './../../../models/request-arguments/connector.op.args.model';
import { Action } from '@ngrx/store';
import { Connector } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';

// load nodes
export const LOAD_CONNECTORS = '[Flowchart] Load Connectors';
export const LOAD_CONNECTORS_FAIL = '[Flowchart] Load Connectors Fail';
export const LOAD_CONNECTORS_SUCCESS = '[Flowchart] Load Connectors Success';

export class LoadConnectors implements Action {
    readonly type = LOAD_CONNECTORS;
    constructor(public connectors: any) {
    }
}

export class LoadConnectorsFial implements Action {
    readonly type = LOAD_CONNECTORS_FAIL;
    constructor(public payload: any) { }
}

export class LoadConnectorsSuccess implements Action {
    readonly type = LOAD_CONNECTORS_SUCCESS;
    constructor(public payload: Connector[]) { }
}

export const CREATE_CONNECTORS = '[Flowchart] Create Connectors';
export const CREATE_CONNECTORS_FAIL = '[Flowchart] Create Connectors Fail';
export const CREATE_CONNECTORS_SUCCESS = '[Flowchart] Create Connectors Success';

export class CreateConnectors implements Action {
    readonly type = CREATE_CONNECTORS;
    constructor(public payload: CreateConnectorArgs[]) { }
}

export class CreateConnectorsFail implements Action {
    readonly type = CREATE_CONNECTORS_FAIL;
    constructor(public payload: any) { }
}

export class CreateConnectorsSuccess implements Action {
    readonly type = CREATE_CONNECTORS_SUCCESS;
    constructor(public payload: any) { }
}

export const UPDATE_CONNECTOR = '[Flowchart] Update Connector';
export const UPDATE_CONNECTOR_FAIL = '[Flowchart] Update Connector Fail';
export const UPDATE_CONNECTOR_SUCCESS = '[Flowchart] Update Connector Success';

export class UpdateConnector implements Action {
    readonly type = UPDATE_CONNECTOR;
    constructor(public payload: Connector) { }
}

export class UpdateConnectorFail implements Action {
    readonly type = UPDATE_CONNECTOR_FAIL;
    constructor(public payload: Connector) { }
}

export class UpdateConnectorSuccess implements Action {
    readonly type = UPDATE_CONNECTOR_SUCCESS;
    constructor(public payload: Connector) { }
}

// remove nodes
export const REMOVE_CONNECTOR = '[Flowchart] Remove Connector';
export const REMOVE_CONNECTOR_FAIL = '[Flowchart] Remove Connector Fail';
export const REMOVE_CONNECTOR_SUCCESS = '[Flowchart] Remove Connector Success';

export class RemoveConnector implements Action {
    readonly type = REMOVE_CONNECTOR;
    constructor(public payload: Connector[]) { }
}

export class RemoveConnectorFail implements Action {
    readonly type = REMOVE_CONNECTOR_FAIL;
    constructor(public payload: Connector[]) { }
}

export class RemoveConnectorSuccess implements Action {
    readonly type = REMOVE_CONNECTOR_SUCCESS;
    constructor(public payload: Connector[]) { }
}

// update nodes
export const UPDATE_CONNECTOR_TYPE = '[Flowchart] Update Connector Type';
export const UPDATE_CONNECTOR_TYPE_FAIL = '[Flowchart] Update Connector Type Fail';
export const UPDATE_CONNECTOR_TYPE_SUCCESS = '[Flowchart] Update Connector Type Success';

export class UpdateConnectorType implements Action {
    readonly type = UPDATE_CONNECTOR_TYPE;
    constructor(public payload: any) { }
}

export class UpdateConnectorTypeFail implements Action {
    readonly type = UPDATE_CONNECTOR_TYPE_FAIL;
    constructor(public payload: any) { }
}

export class UpdateConnectorTypeSuccess implements Action {
    readonly type = UPDATE_CONNECTOR_TYPE_SUCCESS;
    constructor(public payload: any) { }
}

// action types
export type ConnectorsAction =
    | LoadConnectors
    | LoadConnectorsFial
    | LoadConnectorsSuccess
    | CreateConnectors
    | CreateConnectorsFail
    | CreateConnectorsSuccess
    | UpdateConnector
    | UpdateConnectorFail
    | UpdateConnectorSuccess
    | RemoveConnector
    | RemoveConnectorFail
    | RemoveConnectorSuccess
    | UpdateConnectorType
    | UpdateConnectorTypeFail
    | UpdateConnectorTypeSuccess;
