import { ChartData } from 'src/app/core/models/chart-data.model';
import { IData } from './../../../models/base.interface';
import { Action } from '@ngrx/store';
import { Project } from 'libs/flowchart-ts/flowchart-core/src/lib/models/project.model';

// load current project
export const LOAD_CURRENT_PROJECT = '[Flowchart] Load Current Project';
export const LOAD_CURRENT_PROJECT_FAIL = '[Flowchart] Load Current Project Fail';
export const LOAD_CURRENT_PROJECT_SUCCESS = '[Flowchart] Load Current Project Success';

export class LoadCurrentProject implements Action {
    readonly type = LOAD_CURRENT_PROJECT;
    constructor(public payload: string) { }
}

export class LoadCurrentProjectFail implements Action {
    readonly type = LOAD_CURRENT_PROJECT_FAIL;
    constructor(public payload: any) { }
}

export class LoadCurrentProjectSuccess implements Action {
    readonly type = LOAD_CURRENT_PROJECT_SUCCESS;
    constructor(public payload: Project) { }
}

export const SELECT = '[Flowchart] Select';
export class Select implements Action {
    readonly type = SELECT;
    constructor(public payload: ChartData) { }
}

// remove
export const REMOVE_DATA = '[Flowchart] Remove Data';
export const CREATE_DATA = '[Flowchart] Create Data';

export class RemoveData implements Action {
    readonly type = REMOVE_DATA;
    constructor(public payload: IData[]) { }
}

export const UPDATE_PROJECT = '[Flowchart] Update Project';
export const UPDATE_PROJECT_SUCCESS = '[Flowchart] Update Project Success';
export const UPDATE_PROJECT_FAILURE = '[Flowchart] Update Project Success';
export class UpdateProject implements Action {
    readonly type = UPDATE_PROJECT;
    constructor(public payload: Project) { }
}

export class UpdateProjectSuccess implements Action {
    readonly type = UPDATE_PROJECT_SUCCESS;
    constructor(public payload: Project) { }
}

export class UpdateProjectFailure implements Action {
    readonly type = UPDATE_PROJECT_FAILURE;
    constructor(public payload: Project) { }
}




// action types
export type CurrentProjectAction =
    | LoadCurrentProject
    | LoadCurrentProjectFail
    | LoadCurrentProjectSuccess
    | Select
    | UpdateProject
    | UpdateProjectSuccess
    | UpdateProjectFailure;
