import { animate, animation, state, style, transition, trigger } from '@angular/animations';


export const slideIn = trigger('slide-in', [
    state('collapsed', style({
        width: '0',
        opacity: 0
    })),
    state('visible', style({
    })),
    transition('visible => collapsed', [animate('0.2s ease-in')]),
    transition('collapsed => visible', [animate('0.2s ease-out')])
]);
