import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { Point } from 'src/app/models/point.model';

export class ResponseData {
    uId: string;
}
export class AppendNodeJson {
    project: string;
    chart: string;
    uid: string;
    content: string;
    pos: Point;
    inGroup: boolean;
    parent?: number;
    copyid?: number;
    constructor(project: string, chart: string, newNode: ChartNode, parent?: number, copyid?: number) {
        this.project = project;
        this.chart = chart;
        this.content = newNode.text;
        this.pos = new Point(newNode.rect.x, newNode.rect.y);
        this.parent = parent;
        this.copyid = copyid;
        this.uid = newNode.text;
    }
}

export class RemoveNodeJson {
    project: string;
    chart: string;
    uId: number;
    constructor(project: string, chart: string, uId: number) {
        this.project = project;
        this.chart = chart;
        this.uId = uId;
    }
}
// 'project':'new-test', 'chart':'AI.test_01', 'start' : start, 'end': end, 'type': type
export class ConnectorJson {
    uid: string;
    project: string;
    chart: string;
    start: string;
    end: string;
    type: number;
    shape: number;
    constructor(uid: string, project: string, chart: string, start: string, end: string, type: number, shape?: number) {
        this.uid = uid;
        this.project = project;
        this.chart = chart;
        this.start = start;
        this.end = end;
        this.type = type;
        this.shape = shape;
    }
}
export class DisConnectorJson {
    project: string;
    chart: string;
    start: string;
    end: string;
    type: number;
    constructor(project: string, chart: string, start: string, end: string) {
        this.project = project;
        this.chart = chart;
        this.start = start;
        this.end = end;
    }
}
