import { Rect } from './../../../../flowchart-common/src/lib/models/rect.model';
import { Point } from './../../../../flowchart-common/src/lib/models/point.model';
import { ChartJsonKey, ConnectorJsonKey } from './../constants/chart.constants';
import { Chart } from './chart.model';
import { ChartNode } from './node.model';

/**
 * Connector type
 */
export enum ConnectorType {
    ERROR = -1,
    FAIL = 0,
    SUCCESS = 1,
    ALWAYS = 2
}

/**
 * Shape of connector line path
 */
export enum ShapeType {
    LINE = 0,
    POLYLINE = 1,
    CURVE = 2
}

export class ConnectorPathOptions {
    /**
     * Offset to the start point and end point
     */
    offset?: number = 10;
}

/**
 * Connector, line between two nodes which is symbol of the execution and sequence relation of the start node and end node
 */
export class Connector {
    /**
     * id
     */
    uid: string;
    /**
     * The type of the connector， 0 always execute 1 success 2 failure
     */
    type: ConnectorType;
    /**
     * Shape of the connector, it could be 0 line 1 polyline and 2 curve
     */
    shape: ShapeType;
    /**
     * start node
     */
    start: ChartNode;
    /**
     * end node
     */
    end: ChartNode;
    /**
     * 
     */
    points: Point
    /**
     * Connector line path, it won't be stored in json files
     */
    path?: string;
    /**
     * belonged chart
     */
    chart: Chart;
    /**
     * Connector tag
     */
    readonly tag: string = 'connector';

    constructor(start: ChartNode, end: ChartNode, type: ConnectorType, shape?: ShapeType) {
        this.start = start;
        this.end = end;
        this.type = type === undefined || type === null ? ConnectorType.ALWAYS : type;
        this.shape = shape === undefined || shape === null ? ShapeType.CURVE : shape;
    }

    /**
     * load data from json 
     * @param json 
     * @param chart 
     */
    fromJson(json: any, chart: Chart) {
        this.uid = json[ChartJsonKey.Uid];
        this.start = chart.nodeMap.get(json[ConnectorJsonKey.Start]);
        this.end = chart.nodeMap.get(json[ConnectorJsonKey.End]);
        this.shape = json[ChartJsonKey.Shape];
        this.type = json[ChartJsonKey.Type];
    }

    toJson() {
        const c: any = {};
        c[ChartJsonKey.Uid] = this.uid;
        c[ChartJsonKey.Start] = this.start.uid;
        c[ChartJsonKey.End] = this.end.uid;
        c[ChartJsonKey.Shape] = this.shape;
        c[ChartJsonKey.Type] = this.type;
        return c;
    }



    getLinePath(start?: Point, end?: Point) {
        if (!start) {
            start = new Point(this.start.rect.cx, this.start.rect.y + this.start.rect.h);
        }

        if (!end) {
            end = new Point(this.end.rect.cx, this.end.rect.y);
        }
        let isSelfConn = this.start.uid === this.end.uid;
        switch (this.shape) {
            case ShapeType.LINE:
                if (isSelfConn) {
                    return this.drawSelfConnectorPath(start, end);
                } else {
                    return `M ${start.x} ${start.y}, L ${end.x} ${end.y}`;
                }
            case ShapeType.POLYLINE:
                return this.drawPolyline2(start, end);
            case ShapeType.CURVE:
                if (isSelfConn) {
                    return this.drawSelfConnectorPath(start, end);
                } else {
                    return this.drawCurve(start, end);
                }
        }
    }

    private drawCurve(start: Point, end: Point) {
        const startTransPos = this.getTransitionPos(start, 'bottom', 0);
        const endTransPos = this.getTransitionPos(end, 'top', 10);
        const offsetX = endTransPos.x - startTransPos.x;
        const offsetY = endTransPos.y - startTransPos.y;
        const gapX = Math.abs(offsetX / 3);
        const gapY = Math.abs(offsetY / 3);
        if (Math.abs(offsetX) < 5 && offsetY > this.start.rect.h) {
            return `M${start.x},${start.y} L${end.x} ${end.y}`;
        } else if (offsetY < 0) {
            // return this.getPolylineUpwards(start, end, connector);
            return this.drawCurveUpwards(start, end);
        } else {
            const secDot = `${startTransPos.x},${startTransPos.y + gapY}`;
            const thirdDot = `${endTransPos.x},${endTransPos.y - gapY}`;
            return `M${start.x},${start.y} L${startTransPos.x},${startTransPos.y} C${secDot} ${thirdDot} ${endTransPos.x},${endTransPos.y} L${end.x} ${end.y}`;
        }
    }

    private drawPolyline2(start: Point, end: Point) {
        let points = this.generatePolygonePoints(start, end);
        let showPath = this.pointsToPath(points);
        return showPath;
    }

    /**
   * 生成向上连的折线关键点列
   * @param stRect 
   * @param enRect 
   */
    private genPolylinePointsUpward(start: Point, end: Point, stRect: Rect, enRect: Rect, offset: number) {
        // const start = { x: stRect.cx, y: stRect.y + stRect.h };
        // const end = { x: enRect.cx, y: enRect.y };
        let points = [];
        const p1 = new Point(start.x, start.y + offset);
        const p5 = new Point(end.x, end.y - offset);
        // 起始节点右端位于结束节点左端的左边
        if (stRect.x + stRect.w < enRect.x) {
            const x = (stRect.x + stRect.w + enRect.x) / 2;
            const p2 = new Point(x, start.y + offset);
            const p3 = new Point(x, (start.y + end.y) / 2);
            const p4 = new Point(x, end.y - offset);
            points = [start, p1, p2, p3, p4, p5, end];
        } else if (enRect.x + enRect.w < stRect.x) {
            const x = (stRect.x + enRect.w + enRect.x) / 2;
            const p2 = new Point(x, start.y + offset);
            const p3 = new Point(x, (start.y + end.y) / 2);
            const p4 = new Point(x, end.y - offset);
            points = [start, p1, p2, p3, p4, p5, end];
        } else {
            const left = Math.min(stRect.x, enRect.x);
            const right = Math.max(stRect.x + stRect.w, enRect.x + enRect.w);
            const side = Math.abs(start.x - left) > Math.abs(start.x - right) ? right + offset : left - offset;
            const p2 = new Point(side, start.y + offset);
            const p3 = new Point(side, (start.y + end.y) / 2);
            const p4 = new Point(side, end.y - offset);
            points = [start, p1, p2, p3, p4, p5, end];
        }
        return points;
    }

    generatePolygonePoints(start: Point, end: Point, options?: ConnectorPathOptions) {
        let points: Point[] = [];
        const offset = options ? options.offset : 10;
        let stRect: Rect, enRect: Rect;
        if (!this) {
            stRect = Rect.createWithCenter(start.x, start.y - 18, 144, 36);
            enRect = Rect.createWithCenter(end.x, end.y + 18, 144, 36);
        } else {
            stRect = this.start.rect;
            enRect = this.end.rect;
        }
        if (this) {
            if (start.y < end.y) {
                const middleY = (start.y + end.y) / 2;
                const p1 = new Point(start.x, middleY);
                const p2 = new Point(end.x, middleY);
                points = [start, p1, p2, end];
            } else {
                points = this.genPolylinePointsUpward(start, end, stRect, enRect, offset);
            }
        }
        return points;
    }

    pointsToPath(points: Point[]) {
        let path = '';
        let i = 0;
        for (const point of points) {
            if (i === 0) {
                path += `M${point.x},${point.y}`;
            } else {
                path += ` L${point.x},${point.y}`;
            }
            i++;
        }
        return path;
    }

    drawSelfConnectorPath(start: Point, end: Point) {
        const rect = this.start.rect;
        const halfWidth = rect.w / 2;
        // 三次贝塞尔曲线
        return `M${start.x + halfWidth},${start.y - 2} C${start.x + halfWidth} ${start.y - 2} ${start.x + halfWidth + 40} ${(start.y + end.y) / 2} ${end.x + halfWidth} ${end.y + 2} `;
        // 圆形
        // return this.drawArcByRadiusDeg(start.x + rect.w / 2 - 18, start.y, 36, 270, false);
    }

    //#region private methods
    private getTransitionPos(point: Point, anchorKey: 'top' | 'bottom', trans: number) {
        if (point != null) {
            const x: number = point.x;
            let y: number = point.y;
            switch (anchorKey) {
                case 'top':
                    y = point.y - trans;
                    break;
                case 'bottom':
                    y = point.y + trans;
                    break;
            }
            return { x, y };
        }
        return null;
    }

    private drawCurveUpwards(start: Point, end: Point) {
        const pnts = this.generatePolygonePoints(start, end);
        pnts[4].y -= 20;
        pnts[5].y -= 20;
        const paths: string[] = [];
        pnts.forEach(p => paths.push(`${p.x} ${p.y}`));
        let path = `M${paths[0]} `;
        // path += `Q${paths[1]} ${paths[2]}`;
        // path += this.curvV(points[2], points[4], points[1].x > points[2].x, points[4].x < points[5].x);
        // path += `Q${paths[5]} ${paths[6]}`;
        path += `C${this.coor(pnts[1])} ${this.coor(pnts[2])} ${this.coor(pnts[3])}`;
        path += `C${this.coor(pnts[4])} ${this.coor(pnts[5])} ${this.coor(pnts[6])}`;
        return path;
    }

    coor(point: Point) {
        return `${point.x} ${point.y}`;
    }

}