// load groups
export const LOAD_GROUPS = '[Flowchart] Load Groups';
export const LOAD_GROUPS_FAIL = '[Flowchart] Load Groups Fail';
export const LOAD_GROUPS_SUCCESS = '[Flowchart] Load Groups Success';

export class LoadGroups implements Action {
    readonly type = LOAD_GROUPS;
    constructor(public nodes: Group[]) {
        console.log(nodes);
    }
}

export class LoadGroupsFial implements Action {
    readonly type = LOAD_GROUPS_FAIL;
    constructor(public payload: any) { }
}

export class LoadGroupsSuccess implements Action {
    readonly type = LOAD_GROUPS_SUCCESS;
    constructor(public payload: Group[]) {
        console.log(payload);
    }
}

