import { MessageService } from './message.service';
import { Injectable } from '@angular/core';
import { MessageModel } from 'src/app/models/message.model';

export enum OutputLevel {
  log = 0,
  warn = 1,
  error = 99
}

export const OUTPUT_FLAG = 'output';
export const OUTPUT_TAG = 'output';

@Injectable({
  providedIn: 'root'
})
export class OutputService {

  constructor(private messageService: MessageService) { }

  private output(msg: string, level: OutputLevel) {
    this.messageService.publish(new MessageModel(OUTPUT_FLAG, { msg, level }), 'output');
  }

  log(msg: string) {
    this.output(msg, OutputLevel.log);
  }

  warn(msg: string) {
    this.output(msg, OutputLevel.warn);
  }

  error(msg: string) {
    this.output(msg, OutputLevel.error);
  }
}
