import { CreateGroupCommand, UpdateGroupCommand, RemoveGroupCommand, AddNodeToGroupCommand, RemoveNodeFromGroupCommand, LayoutGroupCommand } from './../../models/commands/group.commands';

import { AddNodeToGroupArgs } from './../../models/request-arguments/group.request.args.model';
import { OperationService } from './operation.service';
import { HttpClient } from '@angular/common/http';
import { COMMAND_NAME } from './../../constants/command/command.constants';
import { Injectable } from '@angular/core';
import { Store } from '@ngrx/store';
import * as fromStore from '../../store';
import { Point } from 'src/app/models/point.model';
import { HttpResult } from 'src/app/common/http/http.result.model';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';
import { Group, LayoutType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/group.model';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';

@Injectable({
  providedIn: 'root'
})
export class GroupService {

  constructor(private http: HttpClient,
    private operationService: OperationService,
    private store: Store<fromStore.FlowchartState>,
  ) { }

  // 根据group中的所有节点, 返回容纳所有group节点的四条边
  getNodesBorderRect(groupNodes: ChartNode[], padding = 15): Rect {
    return this.getRectsBorder(groupNodes.map(n => n.rect), padding);
  }

  getRectsBorder(rects: Rect[], padding: number = 15) {
    const bigNum = -999999;
    let leftmost = bigNum;
    let rightmost = bigNum;
    let topmost = bigNum;
    let bottommost = bigNum;
    // leftmost = rightmost = topmost = bottommost = -999999;
    for (const rect of rects) {
      // 在遍历中分别求出所选中节点的四个方向的最值
      // 左
      if (leftmost === bigNum || leftmost > rect.x) {
        leftmost = rect.x;
      }
      // 上
      if (topmost === bigNum || topmost > rect.y) {
        topmost = rect.y;
      }
      // 右
      if (rightmost === bigNum || rightmost < rect.x + rect.w) {
        rightmost = rect.x + rect.w;
      }
      // 下
      if (bottommost === bigNum || bottommost < rect.y + rect.h) {
        bottommost = rect.y + rect.h;
      }
    }
    const x = leftmost - padding;
    const y = topmost - padding;
    const w = rightmost - leftmost + padding * 2;
    const h = bottommost - topmost + padding * 2;
    const rect = Rect.createWithLeftTopVertex(x, y, w, h);
    return rect;
  }

  createGroup(chart: Chart, group: Group) {
    const createGroupCmd = new CreateGroupCommand(group.chart, group);
    const result = this.operationService.executeChartCommands(chart.uid, [createGroupCmd]);
    return result;
  }

  updateGroup(chart: Chart, group: Group, kv: Map<string, any>) {
    const cmd = new UpdateGroupCommand(group, kv);
    const httpResult = this.operationService.executeChartCommands(chart.uid, [cmd]);
    return httpResult;
  }

  removeGroup(chart: Chart, groups: Group[]) {
    const cmds = [];
    for (const group of groups) {
      const cmd = new RemoveGroupCommand(chart, group)
      cmds.push(cmd);
    }
    return this.operationService.executeChartCommands(chart.uid, cmds);
  }

  removeNodeFromGroup(chart: Chart, nodes: ChartNode[]) {
    const cmds = [];
    for (const node of nodes) {
      const cmd = new RemoveNodeFromGroupCommand(node.group, node);
      cmds.push(cmd);
    }

    return this.operationService.executeChartCommands(chart.uid, cmds);
  }

  autoLayout(chart: Chart, group: Group) {
    const cmd = new LayoutGroupCommand(chart, group);
    return this.operationService.executeChartCommands(chart.uid, [cmd]);
  }

  async addNodeToGroup(chart: Chart, group: Group, nodes: ChartNode[]) {
    const cmds = [];
    for (const node of nodes) {
      const cmd = new AddNodeToGroupCommand(group, node);
      cmds.push(cmd);
    }
    return this.operationService.executeChartCommands(chart.uid, cmds);
  }

  // 移除选中的group
  async removeGroupSelection() {
    const chart = await this.store.select(fromStore.getCurrentChartDetails).toPromise();
    if (chart !== undefined && chart.groups !== undefined) {
      chart.groups.forEach(g => {
        // if (g.isSelected) {
        //   const newGroup: Group = {
        //     ...g,
        //     isSelected: false
        //   };
        //   this.store.dispatch(new fromStore.UpdateGroup(newGroup));
        // } TODO
      });
    }
  }

  async layout(group: Group) {
    const url = 'api/chart/group/layout';
    const result = await this.http.post<HttpResult<Map<string, Point>>>(url, { project: group.chart.project, chart: group.chart.uid, group: group.uid, layout: group.layout }).toPromise();
    return result;
  }


  async mergeGroup(chart: Chart, groups: Group[], nodes: ChartNode[], newUid: string) {
    const cmds = [];
    const newNodes = [];
    newNodes.push(...nodes);
    groups.forEach(g => {
      newNodes.push(...g.nodes);
      const cmd = new RemoveGroupCommand(chart, g);
      cmds.push(cmd);
    });
    const newGroup = new Group();
    newGroup.uid = newUid;
    newGroup.name = groups[0].name;
    newGroup.nodes = [...new Set(newNodes)]
    newGroup.layout = LayoutType.MANUAL;
    const createCmd = new CreateGroupCommand(chart, newGroup);
    cmds.push(createCmd);
    return this.operationService.executeChartCommands(chart.uid, cmds);
  }

  getNodeGroupMap(chart: Chart): Map<string, Group> {
    const nodeGroupMap = new Map<string, Group>();
    if (chart && chart.groups) {
      chart.groups.forEach(g => {
        g.nodes.forEach(n => {
          if (!nodeGroupMap.has(n.uid)) {
            nodeGroupMap.set(n.uid, g);
          }
        });
      });
    }
    return nodeGroupMap;
  }

  judgeNodeInGroup(chart: Chart, nodes: ChartNode[]) {
    if (!chart.groups || chart.groups.length === 0) {
      return false;
    }
    const nodeGroupMap = this.getNodeGroupMap(chart);
    let exist = false;
    chart.groups.forEach(g => {
      nodes.forEach(n => {
        if (nodeGroupMap.has(n.uid)) {
          exist = true;
        }
      });
    });
    return exist;
  }

}
