import { PathDialogConfig, PathType } from './../path-info.model';
import { PathService } from './../path.service';
import { Component, Inject, OnInit } from '@angular/core';
import { PathInfo } from '../path-info.model';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';

@Component({
  selector: 'app-path-picker',
  templateUrl: './path-picker.component.html',
  styleUrls: ['./path-picker.component.css']
})
export class PathPickerComponent implements OnInit {

  parentPath: PathInfo;
  currentPath: PathInfo;
  children: PathInfo[];
  selectedPath: PathInfo;
  error: string;
  constructor(
    private pathService: PathService,
    private dialogRef: MatDialogRef<PathPickerComponent>,
    @Inject(MAT_DIALOG_DATA) public data: PathDialogConfig) { }

  ngOnInit() {
    this.initData();
  }

  filter(infos: PathInfo[]) {
    const hasDrive = infos.find(pi => pi.type === PathType.DRIVE);
    const filtered: PathInfo[] = [];
    if (!hasDrive) {
      const parentPath = new PathInfo();
      parentPath.name = '..';
      parentPath.type = PathType.DIR;
      parentPath.path = this.parentPath.path;
      filtered.push(parentPath);
    }
    infos.forEach(pi => {
      if (this.data && this.data.filter) {
        const filter = this.data.filter;
        const prefix = filter.prefix ? filter.prefix.toLowerCase() : undefined;
        const suffix = filter.suffix ? filter.suffix.toLowerCase() : undefined;
        if ((filter.type === 'file' && (!prefix || pi.name.startsWith(prefix)) && (!suffix || pi.name.endsWith(suffix))) || pi.type !== PathType.FILE) {
          filtered.push(pi);
        }
      } else {
        filtered.push(pi);
      }
    });
    return filtered;
  }

  initData() {
    if (this.data.path) {
      let path = this.data.path.replace(/\//g, '\\');
      if (path.endsWith('\\')) {
        path = path.substr(0, path.length - 1);
      }
      const parent = new PathInfo();
      parent.type = PathType.DIR;
      parent.path = path.substring(0, path.lastIndexOf('\\'));
      this.parentPath = parent;
      this.currentPath = new PathInfo();
      this.currentPath.path = this.data.path;
      this.currentPath.type = PathType.DIR;
    }
    this.pathService.getChildrenPathList(this.currentPath ? this.currentPath.path : null).then(result => {
      this.children = this.filter(result.data);
      this.parentPath = new PathInfo();
      this.parentPath.name = '';
      this.parentPath.path = '';
      this.parentPath.type = PathType.DIR;
    }).catch(error => {

    });

  }

  showError(text: string) {
    this.error = text;
    setTimeout(() => {
      this.error = null;
    }, 2 * 1000);
  }

  onFolderClick($event, folder: PathInfo) {
    // this.currentPath = folder.path;
    if (folder.name !== '..') {
      this.selectedPath = folder;
    } else {
      this.selectedPath = this.currentPath;
    }
  }

  onFolderDblClick($event, folder: PathInfo) {
    if (folder.type !== PathType.FILE) {
      const path = folder.name === '..' ? this.parentPath : folder;
      this.pathService.getChildrenPathList(path.path).then(result => {
        this.parentPath = new PathInfo();
        // 如果当前path是磁盘驱动盘符，那么父目录就是电脑磁盘概览
        if (path.type === PathType.DRIVE) {
          this.parentPath.path = '';
          this.parentPath.type = PathType.ROOT;
        } else {
          if (PathInfo.isDrive(folder.path)) {
            this.parentPath.path = '';
            this.parentPath.type = PathType.DRIVE;
          } else {
            this.parentPath.type = PathType.DIR;
            this.parentPath.path = folder.path.substring(0, folder.path.lastIndexOf('\\') + 1);
            if (!PathInfo.isDrive(this.parentPath.path)) {
              this.parentPath.path = this.parentPath.path.substr(0, this.parentPath.path.length);
            }
          }
        }
        this.currentPath = path;
        this.selectedPath = null;
        this.children = this.filter(result.data);
      }).catch(error => {
        this.showError(`${folder.path}不存在或者无权限访问`);
      });
    }
  }

  onSelectPathClick($event) {
    if (this.data.filter.type === 'file' && this.selectedPath && this.selectedPath.type !== PathType.FILE) {
      this.error = '需要选中路径而非文件';
      return;
    }
    this.dialogRef.close(this.selectedPath ? this.selectedPath.path : this.currentPath.path);
  }
}
