import { Action, props } from '@ngrx/store';
import { createAction } from '@ngrx/store';

export const CHANGE_LATEST_CONNECTOR_SHAPE = '[Flowchart] Change Latest Connector Shape';
export const CHANGE_LATEST_CONNECTOR_SHAPE_SUCCESS = '[Flowchart] Change Latest Connector Shape Success';

export class ChangeLatestConnectorShape implements Action {
    readonly type = CHANGE_LATEST_CONNECTOR_SHAPE;
    constructor(public payload: any) {
    }
}

export class ChangeLatestConnectorShapeSuccess implements Action {
    readonly type = CHANGE_LATEST_CONNECTOR_SHAPE_SUCCESS;
    constructor(public shape: number) {
    }
}

export const CHANGE_LATEST_CONNECTOR_TYPE = '[Flowchart] Change Latest Connector Type';
export const CHANGE_LATEST_CONNECTOR_TYPE_SUCCESS = '[Flowchart] Change Latest Connector Type Success';

export class ChangeLatestConnectorType implements Action {
    readonly type = CHANGE_LATEST_CONNECTOR_TYPE;
    constructor(public payload: any) {
    }
}

export class ChangeLatestConnectorTypeSuccess implements Action {
    readonly type = CHANGE_LATEST_CONNECTOR_TYPE_SUCCESS;
    constructor(public Type: number) {
    }
}

// export const FIX_NODE_POSITION = createAction('[Flowchart] Fix Node Position');
export const FIX_NODE_POSITION_SUCCESS = '[Flowchart] Fix Node Position Success';
export class FixNodePositionSuccess implements Action {
    readonly type = FIX_NODE_POSITION_SUCCESS;
    constructor(public payload: any) {
    }
}

export const FIX_CHART_POSITION_SUCCESS = '[Flowchart] Fix Chart Position Success';
export class FixChartPositionSuccess implements Action {
    readonly type = FIX_CHART_POSITION_SUCCESS;
    constructor() { }
}


export type ToolbarAction =
    | ChangeLatestConnectorShape
    | ChangeLatestConnectorShapeSuccess
    | ChangeLatestConnectorType
    | ChangeLatestConnectorTypeSuccess
    | FixNodePositionSuccess
    | FixChartPositionSuccess;
