export class Rect {
    /**
     * 左上角顶点横坐标
     */
    x: number;
    /**
     * 左上角顶点纵坐标
     */
    y: number;
    /**
     * 宽度
     */
    w: number;
    /**
     * 高度
     */
    h: number;
    /**
     * 中心点横坐标
     */
    cx: number;
    /**
     * 中心点纵坐标
     */
    cy: number;

    constructor() {

    }

    //#region static methods
    static createWithCenter(cx: number, cy: number, w: number, h: number) {
        const rect = new Rect();
        rect.cx = cx;
        rect.cy = cy;
        rect.w = w;
        rect.h = h;
        rect.x = cx - w / 2;
        rect.y = cy - h / 2;
        return rect;
    }

    static createWithLeftTopVertex(x: number, y: number, w: number, h: number) {
        const rect = new Rect();
        rect.x = x;
        rect.y = y;
        rect.w = w;
        rect.h = h;
        rect.cx = x + (w / 2);
        rect.cy = y + (h / 2);
        return rect;
    }
}