import { Injectable } from '@angular/core';
import { MessageModel } from '../../models/message.model';

@Injectable({
  providedIn: 'root'
})
export class MessageService {

  private messengerMap: Map<string, Map<any, [string, Function][]>> = new Map<string, Map<any, [string, Function][]>>();

  // 以target为key，方便取消订阅， value为一个msgFlag和回调函数构成的结构数组
  private messageMap: Map<any, [string, Function][]>;


  constructor() {
  }

  private fetchMessageMap(tag: string) {
    if (!this.messengerMap.has(tag)) {
      this.messengerMap.set(tag, new Map<any, [string, Function][]>());
    }
    this.messageMap = this.messengerMap.get(tag);
  }

  public publish(msg: MessageModel, tag = 'default') {
    this.fetchMessageMap(tag);
    this.messageMap.forEach((v, k) => {
      const func = v.find(ff => ff[0] == msg.flag);
      if (func != null) {
        func[1](msg);
      }
    });

  }

  public subscribe(target: any, msgFlag: string, handler: Function, tag = 'default') {
    this.fetchMessageMap(tag);
    let callbacks: [string, Function][];
    if (!this.messageMap.has(target)) {
      this.messageMap.set(target, []);
    }
    callbacks = this.messageMap.get(target);
    callbacks.push([msgFlag, handler]);
  }

  public unsubscribe(target: any, msgFlag: string, tag = 'default') {
    this.fetchMessageMap(tag);
    if (this.messageMap.has(target)) {
      const index = this.messageMap.get(target).findIndex(ff => ff[0] === msgFlag);
      if (index >= 0) {
        this.messageMap.get(target).splice(index);
      }
    }
  }

  public unsubscribeAll(target: any, tag = 'default') {
    this.fetchMessageMap(tag);
    if (this.messageMap.has(target)) {
      this.messageMap.delete(target);
    }
  }
}
