import { InterfaceService } from 'src/app/services/interface/interface.service';
import { Component, OnInit, Input, OnDestroy, Output, EventEmitter, OnChanges, SimpleChanges } from '@angular/core';
import { ChartType, GenericType } from 'libs/flowchart-ts/flowchart-core/src/public-api';
import { StringUtils } from 'libs/flowchart-ts/flowchart-common/src/lib/utils/string.utils';
import { UidUtils } from 'libs/flowchart-ts/flowchart-common/src/public-api';

const MAX_EMBBED_LEVEL = 2;

@Component({
  selector: 'app-type-select',
  templateUrl: './type-select.component.html',
  styleUrls: ['./type-select.component.css']
})
export class TypeSelectComponent implements OnInit, OnDestroy, OnChanges {


  @Input() types: ChartType[];

  @Input() level = 1;

  showListChild: boolean;
  listChildType: ChartType = ChartType.NumberType;

  showDictChildren: boolean;
  dictKeyType: ChartType = ChartType.StringType;
  dictValueType: ChartType = ChartType.NumberType;
  type: ChartType;
  @Input() width = 100;
  @Input() height = 24;
  @Input() showKey: string;
  @Input() valueKey: string;
  @Input() value: ChartType;
  @Output() valueChange: EventEmitter<ChartType> = new EventEmitter();
  @Output() typeChange: EventEmitter<ChartType> = new EventEmitter();
  @Input() needVoid = true;

  @Input() inParamTable = false;
  // 是否只支持基础数据类型  即不支持
  @Input() basicOnly = false;
  @Input() useInputTypes = false;
  constructor(private typeService: InterfaceService) { }

  ngOnInit() {
    this.subscribeMessage();
    this.initData();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.value != undefined && changes.value.currentValue != undefined) {
      this.initTypeValues(this.value);
    }
    if (changes.types && changes.types.currentValue) {
      this.initData();
      this.initTypeValues(this.value);
    }
  }

  subscribeMessage() {
    // this.messageService.subscribe(this, MSG_FLAG.CLASS_CHANGED, args => {
    //   this.initData();
    // });
    // this.messageService.subscribe(this, MSG_FLAG.CLASS_LIST_CHANGED, args => {
    //   this.initData();
    // });
    // this.messageService.subscribe(this, MSG_FLAG.EVENT_CHANGED, args => {
    //   this.initData();
    // });
    // this.messageService.subscribe(this, MSG_FLAG.EVENT_LIST_CHANGED, args => {
    //   this.initData();
    // });
  }

  initData() {
    if (!this.useInputTypes) {
      if (this.basicOnly) {
        this.types = this.typeService.getBasicTypeList();
      } else {
        this.types = this.typeService.getAllTypeList(this.needVoid);
        if (this.level > MAX_EMBBED_LEVEL) {
          this.types.splice(this.types.indexOf(ChartType.ListType), 1);
          this.types.splice(this.types.indexOf(ChartType.DictType), 1);
        }
      }
      let maxLength = 0;
      for (const t of this.types) {
        maxLength = Math.max(t.name.length, maxLength);
      }
      this.width = maxLength * 7 + 25;
    }
  }


  onSelectionChanged($event) {
    // this.initTypeValues(this.value);
    if ($event.target.value) {
      const tp = this.types[$event.target.selectedIndex]
      this.type = tp;
      if (this.type === ChartType.ListType) {
        this.showListChild = true;
        this.showDictChildren = false;
        this.listChildType = ChartType.AnyType;
        const t = new GenericType();
        t.name = `List<Any>`;
        t.outType = this.type;
        t.typeParams = [ChartType.AnyType];
        this.value = t;
      } else if (this.type === ChartType.DictType) {
        this.showDictChildren = true;
        this.showListChild = false;
        this.dictKeyType = ChartType.StringType;
        this.dictValueType = ChartType.AnyType;
        const t = new GenericType();
        t.uid = UidUtils.generateUid();
        t.name = `Dict<String,Any>`;
        t.outType = ChartType.DictType;
        t.typeParams = [this.dictKeyType, this.dictValueType];
        this.value = t;
      } else {
        this.showListChild = false;
        this.showDictChildren = false;
        this.value = tp;
      }
    } else {
      this.value = null;
    }
    // this.valueChange.emit(this.value);
    console.log('type select changed');
    this.valueChange.emit(this.value);
    this.typeChange.emit(this.value);
  }

  onListChildTypeSelectionChanged($event) {
    this.value['typeParams'][0] = this.listChildType;
    this.value.name = `List<${this.listChildType.name}>`
    this.valueChange.emit(this.value);
    this.typeChange.emit(this.value);
  }

  onDictKeyTypeSelectionChanged($event) {
    this.value['typeParams'] = [this.dictKeyType, this.dictValueType];
    this.valueChange.emit(this.value);
    this.typeChange.emit(this.value);
  }

  onDictValueTypeSelectionChanged($event) {
    this.value['typeParams'] = [this.dictKeyType, this.dictValueType];
    this.valueChange.emit(this.value);
    this.typeChange.emit(this.value);
  }

  ngOnDestroy(): void {
  }

  initTypeValues(parentType: ChartType) {
    if (parentType) {
      if (parentType instanceof GenericType) {
        if (parentType.outType === ChartType.ListType) {
          this.showListChild = true;
          this.showDictChildren = false;
          this.type = ChartType.ListType;
          this.listChildType = parentType.typeParams[0];
          this.value = parentType;
        } else if (parentType === ChartType.DictType) {
          this.showListChild = false;
          this.showDictChildren = true;
          this.dictKeyType = parentType.typeParams[0];
          this.dictValueType = parentType.typeParams[1];
          this.type = ChartType.DictType;
        }
      }
      else {
        this.showListChild = false;
        this.showDictChildren = false;
        this.type = this.value;
      }
    }
  }



}
