export enum TokenType {
    UNKOWN = 0,
    STRING = 1,
    NUMBER = 2,
    VARIABLE = 3,
    OPERATOR = 4,
    NAME = 5,
}

enum ConsoleColor {
    Gray = '2m',
    DarkMagenta = '35m',
    Green = '32m',
    Yellow = '33m',
    Blue = '34m'
}

export class Token {
    text: string;
    type: TokenType;

    /**
     * Token id to token type( ref: NodeCommand.tokens)
     * @param tokenId 
     * @returns 
     */
    static getTokenType(tokenId: number) {
        if (tokenId < 38)
            return TokenType.OPERATOR;
        switch (tokenId) {
            case 38:
                return TokenType.VARIABLE;
            case 40:
                return TokenType.NAME;
            case 43:
            case 44:
            case 45:
            case 46:
                return TokenType.NUMBER;
            case 41:
            case 42:
                return TokenType.STRING;
            default:
                return TokenType.UNKOWN;
        }
    }

    static _colors: ConsoleColor[] = [
        ConsoleColor.Gray,
        ConsoleColor.DarkMagenta,
        ConsoleColor.Green,
        ConsoleColor.Yellow,
        ConsoleColor.Gray,
        ConsoleColor.Blue
    ]



    static print(token: Token) {
        console.log('\x1B[' + Token._colors[token.type] + token.text + '\x1B[0m');
    }
}