import { NodeRunningStatus, RunningStatus, ShowingRunningStatus } from './../models/debug-node-status.model';
import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'nodeRunningStatus'
})
export class NodeRunningStatusPipe implements PipeTransform {

  transform(status: NodeRunningStatus, ...args: any[]): any {
    switch (status.status) {
      case RunningStatus.IDLE:
        return ShowingRunningStatus.IDLE;
      case RunningStatus.RUNNING:
        return ShowingRunningStatus.RUNNING;
      case RunningStatus.END_RUNNING:
        if (status.success) {
          return ShowingRunningStatus.SUCCESS;
        } else {
          return ShowingRunningStatus.FAILURE;
        }
      default: return ShowingRunningStatus.FAILURE;
    }
  }

}
