import { Generator } from "../constants/custom.type";
import { ChartType } from "./type.model";

export class ProjectConfig {
    /**
     * 生成器类型 目前定义在{@linkcode Generator}中
     */
    generator: Generator;
    /**
     * 流程图根文件夹
     */
    flowchartTypeDict: Map<string, ChartType>;
    /**
     * 项目输出目录
     */
    outputPath: string;
    //#region Methods
    getChartType(chartPath: string) {
        const dotPos = chartPath.indexOf('.');
        let prefix = '';
        if (dotPos > -1) {
            prefix = chartPath.substr(0, dotPos);
        } else {
            prefix = chartPath;
        }
        if (this.flowchartTypeDict && this.flowchartTypeDict.has(prefix)) {
            return this.flowchartTypeDict.get(prefix);
        }
        return null;
    }

    updateTypeDict(typeDict: Map<string, ChartType>) {
        this.flowchartTypeDict = typeDict;
    }   

    toJson() {
        const typeObj:any = {};
        this.flowchartTypeDict.forEach((v, k)=> {
            typeObj[k] = v.name;
        })
        return {
            generator: this.generator,
            flowchart_type: typeObj,
            output_path: this.outputPath
        }
    }
}