import { Component, OnInit, ViewChild } from '@angular/core';
import { MatMenuTrigger } from '@angular/material';
import { TranslateService } from '@ngx-translate/core';

@Component({
  selector: 'app-choose-language',
  templateUrl: './choose-language.component.html',
  styleUrls: ['./choose-language.component.css']
})
export class ChooseLanguageComponent implements OnInit {
  @ViewChild('contextMenuTrigger', { static: false }) contextMenu: MatMenuTrigger;

  languageList = [
    {
      value: null,
    },
    {
      value: null,
    }
  ];
  constructor(
    public translateService: TranslateService,

  ) { }

  initLanguage() {
    this.translateService.get('chooseLanguage.Chinese').subscribe(data => {
      this.languageList[0].value = data;
    });

    this.translateService.get('chooseLanguage.English').subscribe(data => {
      this.languageList[1].value = data;
    });
  }

  ngOnInit() {
    this.initLanguage();
  }

  chooseLang(index: number) {
    const lang = index === 0 ? 'zh' : 'en';
    this.translateService.use(lang);
    this.initLanguage();
  }

}
