import { ExecuteResultCode } from './../../constants/command/command.constants';

import { Chart } from "libs/flowchart-ts/flowchart-core/src/lib/models/chart.model";
import { Connector } from "libs/flowchart-ts/flowchart-core/src/lib/models/connector.model";
import { Command, CommandExecuteResult } from "./command.model";

export class CreateConnectorCommand extends Command {

    chart: Chart;
    conn: Connector

    constructor(chart: Chart, conn: Connector) {
        super();
        this.chart = chart;
        this.conn = conn;
    }

    execute(): CommandExecuteResult {
        this.chart.connectors.push(this.conn);
        return new CommandExecuteResult(0, '');
    }

    revert(): CommandExecuteResult {

        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}

export class UpdateConnectorCommand extends Command {
    conn: Connector;
    updateKeyValuePairs: Map<string, any>;
    prevKeyValuePairs: Map<string, any>;
    constructor(conn: Connector, kv: Map<string, any>) {
        super();
        this.conn = conn;
        this.updateKeyValuePairs = kv;
        this.prevKeyValuePairs = new Map<string, any>();
    }

    execute(): CommandExecuteResult {
        try {
            this.updateKeyValuePairs.forEach((v, k) => {
                this.prevKeyValuePairs.set(k, this.conn[k]);
                this.conn[k] = v;
                if (k === 'shape') {
                    this.prevKeyValuePairs.set('path', this.conn.path);
                    this.conn.path = this.conn.getLinePath();
                }
            })

        } catch (error) {
            return new CommandExecuteResult(-1, error.message);
        }
        return new CommandExecuteResult(0, '');
    }

    revert(): CommandExecuteResult {
        this.prevKeyValuePairs.forEach((v,k)=>{
            this.conn[k]=v;
        })
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}

export class RemoveConnectorCommand extends Command {
    chart: Chart;
    conn: Connector;
    constructor(chart: Chart, conn: Connector) {
        super();
        this.chart = chart;
        this.conn = conn;
    }

    execute() {
        const index = this.chart.connectors.findIndex(c => c.start.uid === this.conn.start.uid && c.end.uid === this.conn.end.uid);
        if (index === -1) {
            return new CommandExecuteResult(ExecuteResultCode.ERROR, `Connector with start(${this.conn.start.text}) end(${this.conn.end.text})`);
        }
        this.chart.connectors.splice(index, 1);
        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }

    revert(): CommandExecuteResult {

        return new CommandExecuteResult(ExecuteResultCode.OK, '');
    }
}