export enum NodeViewType {
    /**
     * 起始节点，一幅图中有且只有一个
     */
    START = 1,
    /**
     * 普通节点，直接写代码的节点
     */
    COMMON,
    /**
     * 等待节点
     */
    WAIT,
    /**
     * 等待全部，即等待全部前序节点
     */
    WAITALL,
    /**
     * 停止指定节点
     */
    STOP_NODE,
    /**
     * 停止指定节点及之后流程
     */
    STOP_FLOW,
    /**
     * 表达式节点
     */
    EXPRESS,
    /**
     * 函数节点
     */
    FUNCTION,
    /**
     * 事件节点
     */
    EVENT,
    /**
     * 子图节点
     */
    SUBCHART
}

/**
 * Parse result
 */
export enum ParseResult {
    NORMAL = 0,
    WARNING = 1,
    ERROR = 2
}

/**
 * Node parse info type
 */
export enum ParseInfoType {
    PLAIN = 0,
    EVENT = 1,
    SUBCHART = 2,
    ASYNC = 3,
    CONTROL = 4
}


export enum NodeTokenType {
    UNKOWN = 0,
    STRING = 1,
    NUMBER = 2,
    VARIABLE = 3,
    OPERATOR = 4,
    NAME = 5,
}


export enum DebugType {
    NODE = 'node_status',
    EVENT = 'event_status',
    VARIABLE = 'variable_status'
}

export enum RunningStatus {
    IDLE = 0,
    RUNNING = 1,
    END_RUNNING = 2
}

export enum ShowingRunningStatus {
    IDLE = 'idle',
    RUNNING = 'running',
    SUCCESS = 'success',
    FAILURE = 'failure'
}

export enum NodeSource {
    MANUAL_ADD,
    COPY,
    REDO_UNDO
}