import { ExecuteResultCode } from './../../../../constants/command/command.constants';
import { InterfaceService } from './../../../../services/interface/interface.service';
import { KEY_CODE } from './../../../../constants/common/key.code.constants';
import { StringUtils } from './../../../../utils/string.utils';
import { INodeComponent } from './../node-component.interface';
import { Component, Input, OnInit, Output, ViewChild, ElementRef, OnChanges, SimpleChanges, ViewChildren, QueryList } from '@angular/core';
import { Store } from '@ngrx/store';
import * as fromStore from 'src/app/store';
import { EventEmitter } from '@angular/core';
import { ShowingRunningStatus } from 'src/app/debug/models/debug-node-status.model';
import { NodesService } from 'src/app/services/chart/nodes.service';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { Member, MemberType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/member.model';
import { ChartType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/type.model';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/public-api';


@Component({
  selector: 'app-function-node',
  templateUrl: './function-node.component.html',
  styleUrls: ['./function-node.component.css']
})
export class FunctionNodeComponent implements OnInit, OnChanges, INodeComponent {

  @Input() index: number;
  @Input() isSelected: boolean;
  @Input() isEditing: boolean;
  @Input() node: ChartNode;
  @Input() editNode: ChartNode;
  @Output() isEditingChange: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Output() updateNode = new EventEmitter<{ node: ChartNode, local: boolean, textChanged: boolean }>();

  @ViewChild('functionNodeRoot', { static: false, read: ElementRef }) root: ElementRef;
  @ViewChildren('paramInput') paramInputs: QueryList<ElementRef>;
  @Input() runningStatus: ShowingRunningStatus;

  functionList: Member[];
  currentFunction: Member;
  currentParamsValue: any[];
  constructor(
    private store: Store,
    private nodesService: NodesService,
    private typeService: InterfaceService
  ) { }

  ngOnInit() {
    this.initData();
  }

  ngAfterViewInit(): void {
    this.nodesService.updateRealNodeView(this.node);
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes.isEditing && changes.isEditing.currentValue) {

    }
  }
  initData() {
    this.editNode = this.node;
    Object.setPrototypeOf(this.editNode, ChartNode.prototype);
    // 当节点刚被拖入时,node.text为'',若未触发select的change事件,节点的currentFunction和currentParamsValue不能被正确初始化,需特殊处理
    if (this.node.text === '') {
      const initFunction = this.functionList[0];
      this.currentFunction = initFunction;
      if (this.currentFunction.parameters && this.currentFunction.parameters.length > 0) {
        this.currentParamsValue = new Array(this.currentFunction.parameters.length).fill('');
      } else {
        this.currentParamsValue = [];
      }
    } else if (/^[a-zA-Z]{1}[a-zA-Z0-9_]*\([a-zA-Z0-9\$_\+\-\*\/\,\s]*\)$/g.test(this.node.text)) {
      const index = this.node.text.indexOf('(');
      const funcName = this.node.text.substr(0, this.node.text.indexOf('('));
      const paramsValue = this.node.text.substring(index + 1, this.node.text.length - 1);
      this.currentFunction = this.functionList.find(f => f.name === funcName);
      if (!StringUtils.isNullOrWhiteSpace(paramsValue)) {
        this.currentParamsValue = paramsValue.split(', ');
      } else {
        this.currentParamsValue = [];
      }
    }

    this.initFunctions();
  }

  initFunctions() {
    this.functionList = this.fromChartTypes([this.node.chart.type, ChartType.GlobalType]);
    this.typeService.chartTypes.subscribe(types => {
      this.functionList = this.fromChartTypes([this.node.chart.type, ChartType.GlobalType]);
    })
  }

  fromChartTypes(chartTypes: ChartType[]) {
    const funcList = [];
    if (chartTypes) {
      chartTypes.forEach(ct => {
        if (ct.memberMap) {
          ct.memberMap.forEach((v, k) => {
            if (v.memberType === MemberType.METHOD) {
              funcList.push(v);
            }
          })
        }
      });
    }
    return funcList;
  }

  onKeyDown($event) {
    if (this.isEditing) {
      $event.stopPropagation();
    }
  }

  onFunctionChanged($event) {
    this.currentFunction = this.functionList[$event.target.selectedIndex];
    if (this.currentFunction.parameters && this.currentFunction.parameters.length > 0) {
      this.currentParamsValue = new Array(this.currentFunction.parameters.length).fill('');
    } else {
      this.currentParamsValue = [];
    }
  }

  onParamInput($event, i: number) {
    this.currentParamsValue[i] = $event.target.value;
  }

  focusInput(index: number) {
    this.paramInputs.toArray()[index].nativeElement.focus();
  }

  enterDownParamInput(i: number) {
    if (i === this.currentFunction.parameters.length - 1) {
      this.save();
    } else {
      this.focusInput(i + 1);
    }
  }

  onParamInputKeyDown($event, i: number) {
    switch ($event.keyCode) {
      case KEY_CODE.ENTER: {
        $event.stopPropagation();
        this.enterDownParamInput(i);
        break;
      }
    }
  }

  onClickOutside($event) {
    if (this.isEditing) {
      this.save();
    }
  }

  clickToSave($event, save = false) {
    if ($event.path[0].id !== 'select' && $event.path[0].id !== 'paramInput' || save) {
      if (this.isEditing) {
        this.save();
      }
    }
  }

  // save() {
  //   const text = `${this.currentFunction.name}(${this.currentParamsValue.join(', ')})`;
  //   const nodeview = document.getElementById(this.node.uid);
  //   const curHeight = nodeview.clientHeight;
  //   const newNode = {
  //     ...this.node,
  //     rect: Rect.createWithLeftTopVertex(this.node.rect.x, this.node.rect.y,
  //       this.nodesService.calculateNodeRenderWidth(text.length, NodeViewType.FUNCTION, this.node.text), curHeight),
  //     text
  //   };
  //   this.updateNode.emit({ node: newNode, local: false, textChanged: true });
  //   this.isEditing = false;
  //   this.isEditingChange.emit(false);
  // }

  save() {
    const text = `${this.currentFunction.name}(${this.currentParamsValue.join(', ')})`;
    const nodeview = document.getElementById(this.node.uid);
    const curWidth = this.nodesService.calculateNodeRenderWidth(text.length, this.node.viewType, text);
    const curHeight = nodeview.clientHeight;
    const newRect = Rect.createWithCenter(this.node.rect.cx, this.node.rect.cy, curWidth, curHeight);
    const result = this.nodesService.updateNode(this.node, new Map<string, any>([['text', text], ['rect', newRect]]));
    if (result.code === ExecuteResultCode.OK) {
      this.isEditing = false;
      this.isEditingChange.emit(false);
    }
  }
}
