import { Pipe, PipeTransform } from '@angular/core';
import { OutputLevel } from '../services/common/output.service';

@Pipe({
  name: 'msgColor'
})
export class MsgColorPipe implements PipeTransform {

  transform(level: OutputLevel, ...args: any[]): any {
    switch (level) {
      case OutputLevel.log:
        return 'white';
      case OutputLevel.warn:
        return 'orange';
      case OutputLevel.error:
        return 'red';
      default:
        return 'white';
    }
  }

}
