import { APP_HTTP_RESULT_CODE } from './../../constants/http/http.constants';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { StringUtils } from 'src/app/utils/string.utils';
import { HttpResult } from 'src/app/common/http/http.result.model';
import { Store } from '@ngrx/store';
import * as fromStore from 'src/app/store/index';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';
import { ChartGenResult, NodeParseInfo } from 'libs/flowchart-ts/flowchart-core/src/lib/models/parse-info.model';
import { Variable } from 'libs/flowchart-ts/flowchart-core/src/lib/models/variable.model';
import { ParseResult } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.enums';

@Injectable({
  providedIn: 'root'
})
export class CodeGenerationService {

  generationResultMap: Map<string, ChartGenResult> = new Map<string, ChartGenResult>();
  variablesMap: Map<string, Variable> = new Map<string, Variable>();
  constructor(private http: HttpClient,
    private store: Store) { }

  async generate(projectId: string, chartId: string) {
    const url = `api/parser/${projectId}/${!chartId ? '' : chartId}`;
    try {
      const result = await this.http.post<any>(url, null).toPromise();
      this.updateResultMap(result, chartId);
      return new HttpResult(APP_HTTP_RESULT_CODE.OK, 'Success');
    } catch (error) {
      return new HttpResult(APP_HTTP_RESULT_CODE.ERROR, error.message);
    }
  }

  async generateRuntimeJson(projectId: string, chartId: string) {
    const url = `api/parser/generate/${projectId}/${chartId === undefined ? '' : chartId}`;
    try {
      const result = await this.http.post<any>(url, null).toPromise();
      return new HttpResult(APP_HTTP_RESULT_CODE.OK, 'Success');
    } catch (error) {
      return new HttpResult(APP_HTTP_RESULT_CODE.ERROR, error.message);
    }
  }

  getChartGenResult(chartId: string) {
    if (!StringUtils.isNullOrWhiteSpace(chartId) && this.generationResultMap.has(chartId)) {
      return this.generationResultMap.get(chartId);
    }
    return null;
  }

  getVariables(chartId: string) {
    if (!StringUtils.isNullOrWhiteSpace(chartId) && this.variablesMap.has(chartId)) {
      return this.variablesMap.get(chartId);
    }
    return null;
  }

  updateNodeGenResults(chart: Chart) {
    const chartGenResult = this.getChartGenResult(chart.uid);
    if (chartGenResult) {
      const newNodes = [];
      for (const n of chart.nodes) {
        n.parseInfo = chartGenResult.nodeGenResultMap.get(n.uid);
      }
    }
  }

  //#region private methods
  updateResultMap(rawData: any, chartId?: string) {
    if (!chartId) {
      for (const ck of Object.keys(rawData.data)) {
        const chartData = rawData.data[ck];
        this.parseChartResult(chartData, ck);
      }
    } else {
      this.parseChartResult(rawData.data, chartId);
    }

  }

  parseChartResult(chartData, chartId: string) {
    const nodesResultMap = new Map<string, NodeParseInfo>();
    for (const key of Object.keys(chartData.nodes)) {
      const nr = chartData.data.nodes[key];
      const nodeGenResult = new NodeParseInfo();
      nodeGenResult.type = nr.nodeType;
      nodeGenResult.result = nr.isError ? ParseResult.ERROR : nr.isWarning ? ParseResult.WARNING : ParseResult.NORMAL;
      nodeGenResult.tokens = nr.tokens;
      nodeGenResult.codes = nr.codes;
      nodesResultMap.set(key, nodeGenResult);
    }
    const chartResult = new ChartGenResult(chartId, nodesResultMap);
    const params = chartData.variables.filter(v => v.isParameter);
    let index = params ? params.length : 0;
    chartResult.variables = chartData.variables.map(v => {
      const newV = new Variable();
      // newV.fromJson(v, this.project)
      newV.index = index++;
      return newV;
    });
    this.generationResultMap.set(chartId, chartResult);
  }
  //#endregion
}
