import { Directive, HostListener } from '@angular/core';

@Directive({
  selector: '[appPreventContextmenu]'
})
export class PreventContextmenuDirective {

  constructor() { }
  @HostListener('contextmenu', ['$event'])
  onRightClick($event) {
    console.log('asdsad');
    $event.preventDefault();
    $event.stopPropagation();
  }
}
