import { ChartJsonKey } from './../constants/chart.constants';
import { ChartType } from './type.model';
export abstract class Item {
    uid: string;
    name: string;
    type: ChartType;
    description: string;
    fromJson(jobj: any, typeConverter: (typeName: string) => ChartType) {
        if (!jobj) {
            throw new Error('json data undefined');
        }
        this.name = jobj[ChartJsonKey.Name];
        this.description = jobj[ChartJsonKey.Description];
        this.uid = jobj[ChartJsonKey.Uid];
        if (typeConverter) {
            this.type = typeConverter(jobj[ChartJsonKey.Type]);
        }
        return true;
    }
}