import { ConnectorPathService } from './../services/chart/connector-path.service';
import { Pipe, PipeTransform } from '@angular/core';
import { Point } from '../models/point.model';
import { CONNECTOR_SHAPE, CONNECTOR_OFFSET } from '../constants/canvas/connector.constants';
import { Connector } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';

@Pipe({
  name: 'connectorPipe'
})
export class ConnectorPipe implements PipeTransform {
  showPath: string;

  constructor(private pathService: ConnectorPathService) {

  }

  // 接收连线类型、起点坐标、终点坐标
  // 返回直线/折线/贝塞尔曲线的svg的path字符串
  transform(connectorType: number, start: Point, end: Point, connector?: Connector): any {
    const isSelfConn = connector && connector.start.uid === connector.end.uid;
    // 连线中 暂时设置为直线
    if (connectorType === CONNECTOR_SHAPE.STRAIGHT_LINE.index  || connector === undefined) {
      // 直线
      if (isSelfConn) {
        return this.pathService.drawSelfConnectorPath(start, end, connector);
      } else {
        this.showPath = `M ${start.x} ${start.y}, L ${end.x} ${end.y}`;
      }
    } else if (connectorType === CONNECTOR_SHAPE.POLY_LINE.index) {
      // 折线
      // return this.pathService.drawPolyline(start, end, connector);
      return this.pathService.drawPolyline2(start, end, connector);
    } else if (connectorType === CONNECTOR_SHAPE.BEZIER_CURVE.index) {
      // 曲线
      if (isSelfConn) {
        return this.pathService.drawSelfConnectorPath(start, end, connector);
      } else {
        return this.pathService.drawCurve(start, end, connector);
      }
    }
    return this.showPath;
  }

}
