import { Action } from '@ngrx/store';
import { Chart } from 'libs/flowchart-ts/flowchart-core/src/lib/models/chart.model';

export const LOAD_CURRENT_CHARTS_LIST = '[Flowchart] Load Current Charts List';
export const LOAD_CURRENT_CHARTS_LIST_FAIL = '[Flowchart] Load Current Charts List Fail';
export const LOAD_CURRENT_CHARTS_LIST_SUCCESS = '[Flowchart] Load Current Charts List Success';

export class LoadCurrentChartsList implements Action {
    readonly type = LOAD_CURRENT_CHARTS_LIST;
    constructor(public payload: string) {}
}

export class LoadCurrentChartsListFail implements Action {
    readonly type = LOAD_CURRENT_CHARTS_LIST_FAIL;
    constructor(public payload: any) { }
}

export class LoadCurrentChartsListSuccess implements Action {
    readonly type = LOAD_CURRENT_CHARTS_LIST_SUCCESS;
    constructor(public payload: Chart[]) { }
}


export const CREATE_CHART = '[Flowchart] Create Chart';
export const CREATE_CHART_SUCCESS = '[Flowchart] Create Chart Success';
export const CREATE_CHART_FAILURE = '[Flowchart] Create Chart Failure';
export const UPDATE_CHART = '[Flowchart] Update Chart';
export const UPDATE_CHART_SUCCESS = '[Flowchart] Update Chart Success';
export const UPDATE_CHART_FAILURE = '[Flowchart] Update Chart Failure';
export const UPDATE_CHART_PROPERTY = '[Flowchart] Update Chart Property';
export const UPDATE_CHART_PROPERTY_SUCCESS = '[Flowchart] Update Chart Property Success';
export const UPDATE_CHART_PROPERTY_FAILURE = '[Flowchart] Update Chart Property Failure';
export const REMOVE_CHART = '[Flowchart] Remove Chart';
export const REMOVE_CHART_SUCCESS = '[Flowchart] Remove Chart Success';
export const REMOVE_CHART_FAILURE = '[Flowchart] Remove Chart Failure';

export class CreateChart implements Action {
    readonly type = CREATE_CHART;
    constructor(public payload: Chart) { }
}

export class CreateChartSuccess implements Action {
    readonly type = CREATE_CHART_SUCCESS;
    constructor(public payload: Chart) { }
}

export class CreateChartFailure implements Action {
    readonly type = CREATE_CHART_FAILURE;
    constructor(public payload: Chart) { }
}

export class UpdateChart implements Action {
    readonly type = UPDATE_CHART;
    constructor(public payload: Chart) { }
}
export class UpdateChartSuccess implements Action {
    readonly type = UPDATE_CHART_SUCCESS;
    constructor(public payload: Chart) { }
}
export class UpdateChartFailure implements Action {
    readonly type = UPDATE_CHART_FAILURE;
    constructor(public payload: Chart) { }
}

export class UpdateChartProperty implements Action {
    readonly type = UPDATE_CHART_PROPERTY;
    constructor(public payload: {chart: Chart, props: string[]}) { }
}
export class UpdateChartPropertySuccess implements Action {
    readonly type = UPDATE_CHART_PROPERTY_SUCCESS;
    constructor(public payload: {chart: Chart, props: string[]}) { }
}
export class UpdateChartPropertyFailure implements Action {
    readonly type = UPDATE_CHART_PROPERTY_FAILURE;
    constructor(public payload: {chart: Chart, props: string[]}) { }
}


export class RemoveChart implements Action {
    readonly type = REMOVE_CHART;
    constructor(public payload: Chart) { }
}

export class RemoveChartSuccess implements Action {
    readonly type = REMOVE_CHART_SUCCESS;
    constructor(public payload: Chart) { }
}

export class RemoveChartFailure implements Action {
    readonly type = REMOVE_CHART_FAILURE;
    constructor(public payload: Chart) { }
}

export type CurrentChartsListAction =
    | LoadCurrentChartsList
    | LoadCurrentChartsListFail
    | LoadCurrentChartsListSuccess
    | CreateChart
    | CreateChartSuccess
    | CreateChartFailure
    | UpdateChart
    | UpdateChartSuccess
    | UpdateChartFailure
    | UpdateChartProperty
    | UpdateChartPropertySuccess
    | UpdateChartPropertyFailure
    | RemoveChart
    | RemoveChartSuccess
    | RemoveChartFailure;
