import { APP_HTTP_RESULT_CODE } from './../../constants/http/http.constants';
import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { HttpResult } from 'src/app/common/http/http.result.model';
import { EventType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/event.model';

@Injectable({
  providedIn: 'root'
})
export class EventsService {

  private _events: EventType[];
  constructor(private http: HttpClient) { }

  async getEvents(project: string) {
    const url = `api/type/events/${project}`;
    const result = await this.http.get<HttpResult<EventType[]>>(url).toPromise();
    if (result.code === APP_HTTP_RESULT_CODE.OK) {
      result.data = result.data;
      this._events = result.data;
    }
    return result;
  }

  async updateEvents(project: string, events: EventType[]) {
    const url = `api/type/events/update/${project}`;
    const result = await this.http.post<HttpResult<boolean>>(url, events.map(m => m.toJson())).toPromise();
    return result;
  }

  async getEventsLocalFirst(project) {
    if(this._events) {
      return this._events;
    }
    await this.getEvents(project);
    return this._events;
  }
}
