import { Chart, ChartNode, ChartType, Project, Variable } from "./../../../../flowchart-core/src/public-api";

export class Context {
    variablesMap: Map<string, Variable>;
    undefinedVarNode: ChartNode[];
    chartReturnType: ChartType;
    constructor() {
        this.variablesMap = new Map<string, Variable>();
        this.undefinedVarNode = [];
    }

    reset() {
        this.variablesMap.clear();
        this.undefinedVarNode = [];
        this.chartReturnType = null;
    }

    addVariable(v: Variable) {
        if (this.variablesMap.has(v.name)) {
            return;
        }
        const variable = new Variable();
        variable.name = v.name;
        variable.index = this.variablesMap.size;
        variable.type = v.type;
        variable.isParameter = v.isParameter;
        this.variablesMap.set(v.name, variable);
    }

    getVariable(name: string) {
        if (name.startsWith('$')) {
            name = name.substr(1);
        }
        if (this.variablesMap.has(name)) {
            const v = this.variablesMap.get(name);
            return v;
        }
        return null;
    }
}


export class ParseError extends Error {
    constructor(msg?: string) {
        super(msg);
        this.name = 'ParseError';
    }
}

export class TypeError extends ParseError {
    leftType: ChartType;
    rightType: ChartType;
}

export class UndefinedTypeError extends ParseError {
    constructor() {
        super('Undefined error');
        this.name = 'Undefined error';
    }
}

export class MemberNotFoundError extends ParseError {
    typeName: string;
    ownerText: string;
    memberName: string;

    get message() {
        return `Cannot find ${this.memberName} in "${this.ownerText}"(${this.typeName})`
    }
}