import { getFlowChartState, FlowchartState } from '../../reducers/index';
import * as CurrentProjectReducer from '../../reducers/projects/current-project.reducer';
import { createSelector } from '@ngrx/store';

export const getCurrentProjectState = createSelector(getFlowChartState, (state: FlowchartState) => state.currentProject);

export const getCurrentProject = createSelector(getCurrentProjectState, CurrentProjectReducer.getCurrentProject);

export const getSelectedItem = createSelector(getCurrentProjectState, CurrentProjectReducer.getSelectedItem);

export const getTypes = createSelector(getCurrentProjectState, CurrentProjectReducer.getTypes);

export const getEvents = createSelector(getCurrentProjectState, CurrentProjectReducer.getEvents);
