import { Pipe, PipeTransform } from '@angular/core';
import { ConnectorPathService } from '../services/chart/connector-path.service';
import { Point } from '../models/point.model';
import { Connector, ShapeType } from 'libs/flowchart-ts/flowchart-core/src/lib/models/connector.model';

@Pipe({
  name: 'connectorPath'
})
export class ConnectorPathPipe implements PipeTransform {

  constructor(private connectorPathService: ConnectorPathService) { }
  transform(connector: Connector, ...args: any[]): any {
    const isSelfConn = connector && connector.start.uid === connector.end.uid;
    const start: Point = new Point(connector.start.rect.cx, connector.start.rect.y + connector.start.rect.h);
    const end: Point = new Point(connector.end.rect.cx, connector.end.rect.y);
    let showPath;
    switch (connector.shape) {
      case ShapeType.LINE:
        // 直线
        if (isSelfConn) {
          return this.connectorPathService.drawSelfConnectorPath(start, end, connector);
        } else {
          showPath = `M ${start.x} ${start.y}, L ${end.x} ${end.y}`;
        }
        break;
      case ShapeType.CURVE:
        // 曲线
        if (isSelfConn) {
          return this.connectorPathService.drawSelfConnectorPath(start, end, connector);
        } else {
          return this.connectorPathService.drawCurve(start, end, connector);
        }
      // 折线
      case ShapeType.POLYLINE:
        // return this.connectorPathService.drawPolyline(start, end, connector);
        return this.connectorPathService.drawPolyline2(start, end, connector);
    }
    return showPath;
  }

}
