import { createSelector } from '@ngrx/store';
import { getFlowChartState, FlowchartState } from '../../reducers';
import * as fromCurrentChart from '../../reducers/charts/current-chart.reducer';

export const getCurrentChartState = createSelector(
    getFlowChartState,
    (state: FlowchartState) => state.currentChart
);

export const getCurrentChartDetails = createSelector(getCurrentChartState, fromCurrentChart.getCurrentChart);
export const getCurrentChartIndex = createSelector(getCurrentChartState, fromCurrentChart.getCurrentChartIndex);
export const getOpeningCharts = createSelector(getCurrentChartState, fromCurrentChart.getOpeningCharts);
export const getStoredCharts = createSelector(getCurrentChartState, fromCurrentChart.getStoredCharts);
export const getCurrentNodes = createSelector(getCurrentChartState, fromCurrentChart.getCurrentNodes);
export const getCurrentChartLoaded = createSelector(getCurrentChartState, fromCurrentChart.getCurrentChartLoaded);
export const getCreatedNewNodes = createSelector(getCurrentChartState, fromCurrentChart.getCreatedNewNodes);
export const getCreatedNewParameter = createSelector(getCurrentChartState, fromCurrentChart.getCreatedNewParameter);
export const getCreatedNewVariable = createSelector(getCurrentChartState, fromCurrentChart.getCreatedNewVariable);
export const getSelectedChartData = createSelector(getCurrentChartState, fromCurrentChart.getSelectedItems);
export const getVariablesChange = createSelector(getCurrentChartState, fromCurrentChart.getCurrentChartVariables);
export const getParametersChange = createSelector(getCurrentChartState, fromCurrentChart.getCurrentChartParameters);
