import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';
import { Point } from 'src/app/models/point.model';

export class Drag {
    s: Point;
    e: Point;
    r: Rect = Rect.createWithCenter(0, 0, 0, 0);
    delta: Point;
    get rect(): Rect {
        this.r.x = Math.min(this.s.x, this.e.x);
        this.r.y = Math.min(this.s.y, this.e.y);
        this.r.w = Math.abs(this.s.x - this.e.x);
        this.r.h = Math.abs(this.s.y - this.e.y);
        return this.r;
    }

    constructor(startX: number = 0, startY: number = 0, endX: number = 0, endY: number = 0) {
        this.s = new Point(startX, startY);
        this.e = new Point(endX, endY);
    }

    start(start: Point) {
        this.s.x = start.x;
        this.s.y = start.y;
    }

    move(end: Point) {
        this.delta = new Point(0, 0);
        this.e.x = end.x;
        this.e.y = end.y;
        this.delta.x = this.s.x - this.e.x;
        this.delta.y = this.s.y - this.e.y;
    }
}
