import { createSelector } from '@ngrx/store';
import { getFlowChartState, FlowchartState } from '../../reducers';
import * as fromCurrentChartsList from '../../reducers/charts/current-charts-list.reducer';

export const getCurrentChartsListState = createSelector(
    getFlowChartState,
    (state: FlowchartState) => state.currentChartsList
);

export const getChartListSelector = createSelector(getCurrentChartsListState, fromCurrentChartsList.getCurrentChartsList);

export const getCurrentChartsLoadState = createSelector(
    getCurrentChartsListState,
    fromCurrentChartsList.getCurrentChartsListLoadState
);
