import { ChartType } from "libs/flowchart-ts/flowchart-core/src/lib/models/type.model";
import { EventType } from "libs/flowchart-ts/flowchart-core/src/public-api";

export class ChartFolder {
    name: string;
    path: string;
    defaultType: string;
    level: number;
    children: ChartFolder[];
}

export class CreateProject {
    uid: string;
    name: string;
    path: string;
    generator: string;
    description: string;
    folders: ChartFolder[];
    types: ChartType[];
    events: EventType[];
    constructor() {
        this.name = '';
        this.path = '';
        this.generator = 'lua';
        this.folders = [];
        this.types = [];
        this.events = [];
    }
}
