import { ChartType } from "./../../../../flowchart-core/src/lib/models/type.model";
import { BinaryOpNode } from "./../../../../flowchart-parser/src/lib/nodes/ast-node.model";
import { AstNodeInfo, BaseGenerator } from "./base-generator";

export class JsGenerator extends BaseGenerator {

    readonly tag = 'js';

    getFileExtension(){
        return 'js';
    }

    fileHeaderLines() {
        return [''];
    }

    getInvokeOperator() {
        return '.';
    }
    
    generateCode(ni: AstNodeInfo) {
        if (!this.parseInfo.isControlNode) {
            this.codes = [];
            this.codes.push(`// ${this.currentNode.text}`);
            this.codes.push(`function ${ni.functionName}(self){`);
            this.codes.push(`    var ret = ${ni.code}`);
            if (ni.type === ChartType.BoolType) {
                this.codes.push(`    return ${ni.code === 'true' ? 'true' : 'false'}`);
            } else {
                this.codes.push(`    return true`);
            }
            this.codes.push("}");
            this.codes.push(`asyncflow.node_funcs["${ni.functionName}"] = ${ni.functionName}`);
            this.codes.push("");
        }
    }
    translateOp(node: BinaryOpNode) {
        
    }
    
}