    @Effect()
    removeNodes$ = this.actions$.pipe(
        ofType(nodesActions.REMOVE_NODE),
        mergeMap((action: nodesActions.RemoveNode) =>
            this.nodesService.removeNode(action.payload, action.relatedConns)
                .then((result: any) => {
                    if (result.code === 0) {
                        this.messageService.publish(new MessageModel(MSG_FLAG.REMOVED_NODES, action.payload));
                        return new nodesActions.RemoveNodeSuccess(action.payload);
                    } else {
                        return new nodesActions.RemoveNodeFail(action.payload);
                    }
                }).catch(err => new RemoveNodeFail(action.payload))
        )
    );

