import { CommonMenuService } from 'src/app/services/chart/common-menu.service';
import { OperationService } from './../../../../services/chart/operation.service';
import { EventsService } from './../../../../services/project/events.service';
import { APP_HTTP_RESULT_CODE } from './../../../../constants/http/http.constants';
import { debounceTime, distinctUntilChanged } from 'rxjs/operators';
import { Subject } from 'rxjs';
import { CodeHintService } from './../../../../code-hint/services/code-hint.service';
import { debugColorTrans } from './../../../../common/anims/debug-color-transition';
import { ShowingRunningStatus } from './../../../../debug/models/debug-node-status.model';
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, EventEmitter, Input, NgZone, OnInit, Output, SimpleChanges, ViewChild } from '@angular/core';
import { CanvasService } from 'src/app/services/chart/canvas.service';
import { NodesService } from 'src/app/services/chart/nodes.service';
import { MessageService } from 'src/app/services/common/message.service';
import { Point } from 'src/app/models/point.model';
import { KEY_CODE } from 'src/app/constants/common/key.code.constants';
import { MSG_FLAG } from 'src/app/constants/common/message.flags.constants';

import { Store } from '@ngrx/store';
import * as fromStore from '../../../../store';
import { OutputService } from 'src/app/services/common/output.service';
import { Group } from 'libs/flowchart-ts/flowchart-core/src/lib/models/group.model';
import { ChartNode } from 'libs/flowchart-ts/flowchart-core/src/lib/models/node.model';
import { NodeViewType } from 'libs/flowchart-ts/flowchart-core/src/lib/constants/node.enums';
import { Rect } from 'libs/flowchart-ts/flowchart-common/src/lib/models/rect.model';
import { UpdateNodeCommand } from 'src/app/models/commands/node.commands';
import { CompletionItem, CompletionItemType } from 'libs/flowchart-ts/flowchart-generator/src/lib/auto-complete';
@Component({
  selector: 'app-common-node',
  templateUrl: './common-node.component.html',
  styleUrls: ['./common-node.component.css'],
  animations: [debugColorTrans],
  // changeDetection: ChangeDetectionStrategy.OnPush
})
export class CommonNodeComponent implements OnInit {

  constructor(
    private canvasService: CanvasService,
    private nodesService: NodesService,
    private messageService: MessageService,
    private cdref: ChangeDetectorRef,
    private ngZone: NgZone,
    private store: Store<fromStore.FlowchartState>,
    private codehintService: CodeHintService,
    private outputService: OutputService,
    private eventService: EventsService,
    private operationService: OperationService,
    private commonMenuService: CommonMenuService
  ) { }
  @ViewChild('textInput', { static: false }) textInput;
  @ViewChild('nodeText', { static: false }) nodeText: ElementRef;
  @ViewChild('nodeRect', { static: false }) nodeRect: ElementRef;
  @ViewChild('codeHint', { static: false }) codeHint: ElementRef;
  @Input() delta: Point = new Point(0, 0);
  @Input() node: ChartNode;
  @Input() isEditing = false;
  @Output() isEditingChange: EventEmitter<boolean> = new EventEmitter<boolean>();
  @Input() index: number;
  @Output() nodeClick: EventEmitter<any> = new EventEmitter<any>();
  @Input() runningStatus: ShowingRunningStatus;
  isTransitingColor = false;
  statusList: ShowingRunningStatus[] = [];
  temporary: Point;
  toolIconHeight = 24;
  propertyHeight = 20;
  // 节点是否在编辑
  isInputting = false;
  // 当前输入框的宽度
  inputWidth: number;
  // 未加入group的节点正hover于某个group上方
  hoveringGroup: Group = null;

  // 输入框临时变量
  inputValue: string;

  // 当前节点是否被选中
  @Input() isSelected = false;
  // 抛出点击位置
  @Input() mousedownLocation: Point;
  @Output() mousedownLocationChange: EventEmitter<Point> = new EventEmitter();
  // 抛出右键事件
  @Output() nodeContextEvent: EventEmitter<Point> = new EventEmitter();
  // 抛出更新选中节点列表的事件
  @Output() updateSelectedNodesList: EventEmitter<void> = new EventEmitter();

  // 节点的三种操作：添加子节点、连线、编辑节点
  @Output() appendChild: EventEmitter<ChartNode> = new EventEmitter();
  @Output() iconToConnect: EventEmitter<number> = new EventEmitter();
  @Output() updateNode = new EventEmitter<{ node: ChartNode, local: boolean, textChanged: boolean }>();

  @ViewChild('hiddenText', { static: false }) hiddenText: ElementRef;


  @Output() tempCoordinateChange: EventEmitter<Point> = new EventEmitter();

  @Output() keydown: EventEmitter<any> = new EventEmitter<any>();

  codeHintLeft = 40;
  hintList: CompletionItem[];
  selectedHintIndex = -1;
  codeInputUpdate: Subject<any> = new Subject<any>();

  // 是否事件
  isEvent: boolean;
  // 是否子图
  isSubchart: boolean;
  ngOnInit() {
    this.initNode();
    this.startInputCheck();
  }

  initNode() {
    this.temporary = new Point(this.node.rect.x, this.node.rect.y);
    this.inputValue = this.node.text;
    // this.isEvent = this.checkIsEvent();
    // this.isSubchart = this.checkIsSubchart();
  }

  startInputCheck() {
    this.codeInputUpdate.pipe(
      debounceTime(500),
      distinctUntilChanged())
      .subscribe($event => {
        console.log($event);
        this.codeHintLeft = $event.target.value.length * 9 + 40;
        this.codehintService.getHintList(this.node.chart, $event.target.value, $event.target.selectionEnd).then(result => {
          if (result.code === APP_HTTP_RESULT_CODE.OK) {
            this.hintList = result.data.filter(item => item.weight > -100);
          } else {
            this.hintList = [];
          }
          this.selectedHintIndex = -1;
          try {
            this.cdref.detectChanges();
          } catch {
          }
        });
      });
  }

  ngAfterViewInit(): void {
    // console.log('afterViewInit', this.node.uid);
    // const textEle = document.getElementById('node-text' + this.node.uid);
    // console.log(textEle);
    // this.nodesService.updateRealNodeView(this.node);
    // this.cdref.detectChanges();
  }

  ngOnChanges(changes: SimpleChanges): void {
    // this.updateSelectedNodesList.emit();
    if (changes.isEditing) {
      if (changes.isEditing.currentValue) {
        this.editNode(false);
      }
    }
  }

  mousedownNode($event) {
    this.messageService.publish({ flag: MSG_FLAG.SHOW_NODE_PROPERTY, content: '', args: { data: null } });
    if (this.isEditing) {
      return;
    }
    const leftClick = $event.button === 0;
    const rightClick = $event.button === 2;
    const length = this.nodesService.getSelectedNodesNum(this.node.chart);

  }

  MoveNode(x: number, y: number) {
    this.temporary.x = this.node.rect.x + (-x);
    this.temporary.y = this.node.rect.y + (-y);
  }

  updateNodeText(text: string) {
    // 节点内容处理
    const nodeview = document.getElementById(this.node.uid);
    const curHeight = nodeview.clientHeight;
    const width = this.hiddenText.nativeElement.clientWidth;
    const rect = Rect.createWithCenter(this.node.rect.cx, this.node.rect.cy,
      this.nodesService.calculateNodeRenderWidth(text.length, NodeViewType.COMMON, this.node.text), curHeight);
    const cmd = new UpdateNodeCommand(this.node, new Map<string, any>([['text', text], ['rect', rect]]));
    this.operationService.executeChartCommands(this.node.chart.uid, [cmd]);
    this.commonMenuService.generate(this.node.chart);
    this.cdref.detectChanges();
    this.updateNode.emit({ node: this.node, local: false, textChanged: true });
    // this.store.dispatch(new fromStore.UpdateNode(newNode));

  }

  openNodeMenu($event) {
    if (!$event.ctrlKey) {
      // this.isSelected = true;
      const menuLocation = new Point($event.clientX, $event.clientY);
      this.nodeContextEvent.emit(menuLocation);
    }
  }

  editNode(emit: boolean = true) {
    const textEle = document.getElementById('hidden-text' + this.node.uid);
    if (textEle) {
      this.inputWidth = textEle.offsetWidth + 15;
    }
    if (emit) {
      this.setEdit(true);
    }
    setTimeout(() => {
      if (this.textInput) {
        this.textInput.nativeElement.focus();
        this.textInput.nativeElement.selectionStart = 0;
        this.textInput.nativeElement.selectionEnd = this.inputValue.length;
      }
    }, 10);
  }

  onInputKeyDown($event) {
    $event.stopPropagation();
    setTimeout(() => {
      this.inputWidth = this.hiddenText.nativeElement.offsetWidth + 20;
      switch ($event.keyCode) {
        case KEY_CODE.ENTER:
          if (this.selectedHintIndex !== -1) {
            const item = this.hintList[this.selectedHintIndex];
            this.autoCompleteInput(item.type, $event.target.selectionStart, item.text, item.replaceStr);
            this.cdref.detectChanges();
          } else {
            $event.target.blur();
          }
          break;
        case KEY_CODE.UP:
          $event.preventDefault();
          if (this.hintList && this.hintList.length > 1) {
            this.selectedHintIndex = this.selectedHintIndex > 0 ? this.selectedHintIndex - 1 : this.hintList.length - 1;
          }
          break;
        case KEY_CODE.DOWN:
          $event.preventDefault();
          if (this.hintList && this.hintList.length >= 1) {
            this.selectedHintIndex = this.selectedHintIndex < this.hintList.length - 1 ? this.selectedHintIndex + 1 : 0;
            this.cdref.detectChanges();
            this.autoScroll();
          }
          break;
      }
      this.cdref.detectChanges();
    }, 10);
  }

  selectHint(item, index, e) {
    e.stopPropagation();
    e.preventDefault();
    this.selectedHintIndex = index;
    this.autoCompleteInput(item.itemType, this.textInput.nativeElement.selectionStart, item.text, item.replaceStr);
  }

  autoCompleteInput(type: CompletionItemType, index: number, text: string, replaceStr: string) {
    const prefix = this.inputValue.substr(0, index - replaceStr.length);
    const suffix = this.inputValue.substr(index);
    switch (type) {
      case CompletionItemType.FUNCTION:
      case CompletionItemType.SUBCHART:
        // 函数类型则需要自动聚焦到括号内，index加1
        this.inputValue = prefix + text + '()' + suffix;
        index++;
        break;
      case CompletionItemType.EVENT:
      case CompletionItemType.PROPERTY:
      case CompletionItemType.VAR:
      default:
        this.inputValue = prefix + text + suffix;
        break;
    }
    this.ngZone.runOutsideAngular(() => {
      setTimeout(() => {
        this.inputWidth = this.hiddenText.nativeElement.offsetWidth + 20;
        this.textInput.nativeElement.focus();
        this.textInput.nativeElement.selectionStart = index + text.length - replaceStr.length;
        this.textInput.nativeElement.selectionEnd = index + text.length - replaceStr.length;
        this.cdref.detectChanges();
      }, 100);
    })
    this.clearCodeHint();
  }

  autoScroll() {
    if (this.codeHint) {
      if (this.selectedHintIndex >= 10) {
        this.codeHint.nativeElement.scrollTo(0, (this.selectedHintIndex - 9) * 20);
      } else {
        if (this.codeHint.nativeElement.scrollTop > 0) {
          this.codeHint.nativeElement.scrollTo(0, 0);
        }
      }
    }
  }

  clearCodeHint() {
    this.selectedHintIndex = -1;
    this.hintList = [];
  }

  // 失去编辑焦点
  onEditBlur($event) {
    $event.stopPropagation();
    this.inputValue = $event.target.value;
    if (this.inputValue === 'Start') {
      this.outputService.error('Start节点已存在');
      return;
    }
    this.setEdit(false);
    this.updateNodeText(this.inputValue);
  }

  onNodeClick($event) {
    this.nodeClick.emit($event);
  }

  onInputClick($event) {
    $event.stopPropagation();
    $event.preventDefault();
  }

  setEdit(isEditing: boolean) {
    this.isEditing = isEditing;
    this.isEditingChange.emit(this.isEditing);
  }

  async checkIsEvent() {
    if (this.node.text && this.node.text.startsWith('On')) {
      const eventName = this.node.text.substr(2);
      this.eventService.getEventsLocalFirst(this.node.chart.project);
    }
    return false;
  }
}
