import { NodeCommandParser, NodeContext } from './../antlr/NodeCommandParser';
import { NodeCommandLexer } from './../antlr/NodeCommandLexer';
import { ANTLRInputStream, CommonTokenStream } from "antlr4ts";
import { AstNode } from "./nodes/ast-node.model";
import { PredictionMode } from 'antlr4ts/atn/PredictionMode';
import { AstGenerator } from './ast-generator';

export interface IParser {
    parse(str: string): ParseResult;
}

export class ParseResult {
    node: AstNode;
    tokenStream: CommonTokenStream;
}

export class AntlrParser implements IParser {


    parse(str: string) {
        const result = new ParseResult();
        const input = new ANTLRInputStream(str);
        const lexer = new NodeCommandLexer(input);
        const tokenStream = new CommonTokenStream(lexer);
        const parser = new NodeCommandParser(tokenStream);
        parser.interpreter.setPredictionMode(PredictionMode.SLL);
        let tree: NodeContext;
        try {
            tree = parser.node();
        } catch (error) {
            parser.reset();
            parser.interpreter.setPredictionMode(PredictionMode.LL);
            tree = parser.node();
        }
        const visitor = new AstGenerator();
        var node = visitor.visit(tree);
        result.node = node;
        result.tokenStream = tokenStream;
        return result;
    }
}