import { TestComponent } from './components/test/test.component';
import { DebugdemoComponent } from './debug/components/debugdemo/debugdemo.component';
import { ProjectBasicConfigPanelComponent } from './components/project/project-basic-config-panel/project-basic-config-panel.component';
import { ProjectEditorComponent } from './components/project/project-editor/project-editor.component';
// import { MainComponent } from './main/components/main/main.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BeginPageComponent } from './components/begin/begin-page.component';
import { MainComponent } from './components/main/main.component';


const routes: Routes = [
  { path: '', component: BeginPageComponent },
  { path: 'project/:projectId', component: MainComponent },
  {
    path: 'create', children: [
      {
        path: 'project', component: ProjectEditorComponent, children: [
          { path: 'config', component: ProjectBasicConfigPanelComponent }
        ]
      }
    ]
  },
  { path: 'debugdemo', component: DebugdemoComponent},
  { path: 'test', component: TestComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
