import { Component, OnInit, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'app-project-bar',
  templateUrl: './project-bar.component.html',
  styleUrls: ['./project-bar.component.css']
})
export class ProjectBarComponent implements OnInit {
  @Input() width;
  @Input() height;
  @Input() barData;
  @Input() bottomSelectIndex;
  @Output() bottomSelectIndexChange: EventEmitter<number> = new EventEmitter();

  constructor() { }

  ngOnInit() {
  }

  handleClickProjectBar(index: number) {
    this.bottomSelectIndexChange.emit(index);
  }

}
